import { DataSchemaArray } from '@mintlify/validation';
import { OpenAPIV3_1 } from 'openapi-types';

type ObjectSchemaProperties =
  | {
      [key: string]: DataSchemaArray;
    }
  | {
      [key: string]: OpenAPIV3_1.SchemaObject;
    };

type ArraySchemaItems = DataSchemaArray | OpenAPIV3_1.SchemaObject;

type FlattenedObject = {
  [key: string]: string;
};

export function flattenObject(
  obj: ObjectSchemaProperties | ArraySchemaItems,
  prefix = '',
  result: FlattenedObject = {}
): FlattenedObject {
  for (const key in obj) {
    const newKey = prefix ? `${prefix}.${key}` : key;
    const value = obj[key as keyof typeof obj];
    if (typeof value === 'object') {
      flattenObject(value, newKey, result);
    } else {
      result[newKey] = value;
    }
  }
  return result;
}
