import { DataSchema, IncrementalDataSchema } from '@mintlify/validation';

import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';

import { generateNextItems } from './generateNextItems';

const MAX_RECURSIVE_DEPTH = 5;

export const generateTypeStringFromSchema = (
  schema: DataSchema | IncrementalDataSchema,
  componentSchemas: ComponentSchemas | undefined,
  nullable: boolean,
  depth: number = 0
): string => {
  let recursionLimitReached = false as boolean;
  const nullSuffix = nullable ? ' | null' : '';
  switch (schema.type) {
    case 'null':
      return 'null';
    case 'array':
      const items = generateNextItems(schema, componentSchemas, undefined);
      // prevent possibility of infinite recursion
      if (items.some((schema) => schema.type === 'array') && depth >= MAX_RECURSIVE_DEPTH) {
        recursionLimitReached = true;
        return `array${nullSuffix}`;
      }

      const uniqueTypes = new Set();
      items.forEach((itemSchema) =>
        uniqueTypes.add(
          generateTypeStringFromSchema(itemSchema, componentSchemas, false, depth + 1)
        )
      );
      const uniqueTypesArray = Array.from(uniqueTypes);

      if (recursionLimitReached) {
        return `array${nullSuffix}`;
      }
      if (uniqueTypesArray.length === 1) {
        return `${uniqueTypesArray[0]}[]${nullSuffix}`;
      }
      if (uniqueTypesArray.every((type) => type !== 'object' && type !== 'array')) {
        return `(${uniqueTypesArray.join(' | ')})[]${nullSuffix}`;
      }
      return `array${nullSuffix}`;
    default:
      return `${schema.type}${nullSuffix}`;
  }
};
