'use client';

import { StringSchema } from '@mintlify/validation';

import { cn } from '@/utils/cn';

export const StringMetadata = ({
  schema,
  description,
}: {
  schema: StringSchema;
  description?: string;
}) => {
  return (
    <>
      {schema.const !== undefined && (
        <div className={cn('prose prose-sm prose-gray dark:prose-invert', description && 'mt-6')}>
          Allowed value: <code>{`"${schema.const}"`}</code>
        </div>
      )}
      {(schema.minLength !== undefined || schema.maxLength !== undefined) && (
        <div className={cn('prose prose-sm prose-gray dark:prose-invert', description && 'mt-6')}>
          <StringLength minLength={schema.minLength} maxLength={schema.maxLength} />
        </div>
      )}
    </>
  );
};

const StringLength = ({ minLength, maxLength }: { minLength?: number; maxLength?: number }) => {
  if (minLength === maxLength && minLength !== undefined) {
    return (
      <>
        Required string length: <code>{minLength}</code>
      </>
    );
  }
  if (minLength && maxLength) {
    return (
      <>
        Required string length: <code>{`${minLength} - ${maxLength}`}</code>
      </>
    );
  }
  if (minLength) {
    return (
      <>
        Minimum length: <code>{minLength}</code>
      </>
    );
  }
  if (maxLength) {
    return (
      <>
        Maximum length: <code>{maxLength}</code>
      </>
    );
  }
  return '';
};
