'use client';

import { useContext, useMemo } from 'react';

import { ApiReferenceContext } from '@/contexts/ConfigContext';

import { useMDXContent } from './useMDXContent';

// we sometimes only want the OpenAPI endpoint (e.g. Endpoint Fields)
export const useOpenApiEndpoint = () => {
  const {
    apiReferenceData: { endpoint },
  } = useContext(ApiReferenceContext);

  return endpoint;
};

export const useHasOpenApiEndpoint = () => {
  const openApiEndpoint = useOpenApiEndpoint();

  return openApiEndpoint !== undefined;
};

export const useEndpoint = () => {
  const openApiEndpoint = useOpenApiEndpoint();
  const [{ endpoint: mdxEndpoint }] = useMDXContent();

  return useMemo(() => {
    // if both exist, prefer OpenAPI
    return openApiEndpoint ?? mdxEndpoint;
  }, [openApiEndpoint, mdxEndpoint]);
};

export const useHasEndpoint = () => {
  const endpoint = useEndpoint();

  return endpoint !== undefined;
};
