'use client';

import { createContext, ReactNode, useState, SetStateAction, Dispatch } from 'react';

import { WebSocketMessage } from '@/hooks/useWebsocketConnection';

type AsyncApiPlaygroundContextType = {
  result: WebSocketMessage[];
  setResult: Dispatch<SetStateAction<WebSocketMessage[]>>;
  address: string;
  setAddress: (address: string) => void;
  clearResult: () => void;
  onMessage: (message: WebSocketMessage) => void;
  parameterValues: Record<string, unknown>;
  setParameterValues: (values: Record<string, unknown>) => void;
  handleParameterChange: (parameterId: string, value: unknown) => void;
  handleSecuritySchemeChange: (type: string, value: string, name: string) => void;
  securitySchemeValues: Record<string, { name: string; value: string }>;
};

export const AsyncApiPlaygroundContext = createContext<AsyncApiPlaygroundContextType>({
  result: [],
  setResult: () => {},
  address: '',
  setAddress: () => {},
  clearResult: () => {},
  onMessage: () => {},
  parameterValues: {},
  setParameterValues: () => {},
  handleParameterChange: () => {},
  handleSecuritySchemeChange: () => {},
  securitySchemeValues: {},
});

export const AsyncApiPlaygroundContextController = ({ children }: { children: ReactNode }) => {
  const [result, setResult] = useState<WebSocketMessage[]>([]);
  const [address, setAddress] = useState<string>('');
  const [parameterValues, setParameterValues] = useState<Record<string, unknown>>({});
  const [securitySchemeValues, setSecuritySchemeValues] = useState<
    Record<string, { name: string; value: string }>
  >({});

  const clearResult = () => {
    setResult([]);
  };

  const onMessage = (message: WebSocketMessage) => {
    setResult((prev) => [...prev, message]);
  };

  const handleParameterChange = (parameterId: string, value: unknown) => {
    setParameterValues({
      ...parameterValues,
      [parameterId]: value,
    });
  };

  const handleSecuritySchemeChange = (type: string, value: string, name: string) => {
    setSecuritySchemeValues({
      ...securitySchemeValues,
      [type]: {
        name: name,
        value: value,
      },
    });
  };

  return (
    <AsyncApiPlaygroundContext.Provider
      value={{
        result,
        setResult,
        address,
        setAddress,
        clearResult,
        onMessage,
        parameterValues,
        setParameterValues,
        handleParameterChange,
        handleSecuritySchemeChange,
        securitySchemeValues,
      }}
    >
      {children}
    </AsyncApiPlaygroundContext.Provider>
  );
};
