'use client';

import type { LocaleType } from '@mintlify/models';
import { ChevronDownIcon, ChevronUpIcon } from 'lucide-react';
import { useContext, useEffect, useMemo, useState } from 'react';

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/Dropdown';
import { NavigationContext } from '@/contexts/NavigationContext';
import { cn } from '@/utils/cn';
import { getLocale, localeToBCP47 } from '@/utils/locales';

export const getFlag = (locale: LocaleType): React.ReactNode => {
  const localeObject = new Intl.Locale(localeToBCP47(locale));
  const country = localeObject.region;
  return (
    <img
      className="w-full h-full rounded-full"
      alt={country}
      src={`https://purecatamphetamine.github.io/country-flag-icons/1x1/${country}.svg`}
    />
  );
};

export function LocalizationSelect({
  triggerClassName,
  triggerLabelClassName,
}: {
  triggerClassName?: string;
  triggerLabelClassName?: string;
}) {
  const { divisions } = useContext(NavigationContext);
  const { selectedLocale, setSelectedLocale, locales } = useContext(NavigationContext);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    if (selectedLocale && document.documentElement.lang !== selectedLocale) {
      document.documentElement.lang = selectedLocale;
    }
  }, [selectedLocale]);

  const localizationOptions = useMemo(() => {
    return locales?.map((locale) => {
      return {
        label: getLocale(locale).language,
        value: locale,
        icon: (
          <div className="relative w-4 h-4 rounded-full">
            {getFlag(locale)}
            <div className="absolute top-0 left-0 w-full h-full border rounded-full bg-primary-light/10 border-black/10" />
          </div>
        ),
      };
    });
  }, [locales]);

  if (!localizationOptions) return null;

  const selectedOption = localizationOptions.find((option) => option.value === selectedLocale);

  const onChange = (locale: LocaleType) => {
    setSelectedLocale(locale);
  };

  if (!divisions.languages.length) return null;

  return (
    <DropdownMenu open={isOpen} onOpenChange={setIsOpen}>
      <DropdownMenuTrigger
        className={cn(
          'text-xs text-gray-500 dark:text-gray-400 leading-5 font-semibold border border-gray-200 dark:border-gray-800 hover:border-gray-300 dark:hover:border-gray-700 rounded-full py-1 pl-2 pr-3 flex items-center whitespace-nowrap',
          triggerClassName
        )}
      >
        {selectedOption?.icon}
        <p className={cn('truncate pl-0.5 pr-0.5', triggerLabelClassName)}>
          {selectedOption?.label}
        </p>
        {isOpen ? (
          <ChevronUpIcon size={12} strokeWidth={2.5} />
        ) : (
          <ChevronDownIcon size={12} strokeWidth={2.5} />
        )}
      </DropdownMenuTrigger>
      <DropdownMenuContent
        className="min-w-36"
        style={{ width: 'var(--radix-dropdown-menu-trigger-width)' }}
      >
        {localizationOptions.map((option) => {
          const isSelected = option.value === selectedOption?.value;
          return (
            <DropdownMenuItem
              key={option.value}
              onSelect={() => onChange(option.value as LocaleType)}
              className={cn(
                'flex justify-start gap-2 p-1.5 text-xs font-medium',
                isSelected && 'font-semibold'
              )}
              isSelected={isSelected}
            >
              {option.icon}
              <p className="flex-1 text-left">{option.label}</p>
            </DropdownMenuItem>
          );
        })}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
