'use client';

import { ReactNode, useContext, useEffect, useState } from 'react';

import { zIndex } from '@/constants/zIndex';
import { BannerContext } from '@/contexts/BannerContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { SidePanel } from '@/ui/Content/SidePanel';
import { TabsBar } from '@/ui/TabsBar/TabsBar';
import { cn } from '@/utils/cn';

export const MapleContainer = ({ children }: { children: ReactNode }) => {
  const { isWide, isCustom, isCenter } = usePageMode();
  const { divisions } = useContext(NavigationContext);
  const { banner } = useContext(BannerContext);
  const showSidePanel = !(isWide || isCustom || isCenter);
  const [isOpaque, setIsOpaque] = useState(false);
  const hasTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;

  useEffect(() => {
    const offset = 50;
    function onScroll() {
      if (!isOpaque && window.scrollY > offset) {
        setIsOpaque(true);
      } else if (isOpaque && window.scrollY <= offset) {
        setIsOpaque(false);
      }
    }
    onScroll();
    window.addEventListener('scroll', onScroll, { passive: true });

    let baseScrollMarginTop = 3;
    if (hasTabs) baseScrollMarginTop += 2.5;
    if (hasBanner) baseScrollMarginTop += 2.5;

    window.document.documentElement.style.setProperty('--scroll-mt', `${baseScrollMarginTop}rem`);

    return () => {
      window.removeEventListener('scroll', onScroll);
    };
  }, [isOpaque, hasTabs, hasBanner]);

  return (
    <div
      className={cn(
        'relative w-full lg:ml-[19rem] flex gap-x-8 min-h-screen',
        isCustom
          ? 'pt-[120px] lg:pt-0'
          : `${hasBanner ? 'pt-[200px]' : 'pt-40'} px-4 lg:pt-10 lg:pl-16 lg:pr-10`
      )}
      id={Ids.ContentContainer}
    >
      {hasTabs && (
        <div
          id={Ids.NavBarTransition + '-maple'}
          className={cn(
            zIndex.SecondaryNav,
            'hidden lg:flex fixed top-0 left-[19rem] h-12 px-12 right-0 bottom-0 w-full backdrop-blur flex-none transition-colors duration-500 border-b border-gray-200/70 dark:border-white/[0.07]',
            isOpaque
              ? 'bg-background-light supports-backdrop-blur:bg-background-light/95 dark:bg-background-dark/75'
              : 'supports-backdrop-blur:bg-background-light/60 dark:bg-transparent',
            hasBanner && 'lg:mt-10'
          )}
        >
          <TabsBar />
        </div>
      )}
      <div
        className={cn(
          'flex flex-col box-border w-full relative grow',
          !isCustom && 'mx-auto max-w-xl 2xl:max-w-2xl',
          showSidePanel ? 'xl:w-[calc(100%-28rem)]' : 'xl:w-[calc(100%-19rem)]',
          (isCustom || isWide) && 'xl:min-w-full',
          isCenter && 'max-w-3xl',
          hasTabs && 'lg:mt-12'
        )}
        id={Ids.ContentArea}
      >
        {children}
      </div>
      <SidePanel />
    </div>
  );
};
