import { Endpoint } from '@mintlify/validation';

export const getFilteredSecurityOptions = (endpoint: Endpoint) => {
  return endpoint.request.security.filter((security) => {
    return (
      Object.keys(security.parameters.cookie).length > 0 ||
      Object.keys(security.parameters.header).length > 0 ||
      Object.keys(security.parameters.query).length > 0
    );
  });
};
