'use client';

import { ReactNode } from 'react';

import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import { Description } from './Description';
import { OptionDropdown } from './OptionDropdown';

type SectionHeadingProps = {
  title: string;
  subtitle?: string;
  rightElement?: string | ReactNode;
  options?: string[];
  selectedIndex?: number;
  onSelectOption?: (index: number) => void;
  onToggleExpand?: () => void;
};

export const SectionHeading = ({
  title,
  subtitle,
  rightElement,
  options,
  selectedIndex,
  onSelectOption,
}: SectionHeadingProps) => {
  return (
    <div className={cn(Classes.APISectionHeading, 'flex flex-col gap-y-4 w-full')}>
      <div className="flex items-baseline border-b pb-2.5 border-gray-100 dark:border-gray-800 w-full">
        <h4 className={cn(Classes.APISectionHeadingTitle, 'flex-1 mb-0')}>{title}</h4>
        {options && options.length > 1 && (
          <OptionDropdown
            options={options}
            selectedIndex={selectedIndex}
            onSelectOption={onSelectOption}
            noBackground
          />
        )}
        <div className="flex items-center">
          {rightElement &&
            (typeof rightElement === 'string' ? (
              <div className="font-mono px-2 py-0.5 text-xs font-medium text-gray-600 dark:text-gray-300">
                {rightElement}
              </div>
            ) : (
              rightElement
            ))}
        </div>
      </div>
      {subtitle && (
        <div
          className={cn(
            Classes.APISectionHeadingSubtitle,
            'text-sm prose prose-gray dark:prose-invert mb-2'
          )}
        >
          <Description markdown={subtitle} />
        </div>
      )}
    </div>
  );
};
