import Script from 'next/script';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { getThemeStyling } from '@/themes';
import { getDetailedFontType } from '@/utils/font/getDetailedFontType';
import { getFontScript } from '@/utils/font/getFontScript';

export default function FontScript() {
  const { docsConfig } = useContext(DocsConfigContext);

  const themeStylingFonts = getThemeStyling(docsConfig?.theme).fonts;

  if (!docsConfig?.fonts && !themeStylingFonts) return null;

  const fonts = docsConfig?.fonts ?? themeStylingFonts;

  const heading = getDetailedFontType(fonts, 'heading');
  const body = getDetailedFontType(fonts, 'body');

  const headingsScript = getFontScript(heading);
  const bodyScript = getFontScript(body);

  return (
    <>
      {headingsScript}
      {bodyScript}
      {/* eslint-disable-next-line @next/next/no-before-interactive-script-outside-document */}
      <Script
        strategy="beforeInteractive"
        id="font"
        dangerouslySetInnerHTML={{
          __html: `
              document.documentElement.style.setProperty('--font-family-headings-custom', '${heading?.family ?? ''}');
              document.documentElement.style.setProperty('--font-weight-headings-custom', '${heading?.weight ?? ''}');
              document.documentElement.style.setProperty('--font-family-body-custom', '${body?.family ?? ''}');
              document.documentElement.style.setProperty('--font-weight-body-custom', '${body?.weight ?? ''}');
            `,
        }}
      />
    </>
  );
}
