'use client';

import { normal } from 'color-blend';
import { RGBA } from 'color-blend/dist/types';
import hexRgb, { RgbaObject } from 'hex-rgb';
import { useContext, useMemo } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';

type TailwindColorScale = {
  '50': string;
  '100': string;
  '200': string;
  '300': string;
  '400': string;
  '500': string;
  '600': string;
  '700': string;
  '800': string;
  '900': string;
  '950': string;
};

export type Colors = {
  primary: string;
  primaryLight: string;
  primaryDark: string;
  backgroundLight: string;
  backgroundDark: string;
  anchorDefault: string | undefined;
  dropdownDefault: string | undefined;
  gray: TailwindColorScale;
};

export const hexToRgbString = (hex: string): string => {
  try {
    const rgb = hexRgb(hex);
    return `${rgb.red} ${rgb.green} ${rgb.blue}`;
  } catch {
    return '0 0 0';
  }
};

const rgbToRgbString = (rgb: RGBA): string => {
  return `${rgb.r} ${rgb.g} ${rgb.b}`;
};

const rgbToHex = (rgb: RGBA): string => {
  return `#${rgb.r.toString(16).padStart(2, '0')}${rgb.g.toString(16).padStart(2, '0')}${rgb.b.toString(16).padStart(2, '0')}`;
};

export const generateDynamicBackgroundDarkColor = (
  primaryLight: string,
  backgroundDark?: string
) => {
  if (backgroundDark) {
    return hexToRgbString(backgroundDark);
  }

  const combinedBackground = combineColors('#09090b', 1, primaryLight, 0.02);

  return rgbToRgbString(combinedBackground);
};

const combineColors = (
  color1: string,
  color1Opacity: number,
  color2: string,
  color2Opacity: number
) => {
  const color1Rgb = hexRgb(color1);
  const color2Rgb = hexRgb(color2);

  return normal(
    { r: color1Rgb.red, g: color1Rgb.green, b: color1Rgb.blue, a: color1Opacity },
    { r: color2Rgb.red, g: color2Rgb.green, b: color2Rgb.blue, a: color2Opacity }
  );
};

const generateDynamicGrayRgb = (primaryRgb: RgbaObject, grayColor: string) => {
  const grayRgb = hexRgb(grayColor);
  const combinedGray = normal(
    { r: primaryRgb.red, g: primaryRgb.green, b: primaryRgb.blue, a: 0.6 },
    { r: grayRgb.red, g: grayRgb.green, b: grayRgb.blue, a: 0.95 }
  );

  return rgbToRgbString(combinedGray);
};

const generateDynamicGrayScale = (primaryColor: string): TailwindColorScale => {
  const neutralScale: TailwindColorScale = {
    '50': '#fafafa',
    '100': '#f5f5f5',
    '200': '#e5e5e5',
    '300': '#d4d4d4',
    '400': '#a3a3a3',
    '500': '#737373',
    '600': '#525252',
    '700': '#404040',
    '800': '#262626',
    '900': '#171717',
    '950': '#0a0a0a',
  };

  const primaryRgb = hexRgb(primaryColor);

  return {
    '50': generateDynamicGrayRgb(primaryRgb, neutralScale[50]),
    '100': generateDynamicGrayRgb(primaryRgb, neutralScale[100]),
    '200': generateDynamicGrayRgb(primaryRgb, neutralScale[200]),
    '300': generateDynamicGrayRgb(primaryRgb, neutralScale[300]),
    '400': generateDynamicGrayRgb(primaryRgb, neutralScale[400]),
    '500': generateDynamicGrayRgb(primaryRgb, neutralScale[500]),
    '600': generateDynamicGrayRgb(primaryRgb, neutralScale[600]),
    '700': generateDynamicGrayRgb(primaryRgb, neutralScale[700]),
    '800': generateDynamicGrayRgb(primaryRgb, neutralScale[800]),
    '900': generateDynamicGrayRgb(primaryRgb, neutralScale[900]),
    '950': generateDynamicGrayRgb(primaryRgb, neutralScale[950]),
  };
};

export function useColors(): Colors {
  const { docsConfig } = useContext(DocsConfigContext);

  return useMemo(() => {
    const primaryColor = docsConfig?.colors.primary ?? '#16A34A';
    const primaryLight = docsConfig?.colors.light ?? '#4ADE80';
    const primaryDark = docsConfig?.colors.dark ?? '#166534';

    const anchorDefault = docsConfig?.colors.primary;
    const dropdownDefault = docsConfig?.colors.primary;

    if (docsConfig?.theme === 'linden') {
      docsConfig.background = {
        ...docsConfig.background,
        color: {
          light:
            docsConfig.background?.color?.light ||
            rgbToHex(combineColors('#FFFFFF', 1, primaryColor, 0.03)),
          dark:
            docsConfig.background?.color?.dark ||
            rgbToHex(combineColors('#09090B', 1, primaryLight, 0.03)),
        },
      };
    }

    return {
      primary: hexToRgbString(primaryColor),
      primaryLight: hexToRgbString(primaryLight),
      primaryDark: hexToRgbString(primaryDark),
      backgroundLight: hexToRgbString(docsConfig?.background?.color?.light ?? '#ffffff'),
      backgroundDark: generateDynamicBackgroundDarkColor(
        primaryLight,
        docsConfig?.background?.color?.dark
      ),
      anchorDefault,
      dropdownDefault,
      gray: generateDynamicGrayScale(primaryColor),
    };
  }, [docsConfig]);
}

/**
 * The function `convertColorsToRgbWithCommas` takes a color string with spaces and converts it to RGB
 * format with commas.
 * @param {string} color - The `convertColorsToRgbWithCommas` function takes a color string as input
 * and converts it to RGB format with commas separating the values. For example, if the input color is
 * "255 0 128", the function will return "255, 0, 128".
 */
export const convertColorsToRgbWithCommas = (color: string): string => {
  return color.split(' ').join(', ');
};
