import { ChevronLeftIcon, ChevronRightIcon } from 'lucide-react';
import Link from 'next/link';

import { useMDXContent } from '@/hooks/useMDXContent';
import { usePrevNext } from '@/hooks/usePrevNext';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

export function LindenPagination() {
  const [{ pageMetadata }] = useMDXContent();
  const { prev, next: n } = usePrevNext();
  const previous = pageMetadata.hideFooterPagination ? null : prev;
  const next = pageMetadata.hideFooterPagination ? null : n;

  if (previous == null && next == null) {
    return null;
  }

  return (
    <div
      id={Ids.Pagination}
      className={cn('grid gap-4 mb-8', previous && next ? 'lg:grid-cols-2' : 'lg:grid-cols-1')}
    >
      {previous ? (
        <PaginationItem href={previous.href} title={previous.title} align="left" />
      ) : (
        <div />
      )}
      {next ? <PaginationItem href={next.href} title={next.title} align="right" /> : <div />}
    </div>
  );
}

function PaginationItem({
  href,
  title,
  align,
}: {
  href: string;
  title: string;
  align?: 'left' | 'right';
}) {
  return (
    <Link
      href={href || '/'}
      className={cn(
        'text-primary dark:text-primary-light hover:text-primary-dark dark:hover:text-primary border border-current group flex items-center rounded-lg py-1.5 px-3 justify-center text-sm'
      )}
    >
      <div className={cn('flex items-center gap-1', align === 'left' && 'flex-row-reverse')}>
        <div className={cn('font-medium uppercase')}>{title}</div>
        {align === 'right' ? (
          <ChevronRightIcon className="size-3.5" strokeWidth={2.5} />
        ) : (
          <ChevronLeftIcon className="size-3.5" strokeWidth={2.5} />
        )}
      </div>
    </Link>
  );
}
