'use client';

import { DataSchemaArray, IncrementalDataSchemaArray, ResponseSchema } from '@mintlify/validation';
import { useCallback, useMemo, useState } from 'react';

import { Tab, Tabs } from '@/components/Tabs';
import { NoAnyDataSchema } from '@/hooks/usePlaygroundSchemaOptions';
import { Classes } from '@/lib/local/selectors';
import { generateSimpleLabels } from '@/openapi/generateSimpleLabels';
import { cn } from '@/utils/cn';

import { TypeDropdown } from '../ApiPlayground/components/TypeDropdown';
import { RecursiveParamField } from './ParamFields/RecursiveParamField';
import { SectionHeading } from './components/SectionHeading';

export type EndpointResponseParams = {
  response: ResponseSchema;
};

export function EndpointResponse({ response }: EndpointResponseParams) {
  const [selectedContentTypeIndex, setSelectedContentTypeIndex] = useState(0);
  const [selectedStatusIndex, setSelectedStatusIndex] = useState(0);

  const statusEntries = useMemo(() => Object.entries(response), [response]);
  const contentTypeEntries = useMemo(
    () => Object.entries(statusEntries[selectedStatusIndex]?.[1] || {}),
    [statusEntries, selectedStatusIndex]
  );

  const handleStatusChange = useCallback(
    (newIndex: number) => {
      setSelectedStatusIndex(newIndex);
      if (selectedContentTypeIndex >= Object.entries(statusEntries[newIndex]?.[1] || {}).length) {
        setSelectedContentTypeIndex(0);
      }
    },
    [statusEntries, selectedContentTypeIndex]
  );

  const handleContentTypeChange = useCallback((newIndex: number) => {
    setSelectedContentTypeIndex(newIndex);
  }, []);

  const { contentType, content, description } = useMemo(() => {
    const contentTypeAndContent = contentTypeEntries[selectedContentTypeIndex];
    if (!contentTypeAndContent) {
      return { contentType: undefined, content: undefined, description: undefined };
    }
    const [contentType, content] = contentTypeAndContent;
    const description =
      content.description !== content.schemaArray[0].description ? content.description : undefined;
    if (contentType === '_mintlify/placeholder') {
      return {
        content,
        contentType: '',
        description,
      };
    }
    return {
      content,
      contentType,
      description,
    };
  }, [contentTypeEntries, selectedContentTypeIndex]);

  if (!content) return null;

  const { schemaArray: selectedSchemaArray } = content;
  const labels = generateSimpleLabels(selectedSchemaArray);

  const statusOptions = statusEntries.map(([statusCode, body]) => ({
    label: statusCode,
    schema: body as unknown as NoAnyDataSchema,
  }));

  const contentTypeOptions = contentTypeEntries
    .filter(([contentType]) => contentType !== '_mintlify/placeholder')
    .map(([contentType, schema]) => ({
      label: contentType,
      schema: schema as unknown as NoAnyDataSchema,
    }));

  const statusString = statusEntries[0] ? `${statusEntries[0][0]}` : '';
  const contentTypeString = contentType === '' ? '' : `${contentType}`;
  const statusAndContentType = `${statusString}${statusString && contentTypeString ? ' - ' : ''}${contentTypeString}`;

  const hasMultipleStatuses = statusEntries.length > 1;
  const hasMultipleContentTypes = contentTypeEntries.length > 1;
  const showDropdowns = hasMultipleStatuses || hasMultipleContentTypes;

  return (
    <div className={cn(Classes.APISection)}>
      <SectionHeading
        title="Response"
        subtitle={description}
        rightElement={
          showDropdowns ? (
            <div className="flex items-center gap-x-3 font-mono text-xs">
              <TypeDropdown
                onSelectOption={handleStatusChange}
                options={statusOptions}
                selectedIndex={selectedStatusIndex}
                className={cn(
                  'bg-transparent dark:bg-transparent',
                  hasMultipleStatuses && 'hover:text-zinc-950 dark:hover:text-white transition-all'
                )}
              />
              <TypeDropdown
                onSelectOption={handleContentTypeChange}
                options={contentTypeOptions}
                selectedIndex={selectedContentTypeIndex}
                className={cn(
                  'bg-transparent dark:bg-transparent',
                  hasMultipleContentTypes &&
                    'hover:text-zinc-950 dark:hover:text-white transition-all'
                )}
              />
            </div>
          ) : (
            statusAndContentType
          )
        }
      />
      {selectedSchemaArray.length > 1 ? (
        <Tabs>
          {selectedSchemaArray.map((schema, i) => (
            <Tab key={i} title={labels[i] ?? ''}>
              <RecursiveParamField
                fieldType="response"
                schemaArray={[schema] as DataSchemaArray | IncrementalDataSchemaArray}
              />
            </Tab>
          ))}
        </Tabs>
      ) : (
        <RecursiveParamField fieldType="response" schemaArray={selectedSchemaArray} />
      )}
    </div>
  );
}
