'use client';

import { ApiPlaygroundDisplayType } from '@mintlify/models';
import { ApiPlaygroundInputs, Endpoint } from '@mintlify/validation';
import { useContext } from 'react';

import { AuthContext } from '@/contexts/ConfigContext';
import { usePrefilledInputs } from '@/hooks/usePrefilledInputs';
import { useSecurityOption } from '@/hooks/useSecurityOption';
import ErrorBoundary from '@/ui/ErrorBoundary';
import { cn } from '@/utils/cn';

import { ApiPlaygroundContext } from '../ApiPlaygroundContext';
import { EndpointHeader } from '../components/PathHeader';
import { PlaygroundModal } from './PlaygroundModal';

type RequestParams = {
  endpoint: Endpoint;
  inputs: ApiPlaygroundInputs;
  setInputs: (inputs: ApiPlaygroundInputs) => void;
  display: ApiPlaygroundDisplayType;
};

export const Request = ({ endpoint, inputs, setInputs, display }: RequestParams) => {
  const { selectedBaseUrlIndex, setIsPlaygroundExpanded } = useContext(ApiPlaygroundContext);
  const server = endpoint.servers?.[selectedBaseUrlIndex];

  const securityOption = useSecurityOption(endpoint.request.security);
  const { userInfo } = useContext(AuthContext);
  usePrefilledInputs({
    inputs,
    setInputs,
    baseUrl: server?.url,
    securityOption,
    userInfoInputs: userInfo?.apiPlaygroundInputs,
  });

  return (
    <ErrorBoundary>
      <div
        className={cn(
          'flex w-full flex-col bg-background-light dark:bg-background-dark border-standard rounded-2xl',
          display === 'simple' ? 'p-1' : 'p-1.5'
        )}
      >
        <EndpointHeader
          endpoint={endpoint}
          display={display}
          onClickInteractive={() => setIsPlaygroundExpanded?.(true)}
        />
        <PlaygroundModal endpoint={endpoint} inputs={inputs} setInputs={setInputs} />
      </div>
    </ErrorBoundary>
  );
};
