import { useEndpoint } from '@/hooks/useEndpoint';

import { CodeBlock } from '../Code/CodeBlock';
import { CodeGroup } from '../Code/CodeGroup';
import { CodeSnippet } from '../Code/CodeSnippet';

export const WebhookExample = () => {
  const endpoint = useEndpoint();
  let webhookBodyExample: string | undefined;
  if (!endpoint) {
    return null;
  }
  const [contentTypeAndContent] = Object.entries(endpoint.request.body);
  if (contentTypeAndContent) {
    const [_contentType, content] = contentTypeAndContent;
    const firstExample = Object.values(content.examples)[0];
    if (firstExample) {
      webhookBodyExample = JSON.stringify(firstExample.value, null, 2);
    }
  }
  if (!webhookBodyExample) {
    return null;
  }
  return (
    <CodeGroup isSmallText noMargins>
      <CodeBlock language="json" filename="Example Request Body">
        <CodeSnippet language="json">{webhookBodyExample}</CodeSnippet>
      </CodeBlock>
    </CodeGroup>
  );
};
