'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { cn } from '@/utils/cn';

export const ResponseTable = ({ data }: { data: [string, unknown][] }) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div className="w-full rounded-b-xl overflow-y-auto">
      <table className="table-fixed pb-8 w-full pt-10">
        {data.flatMap(([key, value]) => {
          return (
            <tr
              className={cn(
                'w-full flex divide-x border-b last:border-b-0 text-xs text-gray-600 dark:text-gray-400',
                codeBlockTheme === 'system'
                  ? 'divide-gray-100 dark:divide-white/10 border-gray-100 dark:border-white/10 text-gray-600 dark:text-gray-400'
                  : 'divide-white/10 border-white/10 text-gray-400'
              )}
              key={key}
            >
              <td className="flex items-center basis-1/2 py-2 px-4 overflow-x-autotext-gray-600">
                <div className="min-w-max">{key}</div>
              </td>
              <td className="flex items-center basis-1/2 py-2 px-4 overflow-x-auto">
                <div className="min-w-max">{`${value}`}</div>
              </td>
            </tr>
          );
        })}
      </table>
    </div>
  );
};
