'use client';

import { uniqueId } from 'lodash';
import { XIcon } from 'lucide-react';
import { useCallback, useContext, useState } from 'react';
import { useMediaQuery } from 'usehooks-ts';

import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { NavigationContext, useSelectedLocale } from '@/contexts/NavigationContext';
import { Ids } from '@/lib/local/identifiers';
import { Classes } from '@/lib/local/selectors';
import { getSidePanelSheetClassNames } from '@/ui/Content/ContentSideLayout';
import { SparklesIcon, TrashNucleoIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

import { ChatAssistantContext } from '..';
import { ChatHistoryList } from './ChatHistoryList';
import { ChatTextArea } from './ChatTextArea';
import { EmptyState } from './EmptyState';

export const CHAT_SHEET_WIDTH = 368;

type ChatAssistantSheetProps = {
  className?: string;
};

export const ChatAssistantSheet = ({ className }: ChatAssistantSheetProps) => {
  const { messages, setMessages, isChatSheetOpen, onChatSheetToggle } =
    useContext(ChatAssistantContext);
  const { docsConfig } = useContext(DocsConfigContext);
  const { divisions } = useContext(NavigationContext);
  const { banner } = useContext(BannerContext);
  const { assistant } = useSelectedLocale();

  const [sessionId, setSessionId] = useState<string>();

  const hasTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;

  const isMobile = useMediaQuery('(max-width: 1024px)');

  const onSendMessage = useCallback(
    (query: string) => {
      if (query.trim() === '') {
        return;
      }

      setMessages((prevMessages) => [
        {
          message: query,
          key: uniqueId(),
          isLoading: true,
          isGeneratingError: false,
          isRateLimited: false,
        },
        ...prevMessages,
      ]);
    },
    [setMessages]
  );

  const onClearChat = useCallback(() => {
    setMessages([]);
    setSessionId(undefined);
  }, [setMessages, setSessionId]);

  const onRetryMessage = useCallback(
    (index: number) => {
      const newMessages = [...messages];
      if (newMessages[index]) {
        newMessages[index] = {
          message: newMessages[index].message,
          key: uniqueId(),
          isLoading: true,
          isGeneratingError: false,
          isRateLimited: false,
        };
        setMessages(newMessages);
      }
    },
    [messages, setMessages]
  );

  const horizontalPadding = 'px-4';

  return (
    <div
      className={cn(
        getSidePanelSheetClassNames(docsConfig?.theme, hasTabs, hasBanner, isMobile),
        !isChatSheetOpen && 'invisible',
        className
      )}
    >
      <div
        id={Ids.ChatAssistantSheet}
        className={cn(
          'absolute inset-0 -top-px min-h-full flex flex-col overflow-hidden shrink-0',
          Classes.ChatAssistantSheet
        )}
        aria-hidden={!isChatSheetOpen}
      >
        <div className={cn('w-full flex flex-col pb-4 h-full pt-[7.5rem] lg:pt-3')}>
          <div
            className={cn(
              Classes.ChatAssistantSheetHeader,
              'hidden lg:flex items-center justify-between',
              horizontalPadding
            )}
          >
            <div className="flex items-center gap-2">
              <SparklesIcon className="size-5 text-primary dark:text-primary-light" />
              <span className="font-semibold text-gray-900 dark:text-gray-100">{assistant}</span>
            </div>

            <div className="flex items-center gap-1.5">
              <button
                onClick={onClearChat}
                className="group hover:bg-gray-100 dark:hover:bg-white/5 p-1.5 rounded-lg"
              >
                <TrashNucleoIcon className="size-4 text-gray-400 dark:text-gray-500 group-hover:text-gray-700 dark:group-hover:text-gray-300" />
              </button>
              <button
                onClick={onChatSheetToggle}
                className="group hover:bg-gray-100 dark:hover:bg-white/5 p-1.5 rounded-lg"
              >
                <XIcon className="size-4 text-gray-400 dark:text-gray-500 group-hover:text-gray-700 dark:group-hover:text-gray-300" />
              </button>
            </div>
          </div>

          <div
            className={cn(
              Classes.ChatAssistantSheetContent,
              'flex-1 overflow-y-auto relative',
              horizontalPadding
            )}
          >
            <div className="sticky top-0 left-0 right-0 bg-gradient-to-b from-background-light from-50% dark:from-background-dark to-transparent h-8"></div>
            {messages.length > 0 ? (
              <ChatHistoryList
                sessionId={sessionId}
                onRetry={onRetryMessage}
                setSessionId={setSessionId}
              />
            ) : (
              <EmptyState />
            )}
          </div>

          <div className={cn(horizontalPadding)}>
            <ChatTextArea onSendMessage={onSendMessage} />
          </div>
        </div>
      </div>
    </div>
  );
};
