'use client';

import { IconType } from '@mintlify/models';
import clsx from 'clsx';
import { ReactNode } from 'react';

import { Classes } from '@/lib/local/selectors';
import { ComponentIcon } from '@/ui/Icon';
import { cn } from '@/utils/cn';

type Numberish = number | `${number}`;

export type StepProps = {
  /**
   * The icon for the step. Defaults to the step number, but can also accept an Icon.
   */
  icon?: ReactNode | string;
  /**
   * The number of the step. Defaults to 1.
   */
  stepNumber?: Numberish;
  /**
   * Determines the icon type. Defaults to `regular`.
   */
  iconType?: IconType;
  /**
   * The title is the primary text for the step and shows up next to the indicator.
   */
  title: string | ReactNode;
  /**
   * The content of the step.
   */
  children?: string | ReactNode;
  /**
   * The size of the step title. Defaults to p.
   */
  titleSize?: 'p' | 'h2' | 'h3';
  /**
   * Internal: Used to mark whether the index is the last index
   */
  isLast?: boolean;
};

export const Step = ({
  stepNumber = 1,
  icon,
  iconType,
  title,
  children,
  titleSize = 'p',
  isLast = false,
}: StepProps) => {
  const transformedIconOrNumber =
    typeof icon === 'string' ? (
      <ComponentIcon
        icon={icon}
        iconType={iconType}
        className="h-3.5 w-3.5 bg-gray-800 dark:bg-white"
        overrideColor
      />
    ) : typeof icon === 'undefined' ? (
      Number(stepNumber)
    ) : (
      icon
    );

  return (
    <div role="listitem" className={cn(Classes.Step, 'relative flex items-start pb-2')}>
      <div
        className={clsx(
          'absolute w-px h-[calc(100%-2.75rem)] top-[2.75rem]',
          isLast
            ? 'bg-transparent bg-gradient-to-b from-gray-200 dark:from-white/10 via-80% to-transparent'
            : 'bg-gray-200/70 dark:bg-white/10'
        )}
        contentEditable={false}
      ></div>
      <div
        className="absolute ml-[-14px] py-2"
        data-component-part="step-number"
        contentEditable={false}
      >
        <div className="w-7 h-7 shrink-0 rounded-lg bg-gray-100 dark:text-white dark:bg-[#26292E] text-sm text-gray-800 font-semibold flex items-center justify-center">
          {transformedIconOrNumber}
        </div>
      </div>
      <div className="w-full overflow-hidden pl-12 pr-px">
        {
          {
            p: (
              <p
                className="mt-2 font-semibold prose dark:prose-invert text-gray-900 dark:text-gray-200"
                contentEditable={false}
                data-component-part="step-title"
              >
                {title}
              </p>
            ),
            h2: (
              <h2 className="mt-2" contentEditable={false} data-component-part="step-title">
                {title}
              </h2>
            ),
            h3: (
              <h3 className="mt-2" contentEditable={false} data-component-part="step-title">
                {title}
              </h3>
            ),
          }[titleSize]
        }
        <div data-component-part="step-content">{children}</div>
      </div>
    </div>
  );
};
