'use client';

import clsx from 'clsx';
import { ReactNode, useCallback, useContext, useEffect, useState } from 'react';
import { Rect, useRect } from 'react-use-rect';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useMDXContent } from '@/hooks/useMDXContent';
import { useTop } from '@/hooks/useTop';
import { getThemeStyling } from '@/themes';
import { LinkIcon } from '@/ui/icons';

import { copyToClipboard } from '../utils/copyToClipboard';

type HeadingProps = {
  level: number;
  id: string;
  children: ReactNode;
  className?: string;
  hidden?: boolean;
  ignore?: boolean;
  style?: object;
  isAtRootLevel?: string;
};

export function Heading({
  level,
  id,
  children,
  className = '',
  hidden = false,
  style = {},
  isAtRootLevel,
  ...props
}: HeadingProps) {
  const { docsConfig } = useContext(DocsConfigContext);
  const Component = `h${level}` as 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6';
  const [context, dispatch] = useMDXContent();
  const [rect, setRect] = useState<Rect | null>(null);
  const [rectRef] = useRect(setRect);
  const top = useTop(rect);

  const copyAnchorLink = useCallback(() => {
    void copyToClipboard(`https://${window.location.host}${window.location.pathname}#${id}`);
    window.location.hash = id;
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView();
    }
  }, [id]);

  // We cannot include context in the dependency array because it changes every render.
  const hasContext = Boolean(context);

  useEffect(() => {
    if (!hasContext) return;
    if (typeof top !== 'undefined') {
      dispatch({
        type: 'register_heading',
        payload: { id, top, isAtRootLevel: isAtRootLevel === 'true' },
      });
    }
    return () => {
      dispatch({
        type: 'unregister_heading',
        payload: id,
      });
    };
  }, [top, id, hasContext, dispatch, isAtRootLevel]);

  return (
    <Component
      className={clsx(
        'flex whitespace-pre-wrap group font-semibold',
        getThemeStyling(docsConfig?.theme).headings,
        className,
        {
          'text-2xl sm:text-3xl mt-8': Component === 'h1',
        }
      )}
      id={id}
      ref={rectRef}
      style={{ ...(hidden ? { marginBottom: 0 } : {}), ...style }}
      {...props}
    >
      {!hidden && (
        <div className="absolute">
          <a
            href={`#${id}`}
            className="-ml-10 flex items-center opacity-0 border-0 group-hover:opacity-100"
            aria-label="Navigate to header"
            onClick={copyAnchorLink}
          >
            &#8203;
            <div className="w-6 h-6 rounded-md flex items-center justify-center shadow-sm text-gray-400 dark:text-white/50 dark:bg-background-dark dark:brightness-[1.35] dark:ring-1 dark:hover:brightness-150 bg-white ring-1 ring-gray-400/30 dark:ring-gray-700/25 hover:ring-gray-400/60 dark:hover:ring-white/20">
              <LinkIcon />
            </div>
          </a>
        </div>
      )}
      <span className={clsx('cursor-pointer', hidden && 'sr-only')} onClick={copyAnchorLink}>
        {children}
      </span>
    </Component>
  );
}
