import { Ids } from '@/lib/local/identifiers';

export function handleRouteChange(path: string) {
  const navItems = document.querySelectorAll(`[id="${path}"]`);
  if (navItems.length === 1 && navItems[0] instanceof HTMLElement) {
    const item = navItems[0];
    import('scroll-into-view-if-needed')
      .then((module) => {
        const { default: scrollIntoView } = module;
        scrollIntoView(item, {
          scrollMode: 'if-needed',
          boundary: (parent) => {
            if (parent.id === Ids.SidebarContent) {
              return false;
            }
            return true;
          },
        });
      })
      .catch(() => 'Error auto scroll the sidebar');
  }
}
