'use client';

import { ResponseBodyData } from '..';
import { ResponsePlaceholder } from '../ResponsePlaceholder';
import { AudioResponseContent } from './AudioResponseContent';
import { CodeResponseContent } from './CodeResponseContent';
import { ImageResponseContent } from './ImageResponseContent';

type ResponseBodyParams = {
  data: ResponseBodyData | undefined;
};

export const ResponseBody = ({ data }: ResponseBodyParams) => {
  if (data == null) {
    return (
      <ResponsePlaceholder message="No body data received from the server" icon="brackets-curly" />
    );
  }

  switch (data.type) {
    case 'audio':
      return <AudioResponseContent data={data} />;
    case 'image':
      return <ImageResponseContent data={data} />;
    case 'other':
      return <CodeResponseContent data={data} />;
  }
};
