'use client';

import { ReactElement } from 'react';

import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { MainContentLayout } from '@/themes/mint/MainContentLayout';
import { TopBar } from '@/themes/mint/TopBar/TopBar';

import { MDXContentController } from './MainContentLayout/MDXContentController';

export const MintLayout = ({ children }: { children: ReactElement }) => {
  const router = useMintlifyRouter();
  return (
    <>
      <TopBar />
      <MainContentLayout>
        <MDXContentController key={router.asPath}>{children}</MDXContentController>
      </MainContentLayout>
    </>
  );
};
