import CryptoJS from 'crypto-js';
import cookie from 'js-cookie';
import uniqueString from 'unique-string';

const COOKIE_KEY = 'mintlify-auth-key';

export function storeEncrypted(localStorageKey: string, value: unknown) {
  const key = loadEncryptionKey();
  const stringifiedValue = JSON.stringify(value);
  const encryptedValue = CryptoJS.AES.encrypt(stringifiedValue, key).toString();
  localStorage.setItem(localStorageKey, encryptedValue);
}

export function loadEncrypted<T = unknown>(
  localStorageKey: string,
  validator?: (value: unknown) => value is T
): T | undefined {
  const key = loadEncryptionKey();
  const encryptedString = localStorage.getItem(localStorageKey);

  if (encryptedString != null) {
    try {
      const decryptedString = CryptoJS.AES.decrypt(encryptedString, key).toString(
        CryptoJS.enc.Utf8
      );
      const decryptedValue = JSON.parse(decryptedString);
      if (!validator || validator(decryptedValue)) {
        return decryptedValue;
      }
    } catch (e) {
      console.log(`unable to decrypt stored credentials: ${e}`);
    }
  }

  return undefined;
}

const loadEncryptionKey = () => {
  const key = cookie.get(COOKIE_KEY);
  if (key !== undefined) {
    return key;
  }

  const newKey = uniqueString();
  cookie.set(COOKIE_KEY, newKey, {
    secure: location.protocol === 'https:',
    sameSite: 'strict',
  });
  return newKey;
};
