import { useState } from 'react';

import { useOptionInformation } from '@/hooks/useOptionInformation';

import { ArrayParamField } from './ArrayParamField';
import { ObjectParamField } from './ObjectParamField';
import { PrimitiveParamField } from './PrimitiveParamField';
import { buildRecursiveParamFieldId, RecursiveParamFieldParams } from './RecursiveParamField';

// When we have to scroll to an anchor, renders all ParamHeads but only renders children when the anchor is in view
export const RecursiveParamFieldWithChildrenObserver = ({
  fieldType,
  schemaArray,
  name,
  location,
  parentName,
  depth = 0,
  hideDescription = false,
  renderedRefs = new Set(),
  circularRefs,
  paramId,
  style,
  explode,
}: RecursiveParamFieldParams) => {
  const { originalIndices, typeOptionLabels, nullable, allObjects } =
    useOptionInformation(schemaArray);

  const [selectedIndex, setSelectedIndex] = useState(0);
  const originalIndex = originalIndices[selectedIndex] ?? 0;

  const selectedSchema = schemaArray[originalIndex];

  if (!selectedSchema) return null;

  const id = paramId ?? buildRecursiveParamFieldId(fieldType, name, parentName);

  let children;
  switch (selectedSchema.type) {
    case 'array':
      children = (
        <ArrayParamField
          paramId={id}
          fieldType={fieldType}
          schema={selectedSchema}
          name={name}
          location={location}
          typeOptions={typeOptionLabels}
          selectedIndex={selectedIndex}
          onSelectTypeOption={setSelectedIndex}
          parentName={parentName}
          depth={depth}
          renderedRefs={renderedRefs}
          circularRefs={circularRefs}
        />
      );
      break;
    case 'object':
      children = (
        <ObjectParamField
          paramId={id}
          fieldType={fieldType}
          schema={selectedSchema}
          name={name}
          nullable={nullable}
          allObjects={allObjects}
          location={location}
          typeOptions={typeOptionLabels}
          selectedIndex={selectedIndex}
          onSelectTypeOption={setSelectedIndex}
          parentName={parentName}
          depth={depth}
          hideDescription={hideDescription}
          renderedRefs={renderedRefs}
          circularRefs={circularRefs}
          style={style}
          explode={explode}
        />
      );
      break;
    default:
      children = (
        <PrimitiveParamField
          paramId={id}
          fieldType={fieldType}
          schema={selectedSchema}
          name={name}
          location={location}
          typeOptions={typeOptionLabels}
          selectedIndex={selectedIndex}
          onSelectTypeOption={setSelectedIndex}
          parentName={parentName}
          style={style}
          explode={explode}
        />
      );
      break;
  }

  return (
    <div className="border-gray-100 dark:border-gray-800 border-b last:border-b-0">{children}</div>
  );
};
