'use client';

import { TableOfContentsSectionType } from '@mintlify/common';
import { DocsConfig } from '@mintlify/validation';
import clsx from 'clsx';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useSelectedLocale } from '@/contexts/NavigationContext';
import { useTableOfContents } from '@/hooks/useTableOfContents';
import { Ids } from '@/lib/local/identifiers';
import { Classes } from '@/lib/local/selectors';

import { TableOfContentsSection, TableOfContentsStyle } from './TableOfContentsSection';

export function TableOfContentsContent() {
  const { docsConfig } = useContext(DocsConfigContext);
  const { onThisPage } = useSelectedLocale();
  const { closeNav, isActive, pageHasSubsections, tableOfContents } = useTableOfContents();

  if (!Array.isArray(tableOfContents) || tableOfContents.length === 0) {
    return null;
  }

  return (
    <div
      className={clsx(
        'text-gray-600 text-sm leading-6 w-[16.5rem] overflow-y-auto space-y-2 pb-4 -mt-10 pt-10'
      )}
      id={Ids.TableOfContents}
    >
      <div className="text-gray-700 dark:text-gray-300 font-medium flex items-center space-x-2">
        <LinesIcon />
        <span>{onThisPage}</span>
      </div>
      <ul id={Ids.TableOfContentsContent} className={Classes.TableOfContents}>
        {tableOfContents.map((section: TableOfContentsSectionType) => {
          return (
            <TableOfContentsSection
              key={`TableOfContentSection-${section.slug}`}
              section={section}
              closeNav={closeNav}
              isActive={isActive}
              pageHasSubsections={pageHasSubsections}
              style={getTableOfContentsStyle(docsConfig?.theme)}
            />
          );
        })}
      </ul>
    </div>
  );
}

function getTableOfContentsStyle(theme: DocsConfig['theme'] | undefined): TableOfContentsStyle {
  if (theme === 'maple' || theme === 'aspen') {
    return 'border';
  }

  if (theme === 'linden') {
    return 'arrow';
  }

  if (theme === 'almond') {
    return 'border';
  }

  return 'default';
}

function LinesIcon() {
  return (
    <svg
      width="16"
      height="16"
      viewBox="0 0 16 16"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      xmlns="http://www.w3.org/2000/svg"
      className="h-3 w-3"
    >
      <path d="M2.44434 12.6665H13.5554" strokeLinecap="round" strokeLinejoin="round" />
      <path d="M2.44434 3.3335H13.5554" strokeLinecap="round" strokeLinejoin="round" />
      <path d="M2.44434 8H7.33323" strokeLinecap="round" strokeLinejoin="round" />
    </svg>
  );
}
