'use client';

import { ReactNode, useState } from 'react';

import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

type InputSectionContainerParams = {
  name: string;
  validationState?: 'valid' | 'invalid';
  children?: ReactNode;
  rightElement?: ReactNode;
};

export const InputSectionContainer = ({
  name,
  validationState,
  children,
  rightElement,
}: InputSectionContainerParams) => {
  const [expanded, setExpanded] = useState(true);

  return (
    <div>
      <button
        aria-label="expand input section"
        onClick={() => setExpanded((prev) => !prev)}
        className={cn(
          'flex w-full px-4 py-2.5 items-center justify-between border-standard cursor-pointer hover:bg-gray-50 dark:hover:bg-white/5',
          expanded ? '!border-b-0 border-t border-x rounded-t-2xl' : 'rounded-2xl'
        )}
      >
        <div className="flex items-center gap-x-1.5">
          <Icon
            icon={'angle-right'}
            iconType="solid"
            className={cn(
              'h-3 w-3 bg-gray-400 dark:bg-gray-500 transition-transform',
              expanded && 'rotate-90'
            )}
          />
          <div className="flex-1 flex items-center space-x-2">
            <div className="text-sm font-medium text-gray-800 dark:text-gray-200 leading-6">
              {name}
            </div>
            {validationState ? (
              validationState === 'invalid' ? (
                <ValidIcon />
              ) : (
                <InvalidIcon />
              )
            ) : null}
          </div>
        </div>
        {rightElement}
      </button>
      {/* since the UI components maintain some state, we can't unmount them - hide instead */}
      <div
        className={cn(
          'bg-background-light dark:bg-background-dark flex-1 px-4 rounded-b-xl border-standard !border-t-0 divide-y divide-gray-100 dark:divide-white/10',
          expanded && children ? '' : 'hidden'
        )}
      >
        {/* Used to create the top border */}
        <div />
        {children}
      </div>
    </div>
  );
};

const ValidIcon = () => <Icon className="h-4 w-4 bg-[#2AB673]" icon="circle-check" />;

const InvalidIcon = () => <Icon className="h-4 w-4 bg-[#CB3A32]" icon="circle-exclamation" />;
