import { XIcon } from 'lucide-react';
import { useContext } from 'react';
import ReactMarkdown from 'react-markdown';

import { zIndex } from '@/constants/zIndex';
import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

export const Banner = () => {
  const { docsConfig } = useContext(DocsConfigContext);
  const { banner, dismissBanner } = useContext(BannerContext);
  const bannerConfig = docsConfig?.banner;

  if (!banner) return null;

  return (
    <div
      id={Ids.Banner}
      className={cn(
        'px-2 w-full [&>*]:text-white/90 md:h-10 relative text-center flex items-center justify-center text-sm prose prose-dark dark:prose-invert line-clamp-2 md:truncate md:[&>*]:truncate bg-primary-dark font-medium',
        zIndex.Banner
      )}
    >
      {typeof banner === 'string' ? (
        banner
      ) : (
        <ReactMarkdown className="my-2 md:[&>p]:m-0">{bannerConfig?.content || ''}</ReactMarkdown>
      )}

      {docsConfig?.banner?.dismissible && (
        <button
          className="absolute right-4 top-1/2 -translate-y-1/2 text-zinc-200/80 hover:backdrop-blue-sm"
          onClick={() => {
            dismissBanner();
          }}
        >
          <XIcon className="w-4 h-4" />
        </button>
      )}
    </div>
  );
};
