'use client';

import {
  ApiPlaygroundInputs,
  UserInfoInputs,
  SecurityOption,
  SecurityParameterSchema,
} from '@mintlify/validation';
import { useEffect, useState } from 'react';

import { SecurityInputs, loadCredentialStore, storeCredentials } from './storedCredentials';
import {
  ServerVariableStore,
  loadServerVariableStore,
  storeServerVariables,
} from './storedServerVariables';

type UsePrefilledInputsParams = {
  inputs: ApiPlaygroundInputs;
  setInputs: (inputs: ApiPlaygroundInputs) => void;
  baseUrl?: string;
  securityOption?: SecurityOption;
  userInfoInputs?: UserInfoInputs;
};

export function usePrefilledInputs({
  inputs,
  setInputs,
  baseUrl,
  securityOption,
  userInfoInputs,
}: UsePrefilledInputsParams) {
  const [serverVariableStore, setServerVariableStore] = useState<ServerVariableStore>();
  const [credentialStore, setCredentialStore] = useState<SecurityInputs>();

  useEffect(() => {
    setServerVariableStore(loadServerVariableStore());
    setCredentialStore(loadCredentialStore());
  }, []);

  useEffect(() => {
    const authInputs = parseAuthInputs(securityOption, credentialStore, userInfoInputs);
    const serverInputs = parseServerInputs(baseUrl, serverVariableStore, userInfoInputs);

    setInputs({
      ...authInputs,
      server: serverInputs,
      path: inputs.path,
      body: inputs.body,
    });
    // these values should only ever change on page load
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [serverVariableStore, credentialStore, userInfoInputs]);

  // load security variables
  useEffect(() => {
    const authInputs = parseAuthInputs(securityOption, credentialStore, userInfoInputs);
    setInputs({ ...inputs, ...authInputs });
    // we only want to update the security variables when the security option changes
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [securityOption]);

  // store security variables
  useEffect(() => {
    if (!securityOption) return;
    storeCredentials(
      securityOption,
      {
        query: inputs.query,
        header: inputs.header,
        cookie: inputs.cookie,
      },
      userInfoInputs
    );
    // we only want to store the credentials when their values change
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inputs.cookie, inputs.header, inputs.query]);

  // load server variables
  useEffect(() => {
    const serverInputs = parseServerInputs(baseUrl, serverVariableStore, userInfoInputs);
    setInputs({ ...inputs, server: serverInputs });
    // we only want to update the server variables when the base url changes
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [baseUrl]);

  // store server variables
  useEffect(() => {
    if (!baseUrl) return;
    storeServerVariables(baseUrl, inputs.server, userInfoInputs);
    // we only want to store the server variables when their values change
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [inputs.server]);
}

const parseAuthInputs = (
  securityOption: SecurityOption | undefined,
  credentialStore: SecurityInputs | undefined,
  userInfoInputs: UserInfoInputs | undefined
): SecurityInputs => {
  const inputs: SecurityInputs = {
    query: {},
    header: {},
    cookie: {},
  };

  if (!securityOption) {
    return inputs;
  }

  for (const section of ['query', 'header', 'cookie'] as const) {
    Object.entries(securityOption.parameters[section]).forEach(([name, schema]) => {
      inputs[section][name] =
        credentialStore?.[section][name] ??
        cleanUserInfoValue(userInfoInputs?.[section]?.[name], schema);
    });
  }
  return inputs;
};

// do a bit of preprocessing for common auth types
const cleanUserInfoValue = (value: unknown, schema: SecurityParameterSchema) => {
  if (!value) return value;

  if (schema.type === 'http' && schema.scheme === 'basic') {
    if (typeof value === 'object') {
      const username =
        'username' in value && typeof value.username === 'string' ? value.username : '';
      const password =
        'password' in value && typeof value.password === 'string' ? value.password : '';
      return `Basic ${Buffer.from(`${username}:${password}`).toString('base64')}`;
    }
    return undefined;
  }

  if (schema.type === 'http' && schema.scheme === 'bearer') {
    if (typeof value === 'string') {
      return value.startsWith('Bearer ') ? value : `Bearer ${value}`;
    }
    return undefined;
  }

  return value;
};

const parseServerInputs = (
  baseUrl: string | undefined,
  serverVariableStore: ServerVariableStore | undefined,
  userInfoInputs: UserInfoInputs | undefined
): Record<string, string> => {
  const userInfoServerVariables = userInfoInputs?.server;
  const storedServerVariables =
    serverVariableStore && baseUrl ? serverVariableStore[baseUrl] : undefined;

  return { ...userInfoServerVariables, ...storedServerVariables };
};
