'use client';

import { DocsConfig } from '@mintlify/validation';
import { ReactNode, useContext } from 'react';

import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

import { ChatAssistantContext } from '../search';

export function ContentSideLayout({ children }: { children?: ReactNode }) {
  const { divisions } = useContext(NavigationContext);
  const { docsConfig } = useContext(DocsConfigContext);
  const { banner } = useContext(BannerContext);
  const { isCustom, isCenter, isWide } = usePageMode();
  const { isChatSheetOpen } = useContext(ChatAssistantContext);

  const hasTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;

  if (isCustom || isCenter || isWide) {
    return null;
  }

  return (
    <div
      className={cn(
        getContentSideLayoutClassNames(docsConfig?.theme, hasTabs, hasBanner),
        isChatSheetOpen && 'invisible 3xl:visible'
      )}
      id={Ids.ContentSideLayout}
    >
      {children}
    </div>
  );
}

export function getContentSideLayoutClassNames(
  theme?: DocsConfig['theme'],
  hasTabs?: boolean,
  hasBanner?: boolean
) {
  const defaultClassNames = 'hidden xl:flex self-start sticky xl:flex-col max-w-[28rem]';
  if (theme === 'maple') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-8rem)] top-[8rem]'
        : hasTabs
          ? 'h-[calc(100vh-5.5rem)] top-[6rem]'
          : hasBanner
            ? 'h-[calc(100vh-5rem)] top-[6rem]'
            : 'h-[calc(100vh-4rem)] top-[2.5rem]'
    );
  }

  if (theme === 'palm') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-15rem)] top-[12rem]'
        : hasTabs
          ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
          : hasBanner
            ? 'h-[calc(100vh-9.5rem)] top-[9rem]'
            : 'h-[calc(100vh-8rem)] top-[6.5rem]'
    );
  }

  if (theme === 'willow') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-9rem)] top-[9rem]'
        : hasTabs
          ? 'h-[calc(100vh-6rem)] top-[5.5rem]'
          : hasBanner
            ? 'h-[calc(100vh-6rem)] top-[5rem]'
            : 'h-[calc(100vh-4rem)] top-10'
    );
  }

  if (theme === 'linden') {
    return cn(
      defaultClassNames,
      hasBanner ? 'h-[calc(100vh-12rem)] top-[12rem]' : 'h-[calc(100vh-6.5rem)] top-[6.5rem]'
    );
  }

  if (theme === 'almond') {
    return cn(defaultClassNames, 'h-[calc(100vh-6.5rem)] top-0');
  }

  if (theme === 'aspen') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-15rem)] top-[12rem]'
        : hasTabs
          ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
          : hasBanner
            ? 'h-[calc(100vh-9.5rem)] top-[9rem]'
            : 'h-[calc(100vh-8rem)] top-[6.5rem]'
    );
  }

  // default to mint theme, to avoid breaking changes from unsupported themes
  return cn(
    defaultClassNames,
    hasTabs && hasBanner
      ? 'h-[calc(100vh-12rem)] top-[12rem]'
      : hasTabs
        ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
        : hasBanner
          ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
          : 'h-[calc(100vh-8rem)] top-[6.5rem]'
  );
}

export function getSidePanelSheetClassNames(
  theme?: DocsConfig['theme'],
  hasTabs?: boolean,
  hasBanner?: boolean,
  isMobile?: boolean
) {
  if (isMobile) {
    return cn('z-10 fixed inset-0 h-screen w-full bg-background-light dark:bg-background-dark');
  }
  const defaultClassNames =
    'z-10 fixed right-0 w-[368px] border-l border-gray-500/5 dark:border-gray-300/[0.06] bg-background-light dark:bg-background-dark';
  if (theme === 'maple') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-5.5rem)] top-[5.5rem]'
        : hasTabs
          ? 'h-[calc(100vh-3rem)] top-[3rem]'
          : hasBanner
            ? 'h-[calc(100vh-2.5rem)] top-[2.5rem]'
            : 'h-[calc(100vh)] top-0'
    );
  }

  if (theme === 'palm') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
        : hasTabs
          ? 'h-[calc(100vh-7rem)] top-[7rem]'
          : hasBanner
            ? 'h-[calc(100vh-7rem)] top-[7rem]'
            : 'h-[calc(100vh-4rem)] top-[4rem]'
    );
  }

  if (theme === 'willow') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-5.5rem)] top-[5.5rem]'
        : hasTabs
          ? 'h-[calc(100vh-3rem)] top-[3rem]'
          : hasBanner
            ? 'h-[calc(100vh-2.5rem)] top-[2.5rem]'
            : 'h-[calc(100vh)] top-0'
    );
  }

  if (theme === 'linden') {
    return cn(
      defaultClassNames,
      hasBanner ? 'h-[calc(100vh-6.5rem)] top-[6.5rem]' : 'h-[calc(100vh-4rem)] top-[4rem]'
    );
  }

  if (theme === 'almond') {
    return cn(defaultClassNames, 'h-[calc(100vh-4.5rem)] top-[3.5rem]');
  }

  if (theme === 'aspen') {
    return cn(
      defaultClassNames,
      hasTabs && hasBanner
        ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
        : hasTabs
          ? 'h-[calc(100vh-6rem)] top-[6rem]'
          : hasBanner
            ? 'h-[calc(100vh-6rem)] top-[6rem]'
            : 'h-[calc(100vh-3rem)] top-[3rem]'
    );
  }

  // default to mint theme, to avoid breaking changes from unsupported themes
  return cn(
    defaultClassNames,
    hasTabs && hasBanner
      ? 'h-[calc(100vh-9.5rem)] top-[9.5rem]'
      : hasTabs
        ? 'h-[calc(100vh-7rem)] top-[7rem]'
        : hasBanner
          ? 'h-[calc(100vh-7rem)] top-[7rem]'
          : 'h-[calc(100vh-4rem)] top-[4em]'
  );
}
