'use client';

import { AnyRecord } from '@mintlify/common';
import { useCallback, useEffect, useRef, useState } from 'react';

export type WebSocketMessageData = string | AnyRecord;

export type WebSocketMessage = {
  type: 'send' | 'receive' | string;
  timestamp: number;
  data: WebSocketMessageData;
};

export type UseWebsocketConnectionProps = {
  address: string;
  onMessage?: (message: WebSocketMessage) => void;
};

export const useWebsocketConnection = ({ address, onMessage }: UseWebsocketConnectionProps) => {
  const wsRef = useRef<WebSocket | null>(null);
  const [error, setError] = useState<string>('');
  const [isConnected, setIsConnected] = useState(false);
  const [connecting, setIsConnecting] = useState(false);

  useEffect(() => {
    return () => {
      if (wsRef.current) {
        wsRef.current.close();
        wsRef.current = null;
      }
    };
  }, []);

  const connect = useCallback(() => {
    try {
      if (wsRef.current) {
        wsRef.current.close();
      }

      const ws = new WebSocket(address);
      wsRef.current = ws;

      setIsConnecting(true);

      ws.onopen = () => {
        setIsConnected(true);
        setError('');
        setIsConnecting(false);
      };

      ws.onclose = () => {
        setIsConnected(false);
        setError('');
      };

      ws.onerror = (event) => {
        setError('WebSocket connection error');
        setIsConnected(false);
        setIsConnecting(false);
        console.error('WebSocket error:', event);
      };

      ws.onmessage = (event) => {
        if (onMessage) {
          try {
            const data = JSON.parse(event.data);
            onMessage({
              type: 'receive',
              timestamp: Date.now(),
              data,
            });
          } catch (e) {
            onMessage({
              type: 'receive',
              timestamp: Date.now(),
              data: event.data,
            });
          }
        }
      };
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to connect to WebSocket');
      setIsConnected(false);
      setIsConnecting(false);
    }
  }, [address, onMessage, setIsConnected]);

  const disconnect = useCallback(() => {
    setIsConnecting(true);
    if (wsRef.current) {
      wsRef.current.onclose = () => {
        setIsConnecting(false);
        setIsConnected(false);
      };
      wsRef.current.close();
      wsRef.current = null;
    }
  }, []);

  const sendMessage = useCallback(
    (data: WebSocketMessageData) => {
      if (!wsRef.current || wsRef.current.readyState !== WebSocket.OPEN) {
        setError('WebSocket is not connected');
        return;
      }

      try {
        const message = typeof data === 'string' ? data : JSON.stringify(data);
        wsRef.current.send(message);

        if (onMessage) {
          onMessage({
            type: 'send',
            timestamp: Date.now(),
            data,
          });
        }
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to send message');
      }
    },
    [onMessage]
  );

  return {
    connect,
    disconnect,
    sendMessage,
    error,
    connecting,
    setIsConnecting,
    isConnected,
    setIsConnected,
  };
};
