'use client';

import { ApiPlaygroundInputs } from '@mintlify/validation';
import { useCallback } from 'react';

import { useApiPlaygroundInputs } from '@/hooks/useApiPlaygroundInputs';

export const useSetApiPlaygroundInputsCallback = () => {
  const [, dispatch] = useApiPlaygroundInputs();
  return useCallback(
    (inputs: ApiPlaygroundInputs) =>
      dispatch({
        type: 'set_api_playground_inputs',
        payload: inputs,
      }),
    [dispatch]
  );
};
