import { debounce } from 'lodash';
import { SearchIcon } from 'lucide-react';

import { useDarkMode } from '@/hooks/useDarkMode';
import { DEBOUNCE_TIME_IN_MS } from '@/ui/search';
import { cn } from '@/utils/cn';

export const EndpointsMenuSearch = ({
  search,
  setSearch,
}: {
  search: string;
  setSearch: (search: string) => void;
}) => {
  const isDarkMode = useDarkMode();
  const handleSearchChange = debounce(async (search: string) => {
    setSearch(search);
  }, DEBOUNCE_TIME_IN_MS);

  return (
    <div
      className={cn(
        'h-[52px] sticky z-[2] -mb-1 -mx-2 pb-0 p-2 left-0 top-0',
        isDarkMode ? 'fade-down-dark' : 'fade-down-light'
      )}
    >
      <div className="p-1.5 border border-gray-200/70 dark:border-white/10 rounded-[0.6rem] bg-white dark:bg-gray-950 border-solid flex items-center gap-x-2 mb-2">
        <SearchIcon size={16} className="min-w-4 flex-none text-gray-400 dark:text-gray-600" />
        <input
          type="text"
          value={search}
          onChange={(e) => handleSearchChange(e.target.value)}
          autoFocus
          placeholder="Search for endpoint..."
          className="w-full text-sm text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-600 font-light focus:outline-none dark:bg-gray-950"
        />
      </div>
    </div>
  );
};
