'use client';

import {
  BodySchema,
  DataSchema,
  DataSchemaArray,
  IncrementalDataSchema,
} from '@mintlify/validation';
import { useMemo, useState } from 'react';

import { Tab, Tabs } from '@/components/Tabs';
import { useComponentSchemas } from '@/hooks/useComponentSchemas';
import { Classes } from '@/lib/local/selectors';
import { getOptionInformation } from '@/openapi/getOptionInformation';
import { cn } from '@/utils/cn';

import { RecursiveParamField } from './ParamFields/RecursiveParamField';
import { SectionHeading } from './components/SectionHeading';

export type EndpointBodyParams = {
  body: BodySchema;
};

export const EndpointBody = ({ body }: EndpointBodyParams) => {
  const [selectedContentTypeIndex, setSelectedContentTypeIndex] = useState(0);
  const componentSchemas = useComponentSchemas();

  const contentTypeInfo = useMemo(() => {
    return Object.entries(body).map(([contentType, { schemaArray, description }]) => {
      const { typeOptionLabels, originalIndices } = getOptionInformation(
        schemaArray,
        componentSchemas
      );

      const firstSchemaIndex = originalIndices[0];
      const firstSchema =
        firstSchemaIndex !== undefined ? schemaArray[firstSchemaIndex] : undefined;

      // if there is only one option (no tabs) and it is not an object with properties, append type to content type string
      const contentTypeLabel =
        typeOptionLabels.length === 1 && firstSchema && !isObjectWithProperties(firstSchema)
          ? `${contentType}  ${typeOptionLabels[0]}`
          : contentType;

      return {
        contentTypeLabel,
        typeOptionLabels,
        originalIndices,
        description,
        schemaArray: originalIndices.map((index) => schemaArray[index]) as DataSchemaArray,
      };
    });
  }, [body, componentSchemas]);

  const selectedContentTypeInfo = contentTypeInfo[selectedContentTypeIndex];

  if (selectedContentTypeInfo === undefined) {
    return null;
  }

  // edit the option label for the selected content type, in case type has been appended
  const modifiedOptions = Object.keys(body);
  modifiedOptions[selectedContentTypeIndex] = selectedContentTypeInfo.contentTypeLabel;

  const hideBodyDescription =
    selectedContentTypeInfo.description == selectedContentTypeInfo.schemaArray[0].description;

  return (
    <div className={cn(Classes.APISection)}>
      {modifiedOptions.length > 1 ? (
        <SectionHeading
          title="Body"
          options={modifiedOptions}
          selectedIndex={selectedContentTypeIndex}
          onSelectOption={setSelectedContentTypeIndex}
          subtitle={selectedContentTypeInfo.description}
        />
      ) : (
        <SectionHeading
          title="Body"
          rightElement={modifiedOptions[0]}
          subtitle={selectedContentTypeInfo.description}
        />
      )}
      {selectedContentTypeInfo.schemaArray.length > 1 ? (
        <div className="mt-4 rounded-xl border border-gray-100 px-4 pt-2 dark:border-white/10">
          <Tabs
            key={selectedContentTypeIndex}
            className="!-mt-1 !border-gray-100 !mb-0 dark:!border-white/10"
          >
            {selectedContentTypeInfo.schemaArray.map((schema, i) => {
              return (
                <Tab key={i} title={selectedContentTypeInfo.typeOptionLabels[i] ?? ''}>
                  <RecursiveParamField
                    fieldType="body"
                    schemaArray={[schema]}
                    hideDescription={hideBodyDescription}
                  />
                </Tab>
              );
            })}
          </Tabs>
        </div>
      ) : (
        <RecursiveParamField
          fieldType="body"
          schemaArray={selectedContentTypeInfo.schemaArray}
          hideDescription={hideBodyDescription}
        />
      )}
    </div>
  );
};

export const isObjectWithProperties = (schema: DataSchema | IncrementalDataSchema): boolean => {
  return schema.type === 'object' && Object.keys(schema.properties).length > 0;
};
