import { AsyncApiOperation } from '@mintlify/common';

import { WebsocketReceiveIcon, WebsocketSendIcon } from '@/ui/icons';

import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';
import { RecursiveExpandSchemaContainer } from './RecursiveExpandSchemaContainer';

export const OperationMessages = ({
  operations,
  type,
}: {
  operations: AsyncApiOperation[];
  type: 'send' | 'receive';
}) => {
  const name = type === 'send' ? 'Send' : 'Receive';
  const icon = type === 'send' ? <WebsocketSendIcon /> : <WebsocketReceiveIcon />;
  const operationMessages = operations.flatMap((operation) => operation.messages);
  return (
    <ExpandableSectionContainer name={name} rightElement={icon}>
      {operationMessages.map((message) => {
        if (!message.payload) return null;
        return (
          <RecursiveExpandSchemaContainer
            key={message.id}
            schema={message.payload}
            defaultShowChildren={false}
          />
        );
      })}
    </ExpandableSectionContainer>
  );
};
