'use client';

import { ComponentProps } from 'react';

import { getNodeText } from '@/components/Code/getNodeText';
import '@/css/code.css';
import { useGetShikiHighlightedHtml } from '@/utils/shiki';

interface MarkdownCodeProps extends ComponentProps<'code'> {
  codeBlockTheme: 'system' | 'dark' | undefined;
}

export const MarkdownCode = ({
  className,
  children,
  codeBlockTheme,
  ...props
}: MarkdownCodeProps) => {
  const codeString = getNodeText(children);
  const html = useGetShikiHighlightedHtml({
    codeString,
    codeBlockTheme,
    className,
    opts: {
      structure: 'inline',
    },
  });

  return html ? (
    <code {...props} dangerouslySetInnerHTML={{ __html: html }} />
  ) : (
    <code {...props} className={className}>
      {children}
    </code>
  );
};
