import Link from 'next/link';

import { Logo } from './Logo';

export type NavLogoProps = {
  href: string;
  onContextMenu?: (e: React.MouseEvent<HTMLAnchorElement>) => void;
  name?: string;
  logoClassName?: string;
};

export const NavLogo = ({ href, onContextMenu, name, logoClassName }: NavLogoProps) => {
  return (
    <Link href={href} onContextMenu={onContextMenu}>
      <span className="sr-only">{name} home page</span>
      <Logo className={logoClassName} />
    </Link>
  );
};
