'use client';

import { SchemaWithLabelInfo } from '@/hooks/usePlaygroundSchemaOptions';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

type TypeDropdownParams = {
  options: SchemaWithLabelInfo[];
  selectedIndex: number;
  onSelectOption: (index: number) => void;
  className?: string;
};

function getOptionLabel(option?: SchemaWithLabelInfo) {
  if (!option) return;
  if (option.schema.deprecated) {
    return `${option.label}, deprecated`;
  }
  return option.label;
}

export const TypeDropdown = ({
  options,
  selectedIndex,
  onSelectOption,
  className,
}: TypeDropdownParams) => {
  const displayOptions = options.length > 1;
  return (
    <div
      className={cn(
        'relative flex items-center px-2 py-0.5 text-gray-600 dark:text-gray-300 hover:text-gray-950 dark:hover:text-white font-medium',
        className
      )}
    >
      <div className="">{getOptionLabel(options[selectedIndex])}</div>
      {displayOptions && (
        <Icon
          icon={'angle-down'}
          iconType="solid"
          className="h-2.5 w-2.5 bg-gray-500 dark:bg-gray-400 shrink-0 ml-1.5"
        />
      )}
      {displayOptions && (
        <select
          aria-label="parameter type select"
          className="absolute opacity-0 inset-0 cursor-pointer"
          onChange={(e) => onSelectOption(e.target.selectedIndex)}
          value={selectedIndex}
        >
          {options.map((option, index) => (
            <option value={index} key={index}>
              {getOptionLabel(option)}
            </option>
          ))}
        </select>
      )}
    </div>
  );
};
