import {
  isString,
  isNumber,
  isInteger,
  isObject,
  isArray,
  isBoolean,
  isNull,
} from '@mintlify/common';
import { AnySchema, DataSchema, IncrementalDataSchema } from '@mintlify/validation';
import { useRef, useEffect } from 'react';

export const useSetDefaultValue = (
  schema: Exclude<DataSchema | IncrementalDataSchema, AnySchema>,
  value: unknown,
  setValue: (value: unknown) => void
) => {
  const hasMountedRef = useRef(false);

  useEffect(() => {
    if (!hasMountedRef.current) {
      if (schema.default === undefined && schema['x-default' as 'default'] !== undefined) {
        schema.default = schema['x-default' as never];
      }

      switch (schema.type) {
        case 'string': {
          if (isString(schema.default) && !isString(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'number': {
          if (isNumber(schema.default) && !isNumber(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'boolean': {
          if (isBoolean(schema.default) && !isBoolean(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'object': {
          if (schema.default && isObject(schema.default) && !isObject(value)) {
            const safeValue: Record<string, unknown> = {};
            for (const [key, val] of Object.entries(schema.default)) {
              safeValue[key] = val;
            }
            setValue(safeValue);
          }
          break;
        }
        case 'integer': {
          if (isInteger(schema.default) && !isInteger(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'array': {
          if (schema.default !== undefined && !isArray(value)) {
            const defaultValue = isArray(schema.default) ? schema.default : [schema.default];
            setValue([...defaultValue]);
          }
          break;
        }
        case 'enum<string>': {
          if (isString(schema.default) && !isString(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'enum<number>': {
          if (isNumber(schema.default) && !isNumber(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'enum<integer>': {
          if (isInteger(schema.default) && !isInteger(value)) {
            setValue(schema.default);
          }
          break;
        }
        case 'null': {
          if (isNull(schema.default) && value !== null) {
            setValue(schema.default);
          }
          break;
        }
      }
      hasMountedRef.current = true;
    }
  }, [schema, value, setValue]);
};
