'use client';

import { ApiPlaygroundResultType, ApiPlaygroundDisplayType } from '@mintlify/models';
import { Endpoint } from '@mintlify/validation';
import { useContext, useEffect, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { EndpointLocationContext } from '@/contexts/EndpointLocationContext';
import { useBaseUrlOptions } from '@/hooks/useBaseUrlOptions';
import { useCheckHash } from '@/hooks/useCheckHash';
import { useKeyboardShortcut } from '@/hooks/useKeyboardShortcut';
import { useApiPlaygroundInputs } from '@/hooks/useMDXContentController/useApiPlaygroundInputs';
import { useSetApiBaseIndexCallback } from '@/hooks/useMDXContentController/useMDXContentCallbacks/useSetApiBaseIndexCallback';
import { useSetApiPlaygroundInputsCallback } from '@/hooks/useMDXContentController/useMDXContentCallbacks/useSetApiPlaygroundInputsCallback';
import { useSendPlaygroundRequest } from '@/hooks/useSendPlaygroundRequest';
import ErrorBoundary from '@/ui/ErrorBoundary';

import { ApiPlaygroundContext } from './ApiPlaygroundContext';
import { Request } from './Request';

type ApiPlaygroundParams = {
  endpoint: Endpoint;
  display: ApiPlaygroundDisplayType;
};

export const ApiPlayground = ({ endpoint, display }: ApiPlaygroundParams) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const inputs = useApiPlaygroundInputs();
  const setInputs = useSetApiPlaygroundInputsCallback();
  const setApiBaseIndex = useSetApiBaseIndexCallback();

  const baseUrlOptions = useBaseUrlOptions();
  const [selectedBaseUrlIndex, setSelectedBaseUrlIndex] = useState(0);
  const [isPlaygroundExpanded, setIsPlaygroundExpanded] = useState(false);

  const [isSending, setIsSending] = useState(false);
  const [result, setResult] = useState<ApiPlaygroundResultType>();

  const [selectedContentTypeIndex, _setSelectedContentTypeIndex] = useState(0);
  const contentType = Object.keys(endpoint.request.body)[selectedContentTypeIndex];

  const baseUrl = baseUrlOptions?.[selectedBaseUrlIndex] ?? baseUrlOptions?.[0] ?? '';

  useEffect(() => {
    const searchParams = new URLSearchParams(window.location.search);
    if (searchParams.has('playground')) {
      requestAnimationFrame(() => {
        setIsPlaygroundExpanded(true);
      });
    }
  }, []);

  const sendRequest = useSendPlaygroundRequest({
    endpoint,
    baseUrl,
    contentType: contentType ?? 'application/json',
    setIsFetching: setIsSending,
    setResult,
  });

  const setBaseUrlIndex = (index: number) => {
    setSelectedBaseUrlIndex(index);
    setApiBaseIndex(index);
  };

  const setIsPlaygroundExpandedWithQueryParam = (expanded: boolean) => {
    setIsPlaygroundExpanded(expanded);
    if (expanded) {
      const url = new URL(window.location.href);
      url.searchParams.set('playground', 'open');
      window.history.replaceState({}, '', url);
    } else {
      const url = new URL(window.location.href);
      url.searchParams.delete('playground');
      window.history.replaceState({}, '', url);
    }
  };

  useKeyboardShortcut(
    'Enter',
    docsConfig?.api?.playground?.display === 'interactive' ? sendRequest : undefined
  );
  useKeyboardShortcut(
    'Escape',
    isPlaygroundExpanded ? () => setIsPlaygroundExpandedWithQueryParam(false) : undefined,
    true
  );

  const anchor = useCheckHash();

  return (
    <ErrorBoundary>
      <ApiPlaygroundContext.Provider
        value={{
          isSending,
          sendRequest,
          result,
          setSelectedBaseUrlIndex: setBaseUrlIndex,
          selectedBaseUrlIndex,
          baseUrlOptions,
          isPlaygroundExpanded,
          setIsPlaygroundExpanded: setIsPlaygroundExpandedWithQueryParam,
        }}
      >
        <div className="mt-6 flex w-full flex-col space-y-4">
          <EndpointLocationContext.Provider value={{ location: 'request', anchor }}>
            <Request inputs={inputs} setInputs={setInputs} endpoint={endpoint} display={display} />
          </EndpointLocationContext.Provider>
        </div>
      </ApiPlaygroundContext.Provider>
    </ErrorBoundary>
  );
};
