'use client';

import { ComponentPropsWithoutRef } from 'react';

import { CodeBlock } from '@/components/CodeBlock';
import { CodeGroup } from '@/components/CodeGroup';
import { ComponentPropsWithoutRefAndChildren } from '@/types/componentPropsWithoutRefAndChildren';
import { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';

import { CodeSnippet } from './CodeSnippet';

export interface CodeSnippetsProps extends ComponentPropsWithoutRefAndChildren<typeof CodeGroup> {
  snippets: ExampleCodeSnippet[];
}

export const CodeSnippets = ({
  snippets,
  ...props
}: CodeSnippetsProps & ComponentPropsWithoutRef<typeof CodeGroup>) => (
  <CodeGroup isSmallText {...props}>
    {snippets.map(({ code, ...snippet }) => (
      <CodeBlock language={snippet.language} filename={snippet.filename} key={snippet.filename}>
        <CodeSnippet language={snippet.language}>{code}</CodeSnippet>
      </CodeBlock>
    ))}
  </CodeGroup>
);
