import { AsyncApiSecurityScheme } from '@mintlify/common';
import { StringSchema } from '@mintlify/validation';

import { RecursiveInput } from '../Request/inputs/RecursiveInput';
import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';

type InputSecurityProps = {
  name: string;
  securitySchemes: AsyncApiSecurityScheme[];
  securitySchemeValues: Record<string, { name: string; value: string }>;
  handleSecuritySchemeChange: (type: string, value: string, name: string) => void;
};

export const InputSecurity = ({
  name,
  securitySchemes,
  securitySchemeValues,
  handleSecuritySchemeChange,
}: InputSecurityProps) => {
  return (
    <ExpandableSectionContainer name={name}>
      {securitySchemes.map((scheme) => {
        const schema = {
          id: scheme.id,
          type: 'string',
          title: scheme.name,
          description: scheme.description,
          required: true,
        } as StringSchema;
        return (
          <RecursiveInput
            key={scheme.id}
            name={scheme.name}
            schemaArray={[schema]}
            value={(securitySchemeValues[scheme.type]?.value as string) || ''}
            setValue={(value) =>
              handleSecuritySchemeChange(scheme.type, value as string, scheme.name ?? '')
            }
            clearValue={() => handleSecuritySchemeChange(scheme.type, '', scheme.name ?? '')}
          />
        );
      })}
    </ExpandableSectionContainer>
  );
};
