'use client';

import { DataSchema, IncrementalDataSchema } from '@mintlify/validation';
import { ReactNode, useState } from 'react';

import { Description } from '@/api-playground/EndpointFields/components/Description';
import { DeprecatedPill, InfoPill, RequiredPill } from '@/components/Param';
import ErrorBoundary from '@/ui/ErrorBoundary';
import { cn } from '@/utils/cn';

import { ArrayCount } from './ArrayCount';
import { ObjectExpandButton } from './ObjectExpandButton';
import { TrashButton } from './TrashButton';

type InputContainerParams = {
  name?: string;
  typeDropdown: ReactNode;
  schema: DataSchema | IncrementalDataSchema;
  children?: ReactNode;
  onClear: (() => void) | undefined;
  isObject?: boolean;
  isArray?: boolean;
  parentNames?: string[];
  arrayIndex?: number;
  defaultExpanded?: boolean;
  style?: string;
  explode?: boolean;
};

export const InputContainer = ({
  name,
  children,
  schema,
  onClear,
  isObject,
  isArray,
  parentNames,
  arrayIndex,
  typeDropdown,
  defaultExpanded = true,
  style,
  explode,
}: InputContainerParams) => {
  const [showChildren, setShowChildren] = useState(defaultExpanded);
  const propertiesCount = schema.type === 'object' ? Object.keys(schema.properties).length : 0;

  return (
    <ErrorBoundary>
      <div
        className={cn(
          'flex space-x-3 items-start',
          arrayIndex != undefined && !isObject && 'flex items-center'
        )}
      >
        {arrayIndex != undefined && (
          <ArrayCount className={cn(isObject && 'mt-8')}>{arrayIndex + 1}</ArrayCount>
        )}
        <div
          className={cn(
            'flex-1 grid lg:grid-cols-2 gap-x-12 gap-y-4 py-5 max-w-full',
            isArray && 'flex flex-col',
            isObject &&
              'gap-y-0 flex flex-col border border-gray-50 dark:border-white/5 rounded-xl px-4 py-0 my-5',
            arrayIndex !== undefined && 'py-1 my-2'
          )}
        >
          <div
            className={cn(
              'space-y-2',
              isObject && 'pt-8 py-5 border-b border-gray-50 dark:border-white/5',
              !showChildren && 'border-none',
              arrayIndex != undefined && !isObject && 'flex items-center'
            )}
          >
            <div className="flex items-center justify-between font-mono font-bold">
              <div
                className="flex flex-wrap items-center gap-2 text-xs truncate"
                title={parentNames ? `${parentNames.join('.')}.${name}` : name}
              >
                <div className={cn('text-sm truncate', name == undefined && 'hidden')}>
                  {parentNames && parentNames.length > 0 && style === 'deepObject' && explode ? (
                    <>
                      <span className="text-gray-500 dark:text-gray-400">
                        {parentNames.map((parentName, index) => (
                          <>
                            {parentName}
                            {index === 0 ? '[' : ']['}
                          </>
                        ))}
                      </span>
                      <span className="font-semibold text-gray-900 dark:text-gray-100">{name}</span>
                      <span className="text-gray-500 dark:text-gray-400">{']'}</span>
                    </>
                  ) : (
                    <>
                      <span className="text-gray-500 dark:text-gray-400">
                        {parentNames ? `${parentNames.join('.')}.` : ''}
                      </span>
                      <span className="font-semibold text-gray-900 dark:text-gray-100">{name}</span>
                    </>
                  )}
                </div>
                {typeDropdown != undefined ? typeDropdown : <InfoPill>{schema.type}</InfoPill>}
                {schema.required && <RequiredPill />}
                {schema.deprecated && <DeprecatedPill />}
              </div>
              {isObject && (
                <ObjectExpandButton
                  onClick={() => setShowChildren(!showChildren)}
                  count={propertiesCount}
                  labeled={defaultExpanded == false}
                />
              )}
            </div>
            {schema.description && (
              <Description
                markdown={schema.description}
                className="text-gray-500 dark:text-gray-400"
              />
            )}
          </div>
          {showChildren && (
            <div
              className={cn(
                'grid grid-cols-1 w-full items-start divide-y divide-gray-50 dark:divide-white/5',
                isArray && 'divide-y-0'
              )}
            >
              {children}
            </div>
          )}
        </div>
        {(arrayIndex != undefined || parentNames) && onClear && (
          <TrashButton
            className={cn(isObject && 'mt-8', parentNames && 'mt-7')}
            onClick={() => onClear()}
          />
        )}
      </div>
    </ErrorBoundary>
  );
};
