'use client';

import { DropdownNavigation } from '@mintlify/validation/dist/mint-config/schemas/v2/properties/navigation/divisionNav';
import { CheckIcon } from 'lucide-react';
import { useContext, useState } from 'react';

import { DropdownMenu, DropdownMenuContent, DropdownMenuTrigger } from '@/components/Dropdown';
import { DynamicLink } from '@/components/DynamicLink';
import { NavigationContext } from '@/contexts/NavigationContext';
import { Classes } from '@/lib/local/selectors';
import { ComponentIcon } from '@/ui/Icon';
import { DropdownArrowIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';
import { DivisionState } from '@/utils/nav/filterDivisions';

const DEFAULT_ICON = 'layer-group';
const DEFAULT_ICON_TYPE = 'solid';

function DropdownListItem({
  dropdown,
  isActive,
}: {
  dropdown: DropdownNavigation<'decorated'> & DivisionState;
  isActive: boolean;
}) {
  const color = dropdown.color;

  return (
    <>
      <div
        className={cn(
          Classes.DropdownItemIcon,
          'h-8 w-8 flex items-center justify-center rounded-lg flex-shrink-0 border border-gray-200/70 dark:border-white/[0.07]'
        )}
      >
        <ComponentIcon
          icon={
            typeof dropdown.icon === 'string' ? dropdown.icon : dropdown.icon?.name ?? DEFAULT_ICON
          }
          iconType={
            typeof dropdown.icon === 'string'
              ? DEFAULT_ICON_TYPE
              : dropdown.icon?.style || DEFAULT_ICON_TYPE
          }
          className={cn('h-4 w-4 bg-primary dark:bg-primary-light')}
          color={color?.light ?? color?.dark}
        />
      </div>
      <div
        className={cn(
          Classes.DropdownItemTextContainer,
          'flex-1 px-1 flex flex-col grow text-left'
        )}
      >
        <p
          className={cn(
            Classes.DropdownItemTitle,
            'text-base lg:text-sm text-gray-800 dark:text-gray-300 font-medium'
          )}
        >
          {dropdown.dropdown}
        </p>
        {dropdown.description && (
          <p
            className={cn(
              Classes.DropdownItemDescription,
              'hidden lg:block text-sm lg:text-xs text-gray-600 dark:text-gray-400'
            )}
            title={dropdown.description}
          >
            {dropdown.description}
          </p>
        )}
      </div>
      {isActive && <CheckIcon className="ml-2 h-4 w-4 text-primary dark:text-primary-light" />}
    </>
  );
}

export const Dropdowns = ({ triggerClassName }: { triggerClassName?: string }) => {
  const [open, setOpen] = useState(false);
  const { divisions } = useContext(NavigationContext);
  const dropdowns = divisions.dropdowns;
  const activeDropdown = dropdowns.find((dropdown) => dropdown.isActive) ?? dropdowns[0];

  if (dropdowns.length === 0 || !activeDropdown) return null;

  return (
    <DropdownMenu open={open} onOpenChange={setOpen}>
      <DropdownMenuTrigger
        className={cn(
          Classes.DropdownTrigger,
          'z-10 group flex w-full items-center pl-2 pr-3.5 py-1.5 rounded-[0.85rem] border border-gray-200/70 dark:border-white/[0.07] hover:bg-gray-600/5 dark:hover:bg-gray-200/5 mb-4 gap-1',
          triggerClassName
        )}
      >
        <DropdownListItem dropdown={activeDropdown} isActive={false} />
        <DropdownArrowIcon className={cn('rotate-90', open && 'rotate-[270deg]')} />
      </DropdownMenuTrigger>
      <DropdownMenuContent className={cn(Classes.DropdownContent, 'p-1 gap-2')} align="start">
        {dropdowns.map((dropdown) => (
          <DynamicLink
            key={dropdown.name + dropdown.href}
            href={dropdown.href}
            className={cn(
              Classes.DropdownItem,
              'rounded-xl text-gray-800 hover:text-gray-900 px-1.5 pr-2.5 py-1.5 dark:text-gray-300 dark:hover:text-gray-200 flex group items-center gap-1 hover:bg-gray-950/5 dark:hover:bg-white/5'
            )}
            onClick={() => setOpen(false)}
            data-dropdown-item={dropdown.dropdown}
          >
            <DropdownListItem dropdown={dropdown} isActive={dropdown.isActive} />
          </DynamicLink>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  );
};
