'use client';

import { ApiPlaygroundInputs, CodeSample, type Endpoint } from '@mintlify/validation';

import { CodeSnippets } from '@/components/Code/CodeSnippets';
import { shikiLangToDisplayLang } from '@/constants/snippetPresets';
import { generateSnippetMap } from '@/openapi/apiExampleGeneration/generators/generateSnippetMap';

import { CodeGroupSelect } from '../Code/CodeGroupSelect';

export type CodeSamplesExampleParams = {
  codeSamples: CodeSample[];
  baseUrl?: string;
  endpoint: Endpoint;
  inputs: ApiPlaygroundInputs;
  apiPlaygroundMode: string;
  requestExampleLanguages?: string[];
  requiredOnly?: boolean;
};

export const CodeSamplesExample = (props: CodeSamplesExampleParams): JSX.Element | null => {
  const snippetMap = generateSnippetMap(props);

  if (Object.values(snippetMap).every((value) => Object.keys(value).length <= 1)) {
    const codeSnippetMap = Object.values(snippetMap);
    let snippets = codeSnippetMap.flatMap((record) => Object.values(record));
    const uniqueLabels = new Set(snippets.map((snippet) => snippet.filename));
    if (uniqueLabels.size !== snippets.length) {
      snippets = snippets.map((snippet) => ({
        ...snippet,
        filename: shikiLangToDisplayLang[snippet.language] ?? snippet.language,
      }));
    }
    return <CodeSnippets snippets={snippets} noMargins />;
  }

  return <CodeGroupSelect snippets={snippetMap} />;
};
