'use client';

import { isAbsoluteUrl } from '@mintlify/common';
import { ChevronDownIcon, ChevronUpIcon } from 'lucide-react';
import { useEffect, useState } from 'react';

import { cn } from '@/utils/cn';

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from './Dropdown';

export type Option = string | { name: string; href?: string };

const getOptionName = (option: Option) => {
  return typeof option === 'string' ? option : option.name;
};

export function PillSelect({
  options,
  onChange,
  defaultOption,
  selectedOptionClass,
  triggerClassName,
}: {
  /** Array of options to select from */
  options: Option[];

  /** Function called when the selected option changes. */
  onChange?: (selectedOption: Option) => void;

  /** Option to initially show as selected in the dropdown */
  defaultOption?: Option;

  /** Additional classes to apply on dropdown text for the currently selected option */
  selectedOptionClass?: string;

  /** Additional classes to apply on the dropdown trigger */
  triggerClassName?: string;
}) {
  const [isOpen, setIsOpen] = useState(false);
  let defaultState = defaultOption;
  const isOptionsValid = Array.isArray(options) && options.length > 0;
  if (defaultOption == null && isOptionsValid) {
    defaultState = options[0];
  }
  const [selectedOption, setSelectedOption] = useState<Option | undefined>(defaultState);

  useEffect(() => {
    if (defaultOption && selectedOption !== defaultOption) {
      setSelectedOption(defaultOption);
    }
  }, [defaultOption, selectedOption]);

  if (!isOptionsValid) {
    return null;
  }

  const onClickOption = (option: Option) => {
    if (typeof option === 'object' && option.href && isAbsoluteUrl(option.href)) {
      window.open(option.href, '_blank');
      return;
    }
    setSelectedOption(option);
    if (onChange) {
      onChange(option);
    }
  };

  return (
    <DropdownMenu open={isOpen} onOpenChange={setIsOpen}>
      <DropdownMenuTrigger
        className={cn(
          'text-xs gap-1.5 text-gray-500 dark:text-gray-400 leading-5 font-semibold border border-gray-200 dark:border-gray-800 hover:border-gray-300 dark:hover:border-gray-700 rounded-full py-1 px-3 flex items-center space-x-2 whitespace-nowrap',
          triggerClassName
        )}
      >
        {selectedOption && getOptionName(selectedOption)}
        {isOpen ? (
          <ChevronUpIcon size={12} strokeWidth={2.5} />
        ) : (
          <ChevronDownIcon size={12} strokeWidth={2.5} />
        )}
      </DropdownMenuTrigger>
      <DropdownMenuContent className="min-w-36">
        {options.map((option, i) => {
          const optionName = getOptionName(option);
          const isSelected = selectedOption ? optionName === getOptionName(selectedOption) : false;
          return (
            <DropdownMenuItem
              key={`option-${i}`}
              onSelect={() => onClickOption(option)}
              isSelected={isSelected}
              className={cn(
                'flex justify-between gap-2 p-1.5 text-xs',
                isSelected
                  ? cn('text-primary dark:text-primary-light font-semibold', selectedOptionClass)
                  : 'font-medium'
              )}
            >
              {getOptionName(option)}
            </DropdownMenuItem>
          );
        })}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
