'use client';

import { slugToTitle } from '@mintlify/common';
import { MDXClient } from '@mintlify/mdx';
import { useContext } from 'react';

import components from '@/components';
import { DocsConfigContext, PageContext } from '@/contexts/ConfigContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { useEndpoint } from '@/hooks/useEndpoint';
import { useMDXContent } from '@/hooks/useMDXContent';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { getThemeStyling } from '@/themes';
import { Eyebrows } from '@/ui/Eyebrows';
import { DeprecatedLabel } from '@/ui/Labels';
import { cn } from '@/utils/cn';
import { sanitizePathname } from '@/utils/sanitizePathname';

import { PageContextMenu } from '../PageContextMenu';

export function PageHeader() {
  const { description, pageType } = useContext(PageContext);
  const { docsConfig } = useContext(DocsConfigContext);
  const [{ pageMetadata }] = useMDXContent();
  const currentPath = useCurrentPath();
  const endpoint = useEndpoint();
  const { isCustom } = usePageMode();
  const title = pageMetadata.title || slugToTitle(currentPath);

  const isPdf = pageType === 'pdf';

  if (!title && !description) return null;

  if (isCustom) {
    return null;
  }

  const Description = () => {
    if (typeof description === 'string') {
      return <div className="mt-2 text-lg prose prose-gray dark:prose-invert">{description}</div>;
    }

    if (description) {
      return (
        <div className="mt-2 text-lg prose prose-gray dark:prose-invert">
          <MDXClient components={components} {...description} />
        </div>
      );
    }

    return null;
  };

  return (
    <header id={!isPdf ? Ids.Header : sanitizePathname(currentPath)} className="relative">
      <div className="mt-0.5 space-y-2.5">
        <Eyebrows />
        <div className="flex items-center relative gap-2">
          <h1
            id={Ids.PageTitle}
            className={cn(
              'inline-block text-2xl sm:text-3xl font-bold text-gray-900 tracking-tight dark:text-gray-200',
              getThemeStyling(docsConfig?.theme).title
            )}
          >
            {title}
          </h1>
          {endpoint?.deprecated && <DeprecatedLabel />}
          {!isPdf && <PageContextMenu />}
        </div>
      </div>
      <Description />
    </header>
  );
}
