'use client';

import { useCallback } from 'react';

import { useMDXContent } from '@/hooks/useMDXContent';

export const useSetApiBaseIndexCallback = () => {
  const [, dispatch] = useMDXContent();
  return useCallback(
    (x: number) =>
      dispatch({
        type: 'set_api_base_index',
        payload: x,
      }),
    [dispatch]
  );
};
