'use client';

import type { NextRouter } from 'next/router';
import { createContext, useContext } from 'react';

export const MintlifyRouterContext = createContext<NextRouter>({
  route: '',
  pathname: '',
  query: {},
  asPath: '',
  basePath: '',
  isLocaleDomain: false,
  push: async () => true,
  replace: async () => false,
  reload: () => {},
  back: () => {},
  forward: () => {},
  prefetch: async () => {},
  beforePopState: () => {},
  events: {
    on: () => {},
    off: () => {},
    emit: () => {},
  },
  isFallback: false,
  isReady: true,
  isPreview: false,
});

export function useMintlifyRouter() {
  return useContext(MintlifyRouterContext);
}
