import { NODE_ENV } from '@/env';

export const IS_PROD = NODE_ENV === 'production';
export const IS_DEV = NODE_ENV === 'development';

export const SECOND_IN_MILLISECONDS = 1000;
export const MINUTE_IN_MILLISECONDS = SECOND_IN_MILLISECONDS * 60;
export const HOUR_IN_MILLISECONDS = MINUTE_IN_MILLISECONDS * 60;
export const DAY_IN_MILLISECONDS = HOUR_IN_MILLISECONDS * 24;
export const TWO_WEEKS_IN_MILLISECONDS = DAY_IN_MILLISECONDS * 14;

export const SEARCH_KEY_VALIDITY_TIME_IN_MS = HOUR_IN_MILLISECONDS;

export const USERAUTH_OAUTH_CALLBACK_PATH = '/mintlify-oauth-callback';

export const END_USER_AUTH_COOKIE_KEY = 'mintlify_end_user_auth';

export const MAX_PREVIEW_BYTES = 50_000; // 0.05 mb / 50 kb
