'use client';

import { useMDXContent } from '@/hooks/useMDXContent';

export const useMdxCodeExampleData = (type: 'request' | 'response') => {
  const [{ codeExamples }] = useMDXContent();
  return codeExamples[type];
};

export const useHasMdxCodeExample = () => {
  const [{ codeExamples }] = useMDXContent();
  return !!codeExamples.request || !!codeExamples.response;
};
