'use client';

import { Server } from '@mintlify/validation';
import { useMemo } from 'react';

import { InputSectionContainer } from '../components/InputSectionContainer';
import { RecursiveInput } from './inputs/RecursiveInput';

type ServerSectionProps = {
  variables: NonNullable<Server['variables']>;
  value: Record<string, string>;
  setValue: (value: Record<string, string>) => void;
};

export const ServerSection = ({ variables, value, setValue }: ServerSectionProps) => {
  const inputs = useMemo(() => {
    return Object.entries(variables).map(([key, schema]) => {
      const paramValue = value[key];
      const setParamValue = (newParamValue: unknown) => {
        const newObj = { ...value };
        newObj[key] = String(newParamValue);
        setValue(newObj);
      };
      const clearParamValue =
        paramValue === undefined
          ? undefined
          : () => {
              const newObj = { ...value };
              delete newObj[key];
              setValue(newObj);
            };

      return (
        <RecursiveInput
          key={key}
          name={key}
          schemaArray={[schema]}
          value={paramValue}
          setValue={setParamValue}
          clearValue={clearParamValue}
        />
      );
    });
  }, [variables, setValue, value]);

  return <InputSectionContainer name="Server">{inputs}</InputSectionContainer>;
};
