'use client';

import { ReactNode } from 'react';

import { CodeBlock as GenericCodeBlock } from '@/components/Code/CodeBlock';
import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';

export function CodeBlock({
  filename,
  children,
  expandable,
  ...props
}: {
  filename?: string;
  children?: ReactNode;
  expandable?: string;
  language?: string;
}) {
  const trackCodeBlockCopy = useAnalyticsContext('code_block_copy');

  return (
    <GenericCodeBlock
      filename={filename}
      expandable={expandable?.toLowerCase() === 'true'}
      onCopied={(_, textToCopy) => trackCodeBlockCopy({ code: textToCopy })}
      {...props}
    >
      {children}
    </GenericCodeBlock>
  );
}
