import { replaceSlashIndex } from '@mintlify/common';

import { optionallyRemoveLeadingSlash } from './optionallyRemoveLeadingSlash';
import { optionallyRemoveTrailingSlash } from './optionallyRemoveTrailingSlash';

function normalizeIndexPath(path: string): string {
  if (path === '') return 'index';
  if (path === 'index') return path;
  if (path.endsWith('/index')) {
    const normalized = replaceSlashIndex(path);
    return normalized === '' ? 'index' : normalized;
  }
  return path;
}

export function isEqualIgnoringLeadingSlash(path1: string | undefined, path2: string | undefined) {
  if (path1 == null || path2 == null || typeof path1 !== 'string' || typeof path2 !== 'string') {
    return false;
  }
  const first = optionallyRemoveTrailingSlash(optionallyRemoveLeadingSlash(path1));
  const second = optionallyRemoveTrailingSlash(optionallyRemoveLeadingSlash(path2));
  return normalizeIndexPath(first) === normalizeIndexPath(second);
}
