import { AsyncApiParameter } from '@mintlify/common';

import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';
import { RecursiveExpandSchemaContainer } from './RecursiveExpandSchemaContainer';

export const Parameters = ({ parameters }: { parameters: AsyncApiParameter[] }) => {
  return (
    <ExpandableSectionContainer name="Parameters">
      {parameters.map((parameter) => (
        <RecursiveExpandSchemaContainer
          key={parameter.id}
          schema={[
            {
              name: parameter.id,
              type: parameter.type,
              description: parameter.description,
              required: parameter.required,
              deprecated: parameter.deprecated,
            },
          ]}
          className="bindings-schema"
        />
      ))}
    </ExpandableSectionContainer>
  );
};
