import { DataSchemaArray, IncrementalDataSchemaArray } from '@mintlify/validation';
import { useMemo } from 'react';

import { getOptionInformation } from '@/openapi/getOptionInformation';

import { useComponentSchemas } from './useComponentSchemas';

export const useOptionInformation = (schemaArray: DataSchemaArray | IncrementalDataSchemaArray) => {
  const componentSchemas = useComponentSchemas();

  return useMemo(() => {
    return getOptionInformation(schemaArray, componentSchemas);
  }, [schemaArray, componentSchemas]);
};
