'use client';

import { ChevronsUpDownIcon } from 'lucide-react';

type ExpandButtonProps = {
  onClick: () => void;
  count?: number;
  labeled?: boolean;
};

export const ObjectExpandButton = ({ onClick, count, labeled }: ExpandButtonProps) => {
  let expandText = '';
  if (count === 0) {
    return null;
  }
  if (count === 1) {
    expandText = 'show 1 property';
  } else {
    expandText = `show ${count} properties`;
  }

  if (labeled) {
    return (
      <button
        onClick={onClick}
        className="flex justify-center items-center pt-0.5 pb-0.5 pl-2 pr-1.5 rounded-lg border border-gray-200 dark:border-gray-800 text-gray-600 dark:text-gray-400"
      >
        <span className="text-sm font-sans font-medium mr-1 whitespace-nowrap">{expandText}</span>
        <ChevronsUpDownIcon className="w-3.5 h-3.5" strokeWidth={2.5} />
      </button>
    );
  } else {
    return (
      <button onClick={onClick} className="px-1 text-gray-600 dark:text-gray-500 ">
        <ChevronsUpDownIcon className="w-3.5 h-3.5" strokeWidth={2.5} />
      </button>
    );
  }
};
