import { IncrementalDataSchema, DataSchema } from '@mintlify/validation';
import { useEffect, useState } from 'react';

import { useEndpointLocation } from '@/contexts/EndpointLocationContext';
import { generateNextAdditionalProperties } from '@/openapi/generateNextAdditionalProperties';
import { generateNextProperties } from '@/openapi/generateNextProperties';

import { useComponentSchemas } from './useComponentSchemas';

export const usePropertiesIfObject = (schema: DataSchema | IncrementalDataSchema | undefined) => {
  const schemas = useComponentSchemas();
  const { location } = useEndpointLocation();
  const [loading, setLoading] = useState(true);
  const [properties, setProperties] = useState<
    ReturnType<typeof generateNextProperties> | undefined
  >(undefined);
  const [additionalProperties, setAdditionalProperties] = useState<
    ReturnType<typeof generateNextAdditionalProperties> | undefined
  >(undefined);

  useEffect(() => {
    let isMounted = true;

    if (!schema || schema.type !== 'object') {
      setProperties(undefined);
      setAdditionalProperties(undefined);
      setLoading(false);
      return;
    }

    void Promise.resolve().then(() => {
      try {
        const nextProperties = generateNextProperties(schema, schemas, location);
        const nextAdditionalProperties = generateNextAdditionalProperties(
          schema,
          schemas,
          location
        );

        if (isMounted) {
          setProperties(nextProperties);
          setAdditionalProperties(nextAdditionalProperties);
          setLoading(false);
        }
      } catch (error) {
        if (isMounted) {
          console.error('Error generating properties:', error);
          setLoading(false);
        }
      }
    });

    return () => {
      isMounted = false;
    };
  }, [schema, schemas, location]);

  return [loading, properties, additionalProperties] as const;
};
