'use client';

import { ParameterSections, ParameterGroup as EndpointParameterGroup } from '@mintlify/validation';

import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import { RecursiveParamField } from './ParamFields/RecursiveParamField';
import { SectionHeading } from './components/SectionHeading';

export type EndpointParametersParams = {
  parameters: ParameterSections;
};

export const EndpointParameters = ({ parameters }: EndpointParametersParams) => {
  return (
    <>
      <ParameterGroup title="Headers" parameters={parameters.header} />
      <ParameterGroup title="Path Parameters" parameters={parameters.path} />
      <ParameterGroup title="Query Parameters" parameters={parameters.query} />
      <ParameterGroup title="Cookies" parameters={parameters.cookie} />
    </>
  );
};

const ParameterGroup = ({
  parameters,
  title,
}: {
  parameters: EndpointParameterGroup;
  title: string;
}) => {
  if (Object.keys(parameters).length === 0) {
    return null;
  }
  const parameterEntries = Object.entries(parameters).map(([name, parameter]) => (
    <RecursiveParamField
      key={name}
      fieldType="parameter"
      name={name}
      style={parameter.style}
      explode={parameter.explode}
      schemaArray={parameter.schema}
    />
  ));

  return (
    <div className={cn(Classes.APISection)}>
      <SectionHeading title={title} />
      {parameterEntries}
    </div>
  );
};
