import { FontDetailsType } from '@mintlify/validation';

export const getFontScript = (font: FontDetailsType | undefined) => {
  if (!font) return null;
  if (font.source) {
    return (
      <style jsx global>{`
        @font-face {
          font-family: '${font.family}';
          src: url('${font.source}') format('${font.format}');
          font-weight: ${font.weight};
        }
      `}</style>
    );
  }

  return (
    <link
      href={`https://fonts.googleapis.com/css2?family=${font.family.replace(/\s+/g, '+')}:wght@${font.weight ?? '400;500;600;700;800'}&display=swap`}
      rel="stylesheet"
    />
  );
};
