import { Endpoint } from '@mintlify/validation';

import { CodeGroupSelect } from '@/components/Code/CodeGroupSelect';
import { CodeSnippets } from '@/components/Code/CodeSnippets';
import type { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';

import { generateResponseExampleMap } from './generateResponseExampleMap';

type GeneratedResponseExampleParams = {
  endpoint: Endpoint;
  selectedResponseContentType?: string;
};

export const GeneratedResponseExample = ({
  endpoint,
  selectedResponseContentType: selectedContentType,
}: GeneratedResponseExampleParams) => {
  // if there are no status codes, don't render
  if (Object.keys(endpoint.response).length === 0) {
    return null;
  }

  // if none of the status codes have a content type or the content type is placeholder, don't render
  if (
    Object.values(endpoint.response).every((body) => Object.keys(body).length === 0) ||
    Object.values(endpoint.response).every((body) => body['_mintlify/placeholder'])
  ) {
    return null;
  }

  const responseExampleMap = generateResponseExampleMap(endpoint.response, selectedContentType);

  if (Object.values(responseExampleMap).every((value) => Object.keys(value).length <= 1)) {
    const codeSnippetMap: Record<string, ExampleCodeSnippet>[] = Object.values(responseExampleMap);
    const snippets: ExampleCodeSnippet[] = codeSnippetMap.flatMap((record) =>
      Object.values(record)
    );
    return <CodeSnippets snippets={snippets} noMargins />;
  }
  return <CodeGroupSelect snippets={responseExampleMap} />;
};
