'use client';

import { isGroup, isPage } from '@mintlify/common';
import { DecoratedGroupConfig } from '@mintlify/validation';
import { useContext, useEffect, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import {
  getLegacySidebarStyleClassName,
  getPaddingByLevel,
  SidebarItemStyle,
} from '@/themes/mint/MainContentLayout/Nav/utils';
import { ComponentIcon } from '@/ui/Icon';
import { NavItem } from '@/ui/NavItem';
import { cn } from '@/utils/cn';
import { isPathInGroup } from '@/utils/nav/isPathInGroup';

import { NavTagPill } from './NavTagPill';
import { DropdownArrowIcon } from './icons';

export type ArrowPosition = 'trailing' | 'leading' | 'end';

export const GroupDropdown = ({
  group,
  level,
  mobile,
  sidebarItemStyle,
  arrowPosition,
}: {
  group: DecoratedGroupConfig;
  level: number;
  mobile: boolean;
  sidebarItemStyle?: SidebarItemStyle;
  arrowPosition?: ArrowPosition;
}) => {
  const router = useMintlifyRouter();
  const currentPath = useCurrentPath();
  const { legacyThemeSettings } = useContext(DocsConfigContext);
  const [isOpen, setIsOpen] = useState(Boolean(isPathInGroup(currentPath, group)));
  const { group: name, pages } = group;

  // Open the menu when we navigate to a page in the group.
  // We use useEffect instead of modifying the useState default
  // value so we can open menus even after the page has loaded.
  useEffect(() => {
    if (isPathInGroup(currentPath, group)) {
      setIsOpen(true);
    }
  }, [currentPath, group]);

  if (!name) {
    return null;
  }

  const onClick = () => {
    // Do not navigate if:
    // 1. We are on mobile (users need to a larger space to tap to open the menu)
    // 2. closing
    // 3. The first link is another nested menu
    // 4. The current page is in the nested pages being exposed
    const firstPage = pages[0];
    const shouldNavigate =
      !mobile && !isOpen && firstPage && isPage(firstPage) && !isPathInGroup(currentPath, group);
    if (shouldNavigate) {
      // Navigate to the first page if it exists
      void router.push(firstPage.href);
    }
    setIsOpen(!isOpen);
  };

  return (
    <li data-title={name} data-group-tag={group.tag || ''}>
      <div
        className={cn(
          getLegacySidebarStyleClassName(
            sidebarItemStyle || legacyThemeSettings?.sidebarStyle,
            false
          )
        )}
        style={{
          paddingLeft: getPaddingByLevel(level, arrowPosition),
        }}
        onClick={onClick}
      >
        {arrowPosition === 'leading' && (
          <DropdownArrowIcon className={cn('-mr-0.5', isOpen && 'duration-75 rotate-90')} />
        )}
        {group.icon && (
          <ComponentIcon
            icon={typeof group.icon === 'object' ? group.icon.name : group.icon}
            iconType={typeof group.icon === 'object' ? group.icon.style : undefined}
            className={cn('h-4 w-4 bg-gray-400 dark:bg-gray-500')}
            overrideColor={true}
          />
        )}
        <div
          className={cn(
            (arrowPosition === 'end' || arrowPosition === 'leading') &&
              'flex-1 flex items-center gap-2'
          )}
        >
          {name}
          {group.tag && <NavTagPill tag={group.tag} />}
        </div>
        {arrowPosition !== 'leading' && (
          <DropdownArrowIcon className={cn('-mr-0.5', isOpen && 'duration-75 rotate-90')} />
        )}
      </div>
      {isOpen && (
        <ul>
          {pages.map((entry) => {
            const key = isGroup(entry) ? entry.group : entry.href;
            return (
              <NavItem
                entry={entry}
                level={level + 1}
                mobile={mobile}
                key={key}
                sidebarItemStyle={sidebarItemStyle}
                arrowPosition={arrowPosition}
              />
            );
          })}
        </ul>
      )}
    </li>
  );
};
