'use client';

import { ApiPlaygroundResultType } from '@mintlify/models';
import { extension as extensionLookup } from 'mime-types';
import { useContext, useMemo, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import ErrorBoundary from '@/ui/ErrorBoundary';
import { cn } from '@/utils/cn';

import { ResponseBody } from './ResponseBody';
import { ResponseError } from './ResponseError';
import { ResponseHeaders } from './ResponseHeaders';
import { ResponseTopbar } from './ResponseTopbar';

export const responseSections = ['body', 'headers'] as const;
export type ResponseSection = (typeof responseSections)[number];

export const Response = ({
  result,
  className,
}: {
  result: ApiPlaygroundResultType;
  className: string;
}) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const [responseSection, setResponseSection] = useState<ResponseSection>('body');

  // TODO: perform this parsing upon request completion
  const responseBodyData = useResponseBodyData(result);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  const Content = () => (
    <>
      {result.error ? (
        <ResponseError message={result.errorMessage} />
      ) : responseSection === 'body' ? (
        <ResponseBody data={responseBodyData} />
      ) : (
        <ResponseHeaders response={result.response} />
      )}
    </>
  );

  return (
    <ErrorBoundary>
      <div
        className={cn(
          'w-full rounded-2xl border',
          codeBlockTheme === 'system'
            ? 'bg-white dark:bg-codeblock border-gray-200/70 dark:border-gray-800/50'
            : 'bg-[#0F1117] dark:bg-codeblock border-gray-800/50',
          className
        )}
      >
        <ResponseTopbar
          selectedSection={responseSection}
          setSelectedSection={setResponseSection}
          result={result}
          data={responseBodyData}
        />
        <Content />
      </div>
    </ErrorBoundary>
  );
};

const useResponseBodyData = (result: ApiPlaygroundResultType): ResponseBodyData | undefined => {
  return useMemo(() => {
    if (result.error || result.response.data == undefined) {
      return undefined;
    }

    const buffer = Buffer.from(result.response.data, 'base64');
    const contentType = parseContentTypeFromHeaders(result.response.headers);
    const mediaCategory = contentTypeToMediaCategory(contentType);
    const blob = new Blob([buffer], { type: contentType });
    const url = URL.createObjectURL(blob);
    const extension = contentType ? extensionLookup(contentType) || undefined : undefined;

    if (contentType && mediaCategory !== 'other') {
      return {
        type: mediaCategory,
        contentType,
        blob,
        url,
        extension,
      };
    }

    return {
      type: 'other',
      contentType,
      content: buffer.toString('utf-8'),
      blob,
      url,
      extension,
    };
  }, [result]);
};

export type ResponseBodyData = ImageResponseData | AudioResponseData | OtherResponseData;

type BaseResponseData = {
  blob: Blob;
  url: string;
  extension?: string;
};

export type ImageResponseData = BaseResponseData & {
  type: 'image';
  contentType: string;
};

export type AudioResponseData = BaseResponseData & {
  type: 'audio';
  contentType: string;
};

export type OtherResponseData = BaseResponseData & {
  type: 'other';
  contentType?: string;
  content: string;
};

type MediaCategory = ResponseBodyData['type'];

const parseContentTypeFromHeaders = (headers: Record<string, unknown>) => {
  for (const headerName in headers) {
    if (headerName.toLowerCase() === 'content-type') {
      const contentType = headers[headerName];
      if (typeof contentType === 'string') {
        return contentType.split(';')[0]?.toLowerCase();
      }
    }
  }
};

const contentTypeToMediaCategory = (contentType: string | undefined): MediaCategory => {
  const [typeCategory] = contentType?.split('/') ?? [];
  if (typeCategory === 'image' || typeCategory === 'audio') {
    return typeCategory;
  }

  return 'other';
};
