'use client';

import { createContext, ReactNode } from 'react';
import { useLocalStorage as useClientLocalStorage } from 'usehooks-ts';

type LocalStorageContextType = {
  preferredCodeLanguage?: string;
  setPreferredCodeLanguage?: (language: string) => void;
  cookiesEnabled?: boolean;
};

export const LocalStorageContext = createContext<LocalStorageContextType>({
  preferredCodeLanguage: '',
  setPreferredCodeLanguage: () => {
    return;
  },
});

LocalStorageContext.displayName = 'LocalStorageContext';

export const LocalStorageContextController = ({
  cookiesEnabled,
  children,
}: {
  cookiesEnabled?: boolean;
  children?: ReactNode;
}): React.ReactNode => {
  const [preferredCodeLanguage, setPreferredCodeLanguage] = useClientLocalStorage('code', '');
  return (
    <LocalStorageContext.Provider
      value={{
        preferredCodeLanguage,
        setPreferredCodeLanguage,
        cookiesEnabled,
      }}
    >
      {children}
    </LocalStorageContext.Provider>
  );
};
