'use client';

import { useState } from 'react';

import { useSelectedLocale } from '@/contexts/NavigationContext';
import { cn } from '@/utils/cn';

import { FeedbackType } from './ThumbVotingPill';
import ThumbVotingPill from './ThumbVotingPill';

const FEEDBACK = {
  YES: 'Yes' as const,
  NO: 'No' as const,
} satisfies Record<string, FeedbackType>;

export function ThumbsVoting() {
  const [selectedFeedback, setSelectedFeedback] = useState<FeedbackType | null>(null);
  const { yes, no, wasThisPageHelpful } = useSelectedLocale();
  return (
    <div
      className={cn(
        'flex flex-row gap-5 items-center grow justify-between ',
        'md:justify-start',
        'xl:justify-between',
        'min-[1400px]:justify-start'
      )}
    >
      <p className="text-sm text-gray-600 dark:text-gray-400">{wasThisPageHelpful}</p>
      {/* Feedback pills */}
      <div className="flex flex-row gap-3 items-center">
        <ThumbVotingPill
          type={FEEDBACK.YES}
          label={yes}
          selectedFeedback={selectedFeedback}
          setSelectedFeedback={setSelectedFeedback}
        />
        <ThumbVotingPill
          type={FEEDBACK.NO}
          label={no}
          selectedFeedback={selectedFeedback}
          setSelectedFeedback={setSelectedFeedback}
        />
      </div>
    </div>
  );
}
