'use client';

import { ArrowUpRightIcon } from 'lucide-react';
import { useContext } from 'react';

import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { useColors } from '@/hooks/useColors';
import { Ids } from '@/lib/local/identifiers';
import { NavLogo } from '@/ui/NavLogo';
import { DesktopSearchEntry } from '@/ui/search';
import { cn } from '@/utils/cn';
import getLogoHref from '@/utils/getLogoHref';

import { NavItem } from '../../ui/NavItem';
import { SideNavGroups } from '../../ui/SideNavGroups';
import { SwitchThemeToggle } from '../../ui/SwitchThemeToggle';
import { Dropdowns } from '../mint/MainContentLayout/Dropdowns';
import { GitHubCta } from '../mint/TopBar/GitHubCta';
import { AnchorWithoutContainer } from '../mint/shared/Anchor';
import { VersioningSelect } from '../mint/shared/VersioningSelect';

export function MapleNav() {
  const { divisions } = useContext(NavigationContext);
  const { docsConfig } = useContext(DocsConfigContext);
  const { banner } = useContext(BannerContext);

  const hasBanner = !!banner;

  return (
    <div
      className={cn(
        'hidden lg:flex fixed flex-col left-0 top-0 bottom-0 w-[19rem] border-r border-gray-200/70 dark:border-white/[0.07]',
        hasBanner && 'top-10'
      )}
      id={Ids.Sidebar}
    >
      <div className="flex-1 overflow-y-auto px-7 py-6" id={Ids.SidebarContent}>
        <div className="flex justify-between items-center">
          <NavLogo
            href={docsConfig ? getLogoHref(docsConfig) : '/'}
            name={docsConfig?.name}
            logoClassName="px-1 h-6"
          />
          <SwitchThemeToggle />
        </div>
        <DesktopSearchEntry className="mt-6" />
        <Dropdowns triggerClassName="mt-4" />
        <div className="-mx-3 text-sm" id={Ids.NavigationItems}>
          <Anchors />
          <SideNavGroups
            nav={divisions.groupsOrPages}
            mobile={true}
            shouldLimitMarginWithAnchors={true}
            classNames={{
              groupLabel: 'font-medium',
            }}
            navItemProps={{
              sidebarItemStyle: 'border',
              arrowPosition: 'end',
            }}
          />
        </div>
      </div>
      <ul className="px-4 py-3 w-[calc(19rem-1px)] left-0 right-0 bottom-0 bg-background-light dark:bg-background-dark border-t border-gray-200/70 dark:border-white/[0.07] text-sm">
        {docsConfig?.navbar?.links?.map((link) => {
          return (
            <NavItem
              key={link.href}
              entry={{ href: link.href, title: link.label, icon: link.icon }}
              mobile={true}
              sidebarItemStyle="undecorated"
              trailingIcon={
                <ArrowUpRightIcon className="size-3.5 text-gray-400 dark:text-gray-500 group-hover:text-gray-500 dark:group-hover:text-gray-400" />
              }
            />
          );
        })}
        {docsConfig?.navbar?.primary?.type === 'button' && (
          <NavItem
            entry={{ href: docsConfig.navbar.primary.href, title: docsConfig.navbar.primary.label }}
            mobile={true}
            sidebarItemStyle="undecorated"
            trailingIcon={
              <ArrowUpRightIcon className="size-3.5 text-gray-400 dark:text-gray-500 group-hover:text-gray-500 dark:group-hover:text-gray-400" />
            }
          />
        )}
        {docsConfig?.navbar?.primary?.type === 'github' && (
          <GitHubCta className="hidden lg:flex px-2" />
        )}
        <VersioningSelect
          triggerClassName="mt-1 rounded-lg py-1.5 justify-between w-full border-gray-200/70 dark:border-white/10"
          triggerLabelClassName="flex-1 text-left px-1"
        />
      </ul>
    </div>
  );
}

export function Anchors() {
  const { divisions } = useContext(NavigationContext);
  const { anchorDefault } = useColors();

  return (
    <div className="mt-8">
      {divisions.anchors.map((anchor) => {
        return (
          <li key={anchor.name} className="list-none">
            <AnchorWithoutContainer
              key={anchor.name}
              href={anchor.href}
              name={anchor.name}
              icon={typeof anchor.icon === 'string' ? anchor.icon : anchor.icon?.name}
              iconType={typeof anchor.icon === 'string' ? 'solid' : anchor.icon?.style}
              color={anchor.color?.light ?? anchorDefault}
              isActive={anchor.isActive}
            />
          </li>
        );
      })}
    </div>
  );
}
