'use client';

import { uniqueId } from 'lodash';
import * as React from 'react';
import { useCallback, useContext, useEffect, useRef } from 'react';

import { AuthContext, DeploymentMetadataContext } from '@/contexts/ConfigContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { useUserGroups } from '@/hooks/useUserGroups';
import { cn } from '@/utils/cn';

import { ChatAssistantContext } from '..';
import { ChatItem } from './ChatItem';

interface ChatHistoryListProps {
  className?: string;
  sessionId?: string;
  onRetry?: (index: number) => void;
  setSessionId: (sessionId: string) => void;
}

export const ChatHistoryList = ({
  className,
  sessionId,
  onRetry,
  setSessionId,
}: ChatHistoryListProps) => {
  const { selectedVersion } = useContext(NavigationContext);
  const { trieve, subdomain } = useContext(DeploymentMetadataContext);
  const { userInfo } = useContext(AuthContext);
  const { messages, setMessages } = useContext(ChatAssistantContext);

  const bottomRef = useRef<HTMLDivElement>(null);

  const userGroups = useUserGroups();

  useEffect(() => {
    if (bottomRef.current) {
      bottomRef.current.scrollIntoView({ behavior: 'instant', block: 'nearest' });
    }
  }, [messages]);

  const onSendRetryMessage = useCallback(
    (index: number) => {
      if (onRetry) {
        onRetry(index);
      } else {
        const newMessages = messages.slice();
        newMessages[index] = {
          message: newMessages[index]?.message || '',
          key: uniqueId(),
          isLoading: true,
          isGeneratingError: false,
          isRateLimited: false,
        };
        setMessages(newMessages);
      }
    },
    [messages, setMessages, onRetry]
  );

  const setResponse = useCallback(
    (newValue: string, index: number) => {
      setMessages((prevMessages) => {
        const newMessages = prevMessages.slice();
        if (newMessages[index] && !!newValue) {
          const prevResponse = newMessages[index].response || '';
          newMessages[index] = {
            ...newMessages[index],
            response: prevResponse + newValue,
          };
        }
        return newMessages;
      });
    },
    [setMessages]
  );

  const setIsLoading = useCallback(
    (isLoading: boolean, index: number) => {
      setMessages((prevMessages) => {
        const newMessages = prevMessages.slice();
        if (newMessages[index]) {
          newMessages[index] = {
            ...newMessages[index],
            isLoading,
          };
        }
        return newMessages;
      });
    },
    [setMessages]
  );

  const setIsGeneratingError = useCallback(
    (isGeneratingError: boolean, index: number) => {
      setMessages((prevMessages) => {
        const newMessages = prevMessages.slice();
        if (newMessages[index]) {
          newMessages[index] = {
            ...newMessages[index],
            isGeneratingError,
          };
        }
        return newMessages;
      });
    },
    [setMessages]
  );

  const setIsRateLimited = useCallback(
    (isRateLimited: boolean, index: number) => {
      setMessages((prevMessages) => {
        const newMessages = prevMessages.slice();
        if (newMessages[index]) {
          newMessages[index] = {
            ...newMessages[index],
            isRateLimited,
          };
        }
        return newMessages;
      });
    },
    [setMessages]
  );

  return (
    <div className={cn('flex flex-col-reverse gap-3 overflow-y-auto', className)}>
      <div ref={bottomRef} />
      {trieve?.datasetId &&
        messages.map((message, i) => (
          <ChatItem
            key={message.key}
            message={message.message}
            response={message.response || ''}
            isLoading={message.isLoading}
            isGeneratingError={message.isGeneratingError}
            isRateLimited={message.isRateLimited}
            sessionId={sessionId || ''}
            subdomain={subdomain || ''}
            version={selectedVersion}
            setSessionId={setSessionId}
            onGeneratingRetry={() => onSendRetryMessage(i)}
            onCitationClick={() => {}}
            userGroups={userGroups}
            isAuthenticated={!!userInfo}
            className="last:mb-2"
            setResponse={(newValue) => setResponse(newValue, i)}
            setIsLoading={(isLoading) => setIsLoading(isLoading, i)}
            setIsGeneratingError={(isGeneratingError) => setIsGeneratingError(isGeneratingError, i)}
            setIsRateLimited={(isRateLimited) => setIsRateLimited(isRateLimited, i)}
          />
        ))}
    </div>
  );
};
