'use client';

import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';

import { CopyToClipboardButton } from '../CodeBlock';

type CodeGroupCopyButtonProps = {
  code: string;
};

export const CodeGroupCopyButton = ({ code }: CodeGroupCopyButtonProps) => {
  const trackCodeBlockCopy = useAnalyticsContext('code_block_copy');

  return (
    <div className="flex items-center pr-4" data-testid="code-group-select-copy-button">
      <CopyToClipboardButton
        textToCopy={code}
        onCopied={(_, code) => trackCodeBlockCopy({ code })}
      />
    </div>
  );
};
