import { joinWithSingleSlash } from '@/hooks/useSendPlaygroundRequest';

type JoinAddressProps = {
  address: string;
  baseUrl: string;
  parameterValues: Record<string, unknown>;
  securitySchemeValues: Record<string, { name: string; value: string }>;
};

export const joinAddress = ({
  address,
  baseUrl,
  parameterValues,
  securitySchemeValues,
}: JoinAddressProps) => {
  let addressWithParams = Object.entries(parameterValues).reduce(
    (path, [paramName, paramValue]) => {
      if (paramValue === undefined || paramValue === null) return path;
      return path.replace(`{${paramName}}`, encodeURIComponent(String(paramValue)));
    },
    address
  );
  let securitySchemeQuery = '';
  const httpApiKeyQueryName = securitySchemeValues['httpApiKey']?.name;
  const httpApiKeyValue = securitySchemeValues['httpApiKey']?.value;
  if (httpApiKeyQueryName && httpApiKeyValue) {
    securitySchemeQuery = `?${httpApiKeyQueryName}=${httpApiKeyValue}`;
  }

  if (securitySchemeQuery) {
    addressWithParams = `${addressWithParams}${securitySchemeQuery}`;
  }
  const joinedPath = joinWithSingleSlash(baseUrl, addressWithParams);
  return joinedPath;
};
