import { SchemaProperty } from '@mintlify/common';

import { ExpandSchemaContainer } from '../components/ExpandSchemaContainer';

type RecursiveExpandSchemaContainerProps = {
  schema: SchemaProperty[];
  defaultShowChildren?: boolean;
  className?: string;
};

export const RecursiveExpandSchemaContainer = ({
  schema,
  defaultShowChildren,
  className,
}: RecursiveExpandSchemaContainerProps) => {
  const expandable = (type: string | undefined) => {
    if (!type) return false;
    return type === 'object' || type === 'oneOf' || type === 'anyOf' || type === 'allOf';
  };
  return (
    <>
      {schema.map(
        ({ name, type, description, properties, required, deprecated }: SchemaProperty) => {
          return (
            <ExpandSchemaContainer
              key={name}
              label={name}
              description={description}
              typeLabel={type}
              propertiesCount={properties?.length ?? 0}
              defaultShowChildren={defaultShowChildren}
              required={required}
              deprecated={deprecated}
              className={className}
            >
              {expandable(type) && properties && (
                <RecursiveExpandSchemaContainer
                  schema={properties}
                  defaultShowChildren={defaultShowChildren}
                />
              )}
            </ExpandSchemaContainer>
          );
        }
      )}
    </>
  );
};
