import { DataSchemaArray, IncrementalDataSchemaArray } from '@mintlify/validation';

import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';

import { generateOptionLabels } from './generateOptionLabels';

/**
 * This function does some preprocessing on a DataSchemaArray, generating information on which schemas
 * to display, how to display them, and how to map from an option's index to the index of the corresponding
 * schema in the schema array.
 *
 * @param schemaArray An array containing a list of schema options.
 * @returns An object containing the following information, or an empty object if there are no valid options:
 *
 * `typeOptionLabels`: An array containing the human-readable labels of all valid schema options, to be
 * displayed in a dropdown.
 *
 * `originalIndices`: An array containing the original indices of all valid schema options, to be used for
 * mapping option index to schema index.
 *
 * `nullable`: Whether the schema should be marked as `| null`.
 *
 * `allObjects`: Whether all valid schemas are objects. Used to determine whether to display dropdown below the variable name
 */
export const getOptionInformation = (
  schemaArray: DataSchemaArray | IncrementalDataSchemaArray,
  componentSchemas: ComponentSchemas | undefined
) => {
  if (schemaArray.every(({ type }) => type === 'null')) {
    return {
      typeOptionLabels: ['null'],
      originalIndices: [0],
      nullable: true,
      allObjects: false,
    };
  }

  const validOptions = schemaArray
    .map((schema, originalIndex) => ({ schema, originalIndex }))
    .filter(({ schema }) => schema.type !== 'null');

  const nullable = schemaArray.some((schema) => schema.type === 'null');
  const allObjects = validOptions.every(({ schema }) => schema.type === 'object');
  const typeOptionLabels = generateOptionLabels(
    validOptions.map(({ schema }) => schema) as DataSchemaArray | IncrementalDataSchemaArray,
    componentSchemas,
    allObjects,
    nullable
  );
  const originalIndices = validOptions.map(({ originalIndex }) => originalIndex);

  return { typeOptionLabels, originalIndices, nullable, allObjects };
};
