'use client';

import { IncrementalDataSchema } from '@mintlify/validation';

import { Tab, Tabs } from '@/components/Tabs';
import { EndpointLocationContext } from '@/contexts/EndpointLocationContext';
import { useCheckHash } from '@/hooks/useCheckHash';
import { useComponentSchemas } from '@/hooks/useComponentSchemas';
import { useSchema } from '@/hooks/useSchema';
import { getOptionInformation } from '@/openapi/getOptionInformation';

import { RecursiveParamField } from './ParamFields/RecursiveParamField';

export const SchemaFields = () => {
  const schema = useSchema();
  const componentSchemas = useComponentSchemas();
  const anchor = useCheckHash();

  if (schema === undefined) return null;

  const { typeOptionLabels, originalIndices } = getOptionInformation(schema, componentSchemas);
  const schemaArray = originalIndices
    .map((originalIndex) => schema[originalIndex])
    .filter((schema): schema is IncrementalDataSchema => schema !== undefined);

  if (schemaArray[0] === undefined) return null;

  return (
    <EndpointLocationContext.Provider value={{ location: undefined, anchor }}>
      <div>
        {schemaArray.length > 1 ? (
          <Tabs>
            {schemaArray.map((schema, i) => {
              return (
                <Tab key={i} title={typeOptionLabels[i] ?? ''}>
                  <RecursiveParamField fieldType="schema" schemaArray={[schema]} />
                </Tab>
              );
            })}
          </Tabs>
        ) : (
          <RecursiveParamField fieldType="schema" schemaArray={[schemaArray[0]]} />
        )}
      </div>
    </EndpointLocationContext.Provider>
  );
};
