'use client';

import { ApiPlaygroundDisplayType } from '@mintlify/models';
import { useContext, useMemo } from 'react';

import { DocsConfigContext, PageContext } from '@/contexts/ConfigContext';
import { useEndpoint } from '@/hooks/useEndpoint';

export const useApiPlaygroundDisplay = (): ApiPlaygroundDisplayType => {
  const { docsConfig } = useContext(DocsConfigContext);
  const { pageMetadata } = useContext(PageContext);
  const endpoint = useEndpoint();
  const endpointType = endpoint?.type;

  return useMemo(() => {
    const mode =
      endpointType === 'webhook'
        ? 'simple'
        : pageMetadata.playground || docsConfig?.api?.playground?.display;
    return mode ?? 'interactive';
  }, [docsConfig, pageMetadata, endpointType]);
};
