import type { DecoratedNavigationPage } from '@mintlify/models';

export function checkNavAccess(page: DecoratedNavigationPage, userGroups: Set<string>) {
  const pageGroups =
    Array.isArray(page.groups) && page.groups.every((pageGroup) => typeof pageGroup === 'string')
      ? page.groups
      : undefined;

  if (!pageGroups) return true;

  return pageGroups.some((pageGroup) => userGroups.has(pageGroup));
}
