import { optionallyAddLeadingSlash } from '@mintlify/common';
import { divisions, GroupConfig, NavigationConfig, PageOrGroupConfig } from '@mintlify/validation';

import { isNavigationGroup } from '@/utils/nav/isNavigation';

import { isEqualIgnoringLeadingSlash } from './isEqualIgnoringLeadingSlash';

export type Breadcrumb = { title: string; path?: string };

/**
 * Gets the first valid page path from an array of navigation entries
 * @param pages - Array of navigation entries to search through
 * @returns The first page path found, or undefined if none exists
 */
const getFirstPagePath = (pages: PageOrGroupConfig[]): string | undefined => {
  const firstPage = pages[0];
  if (!firstPage) return undefined;
  return typeof firstPage === 'string'
    ? firstPage
    : getFirstPagePath('pages' in firstPage ? firstPage.pages : []);
};

/**
 * Generates an array of breadcrumbs based on the current path and navigation structure
 * @param currentPath - The current page path to generate breadcrumbs for
 * @param nav - The complete navigation structure
 * @returns Array of breadcrumbs containing titles and optional paths
 */
export function getBreadcrumbs(currentPath: string, nav?: NavigationConfig): Breadcrumb[] {
  if (!nav) return [];

  if ('groups' in nav) {
    for (const group of nav.groups) {
      const breadcrumbs = findAllSectionTitlesRecursively(group, currentPath);
      if (breadcrumbs.length > 0) {
        return breadcrumbs;
      }
    }
  }

  for (const key of divisions) {
    if (key in nav) {
      const items = nav[key as keyof typeof nav] as NavigationConfig[];
      for (const item of items) {
        const breadcrumbs = getBreadcrumbs(currentPath, item);
        if (breadcrumbs.length > 0) {
          return breadcrumbs;
        }
      }
    }
  }

  return [];
}

/**
 * Recursively searches through a navigation group to find all section titles that lead to the current path
 * @param navGroup - The navigation group to search through
 * @param currentPath - The target path to find breadcrumbs for
 * @returns Array of breadcrumbs for the matching path, or empty array if no match found
 */
function findAllSectionTitlesRecursively(navGroup: GroupConfig, currentPath: string): Breadcrumb[] {
  if ('pages' in navGroup) {
    const groupInfo = {
      title: navGroup.group,
      path: optionallyAddLeadingSlash(getFirstPagePath(navGroup.pages) ?? ''),
    };

    for (const entry of navGroup.pages) {
      if (isNavigationGroup(entry)) {
        const childBreadcrumbs = findAllSectionTitlesRecursively(entry, currentPath);
        if (childBreadcrumbs.length > 0) {
          return [groupInfo, ...childBreadcrumbs];
        }
      } else if (isEqualIgnoringLeadingSlash(entry, currentPath)) {
        return [groupInfo];
      }
    }
  }

  return [];
}
