'use client';

import { useContext, useState } from 'react';

import { CodeBlockTooltip } from '@/components/Code/CodeBlock';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

import { ResponseBodyData } from '.';

type DownloadButtonParams = {
  data: ResponseBodyData;
};

export const DownloadButton = ({ data }: DownloadButtonParams) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const [isCopiedActive, setIsCopiedActive] = useState(false);
  const onClick = () => {
    setIsCopiedActive(true);
    setTimeout(() => {
      setIsCopiedActive(false);
    }, 2000);
  };

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return data.extension ? (
    <a
      className="group h-7 w-7 flex items-center justify-center cursor-pointer relative"
      onClick={onClick}
      href={data.url}
      download={`api-response.${data.extension}`}
    >
      {isCopiedActive ? (
        <Icon
          icon="circle-check"
          iconType="solid"
          className="h-4 w-4 bg-primary dark:bg-primary-light"
        />
      ) : (
        <Icon
          icon="arrow-down-to-line"
          className={cn(
            'w-4 h-4',
            codeBlockTheme === 'system'
              ? 'bg-gray-400 group-hover:bg-gray-500 dark:bg-white/40 dark:group-hover:bg-white/60'
              : 'bg-white/40 group-hover:bg-white/60'
          )}
        />
      )}
      <CodeBlockTooltip text={isCopiedActive ? 'Copied!' : 'Download'} />
    </a>
  ) : null;
};
