import { ApiPlaygroundInputs } from '@mintlify/validation';
import type { Request } from 'har-format';
import HTTPSnippet from 'httpsnippet';
import uniqueString from 'unique-string';

import { snippetPresets as defaultSnippetPresets } from '@/constants/snippetPresets';
import { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';
import { SnippetPreset } from '@/types/snippetPreset';

export interface GenerateSnippetParams {
  request: Request;
  pathInputs: ApiPlaygroundInputs['path'];
  snippetPresets?: SnippetPreset[];
}

export const generateSnippet = ({
  request,
  pathInputs,
  snippetPresets = defaultSnippetPresets,
}: GenerateSnippetParams): ExampleCodeSnippet[] => {
  const uuidToName: Record<string, string> = {};

  // https://regex101.com/r/3PPRco/1
  const portRegex = /:{[^{}]+}/g;

  // for the port number found in the url that is not populated, replace it with a rare integer
  const safePortUrl = request.url.replace(portRegex, (nameWithColon) => {
    const replacementWithColon = ':62437'; // arbitrary port value which is unlikely to appear in url
    uuidToName[replacementWithColon] = nameWithColon;
    return replacementWithColon;
  });

  // https://regex101.com/r/pwKjrJ/1
  const pathParamRegex = /{[^{}]+}/g;

  // for each path param found in the url, replace it with the value if the input is populated, otherwise replace it with a uuid
  const safeUrl = safePortUrl.replace(pathParamRegex, (name) => {
    const nameNoBrackets = name.slice(1, name.length - 1);
    const paramValue = pathInputs[nameNoBrackets];

    if (paramValue && (typeof paramValue !== 'string' || paramValue.length > 0)) {
      return `${encodeURIComponent(paramValue.toString())}`;
    }

    const uuid = uniqueString();
    uuidToName[uuid] = name;

    return uuid;
  });

  const httpSnippet = new HTTPSnippet({ ...request, url: safeUrl });

  return snippetPresets.map((snippetPreset) => {
    const { filename, snippet, language } = snippetPreset;
    const { target, client } = snippet;

    const code = restorePlaceholders(uuidToName, httpSnippet.convert(target, client).toString());

    return {
      filename,
      code,
      language,
    };
  });
};

/**
 * For each uuid that was substituted into the url, replaces the first (and only) instance of the uuid with the original name.
 *
 * @param uuidNameMap - Mapping of UUIDs to their original names.
 * @param snippet - String where UUIDs need to be replaced with their original names.
 */
const restorePlaceholders = (uuidNameMap: Record<string, string>, snippet: string): string => {
  return Object.entries(uuidNameMap).reduce((acc, [uuid, name]) => {
    return acc.replace(uuid, name);
  }, snippet);
};
