import { Endpoint, SecurityParameterSchema } from '@mintlify/validation';
import type { Header } from 'har-format';

export const createHeaders = (
  endpoint: Endpoint,
  headerInputs: Record<string, unknown>,
  selectedBodyContentType?: string
): Header[] => {
  const headers: Header[] = [];
  const addedHeaders = new Set<string>();

  Object.entries(headerInputs)
    .filter((entry): entry is [string, NonNullable<unknown>] => entry[1] != null)
    .forEach(([name, value]) => {
      addedHeaders.add(name);
      headers.push({
        name,
        value: value.toString(),
      });
    });

  Object.entries(endpoint.request.parameters.header)
    .filter(([_, param]) => param.schema[0].required)
    .forEach(([name]) => {
      if (!addedHeaders.has(name)) {
        headers.push({
          name,
          value: `<${name.toLowerCase()}>`,
        });
      }
    });

  Object.entries(endpoint.request.security[0]?.parameters.header ?? []).forEach(([name, param]) => {
    if (!addedHeaders.has(name)) {
      headers.push({
        name,
        value: generateAuthPlaceholder(param),
      });
    }
  });

  if (selectedBodyContentType) {
    addContentTypeHeader(headers, selectedBodyContentType);
  }

  return headers;
};

const generateAuthPlaceholder = (param: SecurityParameterSchema): string => {
  switch (param.type) {
    case 'http':
      return param.scheme === 'basic' ? 'Basic <encoded-value>' : 'Bearer <token>';
    case 'oauth2':
      return 'Bearer <token>';
    case 'apiKey':
    default:
      return '<api-key>';
  }
};

const addContentTypeHeader = (headers: Header[], contentType: string) => {
  if (headers.some(({ name }) => name.toLowerCase() === 'content-type')) {
    return;
  }

  headers.push({
    name: 'Content-Type',
    value: contentType,
  });
};
