'use client';

import { IncrementalObjectSchema, ObjectSchema } from '@mintlify/validation';
import { useMemo, useState } from 'react';
import uniqueString from 'unique-string';

import { useGenerateNextAdditionalProperties } from '@/hooks/useGenerateNextAdditionalProperties';
import { useGenerateNextProperties } from '@/hooks/useGenerateNextProperties';

import { AdditionalPropertyButton } from '../../components/AdditionalPropertyButton';
import { InputContainer } from '../../components/InputContainer';
import { calculateObjectInputChildren } from './ObjectChildren';
import { RecursiveInput } from './RecursiveInput';

export const ObjectInput: RecursiveInput<ObjectSchema | IncrementalObjectSchema> = ({
  name,
  schema,
  typeDropdown,
  setValue,
  clearValue,
  value,
  depth,
  parentNames,
  arrayIndex,
  defaultExpanded,
  style,
  explode,
  renderedRefs,
}) => {
  const [additionalPropButtonKey, setAdditionalPropButtonKey] = useState(uniqueString());
  const allowsAdditionalProperties = schema.additionalProperties !== false;
  const existingKeys = [...Object.keys(value ?? {}), ...Object.keys(schema.properties)];

  const properties = useGenerateNextProperties(schema);
  const additionalProperties = useGenerateNextAdditionalProperties(schema);

  const refIdentifier = schema.refIdentifier;

  const inputs: React.ReactNode[] = useMemo(
    () =>
      calculateObjectInputChildren({
        properties,
        additionalProperties,
        value,
        setValue,
        name,
        parentNames,
        defaultExpanded,
        depth,
        refId: refIdentifier,
        refs: new Set(renderedRefs),
        style,
        explode,
      }),
    [
      additionalProperties,
      defaultExpanded,
      depth,
      name,
      parentNames,
      properties,
      refIdentifier,
      renderedRefs,
      setValue,
      value,
      style,
      explode,
    ]
  );

  if (depth === 0) {
    return inputs;
  }

  const addKey = (key: string) => {
    const newObj = { ...value };
    newObj[key] = undefined;
    setValue(newObj);
    setAdditionalPropButtonKey(uniqueString());
  };

  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      isObject
      parentNames={parentNames}
      arrayIndex={arrayIndex}
      defaultExpanded={defaultExpanded}
      style={style}
      explode={explode}
    >
      {inputs}
      {allowsAdditionalProperties ? (
        <AdditionalPropertyButton
          key={additionalPropButtonKey}
          onAddKey={addKey}
          existingKeys={existingKeys}
        />
      ) : null}
    </InputContainer>
  );
};
