'use client';

import { useContext } from 'react';

import { toShikiLang } from '@/constants/snippetPresets';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useApiPlaygroundDisplay } from '@/hooks/useApiPlaygroundDisplay';
import { useBaseUrlOptions } from '@/hooks/useBaseUrlOptions';
import { useEndpoint } from '@/hooks/useEndpoint';
import { useMDXContent } from '@/hooks/useMDXContent';
import { useApiPlaygroundInputs } from '@/hooks/useMDXContentController/useApiPlaygroundInputs';
import { useMdxCodeExampleData } from '@/hooks/useMdxCodeExampleData';
import { GeneratedRequestExample } from '@/openapi/apiExampleGeneration/generators/GeneratedRequestExample';

import { CodeSamplesExample } from './CodeSamplesExample';
import { MdxCodeExample } from './MdxCodeExample';
import { WebhookExample } from './WebhookExample';

export const RequestExample = () => {
  const mdxRequestExampleData = useMdxCodeExampleData('request');
  const endpoint = useEndpoint();
  const inputs = useApiPlaygroundInputs();
  const [{ apiBaseIndex }] = useMDXContent();
  const apiPlaygroundMode = useApiPlaygroundDisplay();
  const baseUrlOptions = useBaseUrlOptions();
  const { docsConfig } = useContext(DocsConfigContext);
  const baseUrl = baseUrlOptions?.[apiBaseIndex] ?? baseUrlOptions?.[0];
  const codeSamples = endpoint?.request.codeSamples;
  const requestExampleLanguages = docsConfig?.api?.examples?.languages?.map(toShikiLang);
  const requiredOnly = docsConfig?.api?.examples?.defaults === 'required';

  if (mdxRequestExampleData !== undefined) {
    return <MdxCodeExample data={mdxRequestExampleData} />;
  }

  if (endpoint?.type === 'webhook') {
    return <WebhookExample />;
  }

  if (codeSamples && codeSamples.length > 0) {
    return (
      <CodeSamplesExample
        endpoint={endpoint}
        inputs={inputs}
        baseUrl={baseUrl}
        codeSamples={codeSamples}
        apiPlaygroundMode={apiPlaygroundMode}
        requestExampleLanguages={requestExampleLanguages}
        requiredOnly={requiredOnly}
      />
    );
  }

  if (endpoint !== undefined && baseUrl !== undefined) {
    return (
      <GeneratedRequestExample
        endpoint={endpoint}
        inputs={inputs}
        baseUrl={baseUrl}
        requestExampleLanguages={requestExampleLanguages}
        requiredOnly={requiredOnly}
      />
    );
  }

  return null;
};
