'use client';

import { getSecurityOptionsForAuthMethod } from '@mintlify/common';
import { SecurityOption } from '@mintlify/validation';
import { useContext, useMemo } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';

/**
 * @deprecated
 * Using this to retain support for openapi endpoints + mint.json auth.
 * Going forward, all openapi auth should be defined in the openapi spec.
 */
export const useSecurityOptionsWithConfig = (
  baseSecurityOptions: SecurityOption[]
): SecurityOption[] => {
  const { docsConfig } = useContext(DocsConfigContext);

  return useMemo(() => {
    if (baseSecurityOptions.length > 0) {
      return baseSecurityOptions;
    }

    const auth = docsConfig?.api?.mdx?.auth?.method;

    return getSecurityOptionsForAuthMethod(auth);
  }, [baseSecurityOptions, docsConfig?.api?.mdx?.auth]);
};
