'use client';

import { TableOfContentsSectionType } from '@mintlify/common';
import { useCallback, useContext, useMemo } from 'react';

import { NavigationContext } from '@/contexts/NavigationContext';
import { useMDXContent } from '@/hooks/useMDXContent';

import { useCurrentTableOfContentsSection } from './useMDXContentController/useCurrentTableOfContentsSection';

export const useTableOfContents = () => {
  const [{ tableOfContents }] = useMDXContent();
  const currentTableOfContentsSection = useCurrentTableOfContentsSection();

  const { setNavIsOpen } = useContext(NavigationContext);

  const closeNav = useCallback(() => setNavIsOpen(false), [setNavIsOpen]);

  const isActive = useCallback(
    (section: TableOfContentsSectionType) => {
      if (section.slug === currentTableOfContentsSection) return true;

      if (!section.children.length) return false;

      return section.children.findIndex(isActive) > -1;
    },
    [currentTableOfContentsSection]
  );

  const pageHasSubsections = tableOfContents.some(
    (section: TableOfContentsSectionType) => section.children.length > 0
  );

  return useMemo(
    () => ({ closeNav, isActive, pageHasSubsections, tableOfContents }),
    [closeNav, isActive, pageHasSubsections, tableOfContents]
  );
};
