'use client';

import { ChevronRightIcon } from 'lucide-react';
import Link from 'next/link';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';
import { Breadcrumb, getBreadcrumbs } from '@/utils/paths/getBreadcrumbs';
import { getSectionTitle } from '@/utils/paths/getSectionTitle';

export function Eyebrows() {
  const currentPath = useCurrentPath();

  const { docsConfig } = useContext(DocsConfigContext);
  const section = getSectionTitle(currentPath, docsConfig?.navigation);
  const breadcrumbs = getBreadcrumbs(currentPath, docsConfig?.navigation);

  return (
    <>
      {docsConfig?.styling?.eyebrows === 'breadcrumbs' ? (
        <Breadcrumbs breadcrumbs={breadcrumbs} />
      ) : (
        section && (
          <div
            className={cn(
              Classes.Eyebrow,
              'h-5 text-primary dark:text-primary-light text-sm font-semibold'
            )}
          >
            {section}
          </div>
        )
      )}
    </>
  );
}

const Breadcrumbs = ({ breadcrumbs }: { breadcrumbs: Breadcrumb[] }) => {
  if (breadcrumbs.length === 0) return null;
  return (
    <div className="flex flex-row items-center gap-1.5">
      {breadcrumbs.map((breadcrumb, i) => (
        <div
          className="inline-flex items-center gap-1.5 text-sm text-gray-500 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300"
          key={breadcrumb.title}
        >
          <Link href={breadcrumb.path ?? '#'}>{breadcrumb.title}</Link>
          {i < breadcrumbs.length - 1 && (
            <ChevronRightIcon
              size={12}
              strokeWidth={2.5}
              className="text-gray-400 dark:text-gray-500"
            />
          )}
        </div>
      ))}
    </div>
  );
};
