'use client';

import { useContext, useState, useEffect } from 'react';
import { Rect } from 'react-use-rect';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { Ids } from '@/lib/local/identifiers';

export function useTop(rect: Rect | null) {
  const { docsConfig } = useContext(DocsConfigContext);
  const [top, setTop] = useState<number>();
  const rectTop = rect ? rect.top : undefined;

  const useContentContainer = docsConfig?.theme === 'almond';

  useEffect(() => {
    if (typeof rectTop === 'undefined') return;

    let newTop;
    if (useContentContainer) {
      const contentContainer = document.getElementById(Ids.ContentContainer);
      if (contentContainer) {
        const containerScrollTop = contentContainer.scrollTop;
        newTop = rectTop + containerScrollTop;
      }
    } else {
      newTop = rectTop + window.pageYOffset;
    }

    if (newTop && newTop !== top) {
      setTop(newTop);
    }
  }, [rectTop, top, useContentContainer]);

  return top;
}
