import { MethodPill } from '@/components/ApiExamples/MethodPill';
import { cn } from '@/utils/cn';

import { BasePathSelector } from '../components/BasePathSelector';
import { PrettyPath } from '../components/PrettyPath';

type ChannelPathHeaderProps = {
  onCopy: () => void;
  baseUrlOptions: string[];
  selectedBaseUrlIndex: number;
  setSelectedBaseUrlIndex: (index: number) => void;
  isCopiedActive: boolean;
  hasGrayBackground: boolean;
  display: 'simple' | 'none' | 'interactive';
  path: string;
  method: string;
  onBaseUrlChange?: () => void;
};

export const ChannelPathHeader = ({
  onCopy,
  baseUrlOptions,
  selectedBaseUrlIndex,
  setSelectedBaseUrlIndex,
  isCopiedActive,
  hasGrayBackground,
  display,
  path,
  method,
  onBaseUrlChange,
}: ChannelPathHeaderProps) => {
  return (
    <div
      className={cn(
        'relative flex-1 flex gap-2 min-w-0 rounded-xl items-center cursor-pointer p-1.5',
        display != 'simple' && 'border-standard',
        hasGrayBackground && 'bg-gray-50 dark:bg-white/5'
      )}
      onClick={onCopy}
    >
      <MethodPill method={method} shortMethod={true} active={false} />
      <div className="flex items-center space-x-2 overflow-x-auto flex-1 no-scrollbar pr-8">
        <BasePathSelector
          baseUrlOptions={baseUrlOptions}
          selectedBaseUrlIndex={selectedBaseUrlIndex}
          setSelectedBaseUrlIndex={setSelectedBaseUrlIndex}
          onBaseUrlChange={onBaseUrlChange}
        />
        <PrettyPath
          path={path}
          method={method}
          isCopiedActive={isCopiedActive}
          hasGrayBackground={hasGrayBackground}
        />
      </div>
    </div>
  );
};
