'use client';

import { footerSocialKeys, FooterSocialKeyType } from '@mintlify/models';

import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

type SocialProps = {
  type?: FooterSocialKeyType;
  url: string;
  iconClassName?: string;
};

const isSupportedSocialIcon = (icon: string) => {
  return footerSocialKeys.includes(icon as FooterSocialKeyType);
};

export const Social = ({ type, url, iconClassName }: SocialProps) => {
  let icon = type === 'website' || type == null ? 'earth-americas' : type;
  if (icon === 'x') {
    icon = 'x-twitter';
  }
  if (!isSupportedSocialIcon(icon)) {
    return null;
  }

  return (
    <a href={url} target="_blank">
      <span className="sr-only">{type}</span>
      <Icon
        icon={icon}
        iconType="solid"
        className={cn(
          'w-5 h-5 bg-gray-500 dark:bg-gray-600 hover:bg-gray-600 dark:hover:bg-gray-500',
          iconClassName
        )}
      />
    </a>
  );
};
