'use client';

import { useContext, useMemo } from 'react';

import { AuthContext } from '@/contexts/ConfigContext';

export const useUserGroups = () => {
  const { userAuth, auth, userInfo } = useContext(AuthContext);

  return useMemo(() => {
    if (!userAuth && !auth) {
      return undefined;
    }

    const userGroups = userInfo?.groups ?? [];
    return [...userGroups, '*'];
  }, [auth, userAuth, userInfo?.groups]);
};
