'use client';

import { ReactNode, useContext } from 'react';

import { Footer } from '@/components/Footer';
import { MdxPanel } from '@/components/MdxPanel';
import { BannerContext } from '@/contexts/BannerContext';
import { PageContext } from '@/contexts/ConfigContext';
import { MDXContentContext, useMDXContentReducer } from '@/contexts/MDXContentContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { Api } from '@/ui/Content/Api';
import { Content } from '@/ui/Content/Content';
import { SidePanel } from '@/ui/Content/SidePanel';
import { UserFeedback } from '@/ui/Feedback/UserFeedback';
import { cn } from '@/utils/cn';

import { PageHeader } from './PageHeader';
import { LindenPagination } from './Pagination';

export type MDXContentControllerProps = {
  children: ReactNode;
};

export function MDXContentController({ children }: MDXContentControllerProps) {
  const { pageMetadata, mdxExtracts } = useContext(PageContext);
  const initialSelectedBodyContentType = Object.keys(mdxExtracts?.endpoint?.request.body ?? {})[0];

  // Set initial values.
  const ctx = useMDXContentReducer({
    pageMetadata,
    selectedBodyContentType: initialSelectedBodyContentType,
    ...mdxExtracts,
  });

  if (pageMetadata.mode === 'custom') {
    return (
      <MDXContentContext.Provider value={ctx}>
        <Container>
          <Content>{children}</Content>
        </Container>
      </MDXContentContext.Provider>
    );
  }

  return (
    <MDXContentContext.Provider value={ctx}>
      <Container>
        <PageHeader />
        <MdxPanel mobile />
        <Api />
        <Content>{children}</Content>
        <div className="leading-6 mt-14">
          <UserFeedback />
          <LindenPagination />
          <Footer />
        </div>
      </Container>
    </MDXContentContext.Provider>
  );
}

const Container = ({ children }: { children: ReactNode }) => {
  const { banner } = useContext(BannerContext);
  const { isWide, isCustom, isCenter } = usePageMode();
  const showSidePanel = !(isWide || isCustom || isCenter);

  return (
    <div
      className={cn(
        'flex flex-row gap-12 box-border w-full',
        isCustom ? 'pt-[120px] lg:pt-0' : `${!!banner ? 'pt-[200px]' : 'pt-40'} lg:pt-10`
      )}
    >
      <div
        className={cn(
          'relative grow box-border flex-col w-full',
          !isCustom && !isCenter && 'mx-auto px-1 lg:pl-[20rem] w-full',
          showSidePanel ? 'xl:max-w-4xl' : 'xl:w-[calc(100%-19rem)] xl:min-w-full'
        )}
        id={Ids.ContentArea}
      >
        {children}
      </div>
      <SidePanel />
    </div>
  );
};
