'use client';

import { Dialog, Transition } from '@headlessui/react';
import { Fragment, useContext } from 'react';

import { zIndex } from '@/constants/zIndex';
import { NavigationContext } from '@/contexts/NavigationContext';
import { XOutIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

import { SideNav } from '../shared/SideNav';

export const MobileSlideInNav = () => {
  const { navIsOpen, setNavIsOpen } = useContext(NavigationContext);

  return (
    <Transition.Root
      show={navIsOpen}
      as={Fragment}
      afterLeave={() => {
        setNavIsOpen(false);
      }}
      appear
    >
      <Dialog
        as="div"
        open={navIsOpen}
        onClose={() => setNavIsOpen(false)}
        className={cn(zIndex.Popup, 'fixed inset-0 overflow-y-auto lg:hidden')}
      >
        <Dialog.Overlay className="fixed inset-0 bg-black/20 backdrop-blur-sm dark:bg-background-dark/80" />
        <Transition.Child
          as={Fragment}
          enter="transition ease-in-out duration-300 transform"
          enterFrom="-translate-x-full"
          enterTo="translate-x-0"
          leave="transition ease-in-out duration-300 transform"
          leaveFrom="translate-x-0"
          leaveTo="-translate-x-full"
        >
          <div className="relative bg-background-light w-[19rem] min-h-full py-7 pl-6 pr-8 dark:bg-background-dark">
            <button
              type="button"
              onClick={() => setNavIsOpen(false)}
              className={cn(
                zIndex.Control,
                'absolute bg-background-light dark:bg-background-dark rounded-full top-5 right-5 w-8 h-8 flex items-center justify-center fill-gray-500 hover:fill-gray-600 dark:fill-gray-400 dark:hover:fill-gray-300'
              )}
            >
              <span className="sr-only">Close navigation</span>
              <XOutIcon />
            </button>
            <SideNav mobile />
          </div>
        </Transition.Child>
      </Dialog>
    </Transition.Root>
  );
};
