import { AsyncApiSecurityScheme } from '@mintlify/common';

import { ExpandSchemaContainer } from '../components/ExpandSchemaContainer';
import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';

export const SecuritySchemes = ({
  securitySchemes,
}: {
  securitySchemes: AsyncApiSecurityScheme[];
}) => {
  return (
    <ExpandableSectionContainer name="Security Schemes">
      {securitySchemes.map((securityScheme) => {
        return (
          <ExpandSchemaContainer
            key={securityScheme.id}
            label={securityScheme.name}
            typeLabel={securityScheme.type}
            propertiesCount={0}
            defaultShowChildren={false}
            required={false}
            deprecated={false}
            description={securityScheme.description}
          />
        );
      })}
    </ExpandableSectionContainer>
  );
};
