'use client';

import { CodeBlock } from '@/components/CodeBlock';
import { CodeGroup } from '@/components/CodeGroup';
import { useSchemaExampleString } from '@/hooks/ApiExamples/useOpenApiSchemaExampleString';

import { CodeSnippet } from '../Code/CodeSnippet';

const filename = 'Example';
const key = 'schema-example';

export const OpenApiSchemaExample = () => {
  const example = useSchemaExampleString();

  if (example == null) return null;

  return (
    <CodeGroup isSmallText noMargins>
      <CodeBlock filename={filename} key={key}>
        <CodeSnippet language="json">{example}</CodeSnippet>
      </CodeBlock>
    </CodeGroup>
  );
};
