'use client';

import { EndpointLocationContext } from '@/contexts/EndpointLocationContext';
import { useCheckHash } from '@/hooks/useCheckHash';
import { useOpenApiEndpoint } from '@/hooks/useEndpoint';
import { getFilteredSecurityOptions } from '@/utils/api-reference/getFilteredSecurityOptions';

import { EndpointBody } from './EndpointBody';
import { EndpointParameters } from './EndpointParameters';
import { EndpointResponse } from './EndpointResponse';
import { EndpointSecurity } from './EndpointSecurity';

const EndpointFields = () => {
  const endpoint = useOpenApiEndpoint();
  const anchor = useCheckHash();

  if (endpoint === undefined) {
    return null;
  }

  // TODO(ronan): remove when all security options are handled (none should be empty)
  const filteredSecurityOptions = getFilteredSecurityOptions(endpoint);

  return (
    <>
      {filteredSecurityOptions.length > 0 && (
        <EndpointSecurity securityOptions={filteredSecurityOptions} />
      )}
      <EndpointParameters parameters={endpoint.request.parameters} />
      <EndpointLocationContext.Provider value={{ location: 'request', anchor }}>
        <EndpointBody body={endpoint.request.body} />
      </EndpointLocationContext.Provider>
      <EndpointLocationContext.Provider value={{ location: 'response', anchor }}>
        {Object.entries(endpoint.response)[0] && <EndpointResponse response={endpoint.response} />}
      </EndpointLocationContext.Provider>
    </>
  );
};

export default EndpointFields;
