'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { cn } from '@/utils/cn';
import { getCodeBlockScrollbarClassname } from '@/utils/getScrollbarClassname';
import { useGetShikiHighlightedHtml } from '@/utils/shiki';

import type { OtherResponseData } from '..';

type CodeResponseContentParams = {
  data: OtherResponseData;
};

export const CodeResponseContent = ({ data }: CodeResponseContentParams) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  let isJson = false;
  let jsonData = data.content;
  try {
    jsonData = JSON.parse(data.content);
    isJson = true;
  } catch (e) {
    console.error(e);
  }

  const html = useGetShikiHighlightedHtml({
    codeString: isJson ? JSON.stringify(jsonData, null, 2) : data.content,
    codeBlockTheme,
    language: isJson ? 'json' : 'text',
    opts: {
      structure: 'inline',
    },
  });

  return (
    <div className={cn('overflow-auto h-full', getCodeBlockScrollbarClassname(codeBlockTheme))}>
      <div className="p-5 h-full whitespace-pre border-gray-200 dark:border-gray-700 dark:text-gray-300 font-mono text-xs leading-5">
        {isJson && html !== undefined ? (
          <span suppressHydrationWarning dangerouslySetInnerHTML={{ __html: html }} />
        ) : (
          <span suppressHydrationWarning>{data.content}</span>
        )}
      </div>
    </div>
  );
};
