'use client';

import { ArraySchema, IncrementalArraySchema } from '@mintlify/validation';
import { useMemo, useState } from 'react';
import uniqueString from 'unique-string';

import { useGenerateNextItems } from '@/hooks/useGenerateNextItems';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

import { InputContainer } from '../../components/InputContainer';
import { RecursiveInput } from './RecursiveInput';

export const ArrayInput: RecursiveInput<ArraySchema | IncrementalArraySchema> = ({
  name,
  schema,
  typeDropdown,
  value,
  setValue,
  clearValue,
  depth,
  parentNames,
}) => {
  const [childKeys, setChildKeys] = useState(value?.map(uniqueString) ?? []);
  const items = useGenerateNextItems(schema);

  const children = useMemo(() => {
    const elementInputs =
      value?.map((elemValue, i) => {
        const setElemValue = (newElemValue: unknown) => {
          const newArr = [...value];
          newArr[i] = newElemValue;
          setValue(newArr);
        };

        const clearElemValue = () => {
          const newArr = [...value];
          newArr.splice(i, 1);
          setChildKeys((prev) => {
            const newKeys = [...prev];
            newKeys.splice(i, 1);
            return newKeys;
          });
          setValue(newArr);
        };

        return (
          <RecursiveInput
            key={childKeys[i]}
            schemaArray={items}
            value={elemValue}
            setValue={setElemValue}
            clearValue={clearElemValue}
            depth={depth + 1}
            arrayIndex={i}
          />
        );
      }) ?? [];

    const onAddElement = () => {
      setChildKeys((prev) => [...prev, uniqueString()]);
      setValue([...(value ?? []), undefined]);
    };
    elementInputs.push(
      <AddAdditionalItemButton
        label="Add an item"
        key="add-button"
        className={cn(elementInputs.length > 0 && 'mt-4')}
        onClick={onAddElement}
      />
    );
    return elementInputs;
  }, [childKeys, items, value, setValue, depth]);

  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={parentNames}
      isArray
    >
      {children}
    </InputContainer>
  );
};

export function AddAdditionalItemButton({
  className,
  onClick,
  label,
}: {
  className?: string;
  onClick: () => void;
  label: string;
}) {
  return (
    <div className={className}>
      <button
        className="flex text-playground-input w-fit items-center gap-x-1.5 !py-1 !px-2 hover:bg-gray-50 dark:hover:bg-white/[0.03]"
        onClick={onClick}
      >
        <Icon icon="plus" iconType="solid" className="bg-gray-500 dark:bg-white/40 h-3 w-3" />
        <span className="text-gray-600 dark:text-gray-200">{label}</span>
      </button>
    </div>
  );
}
