import { ApiPlaygroundInputs } from '@mintlify/validation';
import { createContext, Reducer, useReducer } from 'react';

export const initialApiPlaygroundInputs: ApiPlaygroundInputs = {
  server: {},
  path: {},
  query: {},
  header: {},
  cookie: {},
  body: undefined,
};

type ApiPlaygroundInputsAction = {
  type: 'set_api_playground_inputs';
  payload: ApiPlaygroundInputs;
};

export const ApiPlaygroundInputsReducer: Reducer<ApiPlaygroundInputs, ApiPlaygroundInputsAction> = (
  state: ApiPlaygroundInputs,
  action
) => {
  return {
    ...state,
    ...action.payload,
    server: !!Object.keys(action.payload.server).length
      ? {
          ...state.server,
          ...action.payload.server,
        }
      : {},
    path: !!Object.keys(action.payload.path).length
      ? {
          ...state.path,
          ...action.payload.path,
        }
      : {},
    query: !!Object.keys(action.payload.query).length
      ? {
          ...state.query,
          ...action.payload.query,
        }
      : {},
    header: !!Object.keys(action.payload.header).length
      ? {
          ...state.header,
          ...action.payload.header,
        }
      : {},
    cookie: !!Object.keys(action.payload.cookie).length
      ? {
          ...state.cookie,
          ...action.payload.cookie,
        }
      : {},
    body: action.payload.body
      ? typeof action.payload.body === 'object'
        ? {
            ...(state.body || {}),
            ...action.payload.body,
          }
        : action.payload.body
      : action.payload.body === ''
        ? undefined
        : state.body,
  };
};

export const useApiPlaygroundInputsReducer = (state: ApiPlaygroundInputs) =>
  useReducer(ApiPlaygroundInputsReducer, { ...initialApiPlaygroundInputs, ...state });

export type ApiPlaygroundInputsContextType = ReturnType<typeof useApiPlaygroundInputsReducer>;

export const ApiPlaygroundInputsContext = createContext<ApiPlaygroundInputsContextType>([
  initialApiPlaygroundInputs,
  () => null,
]);

ApiPlaygroundInputsContext.displayName = 'ApiPlaygroundInputsContext';
