'use client';

import { ThemeType } from '@mintlify/validation';
import Link from 'next/link';
import { useContext } from 'react';

import { zIndex } from '@/constants/zIndex';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

import { ArrowRightIcon } from '../../../ui/icons';
import { GitHubCta } from '../TopBar/GitHubCta';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const themeStyles: Partial<Record<ThemeType | 'default', any>> = {
  default: {
    link: 'group px-4 py-1.5 relative inline-flex items-center text-sm font-medium',
    container: 'mr-0.5 space-x-2.5 flex items-center',
    background: 'absolute inset-0 bg-primary-dark rounded-full group-hover:opacity-[0.9]',
    textClass: 'text-white',
    arrow: 'text-white/90',
  },

  linden: {
    link: 'group text-primary dark:text-primary-light hover:text-primary-dark dark:hover:text-primary border border-current rounded-lg pl-3 pr-1.5 py-1.5 relative inline-flex items-center text-sm font-semibold',
    container: 'flex items-center',
    arrow: 'h-6 w-6',
  },

  palm: {
    link: 'group py-1.5 relative inline-flex items-center text-sm font-medium',
    container:
      'mr-0.5 flex items-center text-primary dark:text-primary-light hover:text-primary-dark dark:hover:text-primary-light font-medium',
    arrow: 'size-6 opacity-90',
  },

  willow: {
    link: 'group py-1.5 relative inline-flex items-center text-sm font-medium',
    container:
      'space-x-2.5 flex items-center text-gray-950/90 dark:text-white/90 hover:text-gray-950 dark:hover:text-white',
    arrow: 'text-current',
  },

  almond: {
    link: 'group bg-primary-dark hover:opacity-[0.9] rounded-lg px-2.5 py-1.5 relative inline-flex items-center text-sm font-semibold',
    container: 'flex items-center gap-2',
    textClass: 'text-white',
    arrow: 'hidden',
  },
};

export function TopbarPrimary() {
  const { docsConfig } = useContext(DocsConfigContext);
  const trackCtaClick = useAnalyticsContext('cta_click');
  const primary = docsConfig?.navbar?.primary;
  const theme = docsConfig?.theme ?? 'mint';
  const themeStyle = themeStyles[theme] ?? themeStyles.default;

  if (!primary) {
    return null;
  }

  if (primary.type === 'github') {
    return <GitHubCta className="hidden lg:flex" />;
  }
  if (primary.href && primary.label) {
    return (
      <li className="whitespace-nowrap hidden lg:flex" id={Ids.TopbarCtaButton}>
        <Link
          href={primary.href}
          target="_blank"
          className={themeStyle.link}
          onClick={() => trackCtaClick({ name: primary.label, url: primary.href, type: 'button' })}
        >
          {themeStyle.background && <span className={themeStyle.background} />}
          <div className={themeStyle.container}>
            <span className={cn(zIndex.Control, themeStyle.textClass)}>{primary.label}</span>
            <ArrowRightIcon className={themeStyle.arrow} />
          </div>
        </Link>
      </li>
    );
  }

  return null;
}
