import { requestExampleLanguages as defaultRequestExampleLanguages } from '@mintlify/models';
import { CodeSample } from '@mintlify/validation';

import { type CodeSamplesExampleParams } from '@/components/ApiExamples/CodeSamplesExample';
import { shikiLangToDisplayLang, toShikiLang } from '@/constants/snippetPresets';
import { generateInteractiveCodeSample } from '@/openapi/apiExampleGeneration/generators/generateInteractiveCodeSample';
import type { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';

export const generateSnippetMap = ({
  codeSamples,
  endpoint,
  baseUrl,
  inputs,
  apiPlaygroundMode,
  requestExampleLanguages,
  requiredOnly,
}: CodeSamplesExampleParams): Record<string, Record<string, ExampleCodeSnippet>> => {
  const shouldHaveGeneratedExamples =
    (baseUrl && apiPlaygroundMode !== 'hide') || (baseUrl && requestExampleLanguages);
  const langOrder = requestExampleLanguages ?? [];
  codeSamples = codeSamples.map((codeSample) => ({
    ...codeSample,
    lang: toShikiLang(codeSample.lang),
  }));
  if (requestExampleLanguages === undefined) {
    codeSamples.forEach((codeSample) => {
      if (langOrder.includes(codeSample.lang)) return;
      langOrder.push(codeSample.lang);
    });
    if (shouldHaveGeneratedExamples) {
      defaultRequestExampleLanguages.forEach((lang) => {
        if (langOrder.includes(lang)) return;
        langOrder.push(lang);
      });
    }
  }

  return langOrder.reduce(
    (map, shikiLang) => {
      let displayLang = shikiLangToDisplayLang[shikiLang] ?? shikiLang;

      const codeSamplesInLang = codeSamples.filter((codeSample) => codeSample.lang === shikiLang);

      const labelToSnippetMap = map[displayLang] ?? {};
      if (codeSamplesInLang.length === 0) {
        const generatedCodeSample = generateInteractiveCodeSample({
          baseUrl,
          endpoint,
          inputs,
          apiPlaygroundMode,
          lang: shikiLang,
          requiredOnly,
        });
        if (generatedCodeSample) {
          addCodeSampleToLabelToSnippetMap(
            generatedCodeSample,
            shikiLang,
            displayLang,
            labelToSnippetMap,
            true
          );
        }
      } else {
        codeSamplesInLang.forEach((codeSample) => {
          displayLang = shikiLangToDisplayLang[shikiLang] ?? codeSample.label ?? shikiLang;
          addCodeSampleToLabelToSnippetMap(
            codeSample,
            shikiLang,
            displayLang,
            labelToSnippetMap,
            false
          );
        });
      }
      if (Object.keys(labelToSnippetMap).length > 0) {
        map[displayLang] = labelToSnippetMap;
      }
      return map;
    },
    {} as Record<string, Record<string, ExampleCodeSnippet>>
  );
};

const addCodeSampleToLabelToSnippetMap = (
  codeSample: CodeSample,
  shikiLang: string,
  displayLang: string,
  labelToSnippetMap: Record<string, ExampleCodeSnippet>,
  isGenerated: boolean
) => {
  // if we have entered an empty label, retroactively update it to 'Example 1'
  if ('' in labelToSnippetMap) {
    labelToSnippetMap['Example 1'] = labelToSnippetMap[''];
    delete labelToSnippetMap[''];
  }

  const numExistingLabels = Object.keys(labelToSnippetMap).length;
  const label =
    codeSample.label ?? (numExistingLabels > 0 ? `Example ${numExistingLabels + 1}` : '');

  labelToSnippetMap[label] = {
    filename: isGenerated ? displayLang : label || displayLang, // (used in CodeSnippets)
    language: shikiLang,
    code: codeSample.source,
  };
};
