'use client';

import { NumberSchema } from '@mintlify/validation';

export const NumberMetadata = ({ schema }: { schema: NumberSchema }) => {
  const exclusiveMinimum = schema.exclusiveMinimum || schema.exclusiveMinimum !== undefined;
  const exclusiveMaximum = schema.exclusiveMaximum || schema.exclusiveMaximum !== undefined;
  let range = '';
  if (schema.maximum !== undefined) {
    range = `x ${exclusiveMaximum ? '<' : '<='} ${schema.maximum}`;
  }
  if (schema.minimum !== undefined) {
    range = `x ${exclusiveMinimum ? '>' : '>='} ${schema.minimum}`;
  }
  if (schema.maximum !== undefined && schema.minimum !== undefined) {
    range = `${schema.minimum} ${exclusiveMinimum ? '<' : '<='} x ${exclusiveMaximum ? '<' : '<='} ${schema.maximum}`;
  }
  if (range === '' && schema.multipleOf === undefined) {
    return null;
  }
  return (
    <div className="prose prose-sm prose-gray dark:prose-invert mt-6">
      {range && (
        <>
          Required range: <code>{range}</code>
        </>
      )}
      {schema.multipleOf && (
        <>
          Must be a multiple of <code>{schema.multipleOf}</code>
        </>
      )}
    </div>
  );
};
