import { isFile } from '@mintlify/common';
import type { PostData } from 'har-format';

export const getPostData = (
  bodyData: unknown,
  selectedBodyContentType?: string,
  requiredProperties?: string[]
): PostData | undefined => {
  if (bodyData === undefined) {
    return undefined;
  }

  if (
    typeof bodyData === 'object' &&
    bodyData !== null &&
    requiredProperties &&
    requiredProperties.length > 0
  ) {
    bodyData = Object.fromEntries(
      Object.entries(bodyData).filter(([key]) => requiredProperties.includes(key))
    );
  }

  // if body type is form data, convert to a list of form params
  if (
    selectedBodyContentType === 'multipart/form-data' ||
    selectedBodyContentType === 'application/x-www-form-urlencoded'
  ) {
    if (!bodyData || typeof bodyData !== 'object') {
      return undefined;
    }

    const getParamValue = (value: unknown) => {
      if (isFile(value)) {
        return value.name || 'file';
      }
      if (typeof value === 'object') {
        return JSON.stringify(value, undefined, 2);
      }
      return value?.toString();
    };

    return {
      mimeType: selectedBodyContentType,
      params: Object.entries(bodyData).flatMap(([name, value]) => {
        return [
          {
            name,
            value: getParamValue(value),
          },
        ];
      }),
    };
  }

  if (
    typeof bodyData === 'string' &&
    selectedBodyContentType &&
    isPlainText(selectedBodyContentType)
  ) {
    return {
      mimeType: selectedBodyContentType,
      text: bodyData,
    };
  }

  return {
    mimeType: 'application/json',
    text: JSON.stringify(bodyData, null, 2),
  };
};

function isPlainText(rawContentType: string) {
  const contentType = rawContentType.toLowerCase();
  return (
    contentType.startsWith('text/') || contentType.endsWith('+xml') || contentType.endsWith('/xml')
  );
}
