'use client';

import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import { Step, type StepProps } from './Step';

export type StepsProps = {
  /**
   * A list of `Step` components.
   */
  children: React.ReactElement<StepProps>[];
  /**
   * The size of the step title. Defaults to h3.
   */
  titleSize?: 'p' | 'h2' | 'h3';
};

export const Steps = ({ children, titleSize }: StepsProps) => {
  return (
    <>
      <div role="list" className={cn(Classes.Steps, 'ml-3.5 mt-10 mb-6')}>
        {Array.isArray(children)
          ? children.map(({ props }, index) => (
              <Step
                key={`${props.title} + ${index}`}
                {...props}
                stepNumber={props.stepNumber ?? index + 1}
                {...(titleSize && { titleSize })}
                isLast={index === children.length - 1}
              />
            ))
          : children}
      </div>
    </>
  );
};
