import { IncrementalObjectSchema, ObjectSchema } from '@mintlify/validation';
import { useMemo } from 'react';

import { useEndpointLocation } from '@/contexts/EndpointLocationContext';
import { generateNextProperties } from '@/openapi/generateNextProperties';

import { useComponentSchemas } from './useComponentSchemas';

export function useGenerateNextProperties(schema: ObjectSchema | IncrementalObjectSchema) {
  const componentSchemas = useComponentSchemas();
  const { location } = useEndpointLocation();

  return useMemo(() => {
    return generateNextProperties(schema, componentSchemas, location);
  }, [schema, componentSchemas, location]);
}
