'use client';

import {
  ArraySchema,
  DataSchema,
  DataSchemaArray,
  IncrementalArraySchema,
  IncrementalDataSchema,
  IncrementalDataSchemaArray,
  IncrementalObjectSchema,
  ObjectSchema,
} from '@mintlify/validation';
import { ReactNode, useCallback, useState } from 'react';

import { usePlaygroundSchemaOptions } from '@/hooks/usePlaygroundSchemaOptions';
import { RenderedRefs } from '@/types/api';
import ErrorBoundary from '@/ui/ErrorBoundary';

import { TypeDropdown } from '../../components/TypeDropdown';
import { DifferentiatedInput } from './DifferentiatedInput';

type GenericInputParams<T extends IncrementalDataSchema | DataSchema, K = T['example']> = {
  name?: string;
  schema: T;
  typeDropdown: ReactNode;
  value: K;
  setValue: (value: K) => void;
  clearValue: (() => void) | undefined;
  parentNames?: string[];
  arrayIndex?: number;
  defaultExpanded?: boolean;
  style?: string;
  explode?: boolean;
  renderedRefs?: RenderedRefs;
};

export type GenericInput<T extends DataSchema | IncrementalDataSchema> = React.FC<
  GenericInputParams<T>
>;
export type RecursiveInput<
  T extends IncrementalObjectSchema | ObjectSchema | IncrementalArraySchema | ArraySchema,
> = React.FC<GenericInputParams<T> & { depth: number }>;

type RecursiveInputParams = {
  name?: string;
  schemaArray: DataSchemaArray | IncrementalDataSchemaArray;
  value: unknown | undefined;
  setValue: (value: unknown) => void;
  clearValue: (() => void) | undefined;
  isBody?: boolean;
  depth?: number;
  parentNames?: string[];
  arrayIndex?: number;
  defaultExpanded?: boolean;
  style?: string;
  explode?: boolean;
  renderedRefs?: RenderedRefs;
};

export const RecursiveInput = ({
  name,
  value,
  schemaArray,
  setValue,
  clearValue,
  isBody,
  depth = 0,
  parentNames,
  arrayIndex,
  defaultExpanded,
  style,
  explode,
  renderedRefs = new Set(),
}: RecursiveInputParams) => {
  const [selectedSchemaIndex, setSelectedSchemaIndex] = useState(0);
  const schemasWithLabelInfo = usePlaygroundSchemaOptions(schemaArray);
  const selectedSchema = schemasWithLabelInfo[selectedSchemaIndex]?.schema;

  const onSelectTypeOption = useCallback(
    (index: number) => {
      if (schemasWithLabelInfo[index]?.schema.type === 'null') {
        setValue(null);
      } else {
        setValue(undefined);
      }
      setSelectedSchemaIndex(index);
    },
    [schemasWithLabelInfo, setValue]
  );

  const typeDropdown = (
    <TypeDropdown
      options={schemasWithLabelInfo}
      selectedIndex={selectedSchemaIndex}
      onSelectOption={onSelectTypeOption}
    />
  );

  if (selectedSchema === undefined) return null;

  return (
    <ErrorBoundary>
      <DifferentiatedInput
        name={name}
        schema={selectedSchema}
        typeDropdown={typeDropdown}
        value={value}
        setValue={setValue}
        clearValue={clearValue}
        isBody={isBody}
        depth={depth}
        parentNames={parentNames}
        arrayIndex={arrayIndex}
        defaultExpanded={defaultExpanded}
        style={style}
        explode={explode}
        renderedRefs={renderedRefs}
      />
    </ErrorBoundary>
  );
};
