'use client';

import { SearchIcon } from 'lucide-react';
import { useContext } from 'react';

import { Ids } from '@/lib/local/identifiers';
import { SparklesIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

import { SearchButton, SearchContext, ChatAssistantContext } from '.';

export function DesktopSearchEntry({
  className,
  searchButtonClassName,
  includeAskAiText,
}: {
  className?: string;
  searchButtonClassName?: string;
  includeAskAiText?: boolean;
}) {
  const { searchPrompt, hasChatPermissions } = useContext(SearchContext);
  const { onChatSheetToggle } = useContext(ChatAssistantContext);

  return (
    <div className={cn('relative hidden lg:flex items-center gap-2.5 flex-1', className)}>
      <SearchButton
        className={cn(
          'flex dark:bg-gray-900 pointer-events-auto rounded-xl w-full items-center text-sm leading-6 h-9 pl-3.5 pr-3 shadow-sm text-gray-500 dark:text-white/50 bg-background-light dark:bg-background-dark dark:brightness-[1.1] dark:ring-1 dark:hover:brightness-[1.25] ring-1 ring-gray-400/20 hover:ring-gray-600/25 dark:ring-gray-600/30 dark:hover:ring-gray-500/30 focus:outline-primary justify-between truncate gap-2 min-w-[43px]',
          searchButtonClassName
        )}
        id={Ids.SearchBarEntry}
      >
        {({ actionKey }) => (
          <>
            <div className="flex items-center gap-2 min-w-[42px]">
              <SearchIcon
                size={16}
                className="min-w-4 flex-none text-gray-700 hover:text-gray-800 dark:text-gray-400 hover:dark:text-gray-200"
              />
              <div className="truncate min-w-0">{searchPrompt}</div>
            </div>
            {actionKey && <span className="flex-none text-xs font-semibold">{actionKey[0]}K</span>}
          </>
        )}
      </SearchButton>
      {hasChatPermissions && (
        <button
          type="button"
          className={cn(
            'flex-none hidden lg:flex items-center justify-center gap-1.5 pl-3 pr-3.5 h-9 rounded-xl shadow-sm bg-background-light dark:bg-background-dark dark:brightness-[1.1] dark:ring-1 dark:hover:brightness-[1.25] ring-1 ring-gray-400/20 hover:ring-gray-600/25 dark:ring-gray-600/30 dark:hover:ring-gray-500/30 focus:outline-primary',
            !includeAskAiText && 'w-9',
            searchButtonClassName
          )}
          onClick={onChatSheetToggle}
          id={Ids.AssistantEntry}
        >
          <SparklesIcon
            className={cn(
              'w-4 h-4',
              'shrink-0 text-gray-700 hover:text-gray-800 dark:text-gray-400 hover:dark:text-gray-200'
            )}
          />
          {includeAskAiText && (
            <span className="text-sm text-gray-500 dark:text-white/50 whitespace-nowrap">
              Ask AI
            </span>
          )}
        </button>
      )}
    </div>
  );
}
