'use client';

import clsx from 'clsx';
import { useState } from 'react';

import { AddAdditionalItemButton } from '../Request/inputs/ArrayInput';

type AdditionalPropertyButtonParams = {
  onAddKey: (name: string) => void;
  existingKeys: string[];
};

export const AdditionalPropertyButton = ({
  onAddKey,
  existingKeys,
}: AdditionalPropertyButtonParams) => {
  const [isActive, setIsActive] = useState(false);
  const [key, setKey] = useState('');

  const isDuplicate = existingKeys.includes(key);
  const isValid = key.length > 0 && !isDuplicate;

  if (!isActive) {
    return (
      <AddAdditionalItemButton
        className="py-5"
        onClick={() => setIsActive((prev) => !prev)}
        label="Add new property"
      />
    );
  }

  return (
    <input
      className={clsx(
        'py-6 flex-1 bg-transparent outline-none text-sm text-gray-900 dark:text-gray-100 placeholder-gray-300 dark:placeholder-white/30 font-mono font-medium',
        isDuplicate ? 'underline decoration-wavy decoration-red-400' : ''
      )}
      placeholder="Enter key of new property"
      type="text"
      autoFocus
      value={key}
      onBlur={() => {
        setKey('');
        setIsActive(false);
      }}
      onChange={(event) => setKey(event.target.value)}
      onKeyDown={(event) => {
        if ((event.key === 'Enter' || event.key === 'Tab') && isValid) {
          onAddKey(event.currentTarget.value);
        }
      }}
      spellCheck={false}
    />
  );
};
