import { Endpoint } from '@mintlify/validation';

export const getRequiredProperties = (endpoint: Endpoint, selectedBodyContentType?: string) => {
  const requiredProperties: string[] = [];
  if (selectedBodyContentType) {
    const content = endpoint.request.body[selectedBodyContentType];
    if (content?.schemaArray) {
      content.schemaArray.forEach((schema) => {
        if (schema.type === 'object' && schema.requiredProperties) {
          requiredProperties.push(...schema.requiredProperties);
        }
      });
    }
  }
  return requiredProperties;
};
