'use client';

import gh from 'github-url-to-object';
import { useCallback, useContext, useEffect, useMemo, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { cn } from '@/utils/cn';

import Icon from '../../../ui/Icon';
import { GithubIcon } from '../../../ui/icons';

type RepoDataType = { stargazers_count: number; forks_count: number };

export function GitHubCta({ className }: { className?: string }) {
  const { docsConfig } = useContext(DocsConfigContext);
  const trackCtaClick = useAnalyticsContext('cta_click');
  const [repoData, setRepoData] = useState<RepoDataType>();

  const primary = docsConfig?.navbar?.primary;

  const github = useMemo(() => {
    if (!primary) {
      return null;
    }

    return gh(primary.href);
  }, [primary]);

  const load = useCallback(async () => {
    if (github == null) {
      return;
    }

    const axios = await import('axios').then((x) => x.default);

    const { data } = await axios.get<RepoDataType>(
      `https://api.github.com/repos/${github.user}/${github.repo}`
    );

    setRepoData(data);
  }, [github]);

  useEffect(() => {
    //TODO Should handle error in state/ui as well.
    load().catch(console.error);
  }, [load]);

  if (github == null) {
    return null;
  }

  if (!primary) {
    return null;
  }

  return (
    <li className={cn('cursor-pointer', className)}>
      <a
        href={primary.href}
        target="_blank"
        rel="noreferrer"
        onClick={() => trackCtaClick({ url: primary.href, type: 'github' })}
        className="group flex items-center rounded-md hover:text-primary dark:hover:text-primary-light"
      >
        <div className="flex items-center gap-1.5 h-8">
          <div className="flex items-center gap-2">
            <GithubIcon className="size-4" />
            <span className="text-sm font-medium text-gray-700 dark:text-gray-300 group-hover:text-primary dark:group-hover:text-primary-light">
              {github.user}/{github.repo}
            </span>
          </div>
          {repoData && (
            <div className="flex items-center gap-1.5 ml-1">
              <Icon className="size-3.5" icon="star" iconType="regular" color="currentColor" />
              <span className="text-sm">{repoData.stargazers_count.toLocaleString()}</span>
            </div>
          )}
        </div>
      </a>
    </li>
  );
}
