'use client';

import { Transition } from '@headlessui/react';
import { Fragment } from 'react';

import { zIndex } from '@/constants/zIndex';
import { cn } from '@/utils/cn';

type PlaygroundModalWrapperProps = {
  isPlaygroundExpanded: boolean;
  setIsPlaygroundExpanded?: (isPlaygroundExpanded: boolean) => void;
  children: React.ReactNode;
};

export const PlaygroundModalWrapper = ({
  isPlaygroundExpanded,
  setIsPlaygroundExpanded,
  children,
}: PlaygroundModalWrapperProps) => {
  return (
    <Transition.Root show={isPlaygroundExpanded} as={Fragment}>
      <div className={cn(zIndex.Popup, 'fixed inset-0 overflow-y-auto p-4 sm:p-6 md:p-12')}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-100"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-50"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div
            className="fixed z-0 inset-0 bg-background-dark/20 transition-opacity backdrop-blur-sm"
            onClick={() => setIsPlaygroundExpanded?.(false)}
          />
        </Transition.Child>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-100"
          enterFrom="opacity-0 scale-95"
          enterTo="opacity-100 scale-100"
          leave="ease-in duration-50"
          leaveFrom="opacity-100 scale-100"
          leaveTo="opacity-0 scale-95"
        >
          <div className="flex flex-col mx-auto max-w-screen-xl transform bg-background-light dark:bg-background-dark shadow-search rounded-3xl border-standard">
            <div className="p-4">{children}</div>
          </div>
        </Transition.Child>
      </div>
    </Transition.Root>
  );
};
