'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

export function Logo({ className }: { className?: string }) {
  const { docsConfig } = useContext(DocsConfigContext);
  const baseClassName = 'w-auto h-7 relative object-contain';
  if (typeof docsConfig?.logo === 'object') {
    return (
      <>
        <img
          className={cn(Classes.Logo, baseClassName, 'block dark:hidden', className)}
          src={docsConfig.logo.light}
          alt="light logo"
        />
        <img
          className={cn(Classes.Logo, baseClassName, 'hidden dark:block', className)}
          src={docsConfig.logo.dark}
          alt="dark logo"
        />
      </>
    );
  }
  if (typeof docsConfig?.logo === 'string') {
    return (
      <img
        className={cn(Classes.Logo, baseClassName, className)}
        src={docsConfig.logo}
        alt="logo"
      />
    );
  }
  if (docsConfig?.name) {
    return (
      <div
        className={cn(
          Classes.Logo,
          'inline-block text-2xl sm:text-3xl font-bold text-gray-900 tracking-tight dark:text-gray-200',
          className
        )}
      >
        {docsConfig.name}
      </div>
    );
  }
  return <></>;
}
