import { ApiPlaygroundInputs, Endpoint, ExampleSchema } from '@mintlify/validation';

import { CodeGroupSelect } from '@/components/Code/CodeGroupSelect';
import { CodeSnippets } from '@/components/Code/CodeSnippets';
import { langToPresetMap } from '@/constants/snippetPresets';
import { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';
import { SnippetPreset } from '@/types/snippetPreset';

import { generateRequest } from './generateRequest';
import { generateSnippet } from './generateSnippet';

export type GeneratedRequestExampleParams = {
  baseUrl: string;
  endpoint: Endpoint;
  inputs: ApiPlaygroundInputs;
  requestExampleLanguages?: string[];
  requiredOnly?: boolean;
};

export const GeneratedRequestExample = ({
  baseUrl,
  endpoint,
  inputs,
  requestExampleLanguages,
  requiredOnly,
}: GeneratedRequestExampleParams): JSX.Element | null => {
  const [contentTypeAndContent] = Object.entries(endpoint.request.body);

  if (contentTypeAndContent === undefined) {
    // request has no body
    return (
      <SingleRequest
        endpoint={endpoint}
        baseUrl={baseUrl}
        inputs={inputs}
        requestExampleLanguages={requestExampleLanguages}
        requiredOnly={requiredOnly}
      />
    );
  }

  const [contentType, content] = contentTypeAndContent;

  const firstExample = Object.values(content.examples)[0];

  if (firstExample === undefined) {
    return null;
  }

  return Object.keys(content.examples).length > 1 ? (
    <MultipleRequest
      endpoint={endpoint}
      selectedBodyContentType={contentType}
      baseUrl={baseUrl}
      inputs={inputs}
      examples={content.examples}
      requiredOnly={requiredOnly}
    />
  ) : (
    <SingleRequest
      endpoint={endpoint}
      selectedBodyContentType={contentType}
      baseUrl={baseUrl}
      inputs={inputs}
      exampleValue={firstExample.value}
      requestExampleLanguages={requestExampleLanguages}
      requiredOnly={requiredOnly}
    />
  );
};

type SingleRequestParams = GeneratedRequestExampleParams & {
  selectedBodyContentType?: string;
  exampleValue?: unknown;
};

const SingleRequest = ({
  baseUrl,
  endpoint,
  inputs,
  selectedBodyContentType,
  exampleValue,
  requestExampleLanguages,
  requiredOnly,
}: SingleRequestParams) => {
  const request = generateRequest({
    baseUrl,
    endpoint,
    inputs: inputs,
    selectedBodyContentType,
    exampleData: exampleValue,
    requiredOnly,
  });

  let snippets: ExampleCodeSnippet[];
  const snippetPresets = requestExampleLanguages?.reduce<SnippetPreset[]>((acc, lang) => {
    const preset = langToPresetMap[lang];
    if (preset) acc.push(preset);
    return acc;
  }, []);

  try {
    snippets = generateSnippet({
      pathInputs: inputs.path,
      request,
      snippetPresets,
    });
  } catch (err) {
    console.error(err);
    return null;
  }

  return <CodeSnippets snippets={snippets} noMargins />;
};

type MultipleRequestParams = GeneratedRequestExampleParams & {
  selectedBodyContentType?: string;
  examples: Record<string, ExampleSchema>;
};

const MultipleRequest = ({
  baseUrl,
  endpoint,
  inputs,
  selectedBodyContentType,
  examples,
  requiredOnly,
}: MultipleRequestParams): JSX.Element | null => {
  const nameToSnippetExamplesMap: Record<string, Record<string, ExampleCodeSnippet>> = {};
  const pathInputs = inputs.path;

  for (const [name, { value: exampleData }] of Object.entries(examples)) {
    if (!exampleData) continue;

    const request = generateRequest({
      baseUrl,
      endpoint,
      inputs,
      selectedBodyContentType,
      exampleData,
      requiredOnly,
    });

    try {
      const snippets = generateSnippet({
        pathInputs,
        request,
      });

      snippets.forEach((snippet) => {
        nameToSnippetExamplesMap[snippet.filename] = {
          ...nameToSnippetExamplesMap[snippet.filename],
          [name]: snippet,
        };
      });
    } catch (err) {
      console.error(err);
      return null;
    }
  }

  return <CodeGroupSelect snippets={nameToSnippetExamplesMap} />;
};
