import { useContext } from 'react';

import { Ids } from '@/lib/local/identifiers';
import { SparklesIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

import { ChatAssistantContext, SearchContext } from '..';

function ChatMobileButton() {
  const { hasChatPermissions } = useContext(SearchContext);
  const { isChatSheetOpen, onChatSheetToggle } = useContext(ChatAssistantContext);

  if (!hasChatPermissions) {
    return null;
  }

  return (
    <button onClick={onChatSheetToggle} id={Ids.AssistantEntryMobile}>
      <SparklesIcon
        className={cn(
          'size-4.5 text-gray-500 dark:text-gray-400 hover:text-gray-600 dark:hover:text-gray-300',
          isChatSheetOpen &&
            'text-primary dark:text-primary-light hover:text-primary-dark dark:hover:text-primary'
        )}
      />
    </button>
  );
}

export default ChatMobileButton;
