import { ChevronLeftIcon, ChevronRightIcon } from 'lucide-react';
import Link from 'next/link';

import { useSelectedLocale } from '@/contexts/NavigationContext';
import { useMDXContent } from '@/hooks/useMDXContent';
import { usePrevNext } from '@/hooks/usePrevNext';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

export function AspenPagination() {
  const [{ pageMetadata }] = useMDXContent();
  const { prev, next: n } = usePrevNext();
  const previous = pageMetadata.hideFooterPagination ? null : prev;
  const next = pageMetadata.hideFooterPagination ? null : n;
  const { previous: previousText, next: nextText } = useSelectedLocale();

  if (previous == null && next == null) {
    return null;
  }

  return (
    <div id={Ids.Pagination} className="grid lg:grid-cols-2 gap-4 mb-8">
      {previous ? (
        <PaginationItem
          href={previous.href}
          title={previous.title}
          label={previousText}
          align="left"
        />
      ) : (
        <div />
      )}
      {next ? (
        <PaginationItem href={next.href} title={next.title} label={nextText} align="right" />
      ) : (
        <div />
      )}
    </div>
  );
}

function PaginationItem({
  href,
  title,
  label,
  align = 'left',
}: {
  href: string;
  title: string;
  label: string;
  align?: 'left' | 'right';
}) {
  return (
    <Link
      href={href || '/'}
      className={cn(
        'border border-gray-200/70 dark:border-gray-800/70 group flex items-center rounded-xl py-3 px-4 hover:border-gray-300 dark:hover:border-gray-700',
        align === 'left' ? 'justify-start' : 'justify-end'
      )}
    >
      <div className="space-y-1">
        <div
          className={cn(
            'font-medium text-gray-900 dark:text-gray-200',
            align === 'right' && 'text-right'
          )}
        >
          {title}
        </div>
        <div
          className={cn(
            'flex items-center text-sm text-gray-500 dark:text-gray-500 group-hover:text-gray-600 dark:group-hover:text-gray-400 gap-x-1',
            align === 'right' && 'flex-row-reverse'
          )}
        >
          {align === 'left' ? (
            <ChevronLeftIcon className="size-3.5 text-gray-400 dark:text-gray-600 group-hover:text-gray-500 dark:group-hover:text-gray-500" />
          ) : (
            <ChevronRightIcon className="size-3.5 text-gray-400 dark:text-gray-600 group-hover:text-gray-500 dark:group-hover:text-gray-500" />
          )}
          <span className="font-medium">{label}</span>
        </div>
      </div>
    </Link>
  );
}
