'use client';

import { useContext } from 'react';

import { NavigationContext } from '@/contexts/NavigationContext';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import { Tab } from '../../themes/mint/shared/Tab';

export function TabsBar({
  className,
  activeClassName,
  underlineClassName,
}: {
  className?: string;
  activeClassName?: string;
  underlineClassName?: string;
}) {
  const { divisions } = useContext(NavigationContext);

  return (
    <div className={cn(Classes.TabsBar, 'h-full flex text-sm space-x-6')}>
      {divisions.tabs.map((tab) => (
        <Tab
          key={tab.name}
          name={tab.name}
          href={tab.href}
          isActive={tab.isActive}
          className={className}
          activeClassName={activeClassName}
          underlineClassName={underlineClassName}
        />
      ))}
    </div>
  );
}
