'use client';

import {
  AnySchema,
  BooleanSchema,
  FileSchema,
  NullSchema,
  NumberEnumSchema,
  NumberSchema,
  StringEnumSchema,
  StringSchema,
  ArraySchema,
  ObjectSchema,
  IncrementalArraySchema,
  IncrementalObjectSchema,
} from '@mintlify/validation';

import { cn } from '@/utils/cn';
import { formatJSON } from '@/utils/formatJSON';

import { Description } from '../components/Description';

function Example({ example }: { example: Exclude<unknown, undefined> }) {
  return (
    <Description
      className="overflow-wrap-anywhere text-[13px] [&_*]:text-[13px]"
      markdown={
        typeof example === 'object' && example !== null
          ? `\`\`\`json\n${formatJSON(example)}\n\`\`\``
          : `\`${example === 'null' ? null : JSON.stringify(example, undefined, 2)}\``
      }
    />
  );
}

export const ExampleDescription = ({
  schema,
}: {
  schema:
    | ArraySchema
    | IncrementalArraySchema
    | ObjectSchema
    | IncrementalObjectSchema
    | BooleanSchema
    | StringSchema
    | NumberSchema
    | StringEnumSchema
    | NumberEnumSchema
    | FileSchema
    | NullSchema
    | AnySchema;
}) => {
  const example = schema.example;
  const examples =
    schema.examples !== undefined
      ? Array.isArray(schema.examples)
        ? schema.examples
        : [schema.examples]
      : undefined;
  return (
    example !== undefined && (
      <div
        className={cn(
          'flex prose prose-sm prose-gray dark:prose-invert mt-6 gap-1.5',
          (typeof example === 'object' || typeof examples?.[0] === 'object') && 'flex-col gap-y-3'
        )}
      >
        <span>Example{examples && examples.length ? 's' : ''}:</span>
        {examples !== undefined ? (
          <div
            className={cn(
              '*:my-2',
              typeof examples[0] !== 'object' && 'inline *:mr-2 [&_*]:inline'
            )}
          >
            {examples.map((ex, index) => {
              return <Example key={index} example={ex} />;
            })}
          </div>
        ) : (
          <Example example={example} />
        )}
      </div>
    )
  );
};
