'use client';

import { useContext, useEffect, useRef, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { Ids } from '@/lib/local/identifiers';

import { useMDXContent } from '../useMDXContent';

/**
 * Registers scroll event to get the current section
 */
export const useCurrentTableOfContentsSection = () => {
  const { docsConfig } = useContext(DocsConfigContext);
  const [{ tableOfContents, headings }] = useMDXContent();
  const [currentHash, setCurrentHash] = useState<string>('');
  const [currentSection, setCurrentSection] = useState<string>();
  const contentContainerRef = useRef<HTMLDivElement>();

  const useContentContainer = docsConfig?.theme === 'almond';

  useEffect(() => {
    if (useContentContainer) {
      contentContainerRef.current = document.getElementById(Ids.ContentContainer) as
        | HTMLDivElement
        | undefined;
    }

    return () => {
      contentContainerRef.current = undefined;
    };
  }, [useContentContainer]);

  useEffect(() => {
    const onHashChange = () => {
      const hash = window.location.hash;
      if (!hash) return;
      else setCurrentHash(hash.substring(1));
    };
    window.addEventListener('hashchange', onHashChange);
    onHashChange();
    return () => {
      window.removeEventListener('hashchange', onHashChange);
    };
  }, []);

  useEffect(() => {
    if (tableOfContents.length === 0 || headings.length === 0) return;

    function onScroll() {
      let isAtBottom = false;
      if (useContentContainer) {
        if (contentContainerRef.current) {
          const { scrollTop, scrollHeight, clientHeight } = contentContainerRef.current;
          isAtBottom = Math.abs(scrollTop + clientHeight - scrollHeight) < 5;
        }
      } else {
        isAtBottom = window.innerHeight + window.scrollY === document.body.offsetHeight;
      }

      if (isAtBottom) {
        const headingElement = document.getElementById(currentHash);
        if (headingElement) {
          const rect = headingElement.getBoundingClientRect();
          if (rect.top >= 0 && rect.bottom <= window.innerHeight) {
            setCurrentSection(currentHash);
            return;
          }
        }
      }

      const style = window.getComputedStyle(document.documentElement);
      let scrollMt = parseFloat(style.getPropertyValue('--scroll-mt').match(/[\d.]+/)?.[0] ?? '0');
      if (useContentContainer) scrollMt += 6.5;
      const fontSize = parseFloat(style.fontSize.match(/[\d.]+/)?.[0] ?? '16');
      scrollMt = scrollMt * fontSize;

      const sortedHeadings = headings.concat([]).sort((a, b) => (a.top ?? 0) - (b.top ?? 0));

      let top;
      if (useContentContainer) {
        top = (contentContainerRef.current?.scrollTop ?? 0) + scrollMt + 1;
      } else {
        top = window.scrollY + scrollMt + 1;
      }

      let currentHeading = sortedHeadings[0];
      for (let i = 0; i < sortedHeadings.length; i++) {
        if (top >= (sortedHeadings[i]?.top ?? 0)) {
          currentHeading = sortedHeadings[i];
        }
      }

      if (currentSection != currentHeading?.id && currentHeading?.isAtRootLevel === true) {
        setCurrentSection(currentHeading.id);
      }
    }

    const options: AddEventListenerOptions = {
      capture: true,
      passive: true,
    };

    if (useContentContainer) {
      if (contentContainerRef.current) {
        contentContainerRef.current.addEventListener('scroll', onScroll, options);
        onScroll();
        return () => contentContainerRef.current?.removeEventListener('scroll', onScroll, options);
      }
    } else {
      window.addEventListener('scroll', onScroll, options);
      onScroll();
      return () => window.removeEventListener('scroll', onScroll, options);
    }
  }, [currentHash, currentSection, headings, tableOfContents, useContentContainer]);

  return currentSection;
};
