import { ArraySchema, IncrementalArraySchema } from '@mintlify/validation';
import { useMemo } from 'react';

import { useEndpointLocation } from '@/contexts/EndpointLocationContext';
import { generateNextItems } from '@/openapi/generateNextItems';

import { useComponentSchemas } from './useComponentSchemas';

export function useGenerateNextItems(schema: ArraySchema | IncrementalArraySchema) {
  const componentSchemas = useComponentSchemas();
  const { location } = useEndpointLocation();

  return useMemo(() => {
    return generateNextItems(schema, componentSchemas, location);
  }, [schema, componentSchemas, location]);
}
