import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';

export const useApiParamFieldOption = () => {
  const { mintConfig, docsConfig } = useContext(DocsConfigContext);

  const mintConfigApiParamFieldOption = mintConfig?.api?.paramFields?.expanded;
  const docsConfigApiParamFieldOption = docsConfig?.api?.params?.expanded;

  const apiParamFieldOption = docsConfigApiParamFieldOption ?? mintConfigApiParamFieldOption;

  return apiParamFieldOption;
};
