'use client';

import {
  ComponentPropsWithoutRef,
  forwardRef,
  useCallback,
  useEffect,
  useState,
  useMemo,
} from 'react';
import { Rect, useRect } from 'react-use-rect';

import { useMDXContent } from '@/hooks/useMDXContent';
import { useTop } from '@/hooks/useTop';
import { Classes } from '@/lib/local/selectors';
import { LinkIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';
import { copyToClipboard } from '@/utils/copyToClipboard';

export interface UpdatePropsBase {
  /**
   * ID of the update, required for ToC, not documented in our docs, so it will be never exposed to the end users
   */
  id: string;
  /**
   * The label in the sticky section, required for ToC
   */
  label: string;
  /**
   * Description of the update, right under the label
   */
  description?: string;
  /**
   * Tags of the update, used for filtering
   */
  tags?: string[];
}

export type UpdateProps = UpdatePropsBase &
  Omit<ComponentPropsWithoutRef<'div'>, keyof UpdatePropsBase>;

export const Update = forwardRef<HTMLDivElement, UpdateProps>(
  ({ children, label, id, description, tags, className, ...props }, ref) => {
    const [context, dispatch] = useMDXContent();
    const [rect, setRect] = useState<Rect | null>(null);
    const [rectRef] = useRect(setRect);
    const top = useTop(rect);
    const tagsArray = tags?.map((tag) => tag.trim()).filter(Boolean);

    // We cannot include context in the dependency array because it changes every render.
    const hasContext = Boolean(context);

    /**
     * Register heading for ToC
     */
    useEffect(() => {
      if (!hasContext) return;
      if (typeof top !== 'undefined') {
        dispatch({
          type: 'register_heading',
          payload: { id, top, isAtRootLevel: true },
        });
      }
      return () => {
        dispatch({
          type: 'unregister_heading',
          payload: id,
        });
      };
    }, [top, id, hasContext, dispatch]);

    const copyAnchorLink = useCallback(() => {
      void copyToClipboard(`https://${window.location.host}${window.location.pathname}#${id}`);
      window.location.hash = id;
      const element = document.getElementById(id);
      if (element) {
        element.scrollIntoView();
      }
    }, [id]);

    /**
     * isVisible if
     * - all context.changelogFilters are active or none are active
     * - tagsArray is empty
     * - tagsArray is not empty and some of its tags are active in context.changelogFilters
     */
    const isVisible = useMemo(() => {
      if (!context.changelogFilters.length) return true;
      const activeFilters = context.changelogFilters.filter((filter) => filter.active);
      if (activeFilters.length === 0) return true;
      return tagsArray?.some((tag) => activeFilters.some((filter) => filter.tag === tag));
    }, [context.changelogFilters, tagsArray]);

    if (!isVisible) return null;

    return (
      <div
        className={cn(
          Classes.Update,
          'flex flex-col relative items-start w-full lg:flex-row gap-2 lg:gap-6 py-8 update-container',
          className
        )}
        ref={ref}
        id={id}
        {...props}
      >
        <div className="lg:sticky top-[var(--scroll-mt)] group flex flex-col w-full lg:w-[160px] items-start flex-shrink-0 justify-start">
          <div className="absolute">
            <a
              href={`#${id}`}
              className="-ml-10 flex items-center opacity-0 border-0 group-hover:opacity-100"
              aria-label="Navigate to changelog"
            >
              &#8203;
              <div className="w-6 h-6 rounded-md flex items-center justify-center shadow-sm text-gray-400 dark:text-white/50 dark:bg-background-dark dark:brightness-[1.35] dark:ring-1 dark:hover:brightness-150 bg-white ring-1 ring-gray-400/30 dark:ring-gray-700/25 hover:ring-gray-400/60 dark:hover:ring-white/20">
                <LinkIcon />
              </div>
            </a>
          </div>
          <div
            className="cursor-pointer px-2 py-1 rounded-lg text-sm flex items-center flex-grow-0 justify-center font-medium bg-primary/10 text-primary dark:text-primary-light"
            onClick={copyAnchorLink}
            contentEditable={false}
            data-component-part="update-label"
          >
            {label}
          </div>
          {tagsArray?.length && (
            <div
              className="px-1 flex flex-wrap gap-2 text-secondary dark:text-secondary-light mt-3 text-sm"
              data-component-part="update-tag-list"
            >
              {tagsArray.map((tag) => (
                <span
                  key={tag}
                  className="inline-block rounded-lg text-sm font-medium"
                  data-component-part="update-tag"
                >
                  {tag}
                </span>
              ))}
            </div>
          )}
          {description && (
            <div
              className="px-1 text-secondary dark:text-secondary-light mt-3 text-sm max-w-[160px] break-words"
              contentEditable={false}
              data-component-part="update-description"
            >
              {description}
            </div>
          )}
        </div>

        <div className="flex-1 overflow-hidden px-0.5 max-w-full" ref={rectRef}>
          <div className="prose-sm" data-component-part="update-content">
            {children}
          </div>
        </div>
      </div>
    );
  }
);

Update.displayName = 'Update';
