'use client';

import { useEffect, useRef, useState } from 'react';

const EXPANDED_HEIGHT_OFFSET = 60;

export const useExpandable = (enable: boolean) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [calculatedHeight, setCalculatedHeight] = useState(0);
  const contentRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (contentRef.current && enable) {
      setCalculatedHeight(contentRef.current.scrollHeight + EXPANDED_HEIGHT_OFFSET);
    }
  }, [enable, contentRef.current?.scrollHeight]);

  const toggleExpanded = () => setIsExpanded((prev) => !prev);

  return { isExpanded, calculatedHeight, contentRef, toggleExpanded };
};
