import { slugToTitle } from '@mintlify/common';
import { PageMetaTags } from '@mintlify/models';
import { DocsConfig } from '@mintlify/validation';

import { appendS3IfRelativePath } from './s3';

// Everything here gets injected as metatags when available.
// og:url is a special case handled in SupremePageLayout
const SEO_META_TAGS = [
  'description',
  'og:site_name',
  'og:title',
  'og:description',
  'og:image',
  'og:locale',
  'og:logo',
  'article:publisher',
  'twitter:title',
  'twitter:description',
  'twitter:url',
  'twitter:image',
  'twitter:site',
  'og:image:width',
  'og:image:height',
  'robots',
];

export function getAllMetaTags(
  pageMeta: PageMetaTags,
  config: DocsConfig,
  subdomain?: string
): PageMetaTags {
  const configMetadata = config.seo?.metatags || {};

  const metaTagsDict: PageMetaTags = {
    charset: 'utf-8',
    'og:type': 'website',
    'og:site_name': config.name,
    'twitter:card': 'summary_large_image',
    'og:title': defaultTitle(pageMeta, config.name),
    'twitter:title': defaultTitle(pageMeta, config.name),
  } as { [key: string]: string };

  if (pageMeta.description) {
    // Default value, overriden in the loop below if og:description is set
    metaTagsDict['og:description'] = pageMeta.description;
  }

  SEO_META_TAGS.forEach((tagName) => {
    const metaValue = pageMeta[tagName] ?? configMetadata[tagName];
    if (metaValue) {
      metaTagsDict[tagName] = metaValue;
    }

    // Add special case for og:image and twitter:image to handle relative URLs
    if (
      (tagName === 'og:image' || tagName === 'twitter:image') &&
      subdomain &&
      typeof metaValue === 'string'
    ) {
      metaTagsDict[tagName] = appendS3IfRelativePath(subdomain, metaValue);
    }
  });

  return metaTagsDict;
}

export function getTitle(pageMeta: PageMetaTags): string {
  return pageMeta.title || pageMeta.sidebarTitle || slugToTitle(pageMeta.href || '');
}

export function getSidebarTitle(pageMeta: PageMetaTags): string {
  return pageMeta.sidebarTitle || pageMeta.title || slugToTitle(pageMeta.href || '');
}

function defaultTitle(pageMeta: PageMetaTags, siteName: string) {
  const title = getTitle(pageMeta);
  if (title && siteName) {
    return title + ' - ' + siteName;
  } else if (siteName) {
    return siteName;
  }
  return title;
}
