'use client';

import { MdxExtracts } from '@mintlify/common';
import { SerializeSuccess } from '@mintlify/mdx';
import { InkeepType, MintConfig, PageMetaTags } from '@mintlify/models';
import {
  DeploymentEntitlements,
  GitSource,
  DeploymentFeedbackType,
  DocsConfig,
  DecoratedNavigationConfig,
  StaticPropsDeploymentAuthType,
  UserAuth,
  UserInfo,
  TrieveType,
} from '@mintlify/validation';
import { createContext } from 'react';

import { PageType } from '@/types/page';
import { ApiReferenceData } from '@/utils/api-reference/getApiReferenceData';
import { LegacyThemeSettings } from '@/utils/getLegacyThemeSettings';

export type PageContextType = {
  pageMetadata: PageMetaTags;
  pageType?: PageType;
  mdxExtracts?: MdxExtracts;
  description?: SerializeSuccess | string | null;
  panelMdxSource?: SerializeSuccess;
  panelMdxSourceWithNoJs?: SerializeSuccess;
};

export type DeploymentMetadataContextType = {
  subdomain?: string;
  actualSubdomain?: string;
  gitSource?: GitSource;
  inkeep?: InkeepType;
  trieve?: TrieveType;
  feedback?: DeploymentFeedbackType;
  entitlements?: DeploymentEntitlements;
  buildId?: string;
  clientVersion?: string;
};

export type DocsConfigContextType = {
  mintConfig?: MintConfig;
  docsConfig?: DocsConfig;
  docsNavWithMetadata?: DecoratedNavigationConfig;
  legacyThemeSettings?: LegacyThemeSettings;
};

export type AuthContextType = {
  userAuth?: UserAuth;
  userInfo?: UserInfo;
  auth?: StaticPropsDeploymentAuthType;
  isFetchingUserInfo?: boolean;
};

export type ApiReferenceContextType = {
  apiReferenceData: ApiReferenceData;
};

export const PageContext = createContext<PageContextType>({
  pageMetadata: {},
});

export const DeploymentMetadataContext = createContext<DeploymentMetadataContextType>({});

export const DocsConfigContext = createContext<DocsConfigContextType>({});

export const AuthContext = createContext<AuthContextType>({});

export const ApiReferenceContext = createContext<ApiReferenceContextType>({
  apiReferenceData: {},
});

PageContext.displayName = 'PageContext';
DeploymentMetadataContext.displayName = 'DeploymentMetadataContext';
DocsConfigContext.displayName = 'DocsConfigContext';
AuthContext.displayName = 'AuthContext';
ApiReferenceContext.displayName = 'ApiReferenceContext';
