'use client';

import { ApiPlayground } from '@/api-playground/ApiPlayground';
import { AsyncApiPlayground } from '@/api-playground/ApiPlayground/AsyncApi';
import ApiExamples from '@/components/ApiExamples/ApiExamples';
import {
  ApiPlaygroundInputsContext,
  initialApiPlaygroundInputs,
  useApiPlaygroundInputsReducer,
} from '@/contexts/ApiPlaygroundInputsContext';
import { useApiPlaygroundDisplay } from '@/hooks/useApiPlaygroundDisplay';
import { useChannel } from '@/hooks/useChannel';
import { useEndpoint } from '@/hooks/useEndpoint';
import { useMdxCodeExampleData } from '@/hooks/useMdxCodeExampleData';
import { cn } from '@/utils/cn';

export const Api = () => {
  const apiPlaygroundDisplay = useApiPlaygroundDisplay();
  const mdxRequestExampleData = useMdxCodeExampleData('request');
  const mdxResponseExampleData = useMdxCodeExampleData('response');
  const endpoint = useEndpoint();
  const channel = useChannel();

  const showApiPlayground = apiPlaygroundDisplay !== 'none' && endpoint;
  const ctx = useApiPlaygroundInputsReducer(initialApiPlaygroundInputs);

  if (channel) {
    return <AsyncApiPlayground channel={channel} />;
  }

  return (
    <ApiPlaygroundInputsContext.Provider value={ctx}>
      <div className="flex flex-col gap-8">
        {showApiPlayground && <ApiPlayground endpoint={endpoint} display={apiPlaygroundDisplay} />}
        {(endpoint || mdxRequestExampleData || mdxResponseExampleData) && (
          <div className={cn('mt-8 lg:mt-0 flex flex-col gap-6 xl:hidden')}>
            <ApiExamples />
          </div>
        )}
      </div>
    </ApiPlaygroundInputsContext.Provider>
  );
};
