'use client';

import * as RadixTooltip from '@radix-ui/react-tooltip';
import { ReactNode, isValidElement } from 'react';

import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

export type TooltipProps = {
  tip: string;
  children: ReactNode;
};

export function Tooltip({ tip, children }: TooltipProps) {
  if (!children) {
    return null;
  }

  return (
    <RadixTooltip.Provider delayDuration={0}>
      <RadixTooltip.Root>
        <RadixTooltip.Trigger>{underlineWhenTextOnly(children)}</RadixTooltip.Trigger>
        <RadixTooltip.Portal>
          <RadixTooltip.Content
            collisionPadding={8}
            className="z-50 bg-tooltip max-w-[16rem] text-center text-gray-50 text-xs px-1.5 py-1 rounded-lg border border-gray-50 dark:border-gray-500"
            data-component-part="tooltip-content"
          >
            {tip}
          </RadixTooltip.Content>
        </RadixTooltip.Portal>
      </RadixTooltip.Root>
    </RadixTooltip.Provider>
  );
}

function underlineWhenTextOnly(children: ReactNode) {
  if (isValidElement(children)) {
    return children;
  }

  return (
    <span
      className={cn(
        Classes.Tooltip,
        'underline decoration-dotted decoration-2 underline-offset-4 decoration-gray-400 dark:decoration-gray-500'
      )}
    >
      {children}
    </span>
  );
}
