import { useState } from 'react';

import { Description } from '@/api-playground/EndpointFields/components/Description';
import { DeprecatedPill, InfoPill, RequiredPill } from '@/components/Param';
import { cn } from '@/utils/cn';

import { ObjectExpandButton } from './ObjectExpandButton';

type ExpandContainerProps = {
  children?: React.ReactNode;
  label?: string;
  typeLabel?: string;
  required?: boolean;
  deprecated?: boolean;
  propertiesCount: number;
  defaultShowChildren?: boolean;
  description?: string;
  className?: string;
};

export const ExpandSchemaContainer = ({
  children,
  label,
  typeLabel,
  required,
  deprecated,
  propertiesCount,
  defaultShowChildren,
  description,
  className,
}: ExpandContainerProps) => {
  const initialShowChildrenState =
    propertiesCount === 0 ? true : defaultShowChildren ?? propertiesCount <= 2;
  const [showChildren, setShowChildren] = useState(initialShowChildrenState);
  return (
    <div className={cn('expand-schema-container flex space-x-3 items-start px-3.5', className)}>
      <div
        className={cn(
          'flex flex-col flex-1 grid gap-x-12 gap-y-0 rounded-xl',
          showChildren ? 'mt-3.5' : 'my-3.5',
          !description && 'my-3.5'
        )}
      >
        <div className={cn(!showChildren && 'border-none')}>
          <div className="flex items-center justify-between font-mono font-bold">
            <div className="flex flex-wrap items-center gap-2 text-xs truncate" title={label}>
              <div className="text-sm truncate">
                <span className="font-semibold text-gray-900 dark:text-gray-100">{label}</span>
              </div>
              {typeLabel && <InfoPill prefix="type:">{typeLabel}</InfoPill>}
              {required && <RequiredPill />}
              {deprecated && <DeprecatedPill />}
            </div>
            <ObjectExpandButton
              onClick={() => setShowChildren(!showChildren)}
              count={propertiesCount}
              labeled={true}
            />
          </div>
        </div>
        {description && (
          <div className="py-2">
            <Description markdown={description} className="text-gray-500 dark:text-gray-400" />
          </div>
        )}
        {showChildren && (
          <div className="grid grid-cols-1 w-full items-start divide-y divide-gray-100 dark:divide-white/5">
            {children}
          </div>
        )}
      </div>
    </div>
  );
};
