'use client';

import * as React from 'react';
import { useEffect } from 'react';

import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { Citation } from '@/types/chat';
import { createTopic, generateMessage } from '@/utils/chat/trieve';
import { cn } from '@/utils/cn';
import { generateDeeplink } from '@/utils/search/trieve';

import { ChatMessage } from './ChatMessage';
import { ChatResponse } from './ChatResponse';

const getFormattedCitations = (rawContent?: string): Citation[] => {
  try {
    const citations: {
      id: string;
      link: string;
      chunk_html: string;
      metadata: Record<string, string>;
    }[] = JSON.parse(rawContent ?? '[]');

    const uniqueCitations = new Map(
      citations.map((citation, index) => {
        const title = citation.metadata.title ?? '';
        const formattedCitation = {
          citationNumber: index,
          title: citation.metadata.title ?? '',
          url: generateDeeplink(citation),
        };

        return [title, formattedCitation];
      })
    );

    return [...uniqueCitations.values()];
  } catch {
    // If JSON parsing fails for whatever reason, not showing citations is preferable
    // to crashing the chat experience.
    return [];
  }
};

interface ChatItemProps extends React.HTMLAttributes<HTMLDivElement> {
  message: string;
  response: string;
  isLoading: boolean;
  isGeneratingError: boolean;
  isRateLimited: boolean;
  sessionId: string;
  subdomain: string;
  version?: string;
  isAuthenticated?: boolean;
  userGroups?: string[];
  setSessionId: (sessionId: string) => void;
  onGeneratingRetry: () => void;
  onCitationClick: () => void;
  setResponse: (newValue: string) => void;
  setIsLoading: (isLoading: boolean) => void;
  setIsGeneratingError: (isGeneratingError: boolean) => void;
  setIsRateLimited: (isRateLimited: boolean) => void;
}

export const ChatItem = React.forwardRef<HTMLDivElement, ChatItemProps>(
  (
    {
      message,
      response,
      isLoading,
      isGeneratingError,
      isRateLimited,
      sessionId,
      subdomain,
      version,
      isAuthenticated,
      userGroups,
      setSessionId,
      onGeneratingRetry,
      onCitationClick,
      className,
      setResponse,
      setIsLoading,
      setIsGeneratingError,
      setIsRateLimited,
      ...props
    },
    ref
  ) => {
    const trackChatCompleted = useAnalyticsContext('chat_completed');

    useEffect(() => {
      if (!message || !!response) return;
      const generateChat = async () => {
        let topicId = sessionId;
        if (!sessionId) {
          const newTopicId = await createTopic(subdomain, isAuthenticated);
          if (newTopicId) {
            setSessionId(newTopicId);
            topicId = newTopicId;
          }
        }

        setIsLoading(true);
        try {
          const httpError = await generateMessage({
            subdomain,
            topicId,
            userMessage: message,
            version,
            isAuthenticated,
            userGroups,
            setResponse,
          });

          if (httpError) {
            if (httpError.status === 429) {
              setIsRateLimited(true);
            }
            setIsGeneratingError(true);
          }
        } catch (error) {
          setIsGeneratingError(true);
        } finally {
          setIsLoading(false);
        }
      };

      void generateChat();
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [message]);

    useEffect(() => {
      const shouldLogResponse = !isLoading && !isGeneratingError && !!response.length;
      if (shouldLogResponse) {
        const [content, unformattedCitations] = response.split('||');
        void trackChatCompleted({
          query: message,
          content: content ?? '',
          citations: getFormattedCitations(unformattedCitations),
          sessionId: sessionId,
          subdomain,
        });
      }
      // We only want to run this effect when we mark isLoading true.
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isLoading]);

    return (
      <div ref={ref} className={cn(className)} {...props}>
        <ChatMessage message={message} />
        <ChatResponse
          message={message}
          isGenerating={isLoading && !response.length}
          isGeneratingError={isGeneratingError}
          onGeneratingRetry={onGeneratingRetry}
          isRateLimited={isRateLimited}
          formattedContent={response.split('||')[0] ?? ''}
          citations={isLoading ? [] : getFormattedCitations(response.split('||')[1])}
          onCitationClick={onCitationClick}
          isLoading={isLoading}
        />
      </div>
    );
  }
);
ChatItem.displayName = 'ChatItem';
