'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { Ids } from '@/lib/local/identifiers';
import { MoonIcon, SunIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

export function SwitchThemeToggle() {
  const { docsConfig } = useContext(DocsConfigContext);

  if (docsConfig?.appearance?.strict === true) return null;

  function toggleMode() {
    const isDarkMode = document.documentElement.classList.toggle('dark');
    window.localStorage.isDarkMode = isDarkMode;
    window.dispatchEvent(new Event('storage'));
    document
      .getElementById(Ids.NavBarTransition + '-maple')
      ?.classList.remove('transition-colors', 'duration-500');
  }

  return (
    <button
      onClick={toggleMode}
      className="relative flex h-7 w-[3.25rem] items-center rounded-full border border-gray-200/70 dark:border-white/[0.07] hover:border-gray-200 dark:hover:border-white/10 p-1"
      aria-label="Toggle dark mode"
    >
      <div className="z-10 flex w-full items-center justify-between px-1">
        <SunIcon className="size-3 text-gray-600 dark:text-gray-600 fill-current" />
        <MoonIcon className="size-3 text-gray-300 dark:text-gray-300 fill-current" />
      </div>
      <div
        className={cn(
          'absolute left-1 h-5 w-5 rounded-full bg-gray-100 dark:bg-white/[0.07] transition-transform duration-200',
          'dark:translate-x-[1.40rem]'
        )}
      />
    </button>
  );
}
