import { LocaleType } from '@mintlify/models';

import { ar } from './ar';
import { de } from './de';
import { en } from './en';
import { es } from './es';
import { fr } from './fr';
import { frCa } from './fr-CA';
import { id } from './id';
import { it } from './it';
import { ja } from './ja';
import { ko } from './ko';
import { pt } from './pt';
import { ptBR } from './pt-BR';
import { ru } from './ru';
import { tr } from './tr';
import { zh } from './zh';
import { zhTraditional } from './zhTraditional';

export type LocaleKey = keyof typeof en;
export type LocaleMap = Record<LocaleKey, string>;

export const getLocale = (locale: LocaleType | undefined): LocaleMap => {
  switch (locale) {
    case 'en':
      return en;
    case 'cn':
      return zh;
    case 'zh':
      return zh;
    case 'zh-Hans':
      return zh;
    case 'zh-Hant':
      return zhTraditional;
    case 'es':
      return es;
    case 'ja':
      return ja;
    case 'jp':
      return ja;
    case 'pt':
      return pt;
    case 'fr':
      return fr;
    case 'fr-CA':
      return frCa;
    case 'pt-BR':
      return ptBR;
    case 'de':
      return de;
    case 'ko':
      return ko;
    case 'it':
      return it;
    case 'ru':
      return ru;
    case 'id':
      return id;
    case 'ar':
      return ar;
    case 'tr':
      return tr;
    default:
      return en;
  }
};

export const localeToBCP47 = (locale: LocaleType): Intl.UnicodeBCP47LocaleIdentifier => {
  switch (locale) {
    case 'en':
      return 'en-US';
    case 'es':
      return 'es-ES';
    case 'cn':
      return 'zh-CN';
    case 'zh':
      return 'zh-CN';
    case 'zh-Hans':
      return 'zh-CN';
    case 'zh-Hant':
      return 'zh-CN';
    case 'jp':
      return 'ja-JP';
    case 'ja':
      return 'ja-JP';
    case 'pt':
      return 'pt-PT';
    case 'fr':
      return 'fr-FR';
    case 'fr-CA':
      return 'fr-CA';
    case 'pt-BR':
      return 'pt-BR';
    case 'de':
      return 'de-DE';
    case 'ko':
      return 'ko-KR';
    case 'it':
      return 'it-IT';
    case 'ru':
      return 'ru-RU';
    case 'id':
      return 'id-ID';
    case 'ar':
      return 'ar-SA';
    case 'tr':
      return 'tr-TR';
    default:
      return 'en-US';
  }
};
