'use client';

import { ReactNode, useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useGetShikiHighlightedHtml } from '@/utils/shiki';

import { getNodeText } from './getNodeText';

export type CodeSnippetProps = {
  language?: string;
  /**
   * If `children` is a `string`, `Shiki` will be emitted
   */
  children?: ReactNode;
};

export function CodeSnippet({ language, children }: CodeSnippetProps) {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeBlockTheme = docsConfig?.styling?.codeblocks;
  const codeString = typeof children === 'string' ? getNodeText(children) : '';
  language = language ?? 'text';
  const html = useGetShikiHighlightedHtml({ codeString, codeBlockTheme, language });

  if (typeof children === 'string' && language) {
    if (html) {
      return (
        <pre suppressHydrationWarning>
          <code suppressHydrationWarning dangerouslySetInnerHTML={{ __html: html }} />
        </pre>
      );
    }
  }

  return (
    <pre suppressHydrationWarning>
      <code suppressHydrationWarning>{children}</code>
    </pre>
  );
}
