'use client';

import { useEndpoint } from '@/hooks/useEndpoint';
import { useMdxCodeExampleData } from '@/hooks/useMdxCodeExampleData';
import { GeneratedResponseExample } from '@/openapi/apiExampleGeneration/generators/GeneratedResponseExample';

import { MdxCodeExample } from './MdxCodeExample';

export const ResponseExample = () => {
  const mdxResponseExampleData = useMdxCodeExampleData('response');
  const endpoint = useEndpoint();

  if (mdxResponseExampleData !== undefined) {
    return <MdxCodeExample data={mdxResponseExampleData} />;
  }

  if (endpoint !== undefined) {
    return <GeneratedResponseExample endpoint={endpoint} />;
  }

  return null;
};
