'use client';

import { MoonIcon } from 'lucide-react';
import { useContext, useEffect, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { Ids } from '@/lib/local/identifiers';
import { MonitorIcon, SunIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

export const themeOptions = [
  {
    value: 'system',
    icon: MonitorIcon,
  },
  {
    value: 'light',
    icon: SunIcon,
  },
  {
    value: 'dark',
    icon: MoonIcon,
  },
];

export const ModeSwitch = () => {
  const { docsConfig } = useContext(DocsConfigContext);

  const [theme, setTheme] = useState<'system' | 'light' | 'dark'>(
    docsConfig?.appearance?.default ?? 'system'
  );

  // listen to user changes
  useEffect(() => {
    const handleStorageChange = () => {
      const storedDarkMode = localStorage.getItem('isDarkMode');
      if (!storedDarkMode) {
        setTheme('system');
      } else if (storedDarkMode === 'true') {
        setTheme('dark');
      } else if (storedDarkMode === 'false') {
        setTheme('light');
      }

      document
        .getElementById(Ids.NavBarTransition)
        ?.classList.remove('transition-colors', 'duration-500');
    };
    handleStorageChange();

    window.addEventListener('storage', handleStorageChange);
    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, [setTheme]);

  if (docsConfig?.appearance?.strict === true) return null;

  return (
    <div className="flex items-center gap-2">
      {themeOptions.map((themeOption) => {
        return (
          <button
            aria-label={`Switch to ${themeOption.value} theme`}
            data-testid={`mode-switch-${themeOption.value}`}
            key={themeOption.value}
            className={cn(
              'p-1.5 rounded-lg',
              themeOption.value === theme
                ? 'bg-gray-200 dark:bg-gray-800 text-gray-600 dark:text-gray-400'
                : 'text-gray-400 dark:text-gray-600 hover:text-gray-600 dark:hover:text-gray-400'
            )}
            onClick={() => {
              if (themeOption.value === theme) return;
              if (themeOption.value === 'system') {
                localStorage.removeItem('isDarkMode');
                window.dispatchEvent(new Event('storage'));
                const prefersDarkMode = window.matchMedia('(prefers-color-scheme: dark)').matches;
                if (prefersDarkMode) document.documentElement.classList.add('dark');
                else document.documentElement.classList.remove('dark');
                window.dispatchEvent(new Event('storage'));
              } else if (themeOption.value === 'dark') {
                document.documentElement.classList.add('dark');
                window.localStorage.isDarkMode = 'true';
                window.dispatchEvent(new Event('storage'));
              } else {
                document.documentElement.classList.remove('dark');
                window.localStorage.isDarkMode = 'false';
                window.dispatchEvent(new Event('storage'));
              }
            }}
          >
            <themeOption.icon className="size-4" />
          </button>
        );
      })}
    </div>
  );
};
