import {
  ArraySchema,
  generateNextIncrementalSchema,
  IncrementalArraySchema,
} from '@mintlify/validation';

import { LocationType } from '@/types/api';
import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';

export function generateNextItems(
  schema: ArraySchema | IncrementalArraySchema,
  schemas: ComponentSchemas | undefined,
  location: LocationType
) {
  if (Array.isArray(schema.items)) return schema.items;
  return generateNextIncrementalSchema(schema.items, schemas, undefined, location);
}
