'use client';

import { IconType } from '@mintlify/models';
import { useState } from 'react';

import { DynamicLink } from '@/components/DynamicLink';
import { Classes } from '@/lib/local/selectors';
import { ComponentIcon } from '@/ui/Icon';
import { cn } from '@/utils/cn';

export type AnchorProps = {
  name?: string;
  href?: string;
  icon?: string;
  iconType?: IconType;
  isActive: boolean;
  color?: string;
};

export const Anchor = ({
  name,
  href,
  icon = 'book-open',
  iconType = 'duotone',
  isActive,
  color,
}: AnchorProps) => {
  const [hovering, setHovering] = useState(false);

  return (
    <DynamicLink
      href={href}
      onMouseEnter={() => setHovering(true)}
      onMouseLeave={() => setHovering(false)}
      className={cn(
        Classes.Anchor,
        'pl-4 group flex items-center lg:text-sm lg:leading-6 mb-5 sm:mb-4',
        isActive
          ? 'font-semibold text-primary dark:text-primary-light'
          : 'font-medium text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
      )}
    >
      <div
        style={
          (isActive || hovering) && color
            ? {
                background: color,
              }
            : {}
        }
        className={cn(
          'mr-4 rounded-md p-1',
          // We can't use `bg-primary-light` for the anchors/icons because `bg-primary-light` can sometimes be too light for certains customers. Use `bg-primary` instead.
          !color && 'group-hover:bg-primary',
          isActive
            ? [color ? '' : 'bg-primary']
            : 'shadow-sm text-gray-400 dark:text-white/50 dark:bg-background-dark dark:brightness-[1.35] dark:ring-1 dark:hover:brightness-150 group-hover:brightness-100 group-hover:ring-0 ring-1 ring-gray-950/5 dark:ring-gray-700/40'
        )}
      >
        <ComponentIcon
          icon={icon}
          iconType={iconType}
          className={cn(
            `h-4 w-4 secondary-opacity group-hover:fill-primary-dark group-hover:bg-white`,
            isActive ? 'bg-white' : 'bg-gray-400 dark:bg-gray-500'
          )}
          overrideColor={true}
        />
      </div>
      {name ?? href}
    </DynamicLink>
  );
};

export const AnchorWithoutContainer = ({
  name,
  href,
  icon = 'book-open',
  iconType = 'solid',
  isActive,
  color,
}: AnchorProps) => {
  const useGradientColor = color?.includes('linear-gradient');

  return (
    <DynamicLink
      href={href}
      style={isActive && useGradientColor ? { color: color } : {}}
      className={cn(
        Classes.Anchor,
        'pl-4 group flex items-center lg:text-sm lg:leading-6 mb-3 gap-3.5',
        isActive
          ? ['font-semibold', !useGradientColor ? 'text-primary dark:text-primary-light' : '']
          : 'text-gray-700 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
      )}
    >
      <ComponentIcon
        icon={icon.toLowerCase()}
        iconType={iconType}
        className={cn(
          `h-4 w-4 secondary-opacity group-hover:fill-primary-dark group-hover:bg-gray-900 dark:group-hover:bg-gray-300`,
          isActive
            ? 'bg-primary group-hover:bg-primary dark:bg-primary-light dark:group-hover:bg-primary-light'
            : 'bg-gray-400 dark:bg-gray-500'
        )}
        overrideColor={true}
      />
      <span>{name ?? href}</span>
    </DynamicLink>
  );
};
