import {
  DataSchemaArray,
  IncrementalDataSchemaArray,
  ObjectSchema,
  IncrementalObjectSchema,
  generateNextIncrementalSchema,
} from '@mintlify/validation';
import { OpenAPIV3_1 } from 'openapi-types';

import { LocationType } from '@/types/api';
import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';

export function generateNextProperties(
  schema: ObjectSchema | IncrementalObjectSchema,
  schemas: ComponentSchemas | undefined,
  location: LocationType
) {
  const newProperties: [string, DataSchemaArray | IncrementalDataSchemaArray][] = Object.entries<
    DataSchemaArray | OpenAPIV3_1.SchemaObject
  >(schema.properties)
    .sort(([a], [b]) => {
      const isRequiredA = schema.requiredProperties?.includes(a) ?? false;
      const isRequiredB = schema.requiredProperties?.includes(b) ?? false;
      if (isRequiredA === isRequiredB) {
        return 0;
      }
      return isRequiredA ? -1 : 1;
    })
    .map(([name, property]) => {
      if (Array.isArray(property)) return [name, property];

      return [
        name,
        generateNextIncrementalSchema(
          property,
          schemas,
          schema.requiredProperties?.includes(name),
          location
        ),
      ];
    });

  return Object.fromEntries(newProperties);
}
