import { useContext } from 'react';

import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { useColors } from '@/hooks/useColors';
import { Ids } from '@/lib/local/identifiers';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

import { SideNavGroups } from '../../ui/SideNavGroups';
import { SwitchThemeToggle } from '../../ui/SwitchThemeToggle';
import { Dropdowns } from '../mint/MainContentLayout/Dropdowns';
import { AnchorWithoutContainer } from '../mint/shared/Anchor';
import { LocalizationSelect } from '../mint/shared/LocalizationSelect';

export function PalmNav({
  isSidebarOpen,
  setIsSidebarOpen,
}: {
  isSidebarOpen: boolean;
  setIsSidebarOpen: (isSidebarOpen: boolean) => void;
}) {
  const { docsConfig } = useContext(DocsConfigContext);
  const { banner } = useContext(BannerContext);
  const { divisions } = useContext(NavigationContext);

  const hasTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;

  const getSidebarTop = () => {
    const bannerHeight = hasBanner ? 2.5 : 0;
    const topTabsHeight = hasTabs ? 3 : 0;
    const topNavHeight = 4;

    return bannerHeight + topTabsHeight + topNavHeight;
  };

  return (
    <div
      id={Ids.SidebarContent}
      className={cn(
        'hidden sticky lg:flex flex-col left-0 top-[7rem] bottom-0 right-auto border-r border-gray-200/70 dark:border-white/[0.07] transition-transform duration-100',
        isSidebarOpen ? 'w-[19rem]' : 'w-[4rem]'
      )}
      style={{
        top: `${getSidebarTop()}rem`,
        height: `calc(100vh - ${getSidebarTop()}rem)`,
      }}
    >
      <div className="flex-1 px-7 py-6 overflow-y-auto" id={Ids.NavigationItems}>
        <div className={cn('text-sm relative', !isSidebarOpen && 'hidden')}>
          <Dropdowns />
          <Anchors />
          <SideNavGroups
            nav={divisions.groupsOrPages}
            mobile={true}
            shouldLimitMarginWithAnchors={true}
            classNames={{
              groupLabel: 'font-medium',
            }}
            navItemProps={{
              sidebarItemStyle: 'undecorated',
              arrowPosition: 'leading',
            }}
          />
        </div>
      </div>
      <div
        className={cn(
          'w-full flex items-center px-7 py-4 border-t border-gray-200/70 dark:border-white/[0.07]',
          (!isSidebarOpen || docsConfig?.appearance?.strict === true) && 'hidden'
        )}
      >
        <div className="flex-1">
          <LocalizationSelect triggerClassName="border-none px-0 font-medium" />
        </div>
        <SwitchThemeToggle />
      </div>
      <button
        className={cn(
          'absolute top-5 right-5 p-1.5 rounded-md hover:bg-neutral-950/5 dark:hover:bg-white/5 cursor-pointer',
          divisions.dropdowns.length > 0 && isSidebarOpen && 'hidden'
        )}
        onClick={() => setIsSidebarOpen(!isSidebarOpen)}
      >
        {isSidebarOpen ? (
          <Icon icon="arrow-left-from-line" className="h-3.5 w-3.5 bg-gray-700 dark:bg-gray-300" />
        ) : (
          <Icon icon="arrow-right-to-line" className="h-3.5 w-3.5 bg-gray-700 dark:bg-gray-300" />
        )}
      </button>
    </div>
  );
}

export function Anchors() {
  const { divisions } = useContext(NavigationContext);
  const { anchorDefault } = useColors();

  return (
    <div>
      {divisions.anchors.map((anchor) => {
        return (
          <li key={anchor.name} className="list-none">
            <AnchorWithoutContainer
              key={anchor.name}
              href={anchor.href}
              name={anchor.name}
              icon={typeof anchor.icon === 'string' ? anchor.icon : anchor.icon?.name}
              iconType={typeof anchor.icon === 'string' ? 'solid' : anchor.icon?.style}
              color={anchor.color?.light ?? anchorDefault}
              isActive={anchor.isActive}
            />
          </li>
        );
      })}
    </div>
  );
}
