'use client';

import { useContext, useMemo } from 'react';

import { NavigationContext } from '@/contexts/NavigationContext';
import { flattenDecoratedNavigationConfig } from '@/types/metadata';
import { isEqualIgnoringLeadingSlash } from '@/utils/paths/isEqualIgnoringLeadingSlash';

import { useCurrentPath } from './useCurrentPath';

export function usePrevNext() {
  const currentPath = useCurrentPath();
  const { divisions } = useContext(NavigationContext);

  // Return values, if the object is used as a dependency in other hooks, useMemo prevents infinite renders.
  return useMemo(() => {
    const pages = flattenDecoratedNavigationConfig(divisions.groupsOrPages);
    const pageIndex = pages.findIndex((page) => {
      return isEqualIgnoringLeadingSlash(page.href, currentPath);
    });

    return {
      prev: pageIndex > -1 ? pages[pageIndex - 1] : undefined,
      next: pageIndex > -1 ? pages[pageIndex + 1] : undefined,
    };
  }, [currentPath, divisions.groupsOrPages]);
}
