'use client';

import { SecurityOption } from '@mintlify/validation';
import { useState, useMemo } from 'react';

import { useHasOpenApiEndpoint } from './useEndpoint';
import { useSecurityOptionsWithConfig } from './useSecurityOptionsWithConfig';

export function useSecurityOption(baseOptions: SecurityOption[]) {
  const [selectedIndex, _setSelectedIndex] = useState(0);
  const isOpenApi = useHasOpenApiEndpoint();

  // TODO: deprecate openapi security options in mint.json
  const optionsWithConfig = useSecurityOptionsWithConfig(baseOptions);

  // if this is an MDX endpoint, the config has already been factored in
  const options = isOpenApi ? optionsWithConfig : baseOptions;

  const nonemptyOptions = useMemo(() => {
    return options.filter((option) =>
      Object.values(option.parameters).some((section) => Object.keys(section).length > 0)
    );
  }, [options]);

  const selectedSecurity = nonemptyOptions[selectedIndex];
  return selectedSecurity;
}
