import { isAbsoluteUrl } from '@mintlify/common';
import path from 'path';

export const appendS3IfRelativePath = (subdomain: string, resourcePath: string) => {
  if (!isAbsoluteUrl(resourcePath)) {
    return getS3URI(subdomain, resourcePath);
  }
  return resourcePath;
};

export const getS3URI = (subdomain: string, resourcePath: string) => {
  const subpath = path.join(subdomain, resourcePath);
  const baseUrl = new URL('https://mintlify.s3-us-west-1.amazonaws.com');
  return new URL(subpath, baseUrl).toString();
};
