import { isPage } from '@mintlify/common';
import { DecoratedNavigationConfig, divisions } from '@mintlify/validation';

import { isEqualIgnoringLeadingSlash } from '../paths/isEqualIgnoringLeadingSlash';

/**
 * Check if the page is common in multiple divisions
 * @param entry - The navigation config entry
 * @param path - The path to check
 * @returns True if the page is common in multiple divisions, false otherwise
 */
export function isCommonPage(entry: DecoratedNavigationConfig, path: string) {
  const occurrences = { value: 0 };
  isCommonPageRecursive(entry, path, occurrences);
  return occurrences.value > 1;
}

function isCommonPageRecursive(
  entry: DecoratedNavigationConfig,
  path: string,
  occurrences: { value: number }
) {
  if (typeof entry !== 'object') return;
  if (isPage(entry)) {
    if (isEqualIgnoringLeadingSlash(entry.href, path)) {
      occurrences.value++;
    }
  }

  for (const key of [...divisions, 'groups', 'pages']) {
    if (key in entry) {
      const subDivisions = entry[key as keyof typeof entry] as DecoratedNavigationConfig[];
      if (Array.isArray(subDivisions)) {
        for (const subDivision of subDivisions) {
          isCommonPageRecursive(subDivision, path, occurrences);
        }
      }
    }
  }
}
