import { WebSocketMessage } from '@/hooks/useWebsocketConnection';
import Icon from '@/ui/Icon';
import { EyeIcon } from '@/ui/icons';

import { ConnectionPill } from '../components/ConnectionPill';
import { ExpandableMessages } from '../components/ExpandableMessages';

type MessageResultsProps = {
  name: string;
  className?: string;
  messages?: WebSocketMessage[];
  isConnected: boolean;
  clearResult: () => void;
};

const ClearButton = ({ ariaLabel, onClick }: { ariaLabel: string; onClick: () => void }) => {
  return (
    <button
      aria-label={ariaLabel}
      className="flex items-center justify-center h-6 w-6 rounded-lg mouse-pointer hover:opacity-80 gap-1.5 border border-standard"
      onClick={onClick}
    >
      <Icon
        icon="ban"
        iconType="solid"
        className="w-2.5 h-2.5 bg-gray-400 dark:bg-gray-500 rotate-90"
      />
    </button>
  );
};

export const MessageResults = ({
  name,
  className,
  messages,
  isConnected,
  clearResult,
}: MessageResultsProps) => {
  if (!messages) return null;

  const messageResults = messages.map((message, index) => {
    const id = message.timestamp ? `${message.timestamp}` : `message-${index}`;
    const data = JSON.stringify(message.data, null, 2);
    const type = message.type;
    return {
      id,
      type,
      title: data,
      content: data,
    };
  });

  return (
    <ExpandableMessages
      name={name}
      messages={messageResults}
      className={className}
      icon={<EyeIcon />}
      rightElement={
        <div className="flex items-center gap-x-2">
          <ConnectionPill isConnected={isConnected} />
          {messageResults.length > 0 && (
            <ClearButton ariaLabel="Clear messages" onClick={clearResult} />
          )}
        </div>
      }
    />
  );
};
