import { AsyncApiMessage, AsyncApiOperation, AnyRecord } from '@mintlify/common';
import { ObjectSchema } from '@mintlify/validation';
import { useState } from 'react';

import { useKeyboardShortcut } from '@/hooks/useKeyboardShortcut';

import { RecursiveInput } from '../Request/inputs/RecursiveInput';
import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';
import { SendPill } from '../components/SendPill';

type SendMessageProps = {
  sendOperations: AsyncApiOperation[];
  sendMessage: (data: AnyRecord) => void;
  isConnected: boolean;
};

type MessageFieldsFormProps = {
  message: AsyncApiMessage;
  sendMessage: (data: AnyRecord) => void;
  isConnected: boolean;
};

export const MessageFieldsForm = ({
  message,
  sendMessage,
  isConnected,
}: MessageFieldsFormProps) => {
  const [messageValue, setMessageValue] = useState({});
  const formHeader = `${message.title}`;
  useKeyboardShortcut('Enter', () => sendMessage({ ...messageValue }), true);

  if (!message.jsonPayloadSchema || !message.jsonPayloadSchema.properties) return null;

  const jsonSchemaWithLabels = {
    ...message.jsonPayloadSchema,
    requiredProperties: message.jsonPayloadSchema.required,
  };

  return (
    <div className="flex flex-col gap-y-2 items-end">
      <ExpandableSectionContainer
        key={message.title}
        name={formHeader}
        className="w-full"
        defaultExpanded={false}
      >
        <RecursiveInput
          key={message.id}
          schemaArray={[jsonSchemaWithLabels as ObjectSchema]}
          value={messageValue}
          setValue={(value) => setMessageValue(value as AnyRecord)}
          clearValue={() => setMessageValue({})}
          defaultExpanded={false}
        />
        <div className="flex items-center justify-end gap-x-2 p-3">
          <SendPill
            label="Send message"
            method="websocket"
            onClick={() => isConnected && sendMessage({ ...messageValue })}
            className="bg-[#000000] dark:bg-[#52525c]"
            disabled={!isConnected}
          />
        </div>
      </ExpandableSectionContainer>
    </div>
  );
};

export const SendMessage = ({ sendOperations, sendMessage, isConnected }: SendMessageProps) => {
  const sendOperationMessages = sendOperations.flatMap(({ messages }) => messages);
  return (
    <div className="space-y-2 ">
      {sendOperationMessages.map((message) => {
        return (
          <MessageFieldsForm
            key={message.id}
            message={message}
            sendMessage={sendMessage}
            isConnected={isConnected}
          />
        );
      })}
    </div>
  );
};
