'use client';

import Cookies from 'js-cookie';
import { useCallback, useContext, useEffect, useState } from 'react';

import { MINUTE_IN_MILLISECONDS, SEARCH_KEY_VALIDITY_TIME_IN_MS } from '@/constants';
import { AuthContext, DeploymentMetadataContext } from '@/contexts/ConfigContext';
import { NEXT_PUBLIC } from '@/env';
import { fetchResults } from '@/utils/search/trieve';

import { useUserGroups } from './useUserGroups';

const SEARCH_API_KEY_COOKIE = 'search_api_key';
const PUBLIC_AUTH_SEARCH_API_KEY_COOKIE = 'public_search_api_key';

type SearchApiKeyData = {
  key: string;
  expiresAt: number;
};

const getSearchApiKeyData = (useAuthenticatedKey: boolean) => {
  const cookieValue = Cookies.get(
    useAuthenticatedKey ? SEARCH_API_KEY_COOKIE : PUBLIC_AUTH_SEARCH_API_KEY_COOKIE
  );
  if (!cookieValue) {
    return { key: null, expiresAt: null };
  }

  try {
    const data: SearchApiKeyData = JSON.parse(cookieValue);
    return { key: data.key, expiresAt: data.expiresAt };
  } catch {
    return { key: null, expiresAt: null };
  }
};

const setSearchApiKeyData = (key: string, expiresAt: number, useAuthenticatedKey: boolean) => {
  const data: SearchApiKeyData = { key, expiresAt };
  Cookies.set(
    useAuthenticatedKey ? SEARCH_API_KEY_COOKIE : PUBLIC_AUTH_SEARCH_API_KEY_COOKIE,
    JSON.stringify(data),
    {
      expires: new Date(expiresAt),
      secure: true,
      sameSite: 'strict',
    }
  );
};

export function useSearch() {
  const { subdomain, trieve } = useContext(DeploymentMetadataContext);
  const { userInfo, auth } = useContext(AuthContext);
  const groups = useUserGroups();
  const [apiKey, setApiKey] = useState(() => {
    if (!NEXT_PUBLIC.AUTH_ENABLED) {
      return NEXT_PUBLIC.TRIEVE_API_KEY || '';
    }
    const { key } = getSearchApiKeyData(!!userInfo);
    return key || '';
  });

  useEffect(() => {
    if (!NEXT_PUBLIC.AUTH_ENABLED) return;

    // Re-evaluate which key to use when userInfo changes if keys are already generated.
    const { key, expiresAt } = getSearchApiKeyData(!!userInfo);
    if (key) {
      setApiKey(key);
    }

    const fetchApiKey = async () => {
      if (!NEXT_PUBLIC.AUTH_ENABLED) {
        return;
      }

      try {
        const shouldUsePublicEndpoint = !!auth?.partial && !userInfo;
        const apiKeyEndpoint = shouldUsePublicEndpoint
          ? `/_mintlify/api-public/${subdomain}/search-api-key`
          : '/_mintlify/api/search-api-key';

        const apiKeyResponse = await fetch(apiKeyEndpoint, {
          method: 'POST',
        });
        const data = await apiKeyResponse.json();

        const expiresAt = Date.now() + SEARCH_KEY_VALIDITY_TIME_IN_MS;
        setSearchApiKeyData(data.key, expiresAt, !!userInfo);
        setApiKey(data.key);
      } catch (error) {
        console.error(`Failed to fetch search API key`, error);
      }
    };

    const targetExpiry = expiresAt || Date.now() + SEARCH_KEY_VALIDITY_TIME_IN_MS;
    const timeUntilExpiry = targetExpiry - Date.now();
    let keyRefreshInterval: NodeJS.Timer;

    if (!key) {
      void fetchApiKey();
    }

    // Use setTimeout for initial refresh to avoid locking ourselves
    // into a short refresh period.
    const initialTimeout = setTimeout(
      () => {
        void fetchApiKey();

        // Set up recurring refreshes after the first one
        const refreshInterval = setInterval(
          () => void fetchApiKey(),
          SEARCH_KEY_VALIDITY_TIME_IN_MS - MINUTE_IN_MILLISECONDS
        );

        keyRefreshInterval = refreshInterval;
      },
      Math.max(0, timeUntilExpiry - MINUTE_IN_MILLISECONDS)
    );

    // Clean up the refreshes
    return () => {
      clearTimeout(initialTimeout);
      clearInterval(keyRefreshInterval);
    };
    // we don't need this effect to run except when the page loads or user info loads
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [userInfo]);

  const search = useCallback(
    async (query: string) => {
      if (!trieve?.datasetId) {
        return [];
      }

      return await fetchResults({
        datasetId: trieve.datasetId,
        query,
        groups,
        apiKey,
      });
    },
    [trieve?.datasetId, groups, apiKey]
  );

  return { search };
}
