import { X } from 'lucide-react';

import { zIndex } from '@/constants/zIndex';
import { useSelectedLocale } from '@/contexts/NavigationContext';
import { useMDXContent } from '@/hooks/useMDXContent';
import { Ids } from '@/lib/local/identifiers';
import type { ChangelogFilterType } from '@/types/mdxContentController';
import { cn } from '@/utils/cn';

const id = 'changelog-filters';

export function ChangelogFilters() {
  const [{ changelogFilters: filters }, dispatch] = useMDXContent();
  const { filters: filtersWord, clearFilters } = useSelectedLocale();

  const hasActiveFilters = filters.some((filter) => filter.active);

  const toggleFilter = (filter: ChangelogFilterType) => {
    dispatch({
      type: 'toggle_changelog_filter',
      payload: { ...filter, active: !filter.active },
    });
  };

  const clearAllFilters = () => {
    if (hasActiveFilters) {
      filters.forEach((filter) => toggleFilter({ ...filter, active: true }));
    }
  };

  if (!filters.length) return null;

  return (
    <div className={cn(zIndex.Control, 'hidden xl:flex pl-10 box-border w-[19rem]')} id={id}>
      <div
        id={Ids.ChangelogFilters}
        className="relative text-gray-600 text-sm leading-6 w-[16.5rem] space-y-4 overflow-y-auto"
      >
        <div className="w-full flex justify-between items-center">
          <span className="text-gray-700 dark:text-gray-300 font-medium">{filtersWord}</span>
          <button
            className={cn(
              'text-sm font-medium px-3 rounded-full',
              hasActiveFilters
                ? 'text-gray-600 dark:text-gray-400 hover:text-gray-800 dark:hover:text-gray-200'
                : 'hidden'
            )}
            onClick={clearAllFilters}
          >
            <span className="flex items-center">
              <X className="w-4 h-4 mr-1" />
              {clearFilters}
            </span>
          </button>
        </div>
        <div className="flex flex-wrap gap-2 overflow-y-auto" id={Ids.ChangelogFiltersContent}>
          {filters.map((filter) => (
            <FilterButton
              key={filter.tag}
              tag={filter.tag}
              count={filter.count}
              active={filter.active ?? false}
              onClick={() => toggleFilter(filter)}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

const FilterButton = ({
  tag,
  active,
  onClick,
}: {
  tag: string;
  count: number;
  active: boolean;
  onClick: () => void;
}) => {
  return (
    <button
      key={tag}
      onClick={onClick}
      className={cn(
        'px-3 py-1 text-sm rounded-full flex items-center space-x-1',
        active
          ? 'bg-primary text-white/90'
          : 'bg-gray-100 text-gray-700 hover:bg-gray-200 dark:bg-gray-900 dark:text-gray-300'
      )}
    >
      <span>{tag}</span>
    </button>
  );
};
