import { ChevronsUpDownIcon } from 'lucide-react';
import { useState, useEffect } from 'react';

type BasePathSelectorProps = {
  baseUrlOptions?: string[];
  selectedBaseUrlIndex?: number;
  setSelectedBaseUrlIndex?: (index: number) => void;
  onBaseUrlChange?: () => void;
};

export const BasePathSelector = ({
  baseUrlOptions,
  selectedBaseUrlIndex = 0,
  setSelectedBaseUrlIndex,
  onBaseUrlChange,
}: BasePathSelectorProps) => {
  const [selectWidth, setSelectWidth] = useState(0);

  const selectedOption = baseUrlOptions?.[selectedBaseUrlIndex];

  useEffect(() => {
    // Create a temporary span to measure the text width to dynamically set the width of the select
    const span = document.createElement('span');
    span.className = 'text-sm absolute font-mono invisible';
    span.textContent = selectedOption ?? '';
    document.body.appendChild(span);

    const paddingWidth = 16;

    setSelectWidth(span.offsetWidth + paddingWidth);
    document.body.removeChild(span);
  }, [selectedOption]);

  if (!baseUrlOptions || baseUrlOptions.length === 0 || !setSelectedBaseUrlIndex) return null;

  if (baseUrlOptions.length === 1) {
    return (
      <div className="min-w-max bg-transparent text-sm text-gray-600 dark:text-gray-400 outline-none font-mono cursor-pointer hover:text-gray-900 dark:hover:text-gray-300">
        {baseUrlOptions[0]}
      </div>
    );
  }

  return (
    <div className="flex items-center gap-1 relative -mr-1">
      <select
        className="bg-transparent text-sm text-gray-600 dark:text-gray-400 outline-none font-mono cursor-pointer hover:text-gray-900 dark:hover:text-gray-300"
        value={selectedBaseUrlIndex}
        onClick={(e) => e.stopPropagation()}
        onChange={(e) => {
          setSelectedBaseUrlIndex(Number(e.target.value));
          onBaseUrlChange?.();
        }}
        style={{ width: `${selectWidth}px` }}
      >
        {baseUrlOptions.map((url, i) => (
          <option key={url} value={i}>
            {url}
          </option>
        ))}
      </select>
      <ChevronsUpDownIcon
        className="absolute right-0 w-3 h-3 text-gray-400 dark:text-white/30 pointer-events-none"
        strokeWidth={2.5}
      />
    </div>
  );
};
