import { useState } from 'react';

import { useIntersectionObserver } from '@/hooks/useIntersectionObserver';
import { useOptionInformation } from '@/hooks/useOptionInformation';

import { ArrayParamField } from './ArrayParamField';
import { ObjectParamField } from './ObjectParamField';
import { PrimitiveParamField } from './PrimitiveParamField';
import { buildRecursiveParamFieldId, RecursiveParamFieldParams } from './RecursiveParamField';

// When we don't have to scroll to an anchor, renders all Primitive Fields, and only renders Array and Object fields and their children when in view
export const RecursiveParamFieldWithObserver = ({
  fieldType,
  schemaArray,
  name,
  location,
  parentName,
  depth = 0,
  hideDescription = false,
  renderedRefs = new Set(),
  circularRefs,
  paramId,
  style,
  explode,
}: RecursiveParamFieldParams) => {
  const { ref, isVisible } = useIntersectionObserver();
  const { originalIndices, typeOptionLabels, nullable, allObjects } =
    useOptionInformation(schemaArray);

  const [selectedIndex, setSelectedIndex] = useState(0);
  const originalIndex = originalIndices[selectedIndex] ?? 0;

  const selectedSchema = schemaArray[originalIndex];

  const shouldRender = isVisible;

  if (!selectedSchema) return null;

  const isPrimitiveFieldType = selectedSchema.type !== 'array' && selectedSchema.type !== 'object';
  const id = paramId ?? buildRecursiveParamFieldId(fieldType, name, parentName);

  // render primitive fields regardless of intersection observer
  if (isPrimitiveFieldType) {
    return (
      <div className="border-gray-100 dark:border-gray-800 border-b last:border-b-0">
        <PrimitiveParamField
          paramId={id}
          fieldType={fieldType}
          schema={selectedSchema}
          name={name}
          location={location}
          typeOptions={typeOptionLabels}
          selectedIndex={selectedIndex}
          onSelectTypeOption={setSelectedIndex}
          parentName={parentName}
          style={style}
          explode={explode}
        />
      </div>
    );
  }

  let children;
  if (selectedSchema.type === 'array') {
    children = (
      <ArrayParamField
        paramId={id}
        fieldType={fieldType}
        schema={selectedSchema}
        name={name}
        location={location}
        typeOptions={typeOptionLabels}
        selectedIndex={selectedIndex}
        onSelectTypeOption={setSelectedIndex}
        parentName={parentName}
        depth={depth}
        renderedRefs={renderedRefs}
        circularRefs={circularRefs}
        shouldRenderChildren={shouldRender}
      />
    );
  }
  if (selectedSchema.type === 'object') {
    children = (
      <ObjectParamField
        paramId={id}
        fieldType={fieldType}
        schema={selectedSchema}
        name={name}
        nullable={nullable}
        allObjects={allObjects}
        location={location}
        typeOptions={typeOptionLabels}
        selectedIndex={selectedIndex}
        onSelectTypeOption={setSelectedIndex}
        parentName={parentName}
        depth={depth}
        hideDescription={hideDescription}
        renderedRefs={renderedRefs}
        circularRefs={circularRefs}
        shouldRenderChildren={shouldRender}
        style={style}
        explode={explode}
      />
    );
  }

  return (
    <div ref={ref} className="border-gray-100 dark:border-gray-800 border-b last:border-b-0">
      {children}
    </div>
  );
};
