import { getMethodColor } from '@/components/Api/colors';
import { MethodPillType } from '@/types/api';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

type SendPillProps = {
  label: string;
  method: MethodPillType;
  onClick?: () => void;
  isSending?: boolean;
  className?: string;
  icon?: React.ReactNode;
  disabled?: boolean;
};

export const SendPill = ({
  label,
  method,
  onClick,
  isSending,
  className,
  icon,
  disabled,
}: SendPillProps) => {
  const { activeNavPillBg, activeNavPillText } = getMethodColor(method);
  return (
    <button
      className={cn(
        'flex items-center justify-center px-3 h-9 font-medium rounded-xl mouse-pointer hover:opacity-80 gap-1.5',
        activeNavPillBg,
        activeNavPillText,
        className,
        (disabled || isSending) &&
          'opacity-50 hover:opacity-50 cursor-not-allowed pointer-events-none'
      )}
      onClick={onClick}
      disabled={isSending || disabled}
    >
      {isSending ? (
        <Icon icon="spinner-third" iconType="duotone" className="w-4 h-4 bg-white animate-spin" />
      ) : (
        <>
          <span>{label}</span>
          {icon ? icon : <Icon icon="play" iconType="solid" className="w-3 h-3 bg-white" />}
        </>
      )}
    </button>
  );
};
