import { ReactElement, useContext, useEffect } from 'react';

import { AdvancedFooter, Footer } from '@/components/Footer';
import { MdxPanel } from '@/components/MdxPanel';
import { BannerContext } from '@/contexts/BannerContext';
import { PageContext } from '@/contexts/ConfigContext';
import { MDXContentContext } from '@/contexts/MDXContentContext';
import { useMDXContentReducer } from '@/contexts/MDXContentContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { Classes } from '@/lib/local/selectors';
import { Api } from '@/ui/Content/Api';
import { Content } from '@/ui/Content/Content';
import { PageHeader } from '@/ui/Content/PageHeader';
import { SidePanel } from '@/ui/Content/SidePanel';
import { UserFeedback } from '@/ui/Feedback/UserFeedback';
import { ChatAssistantContext } from '@/ui/search';
import { ChatAssistantSheet } from '@/ui/search/ChatAssistantSheet';
import { cn } from '@/utils/cn';

import { AlmondNav } from './Nav';
import { AlmondPagination } from './Pagination';
import { AlmondTopBar } from './Topbar/AlmondTopbar';

export function AlmondLayout({ children }: { children: ReactElement }) {
  const router = useMintlifyRouter();
  const { pageMetadata } = useContext(PageContext);
  const isCustom = pageMetadata.mode === 'custom';
  const { banner } = useContext(BannerContext);
  const hasBanner = !!banner;

  return (
    <>
      {!isCustom && <AlmondTopBar />}
      <div
        className={cn(
          Classes.AlmondLayout,
          'top-[7rem] lg:top-[3.5rem] w-full',
          hasBanner && 'lg:top-[6.5rem]',
          !isCustom ? 'fixed pb-2 pr-2 pt-0' : 'absolute'
        )}
      >
        {!isCustom && <AlmondNav />}
        <MDXContentController key={router.asPath}>{children}</MDXContentController>
      </div>
    </>
  );
}

function MDXContentController({ children }: { children: ReactElement }) {
  const { pageMetadata, mdxExtracts } = useContext(PageContext);

  const initialSelectedBodyContentType = Object.keys(mdxExtracts?.endpoint?.request.body ?? {})[0];

  const ctx = useMDXContentReducer({
    pageMetadata,
    selectedBodyContentType: initialSelectedBodyContentType,
    ...mdxExtracts,
  });

  return (
    <MDXContentContext.Provider value={ctx}>
      <AlmondContentContainer>
        <>
          <PageHeader />
          <MdxPanel mobile />
          <Api />
          <Content>{children}</Content>
        </>
      </AlmondContentContainer>
    </MDXContentContext.Provider>
  );
}

function AlmondContentContainer({ children }: { children: ReactElement }) {
  const { isCustom, isWide, isCenter } = usePageMode();
  const { banner } = useContext(BannerContext);
  const { isChatSheetOpen } = useContext(ChatAssistantContext);
  const hasBanner = !!banner;

  useEffect(() => {
    window.document.documentElement.style.setProperty('--scroll-mt', `2.5rem`);
  }, []);

  if (isCustom) {
    return (
      <>
        <AlmondTopBar />
        {children}
      </>
    );
  }

  const containerClassName =
    'rounded-2xl bg-background-light dark:bg-background-dark border border-gray-200/70 dark:border-white/[0.07]';

  return (
    <div className="flex rounded-2xl h-[calc(100vh-8rem)] lg:h-[calc(100vh-4.5rem)]">
      <div
        className={cn(
          'flex-1 px-5 pt-5 lg:py-12 lg:ml-[16.5rem] lg:px-16 overflow-y-auto',
          containerClassName,
          isChatSheetOpen && 'rounded-r-none',
          isWide && 'px-14',
          hasBanner && 'h-[calc(100vh-8rem)]'
        )}
        id={Ids.ContentContainer}
      >
        <div className="lg:pl-12 flex gap-x-12 justify-center mx-auto w-full">
          <div
            className={cn(
              'w-full overflow-x-auto max-w-xl',
              isWide && 'max-w-2xl',
              isCenter && 'max-w-3xl'
            )}
            id={Ids.ContentArea}
          >
            {children}
            <div className="leading-6 mt-14">
              <UserFeedback />
              <AlmondPagination />
            </div>
            <Footer className="border-t-0" />
          </div>
          {!isWide && <SidePanel />}
        </div>

        <AdvancedFooter disableSidebarOffset />
      </div>

      <ChatAssistantSheet className={cn(containerClassName, 'rounded-l-none')} />
    </div>
  );
}
