'use client';

import { IncrementalObjectSchema, ObjectSchema } from '@mintlify/validation';
import { useCallback } from 'react';

import { Expandable } from '@/components/Expandable';
import { ParamHead } from '@/components/Param';
import { useEndpointLocation } from '@/contexts/EndpointLocationContext';
import { useApiParamFieldOption } from '@/hooks/useApiParamFieldOption';
import { useIntersectionObserver } from '@/hooks/useIntersectionObserver';
import { usePropertiesIfObject } from '@/hooks/usePropertiesIfObject';
import { FieldType, RenderedRefs } from '@/types/api';
import { cn } from '@/utils/cn';

import { Description } from '../components/Description';
import { OneOfSelection } from '../components/OneOfDropdown';
import { ExampleDescription } from './ExampleDescription';
import {
  renderNextPropertiesRecursively,
  renderTruncatedNextProperties,
  RecursiveParamField,
  getRecursiveParentName,
  getDeepObjectParentName,
} from './RecursiveParamField';

export type ObjectParamFieldParams = {
  fieldType: FieldType;
  schema: ObjectSchema | IncrementalObjectSchema;
  typeOptions: string[];
  renderedRefs: RenderedRefs;
  name?: string | null;
  nullable?: boolean;
  allObjects?: boolean;
  location?: string;
  selectedIndex?: number;
  onSelectTypeOption?: (index: number) => void;
  parentName?: string;
  /** Tracks the nesting level of object fields. Used to control lazy loading of deeply nested fields. */
  depth?: number;
  hideDescription?: boolean;
  circularRefs?: string[];
  paramId?: string;
  shouldRenderChildren?: boolean;
  style?: string;
  explode?: boolean;
};

export const ObjectParamField = ({
  fieldType,
  schema,
  name,
  nullable,
  allObjects,
  location,
  selectedIndex,
  typeOptions,
  onSelectTypeOption,
  parentName,
  depth = 0,
  hideDescription = false,
  renderedRefs,
  circularRefs,
  paramId,
  shouldRenderChildren,
  style,
  explode,
}: ObjectParamFieldParams) => {
  const { ref, isVisible } = useIntersectionObserver();
  const { anchor } = useEndpointLocation();
  const containsAnchorToExpand = Boolean(paramId && anchor?.includes(paramId));
  const renderChildren = shouldRenderChildren || isVisible || containsAnchorToExpand;
  const isOneOf = (allObjects ?? false) && typeOptions.length > 1;

  const apiParamFieldOption = useApiParamFieldOption();
  const defaultOpen = apiParamFieldOption === 'all' || containsAnchorToExpand;

  const typeLabel = allObjects
    ? `object${nullable ? ' | null' : ''}`
    : typeOptions[selectedIndex ?? 0];

  const [loading, properties, additionalProperties] = usePropertiesIfObject(schema);

  const sortedProperties = properties
    ? Object.entries(properties).sort(([propertyNameA], [propertyNameB]) => {
        if (!schema.requiredProperties?.length) return 0;
        const isRequiredA = schema.requiredProperties.includes(propertyNameA);
        const isRequiredB = schema.requiredProperties.includes(propertyNameB);
        if (isRequiredA === isRequiredB) return 0;
        return isRequiredA ? -1 : 1;
      })
    : [];

  const nextProperties = [...sortedProperties];

  const schemaRef = schema.refIdentifier ?? schema.title;
  const isCircularRef = schemaRef && circularRefs?.includes(schemaRef);

  const nextChildren = isCircularRef
    ? renderTruncatedNextProperties({
        properties: nextProperties,
        fieldType,
        depth,
      })
    : renderNextPropertiesRecursively({
        currentSchemaRef: schemaRef,
        properties: nextProperties,
        fieldType,
        parentName,
        name,
        depth,
        refs: new Set(renderedRefs),
        style,
        explode,
      });

  const children = nextChildren;

  if (typeof additionalProperties === 'object') {
    nextChildren.push(
      <RecursiveParamField
        fieldType={fieldType}
        key={'{key}'}
        schemaArray={additionalProperties}
        name={'{key}'}
        parentName={
          style === 'deepObject' && explode
            ? getDeepObjectParentName(name, parentName)
            : getRecursiveParentName(name, parentName)
        }
        depth={depth + 1}
        renderedRefs={new Set(renderedRefs)}
      />
    );
  }

  const onSelectOption = useCallback(
    (index: number) => {
      onSelectTypeOption?.(index);
    },
    [onSelectTypeOption]
  );

  // we are in a top-level object
  if (name === undefined) {
    const description =
      children.length > 0
        ? schema.description
        : schema.description ?? `The ${fieldType} is of type \`${typeLabel}\`.`;
    return (
      <>
        {description && !hideDescription && (
          <div className="pt-6 pb-4">
            <Description markdown={description} />
          </div>
        )}
        {children.length > 0 && children}
      </>
    );
  }

  const hasChildren = children.length > 0;
  const isLoadingOrCutOff = loading || !renderChildren;

  const ExpandableInfo = isLoadingOrCutOff ? (
    <Expandable />
  ) : (
    <Expandable key={paramId} defaultOpen={defaultOpen} lazy={depth >= 5} uniqueParamId={paramId}>
      {children}
    </Expandable>
  );

  return (
    <div ref={ref} className="py-6">
      <ParamHead
        id={paramId}
        name={name}
        location={location}
        required={schema.required}
        deprecated={schema.deprecated}
        typeLabel={typeLabel}
        fieldType={fieldType}
        typeOptions={allObjects ? [] : typeOptions}
        selectedTypeOptionIndex={selectedIndex}
        onSelectTypeOption={onSelectOption}
        parentName={parentName}
        style={style}
        explode={explode}
      />
      <div
        className={cn({
          'mt-4': schema.description || isOneOf || hasChildren,
        })}
      >
        {name && schema.description && <Description markdown={schema.description} />}
        <div
          className={cn({
            'mt-4 rounded-xl border border-gray-100 px-4 pb-4 pt-2 dark:border-white/10': isOneOf,
          })}
        >
          {isOneOf && (
            <OneOfSelection
              selectedIndex={selectedIndex}
              options={typeOptions}
              onSelectOption={onSelectOption}
            />
          )}
          {!name && schema.description && <Description markdown={schema.description} />}
          {hasChildren && (
            <div className={cn(isOneOf && ((!name && !schema.description) || !!name) && '-mt-4')}>
              {ExpandableInfo}
            </div>
          )}
          <ExampleDescription schema={schema} />
        </div>
      </div>
    </div>
  );
};
