'use client';

import slugify from '@sindresorhus/slugify';
import clsx from 'clsx';
import { useState, ReactElement, Children, useEffect, useRef } from 'react';

import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import Tab from './Tab';

const DEFAULT_TAB_TITLE = 'Tab Title';

const formatTabIds = (children: ReactElement[]) => {
  return children.map((child) => {
    const tabId = child.props?.title || DEFAULT_TAB_TITLE;
    return slugify(tabId);
  });
};

export default function Tabs({
  children,
  defaultTabIndex = 0,
  onClickTab,
  className,
}: {
  children: ReactElement[];
  defaultTabIndex?: number;
  onClickTab?: (tabIndex: number) => void;
  className?: string;
}) {
  const tabContentRef = useRef<HTMLDivElement>(null);
  const visibleContentRef = useRef<HTMLDivElement>(null);
  const [activeTabIndex, setActiveTabIndex] = useState(defaultTabIndex);
  const arrayChildren = Children.toArray(children) as ReactElement[];
  const activeTabContent = arrayChildren[activeTabIndex]?.props?.children;
  const tabIds = formatTabIds(arrayChildren);

  useEffect(() => {
    const hash = window.location.hash.substring(1);
    if (!hash) return;

    const tabIndex = tabIds.indexOf(hash);
    if (tabIndex !== -1) {
      setActiveTabIndex(tabIndex);
      return;
    }
    const escapedHash = CSS.escape(hash);

    if (!tabContentRef.current) return;
    const element = tabContentRef.current.querySelector(`#${escapedHash}`);
    if (!element) return;

    const children = tabContentRef.current.children;
    for (let i = 0; i < children.length; i++) {
      const child = children[i];
      if (child && child.contains(element)) {
        setActiveTabIndex(i);
        setTimeout(() => {
          const visibleElement = visibleContentRef.current?.querySelector(`#${escapedHash}`);
          if (visibleElement) {
            visibleElement.scrollIntoView();
          }
        }, 10);
        break;
      }
    }
  }, [tabIds]);

  const handleTabClick = (index: number) => {
    setActiveTabIndex(index);
    onClickTab?.(index);

    const tabId = tabIds[index];
    if (tabId) {
      const url = new URL(window.location.href);
      url.hash = tabId;
      window.history.pushState({}, '', url.toString());
    }
  };

  return (
    <div className={cn(Classes.Tabs, 'tabs tab-container')} id={tabIds[activeTabIndex]}>
      <ul
        className={clsx(
          'not-prose mb-6 pb-[1px] flex-none min-w-full overflow-auto border-b border-gray-200 gap-x-6 flex dark:border-gray-200/10',
          className
        )}
        data-component-part="tabs-list"
      >
        {arrayChildren.map((child: ReactElement, i: number) => (
          <li
            key={`${tabIds[i]}-${i}`}
            className="cursor-pointer"
            onClick={() => handleTabClick(i)}
          >
            <Tab title={child.props?.title ?? DEFAULT_TAB_TITLE} isActive={i === activeTabIndex} />
          </li>
        ))}
      </ul>
      <div
        className="prose dark:prose-dark overflow-x-auto"
        ref={visibleContentRef}
        data-component-part="tab-content"
      >
        {activeTabContent}
      </div>
      <div className="hidden" ref={tabContentRef}>
        {arrayChildren.map((child: ReactElement, i: number) => (
          <div key={`${child.key}-${i}`}>{child.props.children}</div>
        ))}
      </div>
    </div>
  );
}
