'use client';

import * as TabsPrimitive from '@radix-ui/react-tabs';
import { ArrowDown } from 'lucide-react';
import React, {
  ComponentPropsWithoutRef,
  FormEventHandler,
  ForwardedRef,
  ReactElement,
  ReactNode,
  forwardRef,
  useContext,
  useEffect,
  useCallback,
  useMemo,
  useState,
} from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { LocalStorageContext } from '@/contexts/LocalStorageContext';
import { useExpandable } from '@/hooks/useExpandable';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';
import { getCodeBlockScrollbarClassname } from '@/utils/getScrollbarClassname';
import { useGetShikiHighlightedHtml } from '@/utils/shiki';

import { CopyToClipboardResult } from '../../utils/copyToClipboard';
import { CodeBlockProps, CopyToClipboardButton } from './CodeBlock';
import { getHighlightedLines } from './getHighlightedLines';
import { getNodeText } from './getNodeText';

export type CodeGroupPropsBase = {
  onCopied?: (result: CopyToClipboardResult, textToCopy?: string) => void;
  isSmallText?: boolean;
  children?: ReactElement<CodeBlockProps>[] | ReactElement<CodeBlockProps>;
  onChange?: FormEventHandler<HTMLDivElement> & ((index: number) => void);
  noMargins?: boolean;
};

export type CodeGroupProps = CodeGroupPropsBase &
  Omit<ComponentPropsWithoutRef<'div'>, keyof CodeGroupPropsBase>;

type CodeBlockChild = Exclude<React.ReactElement<CodeBlockProps>, boolean | null | undefined>;

export const CodeGroup = forwardRef(function CodeGroup(
  { children, onCopied, onChange, isSmallText, className, noMargins, ...props }: CodeGroupProps,
  ref: ForwardedRef<HTMLDivElement> | undefined
) {
  const { docsConfig } = useContext(DocsConfigContext);
  const { preferredCodeLanguage, setPreferredCodeLanguage } = useContext(LocalStorageContext);
  const childArr = Array.isArray(children)
    ? children
    : (React.Children.toArray(children) as Array<CodeBlockChild>);

  const [selectedTab, setSelectedTab] = useState(() => {
    if (typeof window === 'undefined') return 0;
    const lang = window.localStorage.getItem('code');
    const index = childArr.findIndex((child) => child.props.filename === lang);
    return index === -1 ? 0 : index;
  });

  const childrenKey = useMemo(
    () => childArr.map((child) => child.props.filename).join(','),
    [childArr]
  );

  useEffect(() => {
    if (!preferredCodeLanguage) {
      if (selectedTab !== 0) {
        setSelectedTab(0);
      }
      return;
    }

    const newIndex = childArr.findIndex((child) => child.props.filename === preferredCodeLanguage);
    if (newIndex !== -1 && selectedTab !== newIndex) {
      setSelectedTab(newIndex);
    } else if (newIndex === -1 && selectedTab !== 0) {
      setSelectedTab(0);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [childrenKey, preferredCodeLanguage]);

  const handleValueChange = useCallback(
    (value: string) => {
      const index = Number(value);
      setSelectedTab(index);
      const filename = childArr[index]?.props.filename;
      if (filename && setPreferredCodeLanguage) {
        setPreferredCodeLanguage(filename);
      }
      onChange && onChange(index);
    },
    [childArr, setPreferredCodeLanguage, onChange]
  );

  if (!children) {
    return null;
  }

  if (childArr.length === 0) {
    console.warn('CodeGroup has no children, expected at least one CodeBlock child.');
    return null;
  }

  const codeBlockTheme = docsConfig?.styling?.codeblocks;
  const selectedIndex = Number(selectedTab);

  return (
    // @ts-expect-error defaultValue should never an issue or passed in to the CodeBlock component
    <TabsPrimitive.Root
      ref={ref}
      value={String(selectedTab)}
      onValueChange={handleValueChange}
      className={cn(
        Classes.CodeGroup,
        'mt-5 mb-8 flex flex-col not-prose relative overflow-hidden rounded-2xl border border-gray-200/70 dark:border-gray-800/50',
        noMargins && 'my-0',
        codeBlockTheme === 'system' &&
          'bg-white/60 dark:bg-codeblock dark:codeblock-dark text-gray-950 dark:text-gray-50 codeblock-light',
        (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
          'bg-[#0F1117] border border-transparent dark:bg-codeblock text-gray-50 codeblock-dark',
        className
      )}
      {...props}
      asChild={false}
    >
      <div
        className={cn(
          'relative border-b',
          codeBlockTheme === 'system' &&
            'border-gray-200/50 dark:border-gray-900/80 bg-gray-100/40 dark:bg-codeblock',
          (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
            'border-gray-900/80 bg-[#0F1117] dark:bg-codeblock'
        )}
        data-component-part="code-group-tab-bar"
      >
        <TabsPrimitive.List
          className={cn(
            'text-xs leading-6 rounded-tl-xl flex overflow-x-auto overflow-y-hidden mr-12',
            getCodeBlockScrollbarClassname(codeBlockTheme),
            className
          )}
        >
          {childArr.map((child, index) => (
            <TabItem
              key={child.props.filename + 'TabItem' + index}
              value={String(index)}
              isSelected={selectedIndex === index}
              tabsLength={childArr.length}
              codeBlockTheme={codeBlockTheme}
            >
              {child.props.filename}
            </TabItem>
          ))}
        </TabsPrimitive.List>
        <div className="flex h-full absolute top-0 pr-4 right-0 rounded-tr z-10">
          <div className="flex items-center justify-center h-[42px]">
            <CopyToClipboardButton
              textToCopy={getNodeText(childArr[selectedIndex]?.props?.children)}
              onCopied={onCopied}
            />
          </div>
        </div>
      </div>
      <div className="flex flex-1 overflow-hidden">
        {childArr.map((child, index) => {
          return (
            <TabsPrimitive.Content
              key={child.props.filename}
              value={String(index)}
              className={cn(
                'w-full min-w-full max-w-full h-full max-h-full overflow-auto',
                getCodeBlockScrollbarClassname(codeBlockTheme)
              )}
              tabIndex={-1}
            >
              <ExpandableTabPanel
                child={child}
                codeBlockTheme={codeBlockTheme}
                isSmallText={isSmallText}
                isExpandable={
                  (child.props.expandable as unknown as string) === 'true' &&
                  index === selectedIndex
                }
                shouldHighlight={index === selectedIndex}
                isVisible={index === selectedIndex}
              />
            </TabsPrimitive.Content>
          );
        })}
      </div>
    </TabsPrimitive.Root>
  );
});

const ExpandableTabPanel = function ExpandableTabPanel({
  child,
  codeBlockTheme,
  isSmallText,
  isExpandable = false,
  shouldHighlight,
  isVisible,
}: {
  child: CodeBlockChild;
  codeBlockTheme: 'dark' | 'system' | undefined;
  isSmallText: boolean | undefined;
  isExpandable: boolean | undefined;
  shouldHighlight: boolean;
  isVisible: boolean;
}) {
  const { isExpanded, calculatedHeight, contentRef, toggleExpanded } = useExpandable(isExpandable);
  const children = child.props.children;
  const className = child.props.className;
  const codeString = getNodeText(children);
  const highlightedLines = getHighlightedLines(
    typeof children === 'object' && children ? (children as JSX.Element) : ({} as JSX.Element)
  );
  const props = { ...child.props };
  delete props.children;
  const opts = highlightedLines.length ? { opts: { highlightedLines } } : undefined;

  const html = useGetShikiHighlightedHtml(
    props.language
      ? { codeString, codeBlockTheme, language: shouldHighlight ? props.language : 'text', ...opts }
      : {
          codeString,
          codeBlockTheme,
          className: shouldHighlight ? className : 'lang-text',
          ...opts,
        }
  );

  const CodeElement = () => {
    return html ? (
      <div
        suppressHydrationWarning
        ref={contentRef}
        tabIndex={-1}
        className={cn(
          'flex-none p-5 text-sm h-full [&_*]:ring-0 [&_*]:outline-none [&_*]:focus:ring-0 [&_*]:focus:outline-none',
          isExpandable && (isExpanded ? 'overflow-y-auto' : 'overflow-y-hidden'),
          isSmallText ? 'text-xs leading-[1.35rem]' : 'leading-6'
        )}
        data-component-part="code-group-tab-content"
        dangerouslySetInnerHTML={{ __html: html }}
      ></div>
    ) : (
      <div
        suppressHydrationWarning
        ref={contentRef}
        tabIndex={-1}
        className={cn(
          'flex-none p-5 text-sm h-full [&_*]:ring-0 [&_*]:outline-none [&_*]:focus:ring-0 [&_*]:focus:outline-none',
          isExpandable && (isExpanded ? 'overflow-y-auto' : 'overflow-y-hidden'),
          isSmallText ? 'text-xs leading-[1.35rem]' : 'leading-6'
        )}
        data-component-part="code-group-tab-content"
      >
        {children}
      </div>
    );
  };

  return (
    <div
      className={cn(
        'relative transition-[height] duration-300 ease-in-out',
        !isVisible && 'hidden'
      )}
      style={{
        fontVariantLigatures: 'none',
        height: isExpandable ? (!isExpanded ? '100px' : `${calculatedHeight}px`) : 'auto',
      }}
      tabIndex={-1}
    >
      <CodeElement />
      {isExpandable && (
        <div
          key={`${child.props.filename}-expandable`}
          className={cn(
            'absolute left-0 z-[10] right-0 flex justify-center items-end',
            isExpanded ? 'bottom-0 rounded-b-xl' : 'rounded-xl h-full top-0 bg-transparent',

            codeBlockTheme === 'system'
              ? 'bg-gradient-to-b from-transparent to-background-light dark:from-transparent dark:to-background-dark'
              : 'bg-gradient-to-b from-transparent to-black/80'
          )}
        >
          <button
            onClick={toggleExpanded}
            className={cn(
              'flex items-center gap-2 rounded-lg px-2 py-1.5 text-sm backdrop-blur-sm',
              codeBlockTheme === 'system'
                ? 'bg-gray-500/20 text-gray-900 dark:bg-white/20 dark:text-white'
                : 'bg-white/20 text-white',
              isExpanded ? 'mb-6' : 'mb-8'
            )}
          >
            <ArrowDown
              className={cn('size-3 transition-transform duration-400', isExpanded && 'rotate-180')}
            />
            {isExpanded ? 'Collapse code' : 'Expand code'}
          </button>
        </div>
      )}
    </div>
  );
};

const TabItem = forwardRef<
  React.ElementRef<typeof TabsPrimitive.Trigger>,
  {
    children: ReactNode;
    value: string;
    isSelected: boolean;
    tabsLength: number;
    codeBlockTheme: 'dark' | 'system' | undefined;
  }
>(function TabItem({ children, value, isSelected, tabsLength, codeBlockTheme }, ref) {
  return (
    <TabsPrimitive.Trigger
      ref={ref}
      value={value}
      className={cn(
        'group flex items-center relative px-2 pt-2.5 pb-2 outline-none whitespace-nowrap font-medium',
        isSelected && codeBlockTheme === 'system' && 'text-primary dark:text-primary-light',
        isSelected &&
          (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
          'text-primary-light',
        !isSelected && codeBlockTheme === 'system' && 'text-gray-500 dark:text-gray-400',
        !isSelected && (codeBlockTheme === 'dark' || codeBlockTheme == undefined) && 'text-gray-400'
      )}
    >
      <div
        className={cn(
          'px-2 rounded-lg z-10',
          tabsLength > 1 &&
            codeBlockTheme === 'system' &&
            'group-hover:bg-gray-200/50 dark:group-hover:bg-gray-700/70 group-hover:text-primary dark:group-hover:text-primary-light',
          tabsLength > 1 &&
            (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
            'group-hover:bg-gray-700/70 group-hover:text-primary-light'
        )}
      >
        {children}
      </div>

      {isSelected && (
        <div className="absolute inset-0 border-b pointer-events-none border-primary dark:border-primary-light" />
      )}
    </TabsPrimitive.Trigger>
  );
});
