'use client';

import { MoonIcon } from 'lucide-react';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { SunIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

export function ModeToggle({
  id,
  sunIconClassName,
  moonIconClassName,
  backgroundClassName,
}: {
  id: string;
  sunIconClassName?: string;
  moonIconClassName?: string;
  backgroundClassName?: string;
}) {
  const { docsConfig } = useContext(DocsConfigContext);

  if (docsConfig?.appearance?.strict === true) return null;

  function toggleMode() {
    const isDarkMode = document.documentElement.classList.toggle('dark');
    window.localStorage.isDarkMode = isDarkMode;
    window.dispatchEvent(new Event('storage'));
    document.getElementById(id)?.classList.remove('transition-colors', 'duration-500');
  }

  return (
    <button
      onClick={() => toggleMode()}
      className={cn('group p-2 flex items-center justify-center', backgroundClassName)}
      aria-label="Toggle dark mode"
    >
      <SunIcon
        className={cn(
          'h-4 w-4 block text-gray-400 dark:hidden group-hover:text-gray-600',
          sunIconClassName
        )}
      />
      <MoonIcon
        className={cn(
          'h-4 w-4 hidden dark:block text-gray-500 dark:group-hover:text-gray-300',
          moonIconClassName
        )}
      />
    </button>
  );
}
