import { generateNextIncrementalSchema } from '@mintlify/validation';
import { IncrementalObjectSchema } from '@mintlify/validation';
import { ObjectSchema } from '@mintlify/validation';

import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';

export function generateNextAdditionalProperties(
  schema: ObjectSchema | IncrementalObjectSchema,
  schemas: ComponentSchemas | undefined,
  location: 'request' | 'response' | undefined
) {
  if (
    typeof schema.additionalProperties !== 'object' ||
    Array.isArray(schema.additionalProperties)
  ) {
    return schema.additionalProperties;
  }

  return generateNextIncrementalSchema(schema.additionalProperties, schemas, undefined, location);
}
