'use client';

import clsx from 'clsx';
import { useContext } from 'react';

import { DeploymentMetadataContext, DocsConfigContext } from '@/contexts/ConfigContext';
import { LocalStorageContext } from '@/contexts/LocalStorageContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { usePageMode } from '@/hooks/usePageMode';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';
import { createGithubEditUrl, createGithubIssueUrl } from '@/utils/feedback';

import IconButton, { FeedbackButtonIconType } from './IconButton';
import { ThumbsVoting } from './ThumbsVoting';

export function UserFeedback() {
  const { gitSource, feedback: feedbackConfig } = useContext(DeploymentMetadataContext);
  const { docsConfig } = useContext(DocsConfigContext);
  const { cookiesEnabled } = useContext(LocalStorageContext);
  const { isCustom } = usePageMode();
  const telemetryDisabled = docsConfig?.integrations?.telemetry?.enabled === false;
  const path = useCurrentPath();
  let createSuggestHref: string | undefined = undefined;
  let createIssueHref: string | undefined = undefined;

  if (path && gitSource?.type === 'github' && !gitSource.isPrivate) {
    createSuggestHref = createGithubEditUrl(path, gitSource);
    createIssueHref = createGithubIssueUrl(path, gitSource);
  }

  const hasAnyFeedbackEnabled =
    (feedbackConfig?.edits && createSuggestHref) ||
    (feedbackConfig?.issues && createIssueHref) ||
    feedbackConfig?.thumbs;
  if (!hasAnyFeedbackEnabled || !cookiesEnabled || telemetryDisabled || isCustom) {
    return null;
  }

  return (
    <div className={cn(Classes.FeedbackToolbar, 'pb-16 w-full')}>
      {/* Main section */}
      <div
        className={clsx(
          'flex flex-col gap-y-4',
          'xl:flex-col xl:gap-6',
          'min-[1400px]:flex-row',
          feedbackConfig.thumbs && 'md:flex-row md:justify-end'
        )}
      >
        {/* Thumb voting */}
        {feedbackConfig.thumbs && <ThumbsVoting />}
        {/* Secondary actions */}
        <div className="flex flex-row gap-3 justify-end">
          {feedbackConfig.edits && createSuggestHref && (
            <IconButton type={FeedbackButtonIconType.Edit} href={createSuggestHref} />
          )}
          {feedbackConfig.issues && createIssueHref && (
            <IconButton type={FeedbackButtonIconType.Alert} href={createIssueHref} />
          )}
        </div>
      </div>
    </div>
  );
}
