'use client';

import { DynamicLink } from '@/components/DynamicLink';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

export function Tab({
  name,
  href,
  isActive,
  underlineClassName,
  className,
  activeClassName,
}: {
  name: string;
  href?: string;
  isActive: boolean;
  className?: string;
  activeClassName?: string;
  underlineClassName?: string;
}) {
  return (
    <DynamicLink
      className={cn(
        Classes.TabsBarItem,
        'group relative h-full flex items-center font-medium text-gray-600 dark:text-gray-400 hover:text-gray-800 dark:hover:text-gray-300',
        className,
        isActive && cn('text-gray-800 dark:text-gray-200 font-semibold', activeClassName)
      )}
      href={href}
    >
      {name}
      <div
        className={cn(
          'absolute bottom-0 h-[1.5px] w-full',
          isActive
            ? 'bg-primary dark:bg-primary-light'
            : 'group-hover:bg-gray-200 dark:group-hover:bg-gray-700',
          underlineClassName
        )}
      ></div>
    </DynamicLink>
  );
}
