'use client';

import type { FooterSocialKeyType } from '@mintlify/models';
import { useContext, useEffect, useMemo } from 'react';

import { BannerContext } from '@/contexts/BannerContext';
import {
  DeploymentMetadataContext,
  DocsConfigContext,
  PageContext,
} from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { NavigationContext, useSelectedLocale } from '@/contexts/NavigationContext';
import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { NavLogo } from '@/ui/NavLogo';
import { cn } from '@/utils/cn';
import { checkAccess } from '@/utils/entitlements';
import getLogoHref from '@/utils/getLogoHref';

import { ModeSwitch } from './ModeSwitch';
import { Social } from './Social';

const FooterSocials = ({ isAdvanced, className }: { isAdvanced?: boolean; className?: string }) => {
  const { docsConfig } = useContext(DocsConfigContext);

  const socials = docsConfig?.footer?.socials;
  if (!socials) return null;

  const iconClassName = isAdvanced
    ? ''
    : 'bg-gray-400 dark:bg-gray-500 hover:bg-gray-500 dark:hover:bg-gray-400';

  const socialsBaseClassName = cn(
    'flex',
    isAdvanced ? `gap-3 min-w-[140px] max-w-[492px] flex-wrap` : `gap-6 flex-wrap`
  );

  if (Array.isArray(socials)) {
    return (
      <div className={cn(socialsBaseClassName, className)}>
        {socials.map((social) => (
          <Social
            key={social.url}
            url={social.url}
            type={social.type}
            iconClassName={iconClassName}
          />
        ))}
      </div>
    );
  } else if (typeof socials === 'object') {
    return (
      <div className={cn(socialsBaseClassName, className)}>
        {Object.entries(socials).map(([socialType, socialUrl]) => (
          <Social
            key={socialUrl}
            url={socialUrl}
            type={socialType as FooterSocialKeyType}
            iconClassName={iconClassName}
          />
        ))}
      </div>
    );
  }
  return null;
};

const FooterColumns = () => {
  const { docsConfig } = useContext(DocsConfigContext);

  const links = docsConfig?.footer?.links;
  const numberOfColumns = links?.length;
  const isSingleColumn = numberOfColumns === 1;

  if (!links) return null;

  return (
    <div
      className="flex flex-col sm:grid max-md:!grid-cols-2 gap-8 flex-1"
      style={{ gridTemplateColumns: `repeat(${links.length}, minmax(0, 1fr))` }}
    >
      {links.map((column) => (
        <div
          className={cn(
            'flex flex-col gap-4 flex-1 whitespace-nowrap',
            isSingleColumn ? 'max-w-full' : 'w-full md:items-center'
          )}
          key={column.header}
        >
          <div
            className={cn(
              'flex gap-4 flex-col',
              isSingleColumn ? 'md:flex-row md:items-center md:gap-8 md:justify-center' : ''
            )}
          >
            {column.header && !isSingleColumn && (
              <p className="text-sm font-semibold text-gray-950 dark:text-white mb-1">
                {column.header}
              </p>
            )}
            {column.items.map((link) => (
              <a
                className="text-sm max-w-36 whitespace-normal md:truncate text-gray-950/50 dark:text-white/50 hover:text-gray-950/70 dark:hover:text-white/70"
                key={`${link.label}-${link.href}}`}
                href={link.href}
                target={
                  link.href.startsWith('http://') || link.href.startsWith('https://')
                    ? '_blank'
                    : '_self'
                }
                rel="noreferrer"
              >
                {link.label}
              </a>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

const FooterPoweredBy = ({ isAdvanced }: { isAdvanced?: boolean }) => {
  const { subdomain, entitlements } = useContext(DeploymentMetadataContext);
  const { poweredByMintlify } = useSelectedLocale();

  const trackPoweredByMintlifyClick = useAnalyticsContext('powered_by_mintlify_click');

  if (checkAccess(entitlements, 'REMOVE_BRANDING')) return null;
  if (isAdvanced) {
    return (
      <>
        <div className="h-[1px] w-full bg-gray-100 dark:bg-white/5" />
        <div className="flex items-center justify-between">
          <div className="sm:flex">
            <a
              href={`https://mintlify.com/preview-request?utm_campaign=poweredBy&utm_medium=referral&utm_source=${subdomain}`}
              target="_blank"
              rel="noreferrer"
              className="text-sm text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300"
              onClick={() => trackPoweredByMintlifyClick({ subdomain }).catch(console.error)}
            >
              {poweredByMintlify}
            </a>
          </div>
          <ModeSwitch />
        </div>
      </>
    );
  }
  return (
    <>
      <div className="flex items-center justify-between">
        <div className="sm:flex">
          <a
            href={`https://mintlify.com/preview-request?utm_campaign=poweredBy&utm_medium=referral&utm_source=${subdomain}`}
            target="_blank"
            rel="noreferrer"
            className="text-sm text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300 text-nowrap"
            onClick={() => trackPoweredByMintlifyClick({ subdomain }).catch(console.error)}
          >
            {poweredByMintlify}
          </a>
        </div>
      </div>
    </>
  );
};

export const Footer = ({ className }: { className?: string }) => {
  const { isCustom } = usePageMode();
  const { docsConfig } = useContext(DocsConfigContext);

  const isAdvancedFooter = useMemo(() => {
    return docsConfig?.footer?.links && docsConfig.footer.links.length > 0;
  }, [docsConfig]);

  if (isCustom || isAdvancedFooter) {
    return null;
  }

  return (
    <footer
      id={Ids.Footer}
      className={cn(
        'flex gap-12 justify-between pt-10 border-t border-gray-100 sm:flex dark:border-gray-800/50 pb-28',
        className
      )}
    >
      <FooterSocials />
      <FooterPoweredBy />
    </footer>
  );
};

export const AdvancedFooter = ({
  className,
  disableSidebarOffset,
}: {
  className?: string;
  disableSidebarOffset?: boolean;
}) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const { pageMetadata } = useContext(PageContext);
  const { banner } = useContext(BannerContext);
  const { divisions } = useContext(NavigationContext);
  const router = useMintlifyRouter();
  const hasTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;
  const isCenter = pageMetadata.mode === 'center';
  const isCustom = pageMetadata.mode === 'custom';

  const socialsLayout =
    docsConfig?.footer?.links && docsConfig.footer.links.length <= 3 ? 'column' : 'row';
  const isSingleColumnFooter = docsConfig?.footer?.links && docsConfig.footer.links.length === 1;

  const getSidebarInitialTopRem = () => {
    let baseScrollMarginTop = 4;
    if (hasTabs && docsConfig?.theme !== 'linden') baseScrollMarginTop += 3.1;
    if (hasBanner) baseScrollMarginTop += 2.5;
    return baseScrollMarginTop;
  };
  const sidebarInitialTopRem = getSidebarInitialTopRem();

  const isAdvancedFooter = useMemo(() => {
    return docsConfig?.footer?.links && docsConfig.footer.links.length > 0;
  }, [docsConfig]);

  useEffect(() => {
    // Keep this check to prevent the sidebar from being offset when the footer is not present
    if (!isAdvancedFooter || disableSidebarOffset) return;
    const navbar = document.getElementById(Ids.Navbar);
    const sidebar = document.getElementById(Ids.Sidebar);
    const footer = document.getElementById(Ids.Footer);
    const toc = document.getElementById(Ids.TableOfContentsContent);

    const handleScroll = () => {
      if (!footer || isCenter) return;
      const footerTop = footer.getBoundingClientRect().top;
      const diff = window.innerHeight - footerTop;

      // offset sidebar by the amount scrolled in footer area
      if (sidebar) {
        if (diff > 0) {
          sidebar.style.top = `-${diff}px`;
          sidebar.style.height = `${window.innerHeight}px`;
        } else {
          sidebar.style.top = `${sidebarInitialTopRem}rem`;
          sidebar.style.height = 'auto';
        }
      }

      // offset toc by the amount scrolled in footer area
      if (toc && navbar) {
        if (diff > 0) {
          // based on tab's initial position in ui/MDXContentController/Container.tsx
          toc.style.top = isCustom
            ? `${navbar.clientHeight - diff}px`
            : `${40 + navbar.clientHeight - diff}px`;
        } else {
          toc.style.top = '';
        }
      }
    };
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [isCustom, sidebarInitialTopRem, isAdvancedFooter, disableSidebarOffset, isCenter]);

  if (!isAdvancedFooter) return null;

  const allColumnsHaveTitle = docsConfig?.footer?.links?.every((column) => column.header);

  return (
    <footer
      id={Ids.Footer}
      className={cn(
        'flex flex-col items-center mx-auto border-t border-gray-100 dark:border-gray-800/50',
        className
      )}
    >
      <div className="flex w-full flex-col gap-12 justify-between px-8 py-16 md:py-20 lg:py-28 max-w-[984px] z-0">
        <div
          className={cn(
            'flex flex-col md:flex-row gap-8 justify-between',
            !isSingleColumnFooter && ' min-h-[76px]'
          )}
        >
          <div className="flex md:flex-col justify-between items-center md:items-start min-w-16 md:min-w-20 lg:min-w-48 md:gap-y-24">
            <NavLogo
              href={docsConfig ? getLogoHref(docsConfig) : '/'}
              onContextMenu={(e) => {
                e.preventDefault();
                void router.push(docsConfig ? getLogoHref(docsConfig) : '/');
              }}
              name={docsConfig?.name}
              logoClassName="max-w-48 h-[26px]"
            />
            <FooterSocials
              isAdvanced
              className={cn(
                'h-fit',
                socialsLayout === 'column'
                  ? allColumnsHaveTitle
                    ? 'md:hidden justify-end'
                    : 'hidden'
                  : allColumnsHaveTitle
                    ? 'flex justify-end md:justify-start'
                    : 'hidden md:flex justify-end md:justify-start'
              )}
            />
          </div>
          <FooterColumns />
          {socialsLayout === 'column' && (
            <FooterSocials isAdvanced className={cn('hidden md:flex justify-end')} />
          )}
        </div>
        {!allColumnsHaveTitle && <FooterSocials isAdvanced className="md:hidden justify-start" />}
        <FooterPoweredBy isAdvanced />
      </div>
    </footer>
  );
};
