'use client';

// TODO - refactor
import { isGroup, potentiallyParseOpenApiString } from '@mintlify/common';
import { replaceSlashIndex } from '@mintlify/common';
import { isAbsoluteUrl } from '@mintlify/common';
import { DecoratedPageConfig, DecoratedPageOrGroupConfig } from '@mintlify/validation';
import Link from 'next/link';
import { forwardRef, useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import {
  getLegacySidebarStyleClassName,
  getPaddingByLevel,
  SidebarItemStyle,
} from '@/themes/mint/MainContentLayout/Nav/utils';
import { ArrowPosition, GroupDropdown } from '@/ui/GroupDropdown';
import { ComponentIcon } from '@/ui/Icon';
import { DeprecatedLabel } from '@/ui/Labels';
import { DropdownArrowIcon, UpAndRightwardFacingArrow } from '@/ui/icons';
import { cn } from '@/utils/cn';
import { getSidebarTitle } from '@/utils/getAllMetaTags';
import { isEqualIgnoringLeadingSlash } from '@/utils/paths/isEqualIgnoringLeadingSlash';
import { removeUrlParamsAndAnchors } from '@/utils/paths/removeUrlParamsAndAnchors';

import { MethodNavPill } from './MethodNavPill';
import { NavTagPill } from './NavTagPill';

export type NavItemProps = {
  entry: DecoratedPageOrGroupConfig | undefined;
  level?: number;
  mobile?: boolean;
  sidebarItemStyle?: SidebarItemStyle;
  arrowPosition?: ArrowPosition;
  trailingIcon?: React.ReactNode;
};

export const NavItem = forwardRef(function NavItemWithRef(
  {
    entry,
    level = 0,
    mobile = false,
    sidebarItemStyle,
    arrowPosition = 'trailing',
    trailingIcon,
  }: NavItemProps,
  ref: React.LegacyRef<HTMLLIElement>
) {
  const { legacyThemeSettings } = useContext(DocsConfigContext);
  // TODO - pass as prop
  const currentPath = useCurrentPath();

  if (entry == null) {
    return null;
  }

  if (isGroup(entry)) {
    return (
      <GroupDropdown
        group={entry}
        level={level}
        mobile={mobile}
        sidebarItemStyle={sidebarItemStyle}
        arrowPosition={arrowPosition}
      />
    );
  }

  const {
    href: uncleanHref,
    api: pageApi,
    openapi,
    url,
    asyncapi,
    deprecated,
  } = entry as DecoratedPageConfig;
  const href = replaceSlashIndex(uncleanHref);

  const isActive = isEqualIgnoringLeadingSlash(href, removeUrlParamsAndAnchors(currentPath));

  const endpointStr = pageApi || openapi || asyncapi;
  const title = getSidebarTitle(entry);

  let method: undefined | string = undefined;
  if (asyncapi) {
    method = 'websocket';
  } else if (endpointStr) {
    const potentiallyParsedOpenApiString = potentiallyParseOpenApiString(endpointStr);
    if (potentiallyParsedOpenApiString != undefined) {
      method = potentiallyParsedOpenApiString.method;
    }
  }

  return (
    <li
      ref={ref}
      id={url || href || '/'}
      className="relative scroll-m-4 first:scroll-m-20"
      data-title={title}
    >
      <Link
        href={url || href || '/'}
        className={getLegacySidebarStyleClassName(
          sidebarItemStyle || legacyThemeSettings?.sidebarStyle,
          isActive
        )}
        style={{
          paddingLeft: getPaddingByLevel(level, arrowPosition),
        }}
        target={url || (href && isAbsoluteUrl(href)) ? '_blank' : undefined}
      >
        {sidebarItemStyle === 'arrow' && isActive && (
          <DropdownArrowIcon className="absolute left-0 text-primary group-hover:text-primary dark:text-primary-light dark:group-hover:text-primary-light" />
        )}
        {endpointStr && entry.hideApiMarker !== true && method && (
          <MethodNavPill isActive={isActive} method={method} deprecated={deprecated} />
        )}
        {entry.icon && (
          <ComponentIcon
            icon={typeof entry.icon === 'string' ? entry.icon : entry.icon?.name}
            iconType={entry.iconType || 'regular'}
            className={cn(
              'h-4 w-4',
              isActive ? 'bg-primary dark:bg-primary-light' : 'bg-gray-400 dark:bg-gray-500'
            )}
            overrideColor={true}
          />
        )}
        <div
          className={cn(
            'flex-1 flex items-center space-x-2.5',
            deprecated && 'w-full max-w-full overflow-x-hidden justify-between'
          )}
        >
          <div
            className={cn(
              deprecated &&
                'min-w-0 max-w-full flex-1 whitespace-nowrap overflow-hidden text-ellipsis'
            )}
          >
            {title}
          </div>
          {url && (
            <div className={cn(arrowPosition === 'end' && 'flex-1 flex justify-end')}>
              <UpAndRightwardFacingArrow className="flex-shrink-0" />
            </div>
          )}
          {entry.tag && <NavTagPill tag={entry.tag} />}
          {deprecated && (
            <span className="flex-shrink-0">
              <DeprecatedLabel />
            </span>
          )}
        </div>
        {trailingIcon}
      </Link>
    </li>
  );
});
