'use client';

import { useContext, useMemo } from 'react';

import AnalyticsContext from '@/analytics/AnalyticsContext';
import { AnalyticsEvent } from '@/enums/AnalyticsEvent';

export const useAnalyticsContext = (event: AnalyticsEvent) => {
  const { analyticsMediator } = useContext(AnalyticsContext);
  return useMemo(() => {
    if (!analyticsMediator) return (_: object) => Promise.resolve();
    return analyticsMediator.createEventListener(event);
  }, [analyticsMediator, event]);
};
