'use client';

import {
  AnySchema,
  DataSchema,
  DataSchemaArray,
  IncrementalDataSchema,
  IncrementalDataSchemaArray,
} from '@mintlify/validation';
import { useMemo } from 'react';

export type NoAnyDataSchema = Exclude<DataSchema | IncrementalDataSchema, AnySchema>;
export type SchemaWithLabelInfo = {
  schema: NoAnyDataSchema;
  label: string;
};

export const usePlaygroundSchemaOptions = (
  schemaArray: DataSchemaArray | IncrementalDataSchemaArray
) => {
  return useMemo(() => getCleanSchemaArray(schemaArray), [schemaArray]);
};

const getCleanSchemaArray = (
  schemaArray: DataSchemaArray | IncrementalDataSchemaArray
): SchemaWithLabelInfo[] => {
  let firstAnySchema: AnySchema | undefined = undefined;

  // filter out all schemas with type `any`, while saving the first instance for meta info
  const filteredSchemaArray: NoAnyDataSchema[] = [];
  for (const schema of schemaArray) {
    if (schema.type === 'any') {
      firstAnySchema = firstAnySchema ?? schema;
    } else {
      filteredSchemaArray.push(schema);
    }
  }

  // for all schemas that aren't `any` schemas, generate labels from titles + types
  const typeStrings = getTypeStrings(filteredSchemaArray, !!firstAnySchema);

  // push a bunch of schemas to represent the `any`
  if (firstAnySchema !== undefined) {
    const baseSchema: Pick<DataSchema, 'description' | 'required'> = {
      description: firstAnySchema.description,
      required: firstAnySchema.required,
    };

    // all schemas that aren't added here are subsets of these schemas (eg, integer is just a more restrictive number input)
    filteredSchemaArray.push(
      { ...baseSchema, type: 'string' },
      { ...baseSchema, type: 'number' },
      { ...baseSchema, type: 'boolean' },
      { ...baseSchema, type: 'object', properties: {} },
      { ...baseSchema, type: 'array', items: [{ type: 'any' }] },
      { ...baseSchema, type: 'null' }
    );
  }

  // return each schema-label pair, just using the type for schemas generated from the `any`
  return filteredSchemaArray.map((schema, i) => ({ schema, label: typeStrings[i] ?? schema.type }));
};

const getTypeStrings = (schemas: NoAnyDataSchema[], hasAnySchema: boolean): string[] => {
  const types = schemas.map(({ type }) => type);
  const uniqueTypes = new Set(types).size === types.length;

  // avoid a situation like [string, number, string, number, boolean, ...]
  if (uniqueTypes && !hasAnySchema) {
    return types;
  }
  return schemas.map((schema, i) => (schema.title ?? `Option ${i + 1}`) + '  ' + schema.type);
};
