import { useState } from 'react';

import { CodeSnippet } from '@/components/Code/CodeSnippet';
import Icon from '@/ui/Icon';
import { WebsocketReceiveIcon, WebsocketSendIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

type ExpandableMessagesProps = {
  name: string;
  messages: {
    id: string;
    type: 'send' | 'receive' | string;
    title: string;
    content: string | undefined;
  }[];
  className?: string;
  icon?: React.ReactNode;
  rightElement?: React.ReactNode;
  children?: React.ReactNode;
};

type ExpandableMessageProps = {
  type: string;
  title: string;
  content: string | undefined;
};

export const ExpandableMessage = ({ type, title, content }: ExpandableMessageProps) => {
  const [expanded, setExpanded] = useState(false);
  return (
    <div className="border-t w-full border-zinc-100 dark:border-white/5">
      <button
        aria-label="expand message example section"
        onClick={() => setExpanded((prev) => !prev)}
        className="flex justify-between w-full px-3 py-2.5 items-center cursor-pointer"
      >
        <div className="flex items-center gap-x-2">
          <div className="w-4 h-4">
            {type === 'send' ? <WebsocketSendIcon /> : <WebsocketReceiveIcon />}
          </div>
          <div className="text-xs font-mono text-gray-600 dark:text-gray-400 whitespace-nowrap overflow-hidden text-ellipsis">
            {title}
          </div>
        </div>
        <Icon
          icon="angle-right"
          iconType="solid"
          className={cn(
            'h-3 w-3 bg-gray-400 dark:bg-gray-600 transition-transform rotate-90',
            expanded && 'rotate-[-90deg]'
          )}
        />
      </button>
      <div
        className={cn(
          'bg-zinc-100 dark:bg-zinc-900 flex-1 px-4 divide-y divide-gray-100 dark:divide-white/5 text-xs',
          expanded ? '' : 'hidden'
        )}
      >
        <div className="overflow-scroll py-2">
          <CodeSnippet language="json">{content}</CodeSnippet>
        </div>
      </div>
    </div>
  );
};

export const ExpandableMessages = ({
  name,
  messages,
  className,
  icon,
  rightElement,
  children,
}: ExpandableMessagesProps) => {
  return (
    <div
      className={cn(
        'w-full xl:w-[28rem] gap-6 grid grid-rows-[repeat(auto-fit,minmax(0,min-content))] grid-rows relative',
        className
      )}
    >
      <div className="flex w-full items-center justify-between border-standard border-t border-x rounded-t-2xl rounded-b-2xl overflow-scroll h-full max-h-[calc(100vh-11rem)]">
        <div className="flex flex-col w-full h-full">
          <div className="flex items-center justify-between px-3">
            <div className="flex items-center gap-x-2">
              {icon && icon}
              <div className="text-sm font-medium text-gray-800 dark:text-gray-200 leading-6 py-2">
                {name}
              </div>
            </div>
            <div className="flex items-center">{rightElement && rightElement}</div>
          </div>
          {messages.map((message) => {
            return (
              <ExpandableMessage
                key={message.id}
                type={message.type}
                title={message.title}
                content={message.content}
              />
            );
          })}
        </div>
      </div>
      {children}
    </div>
  );
};
