'use client';

import { ReactNode, useContext, useEffect } from 'react';

import { AdvancedFooter } from '@/components/Footer';
import { zIndex } from '@/constants/zIndex';
import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext, PageContext } from '@/contexts/ConfigContext';
import { Ids } from '@/lib/local/identifiers';
import { ChatAssistantSheet } from '@/ui/search/ChatAssistantSheet';
import { cn } from '@/utils/cn';

import { SideNav } from './SideNav';

export function MainContentLayout({ children }: { children: ReactNode }) {
  const { docsConfig } = useContext(DocsConfigContext);
  const { pageMetadata } = useContext(PageContext);
  const { banner } = useContext(BannerContext);

  const isCenter = pageMetadata.mode === 'center';
  const isWide = pageMetadata.mode === 'wide';
  const isCustom = pageMetadata.mode === 'custom';

  const hasBanner = !!banner;
  const getSidebarTopOffset = () => {
    const bannerHeight = banner ? 2.5 : 0;
    const topNavHeight = 4;

    return bannerHeight + topNavHeight;
  };

  useEffect(() => {
    if (hasBanner) {
      window.document.documentElement.classList.add('lg:[--scroll-mt:9.5rem]');
    }
  }, [hasBanner, docsConfig?.theme]);

  return (
    <>
      {isCustom ? (
        children
      ) : (
        <>
          <div className={cn(isCenter ? 'max-w-3xl' : 'max-w-8xl', 'px-4 mx-auto lg:px-8')}>
            {!isCenter && (
              <div
                className={cn(
                  zIndex.PrimaryNav,
                  'hidden lg:block fixed bottom-0 right-auto w-[18rem]'
                )}
                id={Ids.Sidebar}
                style={{
                  top: `${getSidebarTopOffset()}rem`,
                }}
              >
                <div
                  className="absolute inset-0 z-10 stable-scrollbar-gutter overflow-auto pr-8 pb-10"
                  id={Ids.SidebarContent}
                >
                  <SideNav />
                </div>
              </div>
            )}
            <div className={cn(isWide && 'max-w-6xl')} id={Ids.ContentContainer}>
              {children}
            </div>
          </div>

          <AdvancedFooter />
        </>
      )}
      <ChatAssistantSheet />
    </>
  );
}
