function processValue(
  value: unknown,
  maxLineLength: number,
  indentLevel: number,
  indent: string
): string {
  if (value === null) return 'null';
  if (typeof value === 'undefined') return 'undefined';
  if (typeof value === 'string') return JSON.stringify(value);
  if (typeof value === 'number' || typeof value === 'boolean') return String(value);
  if (Array.isArray(value)) return processArray(value, maxLineLength, indentLevel, indent);
  // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition
  if (typeof value === 'object' && value !== null) {
    return processObject(value as Record<string, unknown>, maxLineLength, indentLevel, indent);
  }
  return String(value);
}

function processArray(
  arr: Array<unknown>,
  maxLineLength: number,
  indentLevel: number,
  indent: string
) {
  if (arr.length === 0) return '[]';

  const singleLine =
    '[' + arr.map((val) => processValue(val, maxLineLength, indentLevel, indent)).join(', ') + ']';
  if (singleLine.length <= maxLineLength) return singleLine;

  indentLevel++;
  const multiLine =
    '[\n' +
    arr
      .map(
        (val) => indent.repeat(indentLevel) + processValue(val, maxLineLength, indentLevel, indent)
      )
      .join(',\n') +
    '\n' +
    indent.repeat(indentLevel - 1) +
    ']';
  indentLevel--;

  return multiLine;
}

function processObject(
  obj: Record<string | number | symbol, unknown>,
  maxLineLength: number,
  indentLevel: number,
  indent: string
) {
  const keys = Object.keys(obj);
  if (keys.length === 0) return '{}';

  const singleLine =
    '{ ' +
    keys
      .map(
        (key) =>
          `${JSON.stringify(key)}: ${processValue(obj[key], maxLineLength, indentLevel, indent)}`
      )
      .join(', ') +
    ' }';
  if (singleLine.length <= maxLineLength) return singleLine;

  indentLevel++;
  const multiLine =
    '{\n' +
    keys
      .map(
        (key) =>
          indent.repeat(indentLevel) +
          `${JSON.stringify(key)}: ${processValue(obj[key], maxLineLength, indentLevel, indent)}`
      )
      .join(',\n') +
    '\n' +
    indent.repeat(indentLevel - 1) +
    '}';
  indentLevel--;

  return multiLine;
}

export function formatJSON(obj: object, maxLineLength: number = 45) {
  const indentLevel = 0;
  const indent = '  ';
  return processValue(obj, maxLineLength, indentLevel, indent);
}
