'use client';

import { createContext, useContext } from 'react';

import { LocationType } from '@/types/api';

export type EndpointLocationContextType = {
  location?: LocationType;
  anchor?: string;
};

export const EndpointLocationContext = createContext<EndpointLocationContextType>({});

export const useEndpointLocation = () => useContext(EndpointLocationContext);

EndpointLocationContext.displayName = 'EndpointLocationContext';
