'use client';

import React from 'react';

import { Classes } from '@/lib/local/selectors';
import { MethodPillType } from '@/types/api';
import { cn } from '@/utils/cn';

import { getMethodColor, getShortenedMethodName } from '../Api/colors';

interface MethodPillProps extends React.HTMLAttributes<HTMLDivElement> {
  method: MethodPillType;
  shortMethod?: boolean;
  active?: boolean;
}
export const MethodPill = React.forwardRef<HTMLDivElement, MethodPillProps>(
  ({ className, method, shortMethod = false, active = false, ...props }, ref) => {
    const upperMethod = method.toUpperCase();
    const shortenedMethod = getShortenedMethodName(upperMethod);
    const { activeNavPillBg, activeNavPillText, inactiveNavPillText, inactiveNavPillBg } =
      getMethodColor(method);

    return (
      <div
        ref={ref}
        className={cn(
          Classes.MethodPill,
          'rounded-lg font-bold px-1.5 py-0.5 text-sm leading-5',
          active
            ? `${activeNavPillBg} ${activeNavPillText}`
            : `${inactiveNavPillBg} ${inactiveNavPillText}`,
          className
        )}
        {...props}
      >
        {shortMethod ? shortenedMethod : upperMethod}
      </div>
    );
  }
);
MethodPill.displayName = 'MethodPill';
