'use client';

import { getMethodColor, getShortenedMethodName } from '@/components/Api/colors';
import { Classes } from '@/lib/local/selectors';
import { MethodPillType } from '@/types/api';
import { cn } from '@/utils/cn';

type MethodNavPillProps = {
  isActive: boolean;
  method: MethodPillType;
  deprecated?: boolean;
};

export const MethodNavPill = ({ isActive, method, deprecated }: MethodNavPillProps) => {
  const displayMethod = deprecated ? 'DEPRECATED' : method;
  const { activeNavPillBg, activeNavPillText, inactiveNavPillText, inactiveNavPillBg } =
    getMethodColor(displayMethod);
  return (
    <span className={cn(Classes.MethodNavPill, 'flex items-center w-8')}>
      <span
        className={cn(
          'px-1 py-0.5 rounded-md text-[0.55rem] leading-tight font-bold',
          isActive
            ? `${activeNavPillBg} ${activeNavPillText}`
            : `${inactiveNavPillBg} ${inactiveNavPillText}`
        )}
      >
        {getShortenedMethodName(method)}
      </span>
    </span>
  );
};
