'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';

export function RoundedVariables() {
  const { docsConfig } = useContext(DocsConfigContext);

  if (docsConfig?.theme === 'linden') {
    const cssVariables = `:root {
      --rounded-sm: 4px;
      --rounded: 4px;
      --rounded-md: 4px;
      --rounded-lg: 4px;
      --rounded-xl: 4px;
      --rounded-2xl: 4px;
      --rounded-search: 4px;
      --rounded-3xl: 4px;
      --rounded-full: 4px;
  }`;
    return <style>{cssVariables}</style>;
  } else {
    return null;
  }
}
