import { optionallyAddLeadingSlash } from '@mintlify/common';

import { headerToHashAnchor } from '../paths/headerToHashAnchor';

export type SearchChunk = {
  id: string;
  header: string;
  content: string;
  page: string;
  link: string;
  metadata?: SearchChunkMetadata;
  score: number;
};

export type SearchChunkMetadata = {
  title?: string;
  openapi?: string;
  breadcrumbs?: string[];
  icon?: string;
  hash?: string;
};

type TrieveSearchResponse = {
  score_chunks?: Array<{
    highlights: string[];
    metadata: TrieveChunkMetadata[];
    score: number;
  }>;
  chunks?: Array<{
    chunk: TrieveChunkMetadata;
    highlights: string[];
    score: number;
  }>;
};

type TrieveChunkMetadata = {
  id: string;
  link?: string;
  metadata?: Record<string, unknown>;
  chunk_html?: string;
};

const MAX_CHUNK_CHAR_LENGTH = 100;

export const fetchResults = async ({
  datasetId,
  query,
  groups,
  apiKey,
}: {
  datasetId: string;
  query: string;
  groups?: string[];
  apiKey: string;
}) => {
  // We can't set this exactly equal to the number of words as multiple tokens
  // can comprise a word, so we round up to 2x the number of words.
  const maxHighlightLength = query.trim().split(/\s+/).length * 2;

  const trieveSearchResponse = await fetch(
    'https://api.mintlifytrieve.com/api/chunk/autocomplete',
    {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'TR-Dataset': datasetId,
        Authorization: apiKey,
      },
      body: JSON.stringify({
        query,
        search_type: 'fulltext',
        extend_results: true,
        highlight_delimiters: ['?', ',', '.', '!', '\n'],
        score_threshold: 0.2,
        filters: {
          must_not: [
            {
              field: 'tag_set',
              match: ['code'], // Filter out codeblocks for better search results
            },
          ],
          ...(!!groups
            ? {
                should: [
                  {
                    field: 'tag_set',
                    match_any: [...groups.map((group) => `USERAUTH:${group}`)],
                  },
                ],
              }
            : {}),
        },
        highlight_window: 10,
        highlight_max_num: 1,
        highlight_max_length: maxHighlightLength,
        highlight_strategy: 'exactmatch',
        page_size: 6,
      }),
    }
  );

  const trieveHits: TrieveSearchResponse = await trieveSearchResponse.json();
  return parseTrieveChunkSearchResults(trieveHits);
};

const parseTrieveChunkSearchResults = (searchResponse: TrieveSearchResponse) => {
  if (
    typeof searchResponse === 'object' &&
    (('score_chunks' in searchResponse && Array.isArray(searchResponse.score_chunks)) ||
      ('chunks' in searchResponse && Array.isArray(searchResponse.chunks)))
  ) {
    const chunks = (searchResponse.score_chunks || searchResponse.chunks || [])
      .flatMap((chunk) => {
        const metadata = 'metadata' in chunk ? chunk.metadata[0] : chunk.chunk;
        return { metadata, highlights: chunk.highlights, score: chunk.score };
      })
      .map(({ metadata, highlights, score }) => {
        if (!metadata?.chunk_html || !metadata.link) return undefined;

        // Split the content of the chunk into header and content
        const headerDelimiterIndex = metadata.chunk_html.indexOf('\n');
        const header = metadata.chunk_html.substring(0, headerDelimiterIndex);
        const content = metadata.chunk_html.substring(headerDelimiterIndex + 1);

        // Trim off the header if it's part of the highlighted window
        let potentialHighlight = highlights[0];
        if (potentialHighlight?.startsWith(header)) {
          potentialHighlight = potentialHighlight.replace(header, '');
        }

        return {
          id: metadata.id,
          header,
          content: potentialHighlight
            ? potentialHighlight.substring(0, MAX_CHUNK_CHAR_LENGTH)
            : content.substring(0, MAX_CHUNK_CHAR_LENGTH),
          page: metadata.link,
          link: generateDeeplink(metadata),
          metadata: metadata.metadata,
          score: score,
        };
      });

    return getMostRelevantPerPage(chunks);
  }

  return [];
};

const getMostRelevantPerPage = (chunks: (SearchChunk | undefined)[]) => {
  const uniqueMap = new Map<string, SearchChunk>();

  for (const chunk of chunks) {
    if (!chunk) continue;
    if (uniqueMap.has(chunk.page)) continue;
    uniqueMap.set(chunk.page, chunk);
  }

  return [...uniqueMap.values()];
};

export const generateDeeplink = (chunkMetadata: TrieveChunkMetadata) => {
  if (
    !(
      'metadata' in chunkMetadata &&
      !!chunkMetadata.metadata &&
      'title' in chunkMetadata.metadata &&
      'link' in chunkMetadata &&
      typeof chunkMetadata.link === 'string'
    )
  )
    return '';
  const section = chunkMetadata.metadata.title;
  const link = optionallyAddLeadingSlash(chunkMetadata.link);
  // TODO: use hash once the re-indexing is done
  // const hash = chunkMetadata.metadata.hash;
  if (section && typeof section === 'string') {
    return `${link}#${headerToHashAnchor(section)}`;
  }

  return link;
};
