import { DataSchemaArray, IncrementalDataSchemaArray } from '@mintlify/validation';

import { RenderedRefs } from '@/types/api';
import { getNextPropertiesByType } from '@/utils/api-reference/getNextPropertiesByType';

import { RecursiveInput } from './RecursiveInput';

type calculateObjectInputChildrenProps = {
  properties: Record<string, DataSchemaArray | IncrementalDataSchemaArray>;
  additionalProperties: DataSchemaArray | IncrementalDataSchemaArray | boolean | undefined;
  value: Record<string, unknown> | undefined;
  setValue: (value: Record<string, unknown> | undefined) => void;
  name: string | undefined;
  parentNames: string[] | undefined;
  defaultExpanded: boolean | undefined;
  depth: number;
  refId: string | undefined;
  refs: RenderedRefs;
  style?: string;
  explode?: boolean;
};

export const calculateObjectInputChildren = ({
  properties,
  additionalProperties,
  value,
  setValue,
  name,
  parentNames,
  defaultExpanded,
  depth,
  refId,
  refs,
  style,
  explode,
}: calculateObjectInputChildrenProps): React.ReactNode[] => {
  const currentLevelRefs = new Set(refs);

  if (refId) {
    currentLevelRefs.add(refId);
  }

  const children: React.ReactNode[] = [];
  const inputInfo = getInputInfoForObject(properties, additionalProperties, value);

  if (currentLevelRefs.size > 0) {
    inputInfo.map((input) => {
      const {
        key: inputName,
        schema: inputSchema,
        value: inputValue,
        isAdditionalProperty,
      } = input;

      const setInputValue = (newPropValue: unknown) => {
        const newObj = { ...value };
        newObj[inputName] = newPropValue;
        setValue(newObj);
      };

      const clearInput =
        inputValue === undefined && !isAdditionalProperty
          ? undefined
          : () => {
              const newObj = { ...value };
              delete newObj[inputName];
              setValue(newObj);
            };

      const parentNamesForChild = name ? [...(parentNames ?? []), name] : undefined;

      const nextSchemaArray = inputSchema[0];
      const nextSchemaArrayProperties = getNextPropertiesByType(nextSchemaArray);
      const containsCircularReference = Object.values(nextSchemaArrayProperties).some(
        (value) => typeof value === 'string' && currentLevelRefs.has(value)
      );
      children.push(
        <RecursiveInput
          key={inputName}
          name={inputName}
          schemaArray={inputSchema}
          value={inputValue}
          setValue={setInputValue}
          clearValue={clearInput}
          depth={depth + 1}
          parentNames={parentNamesForChild}
          defaultExpanded={containsCircularReference ? false : defaultExpanded}
          renderedRefs={new Set(currentLevelRefs)}
          style={style}
          explode={explode}
        />
      );
    });
  } else {
    inputInfo.map((input) => {
      const {
        key: inputName,
        schema: inputSchema,
        value: inputValue,
        isAdditionalProperty,
      } = input;

      const setInputValue = (newPropValue: unknown) => {
        const newObj = { ...value };
        newObj[inputName] = newPropValue;
        setValue(newObj);
      };

      const clearInput =
        inputValue === undefined && !isAdditionalProperty
          ? undefined
          : () => {
              const newObj = { ...value };
              delete newObj[inputName];
              setValue(newObj);
            };

      const parentNamesForChild = name ? [...(parentNames ?? []), name] : undefined;

      children.push(
        <RecursiveInput
          key={inputName}
          name={inputName}
          schemaArray={inputSchema}
          value={inputValue}
          setValue={setInputValue}
          clearValue={clearInput}
          depth={depth + 1}
          parentNames={parentNamesForChild}
          defaultExpanded={defaultExpanded}
          style={style}
          explode={explode}
          renderedRefs={new Set()}
        />
      );
    });
  }

  return children;
};

const getInputInfoForObject = (
  properties: Record<string, DataSchemaArray | IncrementalDataSchemaArray>,
  additionalProperties: DataSchemaArray | IncrementalDataSchemaArray | boolean | undefined,
  value: Record<string, unknown> | undefined
) => {
  const namedPropertyInfo = Object.entries(properties).map(([key, schema]) => ({
    key,
    schema,
    value: value?.[key],
    isAdditionalProperty: false,
  }));

  // explicitly check for `false`, since `undefined` should be treated as `true`
  if (additionalProperties === false) {
    return namedPropertyInfo;
  }

  const additionalPropertiesSchema: DataSchemaArray | IncrementalDataSchemaArray =
    typeof additionalProperties === 'object' ? additionalProperties : [{ type: 'any' }];

  const additionalPropertyKeys = Object.keys(value ?? {}).filter(
    (key) => !Object.keys(properties).includes(key)
  );
  const additionalPropertyInfo = additionalPropertyKeys.map((key) => ({
    key,
    schema: additionalPropertiesSchema,
    value: value?.[key],
    isAdditionalProperty: true,
  }));

  return [...namedPropertyInfo, ...additionalPropertyInfo];
};
