'use client';

import { TableOfContentsSectionType } from '@mintlify/common';
import { Fragment } from 'react';

import { Classes } from '@/lib/local/selectors';
import { DropdownArrowIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';

const DEFAULT_DEPTH = 2;
const ARROW_DEPTH = 1;

export type TableOfContentsStyle = 'default' | 'border' | 'arrow';

export const TableOfContentsSection = ({
  section,
  isActive,
  closeNav,
  pageHasSubsections,
  style,
}: {
  section: TableOfContentsSectionType;
  closeNav: () => void;
  isActive: (section: TableOfContentsSectionType) => boolean;
  pageHasSubsections: boolean;
  style?: TableOfContentsStyle;
}) => {
  const depthSubtract = style === 'arrow' ? ARROW_DEPTH : DEFAULT_DEPTH;

  return (
    <Fragment key={section.slug}>
      <li
        className={cn(Classes.TableOfContentsItem, 'relative', style === 'arrow' && 'pl-4')}
        data-depth={0}
      >
        {style === 'arrow' && isActive(section) && (
          <DropdownArrowIcon className="absolute left-0 mt-1 text-primary dark:text-primary-light" />
        )}
        <a
          href={`#${section.slug}`}
          onClick={closeNav}
          className={cn(
            'py-1 block',
            pageHasSubsections && 'font-medium',
            style === 'border' &&
              'border-l pl-4 border-gray-950/5 dark:border-white/10 hover:border-gray-950/20 dark:hover:border-white/20',
            isActive(section)
              ? 'font-medium text-primary dark:text-primary-light border-primary dark:border-primary-light hover:border-primary dark:hover:border-primary-light'
              : 'hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
          )}
        >
          {section.title}
        </a>
      </li>
      {section.children.map((subsection: TableOfContentsSectionType) => {
        const depthStartingFromH2 = subsection.depth - depthSubtract;
        const dataDepth = Math.max(subsection.depth - depthSubtract, 0);
        return (
          <li
            key={subsection.slug}
            className={cn(
              Classes.TableOfContentsItem,
              'relative',
              style === 'border' &&
                'border-l pl-4 border-gray-950/5 dark:border-white/10 hover:border-gray-950/20 dark:hover:border-white/20',
              isActive(subsection) &&
                'border-primary hover:border-primary dark:border-primary-light'
            )}
            data-depth={dataDepth}
          >
            {style === 'arrow' && isActive(subsection) && (
              <DropdownArrowIcon className="absolute left-0 mt-1 text-primary dark:text-primary-light" />
            )}
            <a
              style={{ marginLeft: depthStartingFromH2 + 'rem' }}
              href={`#${subsection.slug}`}
              onClick={closeNav}
              className={cn(
                'group flex items-start py-1 whitespace-pre-wrap',
                isActive(subsection)
                  ? 'text-primary dark:text-primary-light'
                  : 'text-gray-500 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
              )}
            >
              {subsection.title}
            </a>
          </li>
        );
      })}
    </Fragment>
  );
};
