'use client';

import { MDXProvider } from '@mdx-js/react';
import { MDXClient } from '@mintlify/mdx';
import type { SerializeSuccess } from '@mintlify/mdx';
import { PageMetaTags } from '@mintlify/models';
import { UserInfo } from '@mintlify/validation';
import { useEffect, useState } from 'react';

import components from '@/components';
import { DynamicLink } from '@/components/DynamicLink';
import { Heading } from '@/components/Heading';
import { getReactHooks } from '@/utils/getReactHooks';

import { contentComponents } from './Content/contentComponents';

export const MdxContent = ({
  mdxSource,
  mdxSourceWithNoJs,
  pageMetadata,
  userInfo,
}: {
  mdxSource: SerializeSuccess;
  mdxSourceWithNoJs: SerializeSuccess;
  pageMetadata: PageMetaTags;
  userInfo?: UserInfo;
}) => {
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  return (
    <MDXProvider components={{ a: DynamicLink, Heading, ...contentComponents }}>
      <MDXClient
        components={components}
        compiledSource={isClient ? mdxSource.compiledSource : mdxSourceWithNoJs.compiledSource}
        frontmatter={mdxSource.frontmatter}
        scope={{
          pageMetadata,
          user: userInfo?.content ?? {},
          ...(isClient ? getReactHooks() : {}),
        }}
      />
    </MDXProvider>
  );
};
