'use client';

import { HTMLAttributes, ImgHTMLAttributes } from 'react';

import { ZoomImage } from '@/components/ZoomImage';
import { cn } from '@/utils/cn';

const DEFAULT_IMAGE_CLASS = 'rounded';

export const contentComponents = {
  img: (props: ImgHTMLAttributes<HTMLImageElement>) => (
    <ZoomImage {...props}>
      {/* can't supply alt text because we don't know what the image will be */}
      {/* eslint-disable-next-line jsx-a11y/alt-text */}
      <img {...props} className={cn(DEFAULT_IMAGE_CLASS, props.className)} />
    </ZoomImage>
  ),
  picture: (props: HTMLAttributes<HTMLElement>) => (
    <ZoomImage {...props}>
      <picture className={cn(DEFAULT_IMAGE_CLASS, props.className)} />
    </ZoomImage>
  ),
  figure: (props: HTMLAttributes<HTMLElement>) => (
    <ZoomImage {...props}>
      <figure {...props} className={cn(DEFAULT_IMAGE_CLASS, props.className)} />
    </ZoomImage>
  ),
};
