'use client';

import { DecoratedNavigationResponseType } from '@mintlify/http-client/dist/leaves-client/types';
import {
  convertMintDecoratedNavToDocsDecoratedNav,
  DecoratedNavigationConfig,
} from '@mintlify/validation';
import { useCallback, useContext, useEffect, useState } from 'react';

import { DeploymentMetadataContext, DocsConfigContext } from '@/contexts/ConfigContext';

import { useCurrentPath } from './useCurrentPath';

const NAVIGATION_CACHE_KEY = 'mintlify-navigation-cache';
const CACHE_EXPIRY_TIME = 1000 * 60 * 15; // 15 mins

type NavigationCache = {
  navigation: DecoratedNavigationConfig;
  timestamp: number;
  buildId: string;
};

export function useNavigationCache(decoratedNav: DecoratedNavigationConfig | undefined) {
  const [cachedNavigation, setCachedNavigation] = useState<DecoratedNavigationConfig | undefined>();
  const pathname = useCurrentPath();
  const { docsConfig } = useContext(DocsConfigContext);
  const { buildId } = useContext(DeploymentMetadataContext);
  const [isUpdatingCache, setIsUpdatingCache] = useState(false);

  useEffect(() => {
    setCachedNavigation(decoratedNav);
  }, [decoratedNav]);

  const updateCache = useCallback(
    async (auth: { partial?: boolean }) => {
      setIsUpdatingCache(true);

      if (!auth.partial) {
        setIsUpdatingCache(false);
        setCachedNavigation(decoratedNav);
        return;
      }

      try {
        setIsUpdatingCache(true);
        const cached = localStorage.getItem(NAVIGATION_CACHE_KEY);
        if (cached) {
          const parsedCache: NavigationCache = JSON.parse(cached);
          // backwards compatibility for old navigation cache
          // if the navigation is an array, we know that it's from mintConfig
          if (!Array.isArray(parsedCache.navigation)) {
            const isExpired = Date.now() - parsedCache.timestamp > CACHE_EXPIRY_TIME;
            const buildIdMatches = parsedCache.buildId === buildId;

            if (!isExpired && buildIdMatches) {
              setCachedNavigation(parsedCache.navigation);
              setIsUpdatingCache(false);
              return;
            }
            localStorage.removeItem(NAVIGATION_CACHE_KEY);
          }
        }

        const response = await fetch('/_mintlify/navigation');

        if (response.status === 401) {
          setCachedNavigation(decoratedNav);
          setIsUpdatingCache(false);
          return;
        }

        const data = (await response.json()) as DecoratedNavigationResponseType;
        let navigation: DecoratedNavigationConfig;

        if (data.docsDecoratedNav) {
          navigation = data.docsDecoratedNav;
        } else if (Array.isArray(data.navigation) && docsConfig) {
          navigation = convertMintDecoratedNavToDocsDecoratedNav({
            type: 'docs',
            docsConfig: docsConfig,
            decoratedNavigation: data.navigation,
            ignoreUnknownPages: true,
          });
        } else {
          navigation = data.navigation as unknown as DecoratedNavigationConfig;
        }

        const cacheData: NavigationCache = {
          navigation: navigation,
          timestamp: Date.now(),
          buildId: buildId ?? 'PLACEHOLDER',
        };

        localStorage.setItem(NAVIGATION_CACHE_KEY, JSON.stringify(cacheData));
        setCachedNavigation(navigation);
      } catch (error) {
        console.error('Error fetching navigation:', error);
        setCachedNavigation(decoratedNav);
      } finally {
        setIsUpdatingCache(false);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [decoratedNav, pathname, NAVIGATION_CACHE_KEY]
  );

  return { navigationData: cachedNavigation, updateCache, isUpdatingCache };
}
