'use client';

import { ArraySchema, IncrementalArraySchema } from '@mintlify/validation';

import { cn } from '@/utils/cn';

export const ArrayMetadata = ({
  schema,
  description,
}: {
  schema: ArraySchema | IncrementalArraySchema;
  description?: string;
}) => {
  return (
    <>
      {(schema.minItems !== undefined || schema.maxItems !== undefined) && (
        <div className={cn('prose prose-sm prose-gray dark:prose-invert', description && 'mt-6')}>
          <ArrayLength minItems={schema.minItems} maxItems={schema.maxItems} />
        </div>
      )}
    </>
  );
};

const ArrayLength = ({ minItems, maxItems }: { minItems?: number; maxItems?: number }) => {
  if (minItems === maxItems && minItems !== undefined) {
    return (
      <>
        Required array length: <code>{minItems}</code> element{minItems > 1 ? 's' : ''}
      </>
    );
  }
  if (minItems && maxItems) {
    return (
      <>
        Required array length: <code>{`${minItems} - ${maxItems}`}</code> element
        {maxItems > 1 ? 's' : ''}
      </>
    );
  }
  if (minItems) {
    return (
      <>
        Minimum length: <code>{minItems}</code>
      </>
    );
  }
  if (maxItems) {
    return (
      <>
        Maximum length: <code>{maxItems}</code>
      </>
    );
  }
  return '';
};
