import { IS_DEV } from '@/constants';
import { NEXT_PUBLIC } from '@/env';

const LEAVES_SUFFIX = IS_DEV ? '-dev' : '';

const getChatEndpoint = (
  resource: 'topic' | 'message',
  subdomain: string,
  suffix: string = '',
  isAuthenticated?: boolean
) => {
  if (NEXT_PUBLIC.AUTH_ENABLED) {
    if (!!isAuthenticated) {
      return `/_mintlify/api/chat/${resource}`;
    } else {
      return `/_mintlify/api-public/chat/${subdomain}/${resource}`;
    }
  }

  return `https://leaves${LEAVES_SUFFIX}.mintlify.com/api/chat/${subdomain}/${resource}${suffix}`;
};

type Filters = {
  groups?: string[];
  version?: string;
};

export const createTopic = async (subdomain: string, isAuthenticated?: boolean) => {
  const topicResponse = await fetch(getChatEndpoint('topic', subdomain, '', isAuthenticated), {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
  });

  if (!topicResponse.ok) {
    return undefined;
  }

  const topic: unknown = await topicResponse.json();

  if (
    topic &&
    typeof topic === 'object' &&
    'topicId' in topic &&
    typeof topic.topicId === 'string'
  ) {
    return topic.topicId;
  } else {
    return undefined;
  }
};

const getFilterClause = ({ userGroups, version }: { userGroups?: string[]; version?: string }) => {
  const filters: Filters = {};

  if (!!userGroups) {
    filters.groups = userGroups;
  }

  if (!!version) {
    filters.version = version;
  }

  return Object.keys(filters).length > 0 ? { filter: filters } : {};
};

export const generateMessage = async ({
  subdomain,
  topicId,
  userMessage,
  userGroups,
  version,
  isAuthenticated,
  setResponse,
}: {
  subdomain: string;
  topicId: string;
  userMessage: string;
  userGroups?: string[];
  version?: string;
  isAuthenticated?: boolean;
  setResponse: (response: string) => void;
}) => {
  const filterClause = getFilterClause({ userGroups, version });
  const messageResponse = await fetch(getChatEndpoint('message', subdomain, '', isAuthenticated), {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ message: userMessage, topicId, ...filterClause }),
  });

  if (!messageResponse.ok || !messageResponse.body) {
    return {
      status: messageResponse.status,
    };
  }
  const streamReader = messageResponse.body.getReader();

  for (;;) {
    const { done, value } = await streamReader.read();
    if (done) {
      setResponse(new TextDecoder().decode(value));
      return;
    }

    const newValue = new TextDecoder().decode(value);
    setResponse(newValue);
  }
};

export const cloneTopic = async (subdomain: string, topicId: string) => {
  try {
    const response = await fetch(getChatEndpoint('topic', subdomain, `/${topicId}/clone`), {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
    });

    if (!response.ok) return undefined;

    const { newTopicId } = (await response.json()) as { newTopicId: string };
    return newTopicId;
  } catch {
    return undefined;
  }
};

export const getTopicMessages = async (subdomain: string, topicId: string) => {
  try {
    const response = await fetch(getChatEndpoint('topic', subdomain, `/${topicId}/messages`), {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' },
    });

    if (!response.ok) return undefined;

    const { messages } = (await response.json()) as {
      messages: { query: string; response: string }[];
    };
    return messages;
  } catch {
    return undefined;
  }
};
