'use client';

import { clsx } from 'clsx';
import { ReactNode } from 'react';

export default function Tab({
  title,
  isActive = true,
  children,
}: {
  title: string;
  isActive?: boolean;
  children?: ReactNode;
}) {
  return (
    <>
      <button
        className={clsx(
          'flex text-sm leading-6 font-semibold whitespace-nowrap pt-3 pb-2.5 -mb-px max-w-max border-b',
          isActive
            ? 'text-primary dark:text-primary-light border-current'
            : 'text-gray-900 border-transparent hover:border-gray-300 dark:text-gray-200 dark:hover:border-gray-700'
        )}
        data-component-part="tab-button"
        data-active={isActive}
      >
        {title}
      </button>
      {children ? (
        <div className="prose dark:prose-dark" data-component-part="tab-content">
          {children}
        </div>
      ) : null}
    </>
  );
}
