'use client';

import { StringSchema } from '@mintlify/validation';

import { InputContainer } from '../../components/InputContainer';
import { GenericInput } from './RecursiveInput';

export const TextInput: GenericInput<StringSchema> = ({
  name,
  schema,
  typeDropdown,
  value,
  setValue,
  clearValue,
  parentNames,
  arrayIndex,
}) => {
  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={parentNames}
      arrayIndex={arrayIndex}
    >
      <textarea
        className="-mx-3.5 px-3.5 flex-1 min-h-[2.5rem] min-w-0 bg-transparent outline-none text-playground-input resize-y"
        rows={2}
        placeholder={schema.placeholder ?? `Enter ${name ?? 'value'}`}
        value={value}
        onChange={(event) => setValue(event.target.value)}
        spellCheck={false}
        autoCapitalize="off"
        autoComplete="off"
        autoCorrect="off"
      />
    </InputContainer>
  );
};
