'use client';

import { useContext } from 'react';

import { NavigationContext } from '@/contexts/NavigationContext';
import { Anchor } from '@/themes/mint/shared/Anchor';

export function TopLevelNavTabsMobile() {
  const { divisions } = useContext(NavigationContext);

  return (
    <ul className="mb-12">
      {divisions.tabs.map((tab) => (
        <li key={tab.name}>
          <Anchor href={tab.href} name={tab.name} isActive={tab.isActive} />
        </li>
      ))}
    </ul>
  );
}
