export const REVALIDATION_TOKEN = process.env.REVALIDATION_TOKEN;
export const EXPORT_TOKEN = process.env.EXPORT_TOKEN;
export const INTERNAL_ANALYTICS_WRITE_KEY = process.env.INTERNAL_ANALYTICS_WRITE_KEY;
export const BASE_PATH = process.env.BASE_PATH || '';
export const HOST_NAME = process.env.HOST_NAME || '';
// eslint-disable-next-line
export const NODE_ENV = process.env.NODE_ENV || 'development';

// Boolean flags
export const IS_MULTI_TENANT = process.env.IS_MULTI_TENANT === 'true';
export const VERCEL = process.env.VERCEL === '1';

/**
 *
 *
 * Public next.js environment variables - NO SECRETS
 *
 *
 * Unlike other env variables, env variables prefixed with `NEXT_PUBLIC_` will be injected into client-side code.
 * This means we **MUST NOT** use this pattern for any env variables containing secrets.
 *
 * Because next.js does inline replacement, these env vars must be referenced using `process.env.NEXT_PUBLIC_*****`
 * (no object destructuring).
 */
export const NEXT_PUBLIC = {
  SENTRY_DSN: process.env.NEXT_PUBLIC_SENTRY_DSN,
  ENV: process.env.NEXT_PUBLIC_ENV || 'local',
  TRIEVE_API_KEY: process.env.NEXT_PUBLIC_TRIEVE_API_KEY,
  AUTH_ENABLED: process.env.NEXT_PUBLIC_AUTH_ENABLED,
  CUSTOM_JS_DISABLED: process.env.NEXT_PUBLIC_CUSTOM_JS_DISABLED,
  ASSET_PREFIX: process.env.NEXT_PUBLIC_ASSET_PREFIX ?? '',
};
