import { isGroup } from '@mintlify/common';
import { DecoratedGroupConfig } from '@mintlify/validation';

import { isEqualIgnoringLeadingSlash } from '../paths/isEqualIgnoringLeadingSlash';
import { removeUrlParamsAndAnchors } from '../paths/removeUrlParamsAndAnchors';

export function isPathInGroup(pathname: string, group: DecoratedGroupConfig): boolean {
  return group.pages.some((entry) => {
    if (isGroup(entry)) {
      return isPathInGroup(pathname, entry);
    }
    return isEqualIgnoringLeadingSlash(entry.href, removeUrlParamsAndAnchors(pathname));
  });
}
