'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { cn } from '@/utils/cn';

import { ImageResponseData } from '..';

type ImageResponseContentParams = {
  data: ImageResponseData;
};

export const ImageResponseContent = ({ data }: ImageResponseContentParams) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeBlockMode = docsConfig?.styling?.codeblocks;
  return (
    <div
      className={cn(
        codeBlockMode === 'system' ? 'bg-response-body-auto' : 'bg-response-body-dark',
        'h-60'
      )}
    >
      <img
        alt="image response from the server"
        className="p-6 w-full h-full object-scale-down"
        src={data.url}
      />
    </div>
  );
};
