'use client';

import { DataSchema, DataSchemaArray, IncrementalDataSchemaArray } from '@mintlify/validation';
import slugify from '@sindresorhus/slugify';

import { useEndpointLocation } from '@/contexts/EndpointLocationContext';
import { FieldType, RenderedRefs } from '@/types/api';
import { getNextPropertiesByType } from '@/utils/api-reference/getNextPropertiesByType';

import { RecursiveParamFieldWithChildrenObserver } from './RecursiveParamFieldWithChildrenObserver';
import { RecursiveParamFieldWithObserver } from './RecursiveParamFieldWithObserver';

export type RecursiveParamFieldParams = {
  fieldType: FieldType;
  schemaArray: DataSchemaArray | IncrementalDataSchemaArray;
  renderedRefs?: RenderedRefs;
  name?: string | null;
  location?: string;
  parentName?: string;
  depth?: number;
  hideDescription?: boolean;
  circularRefs?: string[];
  paramId?: string;
  style?: string;
  explode?: boolean;
};

export const RecursiveParamField = ({
  fieldType,
  schemaArray,
  name,
  location,
  parentName,
  depth = 0,
  hideDescription = false,
  renderedRefs = new Set(),
  circularRefs,
  paramId,
  style,
  explode,
}: RecursiveParamFieldParams) => {
  const { anchor } = useEndpointLocation();

  const RecursiveParamFieldType = anchor
    ? RecursiveParamFieldWithChildrenObserver
    : RecursiveParamFieldWithObserver;

  return (
    <RecursiveParamFieldType
      fieldType={fieldType}
      schemaArray={schemaArray}
      name={name}
      location={location}
      parentName={parentName}
      depth={depth}
      style={style}
      explode={explode}
      hideDescription={hideDescription}
      renderedRefs={renderedRefs}
      circularRefs={circularRefs}
      paramId={paramId}
    />
  );
};

type RenderNextPropertiesRecursivelyParams = {
  properties: [string, DataSchemaArray | IncrementalDataSchemaArray][];
  fieldType: FieldType;
  depth: number;
  refs: Set<string>;
  parentName?: string;
  name?: string | null;
  currentSchemaRef: string | undefined;
  style?: string;
  explode?: boolean;
};

export const renderNextPropertiesRecursively = ({
  currentSchemaRef,
  properties,
  fieldType,
  parentName,
  name,
  depth,
  refs,
  style,
  explode,
}: RenderNextPropertiesRecursivelyParams) => {
  // Create a new set with the current refs for this level
  const currentLevelRefs = new Set(refs);

  if (currentSchemaRef) {
    currentLevelRefs.add(currentSchemaRef);
  }

  if (currentLevelRefs.size > 0) {
    const circularReferenceAwareNextProps = properties.map((property) => {
      const [propertyName, schemaArray] = property;
      const nextSchemaArray = schemaArray[0];
      const nextSchemaArrayProperties = getNextPropertiesByType(nextSchemaArray);
      if (
        Object.values(nextSchemaArrayProperties).some(
          (value: unknown) => typeof value === 'string' && currentLevelRefs.has(value)
        )
      ) {
        const circularReferences = Object.values(nextSchemaArrayProperties).filter(
          (value: unknown) => typeof value === 'string' && currentLevelRefs.has(value)
        );
        return (
          <RecursiveParamField
            fieldType={fieldType}
            key={propertyName}
            schemaArray={schemaArray}
            name={propertyName}
            parentName={
              style === 'deepObject' && explode
                ? getDeepObjectParentName(name, parentName)
                : getRecursiveParentName(name, parentName)
            }
            depth={depth + 1}
            renderedRefs={new Set(currentLevelRefs)}
            circularRefs={circularReferences as string[]}
          />
        );
      } else {
        return (
          <RecursiveParamField
            fieldType={fieldType}
            key={propertyName}
            schemaArray={schemaArray}
            name={propertyName}
            parentName={
              style === 'deepObject' && explode
                ? getDeepObjectParentName(name, parentName)
                : getRecursiveParentName(name, parentName)
            }
            depth={depth + 1}
            renderedRefs={new Set(currentLevelRefs)}
          />
        );
      }
    });
    return circularReferenceAwareNextProps;
  } else {
    return properties.map(([propertyName, schemaArray]) => (
      <RecursiveParamField
        fieldType={fieldType}
        key={propertyName}
        schemaArray={schemaArray}
        name={propertyName}
        parentName={
          style === 'deepObject' && explode
            ? getDeepObjectParentName(name, parentName)
            : getRecursiveParentName(name, parentName)
        }
        depth={depth + 1}
        renderedRefs={new Set()}
        style={style}
        explode={explode}
      />
    ));
  }
};

type RenderTruncatedNextPropertiesParams = {
  properties: [string, DataSchemaArray | IncrementalDataSchemaArray][];
  fieldType: FieldType;
  parentName?: string;
  name?: string | null;
  depth: number;
};

export const renderTruncatedNextProperties = ({
  properties,
  fieldType,
  parentName,
  name,
  depth,
}: RenderTruncatedNextPropertiesParams) => {
  return properties.map((property) => {
    const [propertyName, schemaArray] = property;
    const nextSchemaArray = schemaArray[0];
    const baseSchema: DataSchema = nextSchemaArray as DataSchema;
    if (baseSchema.type === 'array') {
      baseSchema.items = [{ type: 'any' }];
    } else if (baseSchema.type === 'object') {
      baseSchema.properties = {};
    }
    const paramId = buildRecursiveParamFieldId(fieldType, name, parentName);
    const id = `${paramId}-${propertyName}`;
    return (
      <RecursiveParamField
        fieldType={fieldType}
        key={propertyName}
        schemaArray={[baseSchema]}
        name={propertyName}
        paramId={id}
        depth={depth + 1}
        renderedRefs={new Set()}
      />
    );
  });
};

export const buildRecursiveParamFieldId = (
  fieldType: FieldType | undefined,
  name: string | undefined | null,
  parentName: string | undefined
) => {
  return slugify(
    `${fieldType ? `${fieldType}-` : ''}${parentName ? `${parentName}-` : ''}${name ? name : 'element'}`,
    {
      decamelize: true,
    }
  );
};

export const getRecursiveParentName = (name?: string | null, parentName?: string): string => {
  if (!name) {
    return '';
  }

  return `${parentName ? `${parentName}` : ''}${name}.`;
};

export const getDeepObjectParentName = (name?: string | null, parentName?: string): string => {
  if (!name) {
    return '';
  }

  return parentName ? `${parentName}${name}][` : `${name}[`;
};
