import { DataSchema, IncrementalDataSchema } from '@mintlify/validation';

import { flattenObject } from '../flattenObject';

export const getNextPropertiesByType = (schema: DataSchema | IncrementalDataSchema) => {
  if (schema.type === 'object') {
    return flattenObject(schema.properties);
  }
  if (schema.type === 'array') {
    return flattenObject(schema.items);
  }
  return [];
};
