'use client';

import { generatePathToLanguageDict, generatePathToVersionDict } from '@mintlify/common';
import type { LocaleType } from '@mintlify/models';
import {
  ReactNode,
  SetStateAction,
  createContext,
  useCallback,
  useContext,
  useMemo,
  useState,
  Dispatch,
  useEffect,
  useRef,
} from 'react';

import { useCurrentPath } from '@/hooks/useCurrentPath';
import { useMarkdownCopy } from '@/hooks/useMarkdownCopy';
import { useNavigationCache } from '@/hooks/useNavigationCache';
import { useUserGroups } from '@/hooks/useUserGroups';
import { getLocale } from '@/utils/locales';
import { filterDivisions, FilterDivisionsResult } from '@/utils/nav/filterDivisions';
import { getVersionOrLanguageFromPath } from '@/utils/nav/getVersionOrLanguageFromPath';
import { DivisionMatchLevel } from '@/utils/nav/updateFirstHrefInDivision';
import { optionallyRemoveLeadingSlash } from '@/utils/paths/optionallyRemoveLeadingSlash';

import { AuthContext, DeploymentMetadataContext, DocsConfigContext } from './ConfigContext';
import { useMintlifyRouter } from './MintlifyRouterContext';

type NavigationContextType = {
  selectedVersion: string;
  setSelectedVersion: (version: string) => void;
  navIsOpen: boolean;
  setNavIsOpen: Dispatch<SetStateAction<boolean>>;
  selectedLocale: LocaleType | undefined;
  setSelectedLocale: (locale: LocaleType) => void;
  locales?: LocaleType[];
  divisions: Omit<FilterDivisionsResult, 'firstHrefInVersion' | 'firstHrefInLanguage'>;
};

export const NavigationContext = createContext<NavigationContextType>({
  selectedVersion: '',
  setSelectedVersion: () => {
    return;
  },
  navIsOpen: false,
  setNavIsOpen: () => false,
  selectedLocale: undefined,
  setSelectedLocale: () => {
    return;
  },
  divisions: {
    tabs: [],
    anchors: [],
    versions: [],
    languages: [],
    dropdowns: [],
    groupsOrPages: [],
  },
});

type NavigationContextControllerParams = {
  children?: ReactNode;
};

export const NavigationContextController = ({ children }: NavigationContextControllerParams) => {
  const { docsNavWithMetadata } = useContext(DocsConfigContext);
  const { actualSubdomain } = useContext(DeploymentMetadataContext);
  const { auth, isFetchingUserInfo } = useContext(AuthContext);
  const { navigationData, updateCache, isUpdatingCache } = useNavigationCache(docsNavWithMetadata);

  /**
   * We use to this to skip the division match when the user is navigating to the first page of a version or language
   * This will avoid layout shift when user navigate to a version or language with new href
   */
  const shouldUseDivisionMatch = useRef(true);

  const router = useMintlifyRouter();
  const currentPath = useCurrentPath();
  const userGroups = useUserGroups();
  const [navIsOpen, setNavIsOpen] = useState(false);

  // Using markdown copy within navigation context to leverage useMintlifyRouter
  useMarkdownCopy(actualSubdomain);

  const memoizedAuth = useMemo(() => auth ?? undefined, [auth]);

  const shouldUsePartialAuthNavigation = memoizedAuth?.partial;

  const { initialLocale, initialVersion } = useMemo(() => {
    if (!docsNavWithMetadata) return { initialLocale: undefined, initialVersion: undefined };

    const initial = filterDivisions({
      currentPath,
      decoratedNav: shouldUsePartialAuthNavigation ? navigationData : docsNavWithMetadata,
      userGroups: new Set(userGroups),
      currentVersion: undefined,
      currentLanguage: undefined,
      shouldUseDivisionMatch: false,
    });

    if (initial.languages.length === 0 && initial.versions.length === 0)
      return { initialLocale: undefined, initialVersion: undefined };

    const defaultVersion = initial.versions.find((version) => version.default)?.version;
    const defaultLanguage = initial.languages.find((language) => language.default)?.language;

    const nav = shouldUsePartialAuthNavigation
      ? navigationData ?? docsNavWithMetadata
      : docsNavWithMetadata;

    const initialVersion = getVersionOrLanguageFromPath<'version'>(
      'version',
      nav,
      currentPath,
      defaultVersion,
      initial.versions[0]?.name ?? ''
    );

    const initialLocale = getVersionOrLanguageFromPath<'language'>(
      'language',
      nav,
      currentPath,
      defaultLanguage,
      initial.languages[0]?.language ?? undefined
    );

    return { initialLocale: initialLocale || undefined, initialVersion: initialVersion || '' };
  }, [
    currentPath,
    docsNavWithMetadata,
    navigationData,
    userGroups,
    shouldUsePartialAuthNavigation,
  ]);

  const versionsDict = useMemo(() => {
    if (!docsNavWithMetadata) return new Map<string, string | undefined>();
    return generatePathToVersionDict(docsNavWithMetadata);
  }, [docsNavWithMetadata]);

  const languagesDict = useMemo(() => {
    if (!docsNavWithMetadata) return new Map<string, LocaleType | undefined>();
    return generatePathToLanguageDict(docsNavWithMetadata);
  }, [docsNavWithMetadata]);

  const [selectedLocale, setSelectedLocale] = useState<LocaleType | undefined>(initialLocale);
  const [selectedVersion, setSelectedVersion] = useState<string>(initialVersion ?? '');

  /**
   * once the user info is fetched, we set the selected locale and version to the initial locale and version
   * this is to avoid the case where the user info is fetched after the initial render
   * and the selected locale and version are not in the new path
   */
  useEffect(() => {
    if (isFetchingUserInfo) {
      setSelectedLocale(initialLocale);
      setSelectedVersion(initialVersion ?? '');
    }
  }, [initialLocale, initialVersion, isFetchingUserInfo]);

  const filteredDivisions = useMemo(() => {
    const path = optionallyRemoveLeadingSlash(currentPath);
    const correctVersion = versionsDict.get(path) ?? selectedVersion;
    const correctLocale = (languagesDict.get(path) as LocaleType | undefined) ?? selectedLocale;
    return filterDivisions({
      currentPath,
      decoratedNav: shouldUsePartialAuthNavigation ? navigationData : docsNavWithMetadata,
      userGroups: new Set(userGroups),
      currentVersion: correctVersion,
      currentLanguage: correctLocale,
      shouldUseDivisionMatch: shouldUseDivisionMatch.current,
    });
  }, [
    docsNavWithMetadata,
    userGroups,
    currentPath,
    navigationData,
    selectedVersion,
    selectedLocale,
    versionsDict,
    languagesDict,
    shouldUsePartialAuthNavigation,
  ]);

  const prevVersionPath = useRef(currentPath);

  useEffect(() => {
    const path = optionallyRemoveLeadingSlash(currentPath);
    const correctVersion = versionsDict.get(path) ?? selectedVersion;
    const correctLocale = (languagesDict.get(path) as LocaleType | undefined) ?? selectedLocale;

    if (
      correctVersion !== selectedVersion &&
      !!correctVersion &&
      path !== prevVersionPath.current
    ) {
      setSelectedVersion(correctVersion);
    }
    if (correctLocale !== selectedLocale && !!correctLocale && path !== prevVersionPath.current) {
      setSelectedLocale(correctLocale);
    }
    prevVersionPath.current = path;
  }, [currentPath, versionsDict, languagesDict, selectedVersion, selectedLocale]);

  useEffect(() => {
    if (shouldUsePartialAuthNavigation) {
      void updateCache(memoizedAuth);
    }
  }, [memoizedAuth, updateCache, shouldUsePartialAuthNavigation]);

  const setVersion = useCallback(
    (newVersion: string) => {
      if (newVersion === selectedVersion) return;
      setSelectedVersion(newVersion);

      const firstMatch = filteredDivisions.firstHrefInVersion.get(newVersion);
      const firstHref = firstMatch?.href;
      if (firstHref != null) {
        if (
          firstHref != currentPath &&
          (firstMatch?.matchLevel || 0) <= DivisionMatchLevel.DIVISION
        ) {
          shouldUseDivisionMatch.current = false;
          void router.push(firstHref);
        } else {
          shouldUseDivisionMatch.current = true;
          void router.push(firstHref);
        }
      }
    },
    [selectedVersion, filteredDivisions.firstHrefInVersion, currentPath, router]
  );

  const setLocale = useCallback(
    (newLocale: LocaleType) => {
      if (newLocale === selectedLocale) return;
      setSelectedLocale(newLocale);

      const firstMatch = filteredDivisions.firstHrefInLanguage.get(newLocale);
      const firstHref = firstMatch?.href;
      if (firstHref != null) {
        if (
          firstHref != currentPath &&
          (firstMatch?.matchLevel || 0) <= DivisionMatchLevel.DIVISION
        ) {
          shouldUseDivisionMatch.current = false;
          void router.push(firstHref);
        } else {
          shouldUseDivisionMatch.current = true;
          void router.push(firstHref);
        }
      }
    },
    [selectedLocale, filteredDivisions.firstHrefInLanguage, currentPath, router]
  );

  /**
   * This is used to reset the selected version and locale when the user navigates to a new path
   * This is to avoid the case where the user navigates to a new path and the selected version or locale is not in the new path
   */
  useEffect(() => {
    const shouldResetVersion =
      filteredDivisions.versions.length > 0 &&
      !filteredDivisions.versions.some((v) => v.name === selectedVersion);
    const shouldResetLocale =
      filteredDivisions.languages.length > 0 &&
      !filteredDivisions.languages.some((l) => l.language === selectedLocale);

    if (isFetchingUserInfo || isUpdatingCache) return;

    if (shouldResetVersion) {
      const defaultVersion = filteredDivisions.versions.find((v) => v.default)?.name;
      const firstVersion = filteredDivisions.versions[0]?.name;
      setVersion(defaultVersion ?? firstVersion ?? '');
    }
    if (shouldResetLocale && filteredDivisions.languages[0]?.language) {
      const defaultLocale = filteredDivisions.languages.find((l) => l.default)?.language;
      const firstLocale = filteredDivisions.languages[0]?.language;
      setLocale(defaultLocale ?? firstLocale);
    }
  }, [
    filteredDivisions.versions,
    selectedVersion,
    filteredDivisions.languages,
    selectedLocale,
    setVersion,
    setLocale,
    isUpdatingCache,
    isFetchingUserInfo,
  ]);

  useEffect(() => {
    if (!navIsOpen) return;
    function handleRouteChange() {
      setNavIsOpen(false);
    }
    router.events.on('routeChangeComplete', handleRouteChange);
    return () => {
      router.events.off('routeChangeComplete', handleRouteChange);
    };
  }, [navIsOpen, router.events]);

  return (
    <NavigationContext.Provider
      value={{
        navIsOpen,
        setNavIsOpen,
        selectedVersion,
        setSelectedVersion: setVersion,
        selectedLocale,
        setSelectedLocale: setLocale,
        locales: filteredDivisions.languages.map((l) => l.language),
        divisions: filteredDivisions,
      }}
    >
      {children}
    </NavigationContext.Provider>
  );
};

NavigationContext.displayName = 'NavigationContext';

export function useSelectedLocale() {
  const { selectedLocale } = useContext(NavigationContext);

  return useMemo(() => getLocale(selectedLocale), [selectedLocale]);
}
