'use client';

import { ChevronLeftIcon, ChevronRightIcon } from 'lucide-react';
import Link from 'next/link';

import { useSelectedLocale } from '@/contexts/NavigationContext';
import { useMDXContent } from '@/hooks/useMDXContent';
import { usePageMode } from '@/hooks/usePageMode';
import { usePrevNext } from '@/hooks/usePrevNext';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';
import { getSidebarTitle } from '@/utils/getAllMetaTags';

export const MaplePagination = () => {
  const [{ pageMetadata }] = useMDXContent();
  const { prev: previous, next } = usePrevNext();
  const { previous: previousText, next: nextText } = useSelectedLocale();
  const { isCustom } = usePageMode();

  if (pageMetadata.hideFooterPagination || isCustom) {
    return null;
  }

  if (previous == null && next == null) {
    return null;
  }

  return (
    <div
      id={Ids.Pagination}
      className="w-full rounded-xl flex bg-gray-50/80 dark:bg-white/[0.03] p-1 text-sm mb-10"
    >
      {previous != null && next != null && (
        <Link
          href={previous.href || '/'}
          className="group flex items-center justify-between pl-3 pr-6 space-x-1.5"
        >
          <ChevronLeftIcon
            className="size-3 text-gray-300 dark:text-gray-700 group-hover:text-gray-600 dark:group-hover:text-gray-400"
            strokeWidth={3}
          />
          <Label>{previousText}</Label>
        </Link>
      )}
      {previous && next == null && (
        <Link href={previous.href || '/'} className="group w-full">
          <div className="flex-1 flex items-center justify-start h-16 bg-background-light dark:bg-background-dark hover:ring-1 hover:ring-gray-200 dark:hover:ring-gray-800 rounded-xl">
            <div className="flex items-center justify-center pl-3 pr-5 space-x-1.5">
              <ChevronLeftIcon
                className="size-3 text-gray-300 dark:text-gray-700 group-hover:text-gray-600 dark:group-hover:text-gray-400"
                strokeWidth={3}
              />
              <Label>{previousText}</Label>
            </div>
            <div className="w-px h-8 bg-gray-100 dark:bg-white/5"></div>
            <div className="flex flex-col items-start justify-center px-5 min-w-0">
              <span className="font-semibold text-gray-800 dark:text-gray-200">
                {getSidebarTitle(previous)}
              </span>
              {previous.description && (
                <span className="text-left text-gray-500 dark:text-gray-400 w-full truncate">
                  {previous.description}
                </span>
              )}
            </div>
          </div>
        </Link>
      )}
      {next != null && (
        <Link href={next.href || '/'} className="group w-full">
          <div className="flex-1 flex items-center justify-end h-16 bg-background-light dark:bg-background-dark hover:ring-1 hover:ring-gray-200 dark:hover:ring-gray-800 rounded-xl">
            <div className="flex flex-col items-end justify-center px-5 min-w-0">
              <span className="font-semibold text-gray-800 dark:text-gray-200 text-right">
                {getSidebarTitle(next)}
              </span>
              {next.description && (
                <span
                  className={cn(
                    'hidden text-right text-gray-500 dark:text-gray-400 lg:block w-full truncate',
                    previous != null ? 'lg:w-72' : 'lg:w-96'
                  )}
                >
                  {next.description}
                </span>
              )}
            </div>
            <div className="w-px h-8 bg-gray-100 dark:bg-white/5"></div>
            <div className="pl-5 pr-3 text-gray-600 dark:text-gray-400 flex items-center space-x-1.5">
              <Label>{nextText}</Label>
              <ChevronRightIcon
                className="size-3 text-gray-300 dark:text-gray-700 group-hover:text-gray-600 dark:group-hover:text-gray-400"
                strokeWidth={3}
              />
            </div>
          </div>
        </Link>
      )}
    </div>
  );
};

const Label = ({ children }: { children: React.ReactNode }) => {
  return (
    <span className="text-gray-500 dark:text-gray-400 font-medium tracking-tight group-hover:text-gray-900 dark:group-hover:text-gray-100">
      {children}
    </span>
  );
};
