'use client';

import _ from 'lodash';

import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { cn } from '@/utils/cn';

import ThumbsDownIcon from './ThumbsDownIcon';
import ThumbsUpIcon from './ThumbsUpIcon';

export type FeedbackType = 'Yes' | 'No';

export default function ThumbVotingPill({
  type,
  label,
  selectedFeedback,
  setSelectedFeedback,
}: {
  type: FeedbackType;
  label: string;
  selectedFeedback: FeedbackType | null;
  setSelectedFeedback: (feedback: FeedbackType | null) => void;
}) {
  const trackThumbVote = useAnalyticsContext('thumb_vote');
  const onUserFeedback = () => {
    void trackThumbVote({ vote: type });
    setSelectedFeedback(type);
  };

  return (
    <button
      className={cn(
        'px-3.5 py-2 flex flex-row gap-3 items-center border-standard rounded-xl text-gray-600 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300 bg-white/50 dark:bg-codeblock/50 hover:border-gray-500 hover:dark:border-gray-500',
        selectedFeedback === type &&
          'border border-gray-500 dark:border-gray-500 text-gray-700 dark:text-gray-300'
      )}
      onClick={onUserFeedback}
    >
      {type === 'Yes' ? <ThumbsUpIcon /> : <ThumbsDownIcon />}
      <small className="text-sm font-normal leading-4 ">{_.startCase(label)}</small>
    </button>
  );
}
