'use client';

import { ArrowPosition } from '@/ui/GroupDropdown';
import { cn } from '@/utils/cn';

export type SidebarItemStyle = 'container' | 'card' | 'border' | 'undecorated' | 'arrow';

export const getPaddingByLevel = (level: number, arrowPosition?: ArrowPosition) => {
  // level 0 -> 0rem
  // level 1 -> 0.75rem
  // level 2 -> 1.50rem and so on.

  const levelPadding = arrowPosition === 'leading' ? 1.5 : 0.75;

  return `${1 + level * levelPadding}rem`;
};

export const getLegacySidebarStyleClassName = (
  sidebarStyle: SidebarItemStyle | undefined = 'container',
  isActive: boolean
) => {
  const defaultClassNames =
    'group mt-2 lg:mt-0 flex items-center pr-3 py-1.5 cursor-pointer focus:outline-primary dark:focus:outline-primary-light gap-x-3';

  const sidebarClassNames = {
    container: cn(
      'rounded-xl',
      isActive
        ? 'bg-primary/10 text-primary font-semibold dark:text-primary-light dark:bg-primary-light/10'
        : 'hover:bg-gray-600/5 dark:hover:bg-gray-200/5 text-gray-700 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
    ),
    card: cn(
      'lg:ml-4 border-l',
      isActive
        ? 'border-primary dark:border-primary-light bg-primary/10 text-primary font-semibold dark:text-primary-light dark:bg-primary-light/10'
        : 'border-gray-950/5 dark:border-white/10 hover:bg-gray-600/5 dark:hover:bg-gray-200/5 text-gray-700 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
    ),
    border: cn(
      'lg:ml-4 border-l',
      isActive
        ? 'border-primary dark:border-primary-light text-primary font-semibold dark:text-primary-light'
        : 'border-gray-950/5 dark:border-white/10 hover:border-gray-950/20 dark:hover:border-white/20 text-gray-700 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
    ),
    undecorated: cn(
      isActive
        ? 'border-primary dark:border-primary-light text-primary font-semibold dark:text-primary-light'
        : 'border-gray-950/5 dark:border-white/10 hover:border-gray-950/20 dark:hover:border-white/20 text-gray-700 hover:text-gray-950 dark:text-gray-400 dark:hover:text-gray-300'
    ),
    arrow: cn(
      isActive
        ? 'border-primary dark:border-primary-light text-primary font-semibold dark:text-primary-light'
        : 'border-gray-950/5 dark:border-white/10 hover:border-gray-950/20 dark:hover:border-white/20 text-gray-700 hover:text-gray-950 dark:text-gray-400 dark:hover:text-gray-300'
    ),
  };

  return cn(defaultClassNames, sidebarClassNames[sidebarStyle]);
};
