'use client';

import { ReactElement } from 'react';

import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';

import { MainContentLayout } from './Container';
import { MDXContentController } from './MDXContentController';
import { TopBar } from './TopBar/TopBar';

export const LindenLayout = ({ children }: { children: ReactElement }) => {
  const router = useMintlifyRouter();
  return (
    <>
      <TopBar />
      <MainContentLayout>
        <MDXContentController key={router.asPath}>{children}</MDXContentController>
      </MainContentLayout>
    </>
  );
};
