'use client';

import { useCallback, useEffect } from 'react';

export const useKeyboardShortcut = (
  key: string,
  callback: (() => void) | (() => Promise<void>) | undefined,
  isSingleKey?: boolean,
  excludedActiveElementId?: string[]
) => {
  const onKeypress = useCallback(
    (event: KeyboardEvent) => {
      let shouldExclude = false;
      if (excludedActiveElementId) {
        const activeElement = document.activeElement;
        shouldExclude = !!activeElement?.id && excludedActiveElementId.includes(activeElement.id);
      }

      const isShortcut = event.key === key && (isSingleKey || event.metaKey || event.ctrlKey);
      if (isShortcut && !shouldExclude) {
        event.preventDefault();
        void callback?.();
      }
    },
    [callback, key, isSingleKey, excludedActiveElementId]
  );

  useEffect(() => {
    addEventListener('keydown', onKeypress);
    return () => removeEventListener('keydown', onKeypress);
  }, [onKeypress]);
};
