'use client';

import * as DropdownMenuPrimitive from '@radix-ui/react-dropdown-menu';
import { DropdownMenuProps } from '@radix-ui/react-dropdown-menu';
import { CheckIcon } from 'lucide-react';
import * as React from 'react';

import { cn } from '@/utils/cn';

const DropdownMenu = (props: DropdownMenuProps) => (
  <DropdownMenuPrimitive.Root modal={false} {...props} />
);
DropdownMenu.displayName = DropdownMenuPrimitive.Root.displayName;

const DropdownMenuTrigger = React.forwardRef<
  React.ElementRef<typeof DropdownMenuPrimitive.Trigger>,
  React.ComponentPropsWithoutRef<typeof DropdownMenuPrimitive.Trigger>
>(({ className, children, ...props }, ref) => (
  <DropdownMenuPrimitive.Trigger
    ref={ref}
    className={cn(
      'group bg-background-light dark:bg-background-dark disabled:pointer-events-none [&>span]:line-clamp-1 rounded-lg overflow-hidden group outline-none',
      'flex items-center py-0.5 gap-1 text-sm text-gray-950/50 dark:text-white/50 group-hover:text-gray-950/70 dark:group-hover:text-white/70',
      className
    )}
    {...props}
  >
    {children}
  </DropdownMenuPrimitive.Trigger>
));
DropdownMenuTrigger.displayName = DropdownMenuPrimitive.Trigger.displayName;

const DropdownMenuPortal = DropdownMenuPrimitive.Portal;

const DropdownMenuContent = React.forwardRef<
  React.ElementRef<typeof DropdownMenuPrimitive.Content>,
  React.ComponentPropsWithoutRef<typeof DropdownMenuPrimitive.Content>
>(({ className, sideOffset = 4, ...props }, ref) => (
  <DropdownMenuPrimitive.Portal>
    <DropdownMenuPrimitive.Content
      ref={ref}
      sideOffset={sideOffset}
      collisionPadding={16}
      align="start"
      style={{ textRendering: 'geometricPrecision' }}
      className={cn(
        '!shadow-none !drop-shadow-none bg-background-light dark:bg-background-dark p-1 relative z-50 max-h-96 max-w-[var(--radix-dropdown-menu-content-available-width)] min-w-[var(--radix-dropdown-menu-trigger-width)] overflow-y-auto rounded-xl border-standard text-gray-950/70 dark:text-white/70',
        'data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95 data-[side=bottom]:slide-in-from-top-2 data-[side=left]:slide-in-from-right-2 data-[side=right]:slide-in-from-left-2 data-[side=top]:slide-in-from-bottom-2',
        className
      )}
      {...props}
    />
  </DropdownMenuPrimitive.Portal>
));
DropdownMenuContent.displayName = DropdownMenuPrimitive.Content.displayName;

const DropdownMenuItem = React.forwardRef<
  React.ElementRef<typeof DropdownMenuPrimitive.Item>,
  React.ComponentPropsWithoutRef<typeof DropdownMenuPrimitive.Item> & { isSelected?: boolean }
>(({ className, isSelected, ...props }, ref) => (
  <DropdownMenuPrimitive.Item
    ref={ref}
    style={{ fontWeight: 'normal !important' }}
    className={cn(
      'flex items-center justify-between px-2 py-1 gap-2 text-sm rounded-md group hover:bg-background-dark/[0.03] dark:hover:bg-background-light/[0.03] relative w-full cursor-pointer select-none outline-none focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:cursor-default data-[disabled]:opacity-50',
      isSelected
        ? 'text-primary dark:text-primary-light font-medium'
        : 'text-gray-950/50 dark:text-white/50',
      className
    )}
    {...props}
  >
    {props.children}
    <CheckIcon
      className={cn('size-3.5 text-primary dark:text-primary-light', !isSelected && 'opacity-0')}
      strokeWidth={2.5}
    />
  </DropdownMenuPrimitive.Item>
));
DropdownMenuItem.displayName = DropdownMenuPrimitive.Item.displayName;

export {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuPortal,
  DropdownMenuTrigger,
};
