'use client';

import { ApiPlaygroundResultType } from '@mintlify/models';
import { ApiPlaygroundInputs, Endpoint, HttpMethod } from '@mintlify/validation';
import { AxiosResponse } from 'axios';
import { useCallback, useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { MethodPillType } from '@/types/api';

import { useAnalyticsContext } from './useAnalyticsContext';
import { useApiPlaygroundInputs } from './useMDXContentController/useApiPlaygroundInputs';

export type UseSendPlaygroundRequestProps = {
  endpoint: Endpoint;
  baseUrl: string;
  contentType: string;
  setResult: (result: ApiPlaygroundResultType) => void;
  setIsFetching: (isFetching: boolean) => void;
};

export type RequestInfo = {
  method: HttpMethod;
  url: string;
  header: Record<string, unknown>;
  query: Record<string, unknown>;
  cookie: Record<string, unknown>;
  body: unknown;
};

export const useSendPlaygroundRequest = ({
  endpoint,
  baseUrl,
  contentType,
  setResult,
  setIsFetching,
}: UseSendPlaygroundRequestProps) => {
  const trackApiPlaygroundCall = useAnalyticsContext('api_playground_call');
  const { basePath } = useMintlifyRouter();
  const inputs = useApiPlaygroundInputs();
  const { docsConfig } = useContext(DocsConfigContext);

  // TODO: Move to JS pre-processing script
  const isCobo = docsConfig?.api?.mdx?.auth?.method === 'cobo';
  const disableProxy = docsConfig?.api?.playground?.proxy === false;

  return useCallback(async () => {
    setIsFetching(true);
    const axios = await import('axios').then((x) => x.default);

    const pathWithInputs = addPathParams(endpoint.path, inputs.path);

    const url = joinWithSingleSlash(addServerVariables(baseUrl, inputs.server), pathWithInputs);

    let headers = addContentType(inputs.header, contentType);
    if (isCobo) {
      if (pathWithInputs.startsWith('/v1')) {
        headers = await addCoboHeaders(endpoint, inputs, headers);
      } else {
        headers = await addCoboV2Headers(
          endpoint.method,
          pathWithInputs,
          inputs.body,
          inputs.query,
          headers
        );
      }
    }

    const request: RequestInfo = {
      method: endpoint.method,
      url,
      header: headers,
      body: inputs.body,
      cookie: inputs.cookie,
      query: inputs.query,
    };

    try {
      if (disableProxy) {
        const response = await performDirectRequest(request);
        setResult({
          error: false,
          response: {
            status: response.status,
            statusText: response.statusText,
            headers: response.headers,
            data: response.data,
          },
        });
      } else {
        if (contentType.includes('multipart/form-data')) {
          const data = await performFileRequest(request, basePath);
          setResult(data);
        } else {
          const { data } = await axios.post<unknown, AxiosResponse<ApiPlaygroundResultType>>(
            `${basePath}/api/request`,
            request
          );
          setResult(data);
        }
      }
    } catch (err) {
      setResult({
        error: true,
        errorMessage: 'unable to complete request',
      });
      console.error(err);
    }
    setIsFetching(false);

    void trackApiPlaygroundCall({ request });
  }, [
    setIsFetching,
    endpoint,
    inputs,
    baseUrl,
    contentType,
    isCobo,
    trackApiPlaygroundCall,
    disableProxy,
    setResult,
    basePath,
  ]);
};

export const addPathParams = (path: string, pathInputs: Record<string, unknown>) => {
  return Object.entries(pathInputs).reduce((path, [inputName, inputValue]) => {
    if (inputValue == undefined) return path;
    return path.replace(`{${inputName}}`, encodeURIComponent(inputValue.toString()));
  }, path);
};

export const addServerVariables = (baseUrl: string, variables: Record<string, string>): string => {
  let url = baseUrl;
  Object.entries(variables).forEach(([key, input]) => {
    if (input) url = url.replace(`{${key}}`, input);
  });
  return url;
};

export const joinWithSingleSlash = (baseUrl: string, path: string): string => {
  // remove the last slash of the baseUrl and the first slash of the path, and join with slash
  return baseUrl.replace(/\/$/, '') + '/' + path.replace(/^\//, '');
};

const addContentType = (headers: Record<string, unknown>, contentType: string) => {
  if (Object.keys(headers).some((name) => name.toLowerCase() === 'content-type')) {
    return headers;
  }

  return {
    ...headers,
    ['content-type']: contentType,
  };
};

const addCoboHeaders = async (
  endpoint: Endpoint,
  inputs: ApiPlaygroundInputs,
  headers: Record<string, unknown>
) => {
  const secretKey = headers['API-SECRET'];
  const { getCoboV1CustodyGeneratedKeys } = await import('../utils/api/cobo/v1');

  const result = {
    ...headers,
    ...getCoboV1CustodyGeneratedKeys(
      endpoint.method,
      endpoint.path,
      secretKey,
      (inputs.body || inputs.query) as Record<string, string>
    ),
  } as Record<string, unknown>;
  delete result['API-SECRET'];
  return result;
};

const addCoboV2Headers = async (
  method: MethodPillType,
  url: string,
  body: ApiPlaygroundInputs['body'],
  query: ApiPlaygroundInputs['query'],
  headers: Record<string, unknown>
) => {
  const { getCoboV2CustodyGeneratedKeys } = await import('../utils/api/cobo/v2');
  const secretKey = headers['BIZ-API-KEY'];

  if (typeof secretKey === 'string') {
    const result = getCoboV2CustodyGeneratedKeys(secretKey, method, url, body, query);
    return result;
  }
  return {};
};

const performDirectRequest = async (request: RequestInfo) => {
  const axios = await import('axios').then((it) => it.default);

  const contentType = Object.keys(request.header).find(
    (key) => key.toLowerCase() === 'content-type'
  );
  let data = request.body;
  let headers = request.header as Record<string, string>;
  if (contentType?.includes('multipart/form-data')) {
    const { data: formData, headers: formHeaders } = handleFormData(request);
    data = formData;
    headers = formHeaders;
  }

  return axios({
    url: request.url,
    method: request.method,
    params: request.query,
    data,
    headers,
    // prevent axios from adding [] after array query params
    paramsSerializer: {
      indexes: null,
    },
    // prevent axios from erroring on bad status codes
    validateStatus: () => true,
    // prevent axios from parsing json, losing whitespace information, and using default utf-8 encoding
    // we can't use `responseEncoding` as it's ignored client-side
    transformResponse: (data) => {
      return Buffer.from(data).toString('base64');
    },
  });
};

const performFileRequest = async (request: RequestInfo, basePath: string) => {
  const axios = await import('axios').then((it) => it.default);
  const formData = new FormData();
  formData.append(
    'requestInfo',
    JSON.stringify({
      method: request.method,
      url: request.url,
      header: request.header,
      query: request.query,
      cookie: request.cookie,
    })
  );
  if (typeof request.body === 'object' && request.body !== null) {
    Object.entries(request.body).forEach(([key, value]) => {
      if (value instanceof File) {
        formData.append(key, value);
      } else if (value !== undefined && value !== null) {
        formData.append(key, typeof value === 'object' ? JSON.stringify(value) : value.toString());
      }
    });
  }

  const { data } = await axios.post<unknown, AxiosResponse<ApiPlaygroundResultType>>(
    `${basePath}/api/fileRequest`,
    formData
  );
  return data;
};

const handleFormData = (request: RequestInfo) => {
  let data = request.body;
  const headers = request.header as Record<string, string>;

  if (headers['content-type']?.includes('multipart/form-data')) {
    const formData = new FormData();
    if (typeof data === 'object' && data !== null) {
      Object.entries(data).forEach(([key, value]) => {
        if (value instanceof File) {
          formData.append(key, value);
        } else if (value !== undefined && value !== null) {
          formData.append(
            key,
            typeof value === 'object' ? JSON.stringify(value) : value.toString()
          );
        }
      });
      data = formData;
      delete headers['content-type'];
    }
  }
  return { data, headers };
};
