'use client';

import { ReactNode, useState } from 'react';

import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

type ExpandableSectionContainerParams = {
  name: string;
  children?: ReactNode;
  rightElement?: ReactNode;
  className?: string;
  defaultExpanded?: boolean;
};

export const ExpandableSectionContainer = ({
  name,
  children,
  rightElement,
  className,
  defaultExpanded = true,
}: ExpandableSectionContainerParams) => {
  const [expanded, setExpanded] = useState(defaultExpanded);

  return (
    <div
      className={cn(
        className,
        'rounded-2xl border border-zinc-200 dark:border-zinc-800 rounded-2xl border-solid',
        expanded && 'pb-1'
      )}
    >
      <button
        aria-label="expand input section"
        onClick={() => setExpanded((prev) => !prev)}
        className={cn(
          'flex w-full pt-3.5 pb-3 px-3.5 items-center justify-between cursor-pointer rounded-t-2xl hover:bg-gray-50 dark:hover:bg-white/5 ',
          expanded ? 'rounded-t-2xl' : 'rounded-2xl'
        )}
      >
        <div className="flex items-center gap-x-1.5">
          <Icon
            icon={'angle-right'}
            iconType="solid"
            className={cn(
              'h-3 w-3 bg-gray-400 dark:bg-gray-500 transition-transform',
              expanded && 'rotate-90'
            )}
          />
          <div className="flex-1 flex items-center space-x-2">
            <div className="text-sm font-medium text-gray-800 dark:text-gray-200 leading-6">
              {name}
            </div>
          </div>
        </div>
        {rightElement}
      </button>
      <div className={cn('flex-1 rounded-b-xl w-full', expanded && children ? '' : 'hidden')}>
        <div className="h-px mx-3.5 bg-[#f4f4f5] dark:bg-[#18181b]"></div>
        {children}
      </div>
    </div>
  );
};
