'use client';

import { FileSchema } from '@mintlify/validation';
import { useRef } from 'react';

import { cn } from '@/utils/cn';

import { InputContainer } from '../../components/InputContainer';
import { GenericInput } from './RecursiveInput';

export const FileInput: GenericInput<FileSchema> = ({
  name,
  schema,
  typeDropdown,
  value,
  setValue,
  clearValue,
  parentNames,
  arrayIndex,
}) => {
  const hiddenInputRef = useRef<HTMLInputElement>(null);

  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={parentNames}
      arrayIndex={arrayIndex}
    >
      <button
        aria-label="file upload button"
        className={cn(
          'text-left flex text-playground-input',
          value ? '!text-gray-900 dark:!text-gray-100' : '!text-gray-400 dark:!text-white/30'
        )}
        onClick={() => hiddenInputRef.current?.click()}
      >
        {value?.name ?? schema.placeholder ?? 'Drop a file here or click to upload'}
      </button>
      {/* HACK: file inputs are notoriously hard to style, so hide the native input and click it programmatically */}
      <input
        type="file"
        onChange={(event) => setValue(event.target.files?.[0])}
        ref={hiddenInputRef}
        className="hidden"
      />
    </InputContainer>
  );
};
