'use client';

import { Combobox, Dialog, Transition } from '@headlessui/react';
import { generatePathToLanguageDict, generatePathToVersionDict } from '@mintlify/common';
import { LocaleType } from '@mintlify/models';
import { debounce, uniqueId } from 'lodash';
import { SearchIcon } from 'lucide-react';
import {
  Fragment,
  ReactNode,
  createContext,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';

import { zIndex } from '@/constants/zIndex';
import { DeploymentMetadataContext, DocsConfigContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { NavigationContext, useSelectedLocale } from '@/contexts/NavigationContext';
import { NEXT_PUBLIC } from '@/env';
import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { useKeyboardShortcut } from '@/hooks/useKeyboardShortcut';
import { useSearch } from '@/hooks/useSearch';
import { Ids } from '@/lib/local/identifiers';
import { SparklesIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';
import { getDuplicates } from '@/utils/getDuplicates';
import { SearchChunk } from '@/utils/search/trieve';

import { SearchHit } from './SearchHit';

export const DEBOUNCE_TIME_IN_MS = 20;
const CHAT_ASSISTANT_OPTION = '_mintlify-ask-ai-assistant';

export type ChatAssistantMessage = {
  message: string;
  key: string;
  response?: string;
  isLoading: boolean;
  isGeneratingError: boolean;
  isRateLimited: boolean;
};

export const ChatAssistantContext = createContext({
  isChatSheetOpen: false,
  shouldFocusChatSheet: false,
  onChatSheetToggle: () => {},
  setShouldFocusChatSheet: (_: boolean) => {},
  query: '',
  setQuery: (_: string) => {},
  messages: [] as ChatAssistantMessage[],
  setMessages: (
    _: ChatAssistantMessage[] | ((prevMessages: ChatAssistantMessage[]) => ChatAssistantMessage[])
  ) => {},
});

export const SearchContext = createContext({
  onSearchModalOpen: () => {
    return;
  },
  hasChatPermissions: false,
  searchPrompt: 'Search...',
});

SearchContext.displayName = 'SearchContext';

export function SearchProvider({
  subdomain,
  hasChatPermissions = false,
  children,
}: {
  subdomain?: string;
  hasChatPermissions?: boolean;
  children: ReactNode;
}) {
  const router = useMintlifyRouter();
  const { docsConfig: config, docsNavWithMetadata } = useContext(DocsConfigContext);
  const { trieve } = useContext(DeploymentMetadataContext);
  const { search, askAIAssistant, canYouTellMeAbout } = useSelectedLocale();
  const { selectedVersion, selectedLocale } = useContext(NavigationContext);
  const searchPrompt = config?.search?.prompt ?? search;
  const pathToVersion = useMemo(() => {
    if (!docsNavWithMetadata) return new Map();
    return generatePathToVersionDict(docsNavWithMetadata);
  }, [docsNavWithMetadata]);
  const pathToLanguage = useMemo(() => {
    if (!docsNavWithMetadata) return new Map();
    return generatePathToLanguageDict(docsNavWithMetadata);
  }, [docsNavWithMetadata]);
  const [isSearchModalOpen, setIsSearchModalOpen] = useState(false);
  const [isChatSheetOpen, setIsChatSheetOpen] = useState(false);
  const [shouldFocusChatSheet, setShouldFocusChatSheet] = useState(false);
  const [query, setQuery] = useState<string>('');
  const [hits, setHits] = useState<SearchChunk[]>([]);
  const [duplicatedHeaders, setDuplicatedHeaders] = useState<Set<string>>();
  const trackSearchResultClick = useAnalyticsContext('search_result_click');
  const trackSearchClose = useAnalyticsContext('search_close');
  const [chatAssistantQuery, setChatAssistantQuery] = useState<string>('');
  const [chatAssistantMessages, setChatAssistantMessages] = useState<ChatAssistantMessage[]>([]);

  const { search: fetchSearchResults } = useSearch();

  const isCli = NEXT_PUBLIC.ENV === 'cli';

  const onSearchModalOpen = useCallback(() => setIsSearchModalOpen(true), []);
  const onSearchModalClose = useCallback(() => setIsSearchModalOpen(false), []);
  const toggleSearchModalOpen = useCallback(() => setIsSearchModalOpen((val) => !val), []);

  const toggleChatSheetOpen = useCallback(() => {
    if (hasChatPermissions) {
      // If the chat sheet is open for the first time, allow focus
      setShouldFocusChatSheet(!isChatSheetOpen);
      setIsChatSheetOpen((val) => !val);
    }
  }, [hasChatPermissions, isChatSheetOpen]);

  const [isContentScrolled, setIsContentScrolled] = useState(false);

  useKeyboardShortcut('k', toggleSearchModalOpen);
  useKeyboardShortcut('i', toggleChatSheetOpen);
  useKeyboardShortcut('?', toggleChatSheetOpen, true, [
    Ids.ChatAssistantTextArea,
    Ids.SearchInput,
    Ids.APIPlaygroundInput,
  ]);

  useEffect(() => {
    if (!isSearchModalOpen) {
      setIsContentScrolled(false);
    }
  }, [isSearchModalOpen]);

  const onSearch = useMemo(
    () =>
      debounce(async (query: string) => {
        setQuery(query);
        // Search not available for local previews
        if (!query || subdomain == null) {
          setHits([]);
          return;
        }
        if (trieve?.datasetId) {
          const results = await fetchSearchResults(query);
          const hitsToUse = filterHitsToCurrentVersionAndLanguage(
            results,
            selectedVersion,
            selectedLocale,
            pathToVersion,
            pathToLanguage
          );
          setHits(hitsToUse);
          setDuplicatedHeaders(getDuplicates(hitsToUse.map((hit) => hit.header)));
          return;
        }
      }, DEBOUNCE_TIME_IN_MS),
    [
      subdomain,
      trieve?.datasetId,
      fetchSearchResults,
      selectedVersion,
      selectedLocale,
      pathToVersion,
      pathToLanguage,
    ]
  );

  const generateChatAssistantMessage = useCallback(
    (query: string) => {
      return `${canYouTellMeAbout} ${query}?`;
    },
    [canYouTellMeAbout]
  );

  const onSelectOption = useCallback(
    async (hit: SearchChunk | string, query: string) => {
      if (hit === CHAT_ASSISTANT_OPTION) {
        onSearchModalClose();
        setIsChatSheetOpen(true);
        setShouldFocusChatSheet(true);

        const newMessage: ChatAssistantMessage = {
          message: generateChatAssistantMessage(query),
          key: uniqueId(),
          isLoading: true,
          isGeneratingError: false,
          isRateLimited: false,
        };
        setChatAssistantMessages((prevMessages) => [newMessage, ...prevMessages]);
      } else {
        const searchHit = hit as SearchChunk;
        void trackSearchResultClick({
          query,
          path: searchHit.link,
          section: searchHit.header,
        });

        await router.push(searchHit.link);

        onSearchModalClose();
        trackSearchClose({
          query,
          numHits: hits.length,
        }).catch(console.error);
      }

      setQuery('');
      setHits([]);
    },
    [
      onSearchModalClose,
      router,
      trackSearchClose,
      generateChatAssistantMessage,
      trackSearchResultClick,
      hits.length,
    ]
  );

  return (
    <>
      <ChatAssistantContext.Provider
        value={{
          isChatSheetOpen,
          shouldFocusChatSheet,
          onChatSheetToggle: toggleChatSheetOpen,
          setShouldFocusChatSheet,
          query: chatAssistantQuery,
          setQuery: setChatAssistantQuery,
          messages: chatAssistantMessages,
          setMessages: setChatAssistantMessages,
        }}
      >
        <SearchContext.Provider
          value={{
            onSearchModalOpen,
            hasChatPermissions,
            searchPrompt,
          }}
        >
          {children}
        </SearchContext.Provider>
      </ChatAssistantContext.Provider>

      <Transition.Root
        show={isSearchModalOpen}
        as={Fragment}
        afterLeave={() => {
          setQuery('');
          setHits([]);
        }}
        appear
      >
        <Dialog
          as="div"
          className={cn(zIndex.Popup, 'relative')}
          onClose={() => {
            onSearchModalClose();
          }}
        >
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-100"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in duration-50"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-background-dark/20 transition-opacity backdrop-blur-sm" />
          </Transition.Child>
          <div className="fixed inset-0 w-screen h-screen z-10 overflow-y-auto p-4 sm:p-6 md:p-12">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-100"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-50"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <Dialog.Panel className="flex flex-col mx-auto max-w-[640px] transform overflow-hidden border border-gray-200 dark:border-white/10 bg-background-light dark:bg-background-dark shadow-search rounded-search">
                <Combobox
                  onChange={(option) => onSelectOption(option as unknown as SearchChunk, query)}
                  value={query}
                >
                  <div
                    className={cn(
                      'p-1.5 relative z-10 border-b border-transparent h-14 transition',
                      isContentScrolled && 'border-gray-200 dark:border-white/10'
                    )}
                  >
                    {isCli ? (
                      <div className="flex items-center rounded-xl border border-gray-200 dark:border-white/10 bg-background-light dark:bg-background-dark h-full w-full outline-none text-gray-950/50 dark:text-white/50 tracking-tight pl-12 pr-14">
                        <span>Not available on local preview</span>
                      </div>
                    ) : (
                      <Combobox.Input
                        id={Ids.SearchInput}
                        className={cn(
                          'peer rounded-xl border border-gray-200 dark:border-white/10 bg-background-light dark:bg-background-dark h-full w-full outline-none text-gray-950 dark:text-white placeholder:text-gray-400 placeholder:dark:text-white/50 tracking-tight pl-11 pr-14 focus:border-slate-950 dark:focus:border-white transition',
                          !isContentScrolled && query && 'shadow-search'
                        )}
                        autoFocus
                        placeholder={searchPrompt}
                        onChange={(event) => {
                          void onSearch(event.target.value);
                        }}
                        autoComplete="off"
                        onKeyDown={(e) => {
                          if (hits.length > 0 || (query && hasChatPermissions)) {
                            if (e.key === 'Tab') {
                              e.preventDefault();
                              // reset cursor position to end of input
                              e.currentTarget.setSelectionRange(
                                e.currentTarget.value.length,
                                e.currentTarget.value.length
                              );
                            }
                          }
                          if (e.key === 'Escape') {
                            onSearchModalClose();
                            trackSearchClose({
                              query,
                              numHits: hits.length,
                            }).catch(console.error);
                          }
                        }}
                      />
                    )}
                    <SearchIcon
                      size={18}
                      className="absolute left-5 top-1/2 -translate-y-1/2 text-gray-950 dark:text-white opacity-50 peer-focus:opacity-100 peer-disabled:opacity-30"
                    />
                    {query && (
                      <div className="top-1/2 -translate-y-1/2 absolute right-6 flex items-center justify-center rounded-md gap-1 px-1.5 py-1.5 bg-gray-950/5 dark:bg-white/5 text-zinc-950/70 dark:text-white/70 font-medium text-xs leading-[9px]">
                        ESC
                      </div>
                    )}
                  </div>
                  {query && (
                    <Combobox.Options
                      // prevent search results being cleared after chat is opened
                      static
                      className="max-h-[calc(100vh-10rem)] overflow-y-auto mx-1.5"
                      onScroll={(e) => {
                        if (e.currentTarget.scrollTop) setIsContentScrolled(true);
                        else setIsContentScrolled(false);
                      }}
                    >
                      {hits.length > 0 && (
                        <>
                          {hits.map((hit) => (
                            <Combobox.Option key={hit.id} value={hit} className="last:mb-2">
                              {({ active }) => (
                                <SearchHit
                                  isActive={active}
                                  header={hit.header}
                                  description={hit.content}
                                  metadata={hit.metadata}
                                  duplicatedHeaders={duplicatedHeaders}
                                />
                              )}
                            </Combobox.Option>
                          ))}
                        </>
                      )}

                      {hasChatPermissions && (
                        <>
                          <div className="px-2.5 py-2 text-gray-500 text-sm truncate w-full">
                            {askAIAssistant}
                          </div>

                          <Combobox.Option
                            key={CHAT_ASSISTANT_OPTION}
                            value={CHAT_ASSISTANT_OPTION}
                            className="last:mb-2"
                          >
                            {({ active }) => (
                              <div
                                className={cn(
                                  'flex items-center gap-2 px-2.5 py-2 w-full cursor-pointer rounded-xl',
                                  active && 'bg-[#F7F7F8] dark:bg-white/5'
                                )}
                              >
                                <SparklesIcon className="text-primary dark:text-primary-dark h-4 w-4 shrink-0" />{' '}
                                <span className="font-medium text-gray-800 dark:text-gray-200 text-sm truncate">
                                  {generateChatAssistantMessage(query)}
                                </span>
                              </div>
                            )}
                          </Combobox.Option>
                        </>
                      )}
                    </Combobox.Options>
                  )}
                </Combobox>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition.Root>
    </>
  );
}

function filterHitsToCurrentVersionAndLanguage(
  hits: SearchChunk[],
  selectedVersion: string,
  selectedLanguage: LocaleType | undefined,
  pathToVersion: Map<string, string | undefined>,
  pathToLanguage: Map<string, string | undefined>
): SearchChunk[] {
  return hits.filter((hit) => {
    const version = pathToVersion.get(hit.page);
    const language = pathToLanguage.get(hit.page);
    const matchesVersion = version ? version === selectedVersion : true;
    const matchesLanguage = language ? language === selectedLanguage : true;

    return matchesVersion && matchesLanguage;
  });
}

export * from './SearchButton';
export * from './DesktopSearchEntry';
