'use client';

import { IconType } from '@mintlify/models';

import { Classes } from '@/lib/local/selectors';
import { ComponentIcon } from '@/ui/Icon';
import { cn } from '@/utils/cn';

export function Icon({
  icon,
  iconType,
  color,
  size,
}: {
  icon: string;
  iconType?: IconType;
  color?: string;
  size?: number;
}) {
  return (
    <ComponentIcon
      icon={icon}
      iconType={iconType}
      color={color}
      style={{
        width: size || 16,
        height: size || 16,
        display: 'inline-block',
        verticalAlign: 'text-bottom',
      }}
      className={cn(Classes.Icon, !color && 'bg-primary dark:bg-primary-light', 'inline')}
    />
  );
}
