import { isPage } from '@mintlify/common';
import { locales, LocaleType } from '@mintlify/models';
import {
  DecoratedNavigationConfig,
  DecoratedPageOrGroupConfig,
  DecoratedGroupConfig,
  divisions,
  DecoratedVersionConfig,
  DecoratedLanguageConfig,
} from '@mintlify/validation';

import { isEqualIgnoringLeadingSlash } from '../paths/isEqualIgnoringLeadingSlash';
import { NavigationDivisions } from './filterDivisions';

export enum DivisionMatchLevel {
  EXACT,
  PATH,
  GROUP,
  DIVISION,
  NONE,
}

export type DivisionMatch = {
  href: string;
  matchLevel: DivisionMatchLevel;
};

export function updateFirstHrefInDivision({
  type,
  currentPath,
  entry,
  parentGroups,
  nearestDivisionValue,
  isActiveGroup,
  inActiveDivision,
  currentDivisionValue,
  allDivisionValues,
  firstHrefInDivision,
  navigationDivisions,
}: {
  type: 'version' | 'language';
  currentPath: string;
  entry: DecoratedNavigationConfig | DecoratedPageOrGroupConfig;
  parentGroups: DecoratedGroupConfig[];
  nearestDivisionValue: string | undefined;
  isActiveGroup: boolean;
  inActiveDivision: boolean;
  currentDivisionValue: string | undefined;
  allDivisionValues: string[] | undefined;
  firstHrefInDivision: Map<string | undefined, DivisionMatch>;
  navigationDivisions: NavigationDivisions;
}) {
  if (typeof entry !== 'object') return;

  if (isPage(entry)) {
    maybeUpdateFirstHrefInDivision(
      type,
      entry.href,
      nearestDivisionValue,
      isActiveGroup,
      inActiveDivision,
      currentPath,
      currentDivisionValue,
      allDivisionValues,
      firstHrefInDivision
    );
  }

  if ('pages' in entry) {
    const isActivePages =
      entry.pages.some(
        (subEntry: DecoratedPageOrGroupConfig) =>
          isPage(subEntry) && isEqualIgnoringLeadingSlash(subEntry.href, currentPath)
      ) && currentDivisionValue === nearestDivisionValue;
    for (const page of entry.pages) {
      if (typeof page === 'object') {
        updateFirstHrefInDivision({
          type,
          currentPath,
          entry: page,
          parentGroups: parentGroups.length ? parentGroups : entry.pages,
          nearestDivisionValue,
          isActiveGroup: isActivePages,
          inActiveDivision,
          currentDivisionValue,
          allDivisionValues,
          firstHrefInDivision,
          navigationDivisions,
        });
      }
    }
  }

  if ('groups' in entry) {
    for (const group of entry.groups) {
      if (typeof group !== 'object') continue;

      const isActiveGroup =
        checkActiveGroup(group, currentPath) && currentDivisionValue === nearestDivisionValue;

      updateFirstHrefInDivision({
        type,
        currentPath,
        entry: group,
        parentGroups: entry.groups,
        nearestDivisionValue,
        isActiveGroup,
        inActiveDivision,
        currentDivisionValue,
        allDivisionValues,
        firstHrefInDivision,
        navigationDivisions,
      });
    }
  }

  for (const key of [...divisions, 'groups']) {
    if (key in entry) {
      const subDivisions = entry[key as keyof typeof entry] as DecoratedNavigationConfig[];
      if (Array.isArray(subDivisions)) {
        let inActiveDivision = false;

        if (allDivisionValues && allDivisionValues.length) {
          if (type === 'version' && key === 'versions') {
            inActiveDivision = subDivisions.every((subDivision) =>
              allDivisionValues.includes((subDivision as DecoratedVersionConfig).version)
            );
          } else if (type === 'language' && key === 'languages') {
            inActiveDivision = subDivisions.every((subDivision) =>
              allDivisionValues.includes((subDivision as DecoratedLanguageConfig).language)
            );
          }
        }

        for (const subDivision of subDivisions) {
          let divisionValue;
          if (type === 'version' && key === 'versions') {
            divisionValue =
              'version' in subDivision
                ? (subDivision as DecoratedVersionConfig).version
                : undefined;
          } else if (type === 'language' && key === 'languages') {
            divisionValue =
              'language' in subDivision
                ? (subDivision as DecoratedLanguageConfig).language
                : undefined;
          }

          updateFirstHrefInDivision({
            type,
            currentPath,
            entry: subDivision,
            parentGroups: key === 'groups' ? entry.groups : parentGroups,
            nearestDivisionValue: divisionValue ?? nearestDivisionValue,
            isActiveGroup,
            inActiveDivision: inActiveDivision,
            currentDivisionValue,
            allDivisionValues,
            firstHrefInDivision,
            navigationDivisions,
          });
        }
      }
    }
  }
}

/**
 * This function attempts to maintain some context when switching versions by following these rules:
 * 1. stay on the active page, if it is in the new version (unversioned)
 * 2. navigate to the first page that is in the new version AND in the active group, if one exists
 * 3. navigate to the first page that is in the new version
 *
 * @param href The href of the page being processed
 * @param finalVersion The version of the page being processed
 * @param inActiveGroup Whether the page being processed is in the same group as the active page
 * @param currentPath The path of the active page
 * @param currentVersion The version of the active page
 * @param versionStrings If defined, the array of version strings
 * @param firstHrefInDivision A map from version strings to the href that should be navigated to when switching to that version
 */
function maybeUpdateFirstHrefInDivision(
  type: 'version' | 'language',
  href: string,
  finalVersion: string | undefined,
  inActiveGroup: boolean,
  inActiveDivision: boolean,
  currentPath: string,
  currentVersion: string | undefined,
  versionStrings: string[] | undefined,
  firstHrefInDivision: Map<string | undefined, DivisionMatch>
) {
  const versionsToUpdate = finalVersion !== undefined ? [finalVersion] : versionStrings ?? [];
  // now that we support index.mdx, the href might empty string
  // router.push cannot take empty string as url
  href = href || '/';

  // always use the current page if the current page is unversioned
  if (href === currentPath) {
    for (const version of versionsToUpdate) {
      firstHrefInDivision.set(version, { href, matchLevel: DivisionMatchLevel.EXACT });
    }
    return;
  }

  // if currentPath and href differ by exactly one path segment, and it's not the last,
  // we make the BOLD assumption that href is a different version of the same page
  if (currentVersion !== undefined && finalVersion !== undefined) {
    const existingRef = firstHrefInDivision.get(finalVersion);
    if (
      (existingRef === undefined || DivisionMatchLevel.PATH < existingRef.matchLevel) &&
      pathsDifferByOneSegment(href, currentPath, type)
    ) {
      firstHrefInDivision.set(finalVersion, { href, matchLevel: DivisionMatchLevel.PATH });
    }
  }

  if (inActiveGroup) {
    for (const version of versionsToUpdate) {
      const existingHref = firstHrefInDivision.get(version);
      if (existingHref === undefined || DivisionMatchLevel.GROUP < existingHref.matchLevel) {
        firstHrefInDivision.set(version, { href, matchLevel: DivisionMatchLevel.GROUP });
      }
    }
    return;
  }

  if (inActiveDivision) {
    for (const version of versionsToUpdate) {
      const existingHref = firstHrefInDivision.get(version);
      if (existingHref === undefined || DivisionMatchLevel.DIVISION < existingHref.matchLevel) {
        firstHrefInDivision.set(version, { href, matchLevel: DivisionMatchLevel.DIVISION });
      }
    }
    return;
  }

  for (const version of versionsToUpdate) {
    if (!firstHrefInDivision.has(version)) {
      firstHrefInDivision.set(version, { href, matchLevel: DivisionMatchLevel.NONE });
    }
  }
}

function pathsDifferByOneSegment(a: string, b: string, type: 'version' | 'language') {
  const aSegments = a.split('/').filter(Boolean);
  const bSegments = b.split('/').filter(Boolean);

  // ignore locales in the segments
  // for example, if path a is /introduction and b is /es/introduction, we should treat them as the most relevant page
  if (type === 'language' && aSegments[0] && locales.includes(aSegments[0] as LocaleType))
    aSegments.shift();
  if (type === 'language' && bSegments[0] && locales.includes(bSegments[0] as LocaleType))
    bSegments.shift();

  if (
    aSegments.length !== bSegments.length ||
    aSegments.length < 2 ||
    aSegments.at(-1) !== bSegments.at(-1)
  )
    return false;

  let mismatchFound = false;
  for (let i = 0; i < aSegments.length - 1; i++) {
    if (aSegments[i] !== bSegments[i]) {
      if (mismatchFound) return false;
      mismatchFound = true;
    }
  }
  return true;
}

function checkActiveGroup(group: DecoratedGroupConfig, currentPath: string): boolean {
  if (isPage(group)) {
    return isEqualIgnoringLeadingSlash(group.href, currentPath);
  }
  if ('pages' in group) {
    for (const page of group.pages) {
      const isActivePage = checkActiveGroup(page, currentPath);
      if (isActivePage) {
        return true;
      }
    }
  }
  return false;
}
