'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

export const ResponsePlaceholder = ({ message, icon }: { message: string; icon: string }) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div
      className={cn(
        codeBlockTheme === 'system' ? 'bg-response-body-auto' : 'bg-response-body-dark',
        'rounded-b-xl overflow-auto'
      )}
    >
      <div className="pb-8 pt-10 px-6 space-y-4 flex flex-col items-center">
        <Icon icon={icon} className="h-8 w-8 bg-gray-300 dark:bg-white/20" />
        <div className="text-sm text-center">{message}</div>
      </div>
    </div>
  );
};
