'use client';

import { OAuth } from '@mintlify/validation';
import cookie from 'js-cookie';
import pkceChallenge from 'pkce-challenge';

import { MINUTE_IN_MILLISECONDS, USERAUTH_OAUTH_CALLBACK_PATH } from '@/constants';

export const CODE_VERIFIER_COOKIE = 'mintlify-pkce-code-verifier';

export default async function initiatePkceAndRedirect(userAuth: OAuth, basePath: string) {
  const { code_challenge: codeChallenge, code_verifier: codeVerifier } = await pkceChallenge();

  const url = new URL(userAuth.authorizationUrl);
  url.searchParams.append('response_type', 'code');
  url.searchParams.append('client_id', userAuth.clientId);
  url.searchParams.append(
    'redirect_uri',
    window.location.origin + basePath + USERAUTH_OAUTH_CALLBACK_PATH
  );
  if (userAuth.scopes.length) {
    url.searchParams.append('scope', userAuth.scopes.join(' '));
  }
  url.searchParams.append('code_challenge', codeChallenge);
  url.searchParams.append('code_challenge_method', 'S256');

  const tenMinutesFromNow = new Date(Date.now() + 10 * MINUTE_IN_MILLISECONDS);
  cookie.set(CODE_VERIFIER_COOKIE, codeVerifier, {
    secure: true,
    expires: tenMinutesFromNow,
  });

  window.location.href = url.href;
}
