import { useContext } from 'react';

import { ApiReferenceContext } from '@/contexts/ConfigContext';

export const useSchema = () => {
  const { apiReferenceData } = useContext(ApiReferenceContext);
  return apiReferenceData.schemaData?.schemaArray;
};

export const useHasSchema = () => {
  const schema = useSchema();
  return !!schema;
};
