'use client';

import { useMemo, useState } from 'react';

import { CopyIconButton } from '@/components/Code/CodeBlock';

import { ResponseBodyData } from '.';

type CopyFileToClipboardButtonParams = {
  data: ResponseBodyData;
};

export const CopyFileToClipboardButton = ({ data }: CopyFileToClipboardButtonParams) => {
  const [isCopiedActive, setIsCopiedActive] = useState(false);
  const copyToClipboard = useCopyToClipboard(
    data,
    () => {
      setIsCopiedActive(true);
      setTimeout(() => {
        setIsCopiedActive(false);
      }, 2000);
    },
    (e) => console.error(`unable to copy response to clipboard: ${e}`)
  );

  return copyToClipboard ? (
    <CopyIconButton onClick={copyToClipboard} isCopiedActive={isCopiedActive} />
  ) : null;
};

const SUPPORTED_CONTENT_TYPES = ['image/png', 'text/plain'];

const useCopyToClipboard = (
  data: ResponseBodyData,
  onSuccess?: () => void,
  onFail?: (e: unknown) => void
) => {
  return useMemo(() => {
    // image/png, text/plain, and text/html are the only filetypes supported in the chromium
    // clipboard - however, text/html crashes chrome for some reason
    if (data.contentType && SUPPORTED_CONTENT_TYPES.includes(data.contentType)) {
      try {
        const clipboardItem = new ClipboardItem({ [data.contentType]: data.blob });
        return () => navigator.clipboard.write([clipboardItem]).then(onSuccess, onFail);
      } catch (e) {
        console.error(`unable to create clipboard item: ${e}`);
      }
    }

    // not an else-if so that failed clipboard items might succeed here
    if (data.type === 'other') {
      return () => navigator.clipboard.writeText(data.content).then(onSuccess, onFail);
    }

    return undefined;
  }, [data, onSuccess, onFail]);
};
