'use client';

import { BooleanSchema, NumberEnumSchema, StringEnumSchema } from '@mintlify/validation';
import clsx from 'clsx';
import { ChevronDownIcon } from 'lucide-react';

import { InputContainer } from '../../components/InputContainer';
import { GenericInput } from './RecursiveInput';

export const SelectInput: GenericInput<NumberEnumSchema | StringEnumSchema | BooleanSchema> = ({
  name,
  schema,
  typeDropdown,
  value,
  setValue,
  clearValue,
  parentNames,
  arrayIndex,
}) => {
  const options = schema.type === 'boolean' ? [true, false] : schema.enum;

  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={parentNames}
      arrayIndex={arrayIndex}
    >
      <div className="relative">
        <select
          className={clsx(
            'relative text-playground-input w-full text-left flex-1 outline-none cursor-pointer bg-transparent',
            value !== undefined
              ? '!text-gray-900 dark:!text-gray-100'
              : '!text-gray-400 dark:!text-white/30'
          )}
          value={value?.toString() ?? ''}
          onChange={(e) => {
            if (schema.type === 'boolean') {
              setValue(e.target.value === 'true');
            } else {
              setValue(e.target.value);
            }
          }}
        >
          <option value="" disabled>
            {schema.placeholder ?? `select ${name || 'option'}`}
          </option>
          {options.map((opt) => (
            <option key={opt.toString()} value={opt.toString()}>
              {opt.toString()}
            </option>
          ))}
        </select>
        <ChevronDownIcon className="absolute right-2 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400 dark:text-white/30 pointer-events-none" />
      </div>
    </InputContainer>
  );
};
