import { divisions, GroupConfig, NavigationConfig } from '@mintlify/validation';

import { isNavigationGroup } from '../nav/isNavigation';
import { isEqualIgnoringLeadingSlash } from './isEqualIgnoringLeadingSlash';

export function getSectionTitle(currentPath: string, nav?: NavigationConfig): string {
  if (!nav) return '';

  if ('groups' in nav) {
    for (const group of nav.groups) {
      const title = findSectionTitleRecursively(group, currentPath);
      if (title) {
        return title;
      }
    }
  }

  for (const key of divisions) {
    if (key in nav) {
      const items = nav[key as keyof typeof nav] as NavigationConfig[];
      for (const item of items) {
        const title = getSectionTitle(currentPath, item);
        if (title) {
          return title;
        }
      }
    }
  }

  // No matches were found
  return '';
}

function findSectionTitleRecursively(navGroup: GroupConfig, currentPath: string): string {
  if ('pages' in navGroup) {
    for (const entry of navGroup.pages) {
      if (isNavigationGroup(entry)) {
        const title = findSectionTitleRecursively(entry, currentPath);
        if (title) {
          return title;
        }
      } else if (isEqualIgnoringLeadingSlash(entry, currentPath)) {
        return navGroup.group;
      }
    }
  }
  return '';
}
