import { isString, isNumber, isInteger, isObject, isArray, isBoolean } from '@mintlify/common';
import { AnySchema, DataSchema, IncrementalDataSchema } from '@mintlify/validation';
import { ReactNode } from 'react';

import { useSetDefaultValue } from '@/hooks/useSetDefaultValue';
import { RenderedRefs } from '@/types/api';

import { ArrayInput } from './ArrayInput';
import { FileInput } from './FileInput';
import { NullInput } from './NullInput';
import { NumberInput } from './NumberInput';
import { ObjectInput } from './ObjectInput';
import { SelectInput } from './SelectInput';
import { StringInput } from './StringInput';
import { TextInput } from './TextInput';

type DifferentiatedInputParams = {
  name?: string;
  schema: Exclude<DataSchema | IncrementalDataSchema, AnySchema>;
  typeDropdown: ReactNode;
  value: unknown;
  setValue: (value: unknown) => void;
  clearValue: (() => void) | undefined;
  isBody?: boolean;
  depth: number;
  parentNames?: string[];
  arrayIndex?: number;
  defaultExpanded?: boolean;
  renderedRefs?: RenderedRefs;
  style?: string;
  explode?: boolean;
};

export const DifferentiatedInput = ({
  schema,
  value,
  depth,
  isBody,
  setValue,
  defaultExpanded,
  renderedRefs,
  ...sharedProps
}: DifferentiatedInputParams) => {
  useSetDefaultValue(schema, value, setValue);

  switch (schema.type) {
    case 'string': {
      const safeValue = isString(value) ? value : undefined;
      const Input = isBody && depth === 0 ? TextInput : StringInput;
      return <Input schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'number': {
      const safeValue = isNumber(value) ? value : undefined;
      return <NumberInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'boolean': {
      const safeValue = isBoolean(value) ? value : undefined;
      return <SelectInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'object': {
      const safeValue = isObject(value) ? value : undefined;
      return (
        <ObjectInput
          schema={schema}
          value={safeValue}
          setValue={setValue}
          depth={depth}
          defaultExpanded={defaultExpanded}
          renderedRefs={renderedRefs}
          {...sharedProps}
        />
      );
    }
    case 'integer': {
      const safeValue = isInteger(value) ? value : undefined;
      return <NumberInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'array': {
      const safeValue = isArray(value) ? value : undefined;
      return (
        <ArrayInput
          schema={schema}
          value={safeValue}
          setValue={setValue}
          depth={depth}
          {...sharedProps}
        />
      );
    }
    case 'enum<string>': {
      const safeValue = isString(value) ? value : undefined;
      return <SelectInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'enum<number>': {
      const safeValue = isNumber(value) ? value : undefined;
      return <SelectInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'enum<integer>': {
      const safeValue = isInteger(value) ? value : undefined;
      return <SelectInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'file': {
      const safeValue = typeof window !== 'undefined' && value instanceof File ? value : undefined;
      return <FileInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
    case 'null': {
      const safeValue = value === null ? null : undefined;
      return <NullInput schema={schema} value={safeValue} setValue={setValue} {...sharedProps} />;
    }
  }
};
