'use client';

import { ReactNode, useContext } from 'react';

import { BannerContext } from '@/contexts/BannerContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

function AccordionCover({
  id,
  title,
  description,
  open,
  setOpen,
  icon,
  coverClass,
}: {
  id?: string;
  title: string | ReactNode;
  description?: string;
  open: boolean;
  setOpen: (open: boolean) => void;
  icon?: ReactNode;
  coverClass: string;
}) {
  const { divisions } = useContext(NavigationContext);
  const { banner } = useContext(BannerContext);

  const hasTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;

  const getTopOffset = () => {
    return hasBanner && hasTabs
      ? `-top-[10.5rem]`
      : hasBanner
        ? `-top-[7rem]`
        : hasTabs
          ? `-top-[8rem]`
          : `-top-[4.5rem]`;
  };

  return (
    <button
      onClick={() => setOpen(!open)}
      className={cn(
        'relative not-prose flex flex-row items-center content-center w-full',
        coverClass
      )}
      aria-controls={id + ' accordion children'}
      aria-expanded={open}
      data-component-part="accordion-button"
    >
      <div id={id} className={cn('absolute', getTopOffset())} />
      <div className="mr-0.5" data-component-part="accordion-caret-right">
        <Icon
          icon="caret-right"
          iconType="solid"
          className={cn(
            'h-3 w-3 transition bg-gray-700 dark:bg-gray-400',
            open && 'duration-200 rotate-90 -mt-1',
            !open && 'duration-75'
          )}
        />
      </div>
      {icon ? (
        <div
          className="h-4 w-4 fill-gray-800 dark:fill-gray-100 text-gray-800 dark:text-gray-100"
          data-component-part="accordion-icon"
        >
          {icon}
        </div>
      ) : null}
      <div
        className="leading-tight text-left"
        contentEditable={false}
        data-component-part="accordion-title-container"
      >
        <p
          className="m-0 font-medium text-gray-900 dark:text-gray-200"
          data-component-part="accordion-title"
        >
          {title}
        </p>
        {description ? (
          <p
            className="m-0 text-gray-900 dark:text-gray-200"
            data-component-part="accordion-description"
          >
            {description}
          </p>
        ) : null}
      </div>
    </button>
  );
}

export default AccordionCover;
