'use client';

import { Dialog, Transition } from '@headlessui/react';
import { Fragment, useContext, useEffect, useState } from 'react';

import { Banner } from '@/components/Banner';
import { zIndex } from '@/constants/zIndex';
import { BannerContext } from '@/contexts/BannerContext';
import { DocsConfigContext, PageContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { NavigationContext, useSelectedLocale } from '@/contexts/NavigationContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { Ids } from '@/lib/local/identifiers';
import { MobileSlideInNav } from '@/themes/mint/MainContentLayout/MobileSlideInNav';
import { ModeToggle } from '@/themes/mint/shared/ModeToggle';
import { VersioningSelect } from '@/themes/mint/shared/VersioningSelect';
import { NavLogo } from '@/ui/NavLogo';
import { SearchButton } from '@/ui/search';
import ChatMobileButton from '@/ui/search/ChatAssistantSheet/ChatMobileButton';
import { cn } from '@/utils/cn';
import getLogoHref from '@/utils/getLogoHref';
import { getSectionTitle } from '@/utils/paths/getSectionTitle';

import Icon from '../../../ui/Icon';
import { TabsBar } from '../../../ui/TabsBar/TabsBar';
import { ArrowRightIcon, CloseIcon, MenuIcon } from '../../../ui/icons';
import { NavbarLinks } from './NavbarLinks';

const TopbarDialog = ({
  topbarDialogOpen,
  setTopbarDialogOpen,
}: {
  topbarDialogOpen: boolean;
  setTopbarDialogOpen: (open: boolean) => void;
}) => {
  return (
    <Transition.Root
      show={topbarDialogOpen}
      as={Fragment}
      afterLeave={() => {
        setTopbarDialogOpen(false);
      }}
      appear
    >
      <Dialog
        as="div"
        open={topbarDialogOpen}
        onClose={() => setTopbarDialogOpen(false)}
        className={cn(zIndex.Popup, 'fixed inset-0 overflow-y-auto lg:hidden')}
      >
        <Dialog.Overlay className="fixed inset-0 bg-gray-500/25 transition-opacity backdrop-blur-sm" />
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="relative float-right bg-white w-[16rem] rounded-md m-2 py-7 px-8 dark:bg-background-dark">
            <button
              type="button"
              onClick={() => setTopbarDialogOpen(false)}
              className={cn(
                zIndex.Control,
                'absolute top-5 right-5 w-8 h-8 flex items-center justify-center fill-gray-500 hover:fill-gray-600 dark:fill-gray-400 dark:hover:fill-gray-300'
              )}
            >
              <span className="sr-only">Close</span>
              <CloseIcon />
            </button>
            <nav className="text-sm">
              <ul className="items-center space-y-4">
                <NavbarLinks />
              </ul>
            </nav>
          </div>
        </Transition.Child>
      </Dialog>
    </Transition.Root>
  );
};

export function TopBar() {
  const { docsConfig } = useContext(DocsConfigContext);
  const { pageMetadata } = useContext(PageContext);
  const { banner } = useContext(BannerContext);

  const currentPath = useCurrentPath();
  const router = useMintlifyRouter();
  const { navIsOpen, setNavIsOpen, divisions } = useContext(NavigationContext);
  const { search } = useSelectedLocale();
  const [isOpaque, setIsOpaque] = useState(false);
  const [topbarDialogOpen, setTopbarDialogOpen] = useState(false);
  const usingTabs = divisions.tabs.length > 0;
  const hasBanner = !!banner;

  useEffect(() => {
    const offset = 50;
    function onScroll() {
      if (!isOpaque && window.scrollY > offset) {
        setIsOpaque(true);
      } else if (isOpaque && window.scrollY <= offset) {
        setIsOpaque(false);
      }
    }
    onScroll();
    window.addEventListener('scroll', onScroll, { passive: true });

    return () => {
      window.removeEventListener('scroll', onScroll);
    };
  }, [isOpaque, usingTabs, hasBanner, docsConfig?.theme]);

  const { title } = pageMetadata;

  const section = getSectionTitle(currentPath, docsConfig?.navigation);

  return (
    <div id={Ids.Navbar} className={cn(zIndex.PrimaryNav, 'fixed lg:sticky top-0 w-full')}>
      <div
        id={Ids.NavBarTransition} // referenced during mode toggle
        className={cn(
          'absolute w-full h-full backdrop-blur flex-none transition-colors duration-500',
          'border-b border-gray-500/5 dark:border-gray-300/[0.06]',
          isOpaque
            ? 'bg-background-light supports-backdrop-blur:bg-background-light/95 dark:bg-background-dark/75'
            : 'supports-backdrop-blur:bg-background-light/60 dark:bg-transparent'
        )}
      />
      <Banner />
      <div className={cn('max-w-8xl mx-auto relative')}>
        <div className={cn('relative')}>
          <div
            className={cn(
              'flex items-center lg:px-12 h-16 min-w-0',
              usingTabs ? 'mx-4 lg:mx-0' : 'px-4'
            )}
          >
            <div
              className={cn(
                'h-full relative flex-1 flex items-center gap-x-4 min-w-0',
                'border-b border-gray-500/5 dark:border-gray-300/[0.06]',
                !usingTabs && 'lg:border-none'
              )}
            >
              <div className={cn('flex-1 lg:flex-none flex items-center gap-x-4')}>
                <NavLogo
                  href={docsConfig ? getLogoHref(docsConfig) : '/'}
                  onContextMenu={(e) => {
                    e.preventDefault();
                    void router.push(docsConfig ? getLogoHref(docsConfig) : '/');
                  }}
                  name={docsConfig?.name}
                />
                <VersioningSelect />
              </div>
              <div className="flex-1 relative hidden lg:flex items-center ml-auto justify-end gap-x-4">
                <div className="flex items-center gap-x-6">
                  <div className="h-16 font-semibold">
                    <TabsBar underlineClassName="hidden" />
                  </div>
                  <nav className="text-sm">
                    <ul className="flex gap-x-6 items-center">
                      <NavbarLinks />
                    </ul>
                  </nav>
                </div>
                <div className="flex items-center gap-x-2">
                  <SearchButton
                    className="group w-8 h-8 flex items-center justify-center"
                    id={Ids.SearchBarEntryMobile}
                  >
                    <span className="sr-only">{search}</span>
                    <Icon
                      icon="magnifying-glass"
                      iconType="solid"
                      className="h-3.5 w-3.5 bg-gray-600 dark:bg-gray-300 group-hover:bg-gray-800 dark:group-hover:bg-gray-100"
                    />
                  </SearchButton>
                  <ChatMobileButton />
                  <ModeToggle
                    id="navbar-transition"
                    sunIconClassName="text-gray-600 group-hover:text-gray-800"
                    moonIconClassName="text-gray-300 dark:group-hover:text-gray-100"
                  />
                </div>
              </div>
              <div className="flex lg:hidden items-center gap-3">
                <SearchButton
                  className="text-gray-500 w-8 h-8 flex items-center justify-center hover:text-gray-600 dark:text-gray-400 dark:hover:text-gray-300"
                  id={Ids.SearchBarEntryMobile}
                >
                  <span className="sr-only">{search}</span>
                  <Icon
                    icon="magnifying-glass"
                    iconType="solid"
                    className="h-4 w-4 bg-gray-500 dark:bg-gray-400 hover:bg-gray-600 dark:hover:bg-gray-300"
                  />
                </SearchButton>
                <ChatMobileButton />
                <button
                  aria-label="More actions"
                  className="h-7 w-5 flex items-center justify-end"
                  onClick={() => setTopbarDialogOpen(true)}
                >
                  <Icon
                    icon="ellipsis-vertical"
                    iconType="solid"
                    className="h-4 w-4 bg-gray-500 dark:bg-gray-400 hover:bg-gray-600 dark:hover:bg-gray-300"
                  />
                </button>
                <TopbarDialog
                  topbarDialogOpen={topbarDialogOpen}
                  setTopbarDialogOpen={setTopbarDialogOpen}
                />
              </div>
            </div>
          </div>
          <button
            type="button"
            className="flex items-center h-14 py-4 px-5 lg:hidden"
            onClick={() => setNavIsOpen(!navIsOpen)}
          >
            <div className="text-gray-500 hover:text-gray-600 dark:text-gray-400 dark:hover:text-gray-300">
              <span className="sr-only">Navigation</span>
              <MenuIcon />
            </div>
            {title && (
              <div className="ml-4 flex text-sm leading-6 whitespace-nowrap min-w-0 space-x-3">
                {section && (
                  <div className="flex items-center space-x-3">
                    <span>{section}</span>
                    <ArrowRightIcon className="fill-gray-400" />
                  </div>
                )}
                <div className="font-semibold text-gray-900 truncate dark:text-gray-200">
                  {title}
                </div>
              </div>
            )}
          </button>
        </div>
      </div>
      <MobileSlideInNav />
    </div>
  );
}
