'use client';

import { useContext, useState } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';
import { cn } from '@/utils/cn';

import { CodeSnippet } from '../CodeSnippet';
import { CodeGroupCopyButton } from './CodeGroupCopyButton';
import { CodeSelectDropdown } from './CodeSelectDropdown';

interface CodeGroupSelectProps {
  snippets: Record<string, Record<string, ExampleCodeSnippet>>;
}

export const CodeGroupSelect = ({ snippets }: CodeGroupSelectProps) => {
  const { docsConfig } = useContext(DocsConfigContext);

  const groups = Object.keys(snippets);
  const [selectedGroup, setSelectedGroup] = useState(groups[0]);

  const groupSnippets = selectedGroup !== undefined ? snippets[selectedGroup] : undefined;
  const options = groupSnippets ? Object.keys(groupSnippets) : undefined;
  const [selectedOption, setSelectedOption] = useState(options && options[0]);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  // if the user switches to a new group and that group doesn't have the selected option, switch to the first option
  const safeSelectedOption =
    selectedOption && options?.includes(selectedOption) ? selectedOption : options?.[0];

  const snippet =
    groupSnippets !== undefined && safeSelectedOption !== undefined
      ? groupSnippets[safeSelectedOption]
      : undefined;

  return (
    <div
      className={cn(
        'text-xs leading-6 rounded-xl ring-1 ring-gray-200 dark:ring-gray-800/50 not-prose overflow-hidden relative max-w-full min-w-full',
        codeBlockTheme === 'system' && 'bg-gray-100/50 dark:bg-codeblock',
        (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
          'bg-[#0F1117] dark:bg-codeblock'
      )}
      data-testid="code-group-select"
    >
      <div
        className={cn(
          'flex justify-between text-xs leading-6 border-b rounded-t-xl w-full',
          codeBlockTheme === 'system' &&
            'bg-gray-100 dark:bg-codeblock border-gray-200/50 dark:border-gray-800',
          (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
            'bg-black/40 border-gray-900/80'
        )}
      >
        <CodeSelectDropdown
          selectedOption={selectedGroup}
          setSelectedOption={setSelectedGroup}
          options={groups}
          data-testid="code-group-select-group"
        />
        <div className="flex overflow-hidden">
          {options && (
            <CodeSelectDropdown
              selectedOption={safeSelectedOption}
              setSelectedOption={setSelectedOption}
              options={options}
              data-testid="code-group-select-option"
            />
          )}
          <CodeGroupCopyButton code={snippet?.code ?? ''} />
        </div>
      </div>

      <div
        className={cn(
          'not-prose rounded-b-xl p-5 min-w-full max-w-0 overflow-x-auto max-h-[calc(100%-44px)] w-full scrollbar-track-transparent scrollbar-thin scrollbar-thumb-rounded text-xs leading-[1.35rem]',
          codeBlockTheme === 'system' &&
            'text-gray-950 dark:text-gray-50 scrollbar-thumb-black/20 dark:scrollbar-thumb-white/10 codeblock-light',
          (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
            'text-gray-50 scrollbar-thumb-white/25 dark:scrollbar-thumb-white/10 codeblock-dark'
        )}
      >
        <CodeSnippet language={snippet?.language}>{snippet?.code}</CodeSnippet>
      </div>
    </div>
  );
};
