import { AsyncApiBinding } from '@mintlify/common';

import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';
import { RecursiveExpandSchemaContainer } from './RecursiveExpandSchemaContainer';

export const Bindings = ({ bindings }: { bindings: AsyncApiBinding[] }) => {
  return (
    <ExpandableSectionContainer name="Bindings" className="bindings-container">
      {bindings.map((binding) => {
        return (
          <RecursiveExpandSchemaContainer
            key={binding.protocol}
            schema={binding.schemaProperties}
            className="bindings-schema"
          />
        );
      })}
    </ExpandableSectionContainer>
  );
};
