'use client';

import {
  ApiPlaygroundInputs,
  DataSchemaArray,
  SecurityOption,
  SecurityParameterSchema,
} from '@mintlify/validation';
import { useMemo } from 'react';

import { NoAnyDataSchema } from '@/hooks/usePlaygroundSchemaOptions';
import { cn } from '@/utils/cn';

import { InputSectionContainer } from '../components/InputSectionContainer';
import { TypeDropdown } from '../components/TypeDropdown';
import { RecursiveInput } from './inputs/RecursiveInput';

export const basicDescription =
  'Basic authentication header of the form `Basic <encoded-value>`, where `<encoded-value>` is the base64-encoded string `username:password`.';
export const bearerDescription =
  'Bearer authentication header of the form `Bearer <token>`, where `<token>` is your auth token.';
export const oauth2Description =
  'The access token received from the authorization server in the OAuth 2.0 flow.';

type SecuritySectionParams = {
  inputs: ApiPlaygroundInputs;
  setInputs: (inputs: ApiPlaygroundInputs) => void;
  securityOptions: SecurityOption[];
  selectedSecurityOptionIndex: number;
  setSelectedSecurityOptionIndex: (index: number) => void;
};

export const SecuritySection = ({
  inputs,
  setInputs,
  securityOptions,
  selectedSecurityOptionIndex,
  setSelectedSecurityOptionIndex,
}: SecuritySectionParams) => {
  const selectedSecurityOption = securityOptions[selectedSecurityOptionIndex];

  const parameters = useMemo(() => {
    if (!selectedSecurityOption) return null;

    return (['header', 'query', 'cookie'] as const).flatMap((location) =>
      Object.entries(selectedSecurityOption.parameters[location]).map(([name, parameter]) => {
        const {
          schemaArray,
          transform,
          value: paramValue,
        } = getPropsForParameter(parameter, inputs[location][name]);

        const setParamValue = (paramValue: unknown) => {
          const newObj = {
            ...inputs,
            [location]: { ...inputs[location] },
          };
          newObj[location][name] = transform(paramValue);
          setInputs(newObj);
        };

        const clearParamValue =
          paramValue === undefined
            ? undefined
            : () => {
                const newObj = {
                  ...inputs,
                  [location]: { ...inputs[location] },
                };
                delete newObj[location][name];
                setInputs(newObj);
              };

        return (
          <RecursiveInput
            name={name}
            key={`${location}:${name}`}
            schemaArray={schemaArray}
            value={paramValue}
            setValue={setParamValue}
            clearValue={clearParamValue}
          />
        );
      })
    );
  }, [selectedSecurityOption, setInputs, inputs]);

  return (
    <InputSectionContainer
      name="Authorization"
      rightElement={
        <div
          onClick={(e) => e.stopPropagation()}
          className={cn('text-xs font-mono', securityOptions.length <= 1 && 'hidden')}
        >
          <TypeDropdown
            options={securityOptions.map((option) => ({
              label: option.title,
              schema: option as unknown as NoAnyDataSchema,
            }))}
            selectedIndex={selectedSecurityOptionIndex}
            onSelectOption={(index) => {
              setSelectedSecurityOptionIndex(index);
            }}
          />
        </div>
      }
    >
      {parameters}
    </InputSectionContainer>
  );
};

const getPropsForParameter = (parameter: SecurityParameterSchema, value: unknown) => {
  return {
    schemaArray: getSchemaArrayForParameter(parameter),
    transform: getTransformForParameter(parameter),
    value: parseValueForParameter(parameter, value),
  };
};

const getSchemaArrayForParameter = (parameter: SecurityParameterSchema): DataSchemaArray => {
  const description = getDescriptionForSecurityParameter(parameter);
  switch (parameter.type) {
    case 'apiKey':
      return [
        {
          type: 'string',
          required: true,
          description,
          default: parameter['x-default' as never],
        },
      ];
    case 'http':
      return parameter.scheme === 'basic'
        ? [
            {
              type: 'object',
              required: true,
              description,
              properties: {
                username: [{ type: 'string' }],
                password: [{ type: 'string' }],
              },
              additionalProperties: false,
              default: parameter['x-default' as never],
            },
          ]
        : [
            {
              type: 'string',
              required: true,
              format: 'bearer',
              description,
              default: parameter['x-default' as never],
            },
          ];
    case 'oauth2':
      return [
        {
          type: 'string',
          required: true,
          format: 'bearer',
          description,
        },
      ];
  }
};

export const getDescriptionForSecurityParameter = (
  parameter: SecurityParameterSchema
): string | undefined => {
  switch (parameter.type) {
    case 'apiKey':
      return parameter.description;
    case 'http':
      return (
        parameter.description ??
        (parameter.scheme === 'basic' ? basicDescription : bearerDescription)
      );
    case 'oauth2':
      return parameter.description ?? oauth2Description;
  }
};

const parseValueForParameter = (parameter: SecurityParameterSchema, value: unknown) => {
  if (
    parameter.type !== 'http' ||
    parameter.scheme !== 'basic' ||
    typeof value !== 'string' ||
    !value.startsWith('Basic ')
  )
    return value;

  const encoded = value.slice('Basic '.length);
  const decoded = Buffer.from(encoded, 'base64').toString('utf-8');
  const [username, password] = decoded.split(':');
  return { username, password };
};

const getTransformForParameter = (parameter: SecurityParameterSchema) => {
  if (parameter.type !== 'http' || parameter.scheme !== 'basic') {
    return (value: unknown) => value;
  }

  return (value: unknown) => {
    const username =
      value && typeof value === 'object' && 'username' in value ? value['username'] : undefined;
    const password =
      value && typeof value === 'object' && 'password' in value ? value['password'] : undefined;
    const unencoded = `${username ?? ''}:${password ?? ''}`;
    const encoded = Buffer.from(unencoded).toString('base64');
    return `Basic ${encoded}`;
  };
};
