'use client';

import { NumberSchema } from '@mintlify/validation';
import { ChangeEvent, useCallback } from 'react';

import { Ids } from '@/lib/local/identifiers';

import { InputContainer } from '../../components/InputContainer';
import { GenericInput } from './RecursiveInput';

export const NumberInput: GenericInput<NumberSchema> = ({
  name,
  typeDropdown,
  schema,
  value,
  setValue,
  clearValue,
  arrayIndex,
  ...sharedProps
}) => {
  const onChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      if (event.target.value.length === 0) {
        setValue(undefined);
        return;
      }

      if (schema.type === 'integer') {
        const parsedInteger = parseInt(event.target.value);
        if (isNaN(parsedInteger)) {
          return;
        }
        setValue(parsedInteger);
      } else {
        setValue(parseFloat(event.target.value));
      }
    },
    [setValue, schema.type]
  );

  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={sharedProps.parentNames}
      arrayIndex={arrayIndex}
    >
      <input
        id={Ids.APIPlaygroundInput}
        className="flex-1 min-w-0 bg-transparent outline-none text-playground-input"
        placeholder={schema.placeholder ?? `enter ${name ?? 'value'}`}
        onKeyDown={(event) => {
          // disallow "e", and disallow "." for integer inputs
          if (event.key === 'e' || (event.key === '.' && schema.type === 'integer')) {
            event.preventDefault();
          }
        }}
        type="number"
        value={value ?? ''}
        onChange={onChange}
      />
    </InputContainer>
  );
};
