'use client';

import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';

import { buildRecursiveParamFieldId } from '@/api-playground/EndpointFields/ParamFields/RecursiveParamField';
import { OptionDropdown } from '@/api-playground/EndpointFields/components/OptionDropdown';
import { Classes } from '@/lib/local/selectors';
import { FieldType } from '@/types/api';
import { LinkIcon } from '@/ui/icons';
import { cn } from '@/utils/cn';
import { copyToClipboard } from '@/utils/copyToClipboard';

const MAX_DEFAULT_VALUE_LENGTH = 50;

export type ParamProps = {
  query?: string;
  path?: string;
  body?: string;
  header?: string;
  children: React.ReactNode;
  default?: string;
  type?: string;
  location?: string;
  required?: boolean;
  deprecated?: boolean;
  hidden?: boolean;
  placeholder?: string;
  enum?: string[];
  id?: string;
  pre?: string[];
  post?: string[];
};

export function InfoPill({
  children,
  prefix,
  className,
}: {
  children: React.ReactNode;
  prefix?: string;
  className?: string;
}) {
  return (
    <div
      className={cn(
        'flex items-center px-2 py-0.5 rounded-md bg-gray-100/50 dark:bg-white/5 text-gray-600 dark:text-gray-200 font-medium break-all',
        className
      )}
      data-component-part="field-info-pill"
    >
      {prefix && <span className="text-gray-400 dark:text-gray-500">{prefix}</span>}
      <span>{children}</span>
    </div>
  );
}

export function RequiredPill() {
  return (
    <div
      className="px-2 py-0.5 rounded-md bg-red-100/50 dark:bg-red-400/10 text-red-600 dark:text-red-300 font-medium whitespace-nowrap"
      data-component-part="field-required-pill"
    >
      required
    </div>
  );
}

export function DeprecatedPill() {
  return (
    <div
      className="px-2 py-0.5 rounded-md bg-amber-100/50 dark:bg-amber-400/10 text-amber-600 dark:text-amber-300 font-medium whitespace-nowrap"
      data-component-part="field-deprecated-pill"
    >
      deprecated
    </div>
  );
}

export function ParamField({
  query,
  path,
  body,
  header,
  children,
  default: defaultValue,
  type,
  location,
  required = false,
  deprecated = false,
  hidden = false,
  id,
  pre,
  post,
}: ParamProps) {
  const name = query || path || body || header;

  if (name == null) {
    return null;
  }

  return (
    <GenericParam
      name={name}
      defaultValue={defaultValue}
      type={type}
      location={location}
      required={required}
      deprecated={deprecated}
      hidden={hidden}
      id={id}
      pre={pre}
      post={post}
    >
      {children}
    </GenericParam>
  );
}

export function ParamHead({
  name,
  typeLabel,
  location,
  required,
  deprecated,
  fieldType,
  defaultValue,
  typeOptions,
  selectedTypeOptionIndex,
  onSelectTypeOption,
  parentName,
  id,
  pre,
  post,
  style,
  explode,
}: {
  name?: string | null;
  typeLabel?: string;
  fieldType?: FieldType;
  location?: string;
  required?: boolean;
  deprecated?: boolean;
  defaultValue?: unknown;
  typeOptions?: string[];
  selectedTypeOptionIndex?: number;
  onSelectTypeOption?: (index: number) => void;
  parentName?: string;
  id?: string;
  pre?: string[];
  post?: string[];
  style?: string;
  explode?: boolean;
}) {
  const pillsRef = useRef<HTMLDivElement>(null);
  const [isMultiLine, setIsMultiLine] = useState(false);
  const paramId = id ?? buildRecursiveParamFieldId(fieldType, name, parentName);

  const copyAnchorLink = useCallback(() => {
    if (paramId) {
      void copyToClipboard(window.location.href.split('#')[0] + '#' + paramId);
      window.location.hash = paramId;
    }
  }, [paramId]);

  useEffect(() => {
    const ref = pillsRef.current;
    if (!ref) return;

    function checkHeight() {
      const height = ref?.offsetHeight ?? 0;
      setIsMultiLine(height > 28);
    }

    checkHeight();
    const resizeObserver = new ResizeObserver(checkHeight);
    resizeObserver.observe(ref);

    return () => {
      resizeObserver.unobserve(ref);
    };
  }, []);

  const paramInfo = !parentName ? (
    name
  ) : style === 'deepObject' && explode ? (
    <>
      <span className="text-gray-500 dark:text-gray-400">{parentName}</span>
      {name}
      <span className="text-gray-500 dark:text-gray-400">]</span>
    </>
  ) : (
    <>
      <span className="text-gray-500 dark:text-gray-400">{parentName}</span>
      {name}
    </>
  );

  return name == null ? null : (
    <div
      className={cn('flex font-mono text-sm group/param-head param-head break-all relative')}
      id={paramId}
      key={paramId}
    >
      <div className="flex-1 flex content-start py-0.5 mr-5">
        <div className="flex items-center flex-wrap gap-2">
          {paramId && (
            <div className="absolute -top-1.5">
              <a
                href={`#${paramId}`}
                className={cn(
                  parentName ? '-ml-[2.1rem]' : '-ml-10',
                  'flex items-center opacity-0 border-0 group-hover/param-head:opacity-100 py-2 [.expandable-content_&]:-ml-[2.1rem]'
                )}
                aria-label="Navigate to header"
                onClick={copyAnchorLink}
              >
                &#8203;
                <div className="w-6 h-6 rounded-md flex items-center justify-center shadow-sm text-gray-400 dark:text-white/50 dark:bg-background-dark dark:brightness-[1.35] dark:ring-1 dark:hover:brightness-150 bg-white ring-1 ring-gray-400/30 dark:ring-gray-700/25 hover:ring-gray-400/60 dark:hover:ring-white/20">
                  <LinkIcon />
                </div>
              </a>
            </div>
          )}
          {pre?.map((item, i) => (
            <div
              key={i}
              className="px-2 py-0.5 rounded-md bg-gray-100/50 dark:bg-white/5 text-gray-600 dark:text-gray-200"
              data-component-part="field-meta-pre"
            >
              {item}
            </div>
          ))}
          {(parentName || name) && (
            <div
              className="font-semibold text-primary dark:text-primary-light cursor-pointer overflow-wrap-anywhere"
              data-component-part="field-name"
              onClick={copyAnchorLink}
            >
              {paramInfo}
            </div>
          )}
          <div
            ref={pillsRef}
            className={cn(
              'inline items-center gap-2 text-xs font-medium [&_div]:inline [&_div]:mr-2',
              isMultiLine ? '[&_div]:leading-6' : '[&_div]:leading-5'
            )}
            data-component-part="field-meta"
          >
            {typeOptions && typeOptions.length > 1 && onSelectTypeOption ? (
              <OptionDropdown
                options={typeOptions}
                selectedIndex={selectedTypeOptionIndex}
                onSelectOption={onSelectTypeOption}
              />
            ) : (
              typeLabel && <InfoPill>{typeLabel}</InfoPill>
            )}
            {location && <InfoPill>{location}</InfoPill>}
            {defaultValue != null && (
              <InfoPill prefix="default:">
                {typeof defaultValue === 'string'
                  ? defaultValue === ''
                    ? '""'
                    : defaultValue
                  : JSON.stringify(defaultValue)}
              </InfoPill>
            )}
            {required && <RequiredPill />}
            {deprecated && <DeprecatedPill />}
            {post?.map((item, i) => (
              <div
                key={i}
                className="px-2 py-0.5 rounded-md bg-gray-100/50 dark:bg-white/5 text-gray-600 dark:text-gray-200"
                data-component-part="field-meta-post"
              >
                {item}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

// TODO: Remove from components
export type GenericParamProps = {
  name: string;
  type?: string;
  location?: string;
  defaultValue?: string;
  required?: boolean;
  deprecated?: boolean;
  hidden?: boolean;
  id?: string;
  children: React.ReactNode;
  pre?: string[];
  post?: string[];
};

export function GenericParam({
  name,
  type,
  location,
  defaultValue,
  required = false,
  deprecated = false,
  hidden = false,
  id,
  pre,
  post,
  children,
}: GenericParamProps) {
  const stringifiedDefaultValue = useMemo(() => {
    if (typeof defaultValue === 'object') {
      // don't display values with nested objects; looks bad on one line
      const containsNestedObject = Object.values(defaultValue).some(
        (value) => typeof value === 'object'
      );
      if (containsNestedObject) {
        return null;
      }
    }

    const stringifiedValue = JSON.stringify(defaultValue);
    if (
      stringifiedValue &&
      stringifiedValue.length > 0 &&
      stringifiedValue.length < MAX_DEFAULT_VALUE_LENGTH
    ) {
      return stringifiedValue;
    }
    return null;
  }, [defaultValue]);

  if (hidden) {
    return null;
  }
  return (
    <div
      className={cn(
        Classes.Field,
        'pt-2.5 pb-5 my-2.5 border-gray-50 dark:border-gray-800/50 border-b'
      )}
    >
      <ParamHead
        name={name}
        typeLabel={type}
        location={location}
        required={required}
        deprecated={deprecated}
        defaultValue={stringifiedDefaultValue}
        id={id}
        pre={pre}
        post={post}
      />
      {children && (
        <div
          className="mt-4 prose-sm prose-gray dark:prose-invert [&_.prose>p:first-child]:mt-0 [&_.prose>p:last-child]:mb-0"
          data-component-part="field-content"
        >
          {children}
        </div>
      )}
    </div>
  );
}
