import { useEffect, useState } from 'react';

export const useCheckHash = () => {
  const [anchor, setAnchor] = useState<string | undefined>(undefined);

  useEffect(() => {
    const checkHashToExpand = () => {
      if (typeof window === 'undefined') return;
      const anchor = window.location.hash;
      if (!anchor) return;
      setAnchor(anchor);
    };

    checkHashToExpand();
    window.addEventListener('hashchange', checkHashToExpand);
    return () => {
      window.removeEventListener('hashchange', checkHashToExpand);
    };
  }, []);

  return anchor;
};
