'use client';

import { DocsConfig } from '@mintlify/validation';
import { ReactElement, useContext, useEffect } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { ThemeStyling } from '@/types/themeStyling';
import { handleRouteChange } from '@/utils/handleRouteChange';

import { AlmondLayout } from './almond';
import { almondStyling } from './almond/styling';
import { AspenLayout } from './aspen';
import { LindenLayout } from './linden';
import { lindenStyling } from './linden/styling';
import { MapleLayout } from './maple';
import { mapleStyling } from './maple/styling';
import { MintLayout } from './mint';
import { PalmLayout } from './palm';
import { WillowLayout } from './willow';

export const ThemeLayout = ({ children }: { children: ReactElement }) => {
  const { docsConfig } = useContext(DocsConfigContext);
  const router = useMintlifyRouter();
  const currentPath = useCurrentPath();

  // perform scrolling when user enters via direct link, only needs to run  on initial load
  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => handleRouteChange(currentPath), []);

  useEffect(() => {
    router.events.on('routeChangeComplete', (url) => handleRouteChange(url));
    return () => router.events.off('routeChangeComplete', (url) => handleRouteChange(url));
  }, [router.events]);

  if (docsConfig?.theme === 'maple') {
    return <MapleLayout>{children}</MapleLayout>;
  }

  if (docsConfig?.theme === 'palm') {
    return <PalmLayout>{children}</PalmLayout>;
  }

  if (docsConfig?.theme === 'willow') {
    return <WillowLayout>{children}</WillowLayout>;
  }

  if (docsConfig?.theme === 'linden') {
    return <LindenLayout>{children}</LindenLayout>;
  }

  if (docsConfig?.theme === 'aspen') {
    return <AspenLayout>{children}</AspenLayout>;
  }

  if (docsConfig?.theme === 'almond') {
    return <AlmondLayout>{children}</AlmondLayout>;
  }

  return <MintLayout>{children}</MintLayout>;
};

export const getThemeStyling = (theme?: DocsConfig['theme']): ThemeStyling => {
  if (theme === 'maple') {
    return mapleStyling;
  }

  if (theme === 'linden') {
    return lindenStyling;
  }

  if (theme === 'almond') {
    return almondStyling;
  }

  return {};
};
