'use client';

import { ApiPlaygroundResultType } from '@mintlify/models';
import clsx from 'clsx';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

import { ResponseBodyData, ResponseSection, responseSections } from '.';
import { CopyFileToClipboardButton } from './CopyFileToClipboardButton';
import { DownloadButton } from './DownloadFileButton';

type ResponseHeaderParams = {
  result: ApiPlaygroundResultType;
  selectedSection: ResponseSection;
  setSelectedSection: (value: ResponseSection) => void;
  data: ResponseBodyData | undefined;
};

export const ResponseTopbar = ({
  result,
  selectedSection,
  setSelectedSection,
  data,
}: ResponseHeaderParams) => {
  const { docsConfig } = useContext(DocsConfigContext);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div
      className={cn(
        'flex w-full px-4 py-2 space-x-3 items-center border-b border-gray-100/70 dark:border-white/5 rounded-t-xl',
        codeBlockTheme === 'system'
          ? 'border-gray-200/50 dark:border-gray-900/80 bg-gray-100/40 dark:bg-codeblock'
          : 'border-gray-900/80'
      )}
    >
      <div className="flex flex-1 space-x-4 items-center">
        <StatusInfo
          status={!result.error ? result.response.status : undefined}
          statusText={!result.error ? result.response.statusText : undefined}
        />
      </div>
      {data ? (
        <>
          <ResponseSectionDropdown
            selectedSection={selectedSection}
            setSelectedSection={setSelectedSection}
          />
          <DownloadButton data={data} />
          <CopyFileToClipboardButton data={data} />
        </>
      ) : null}
    </div>
  );
};

const StatusInfo = ({ status, statusText }: { status?: number; statusText?: string }) => {
  const { docsConfig } = useContext(DocsConfigContext);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div className="flex space-x-2 items-center">
      <StatusIcon status={status} />
      <div
        className={cn(
          'text-xs',
          codeBlockTheme === 'system'
            ? 'text-gray-800 dark:text-gray-200 font-medium'
            : 'text-gray-200 font-medium'
        )}
      >
        {status ? `${status} - ${statusText}` : '--'}
      </div>
    </div>
  );
};

const StatusIcon = ({ status }: { status: number | undefined }) => {
  const commonClassName = 'w-3.5 h-3.5';

  if (status === undefined) {
    // response not completed
    return (
      <Icon
        icon="triangle-exclamation"
        className={clsx(commonClassName, 'bg-red-600 dark:bg-red-500')}
      />
    );
  } else if (status >= 400) {
    // error
    return <Icon icon="circle-x" className={clsx(commonClassName, 'bg-red-600 dark:bg-red-500')} />;
  } else if (status >= 200 && status < 300) {
    // success
    return (
      <Icon
        icon="circle-check"
        className={clsx(commonClassName, 'bg-green-600 dark:bg-green-500')}
      />
    );
  } else {
    // weird status code
    return (
      <Icon
        icon="circle-exclamation"
        className={clsx(commonClassName, 'bg-orange-600 dark:bg-orange-500')}
      />
    );
  }
};

type ResponseSectionDropdownParams = {
  selectedSection: ResponseSection;
  setSelectedSection: (value: ResponseSection) => void;
};

const ResponseSectionDropdown = ({
  selectedSection,
  setSelectedSection,
}: ResponseSectionDropdownParams) => {
  const { docsConfig } = useContext(DocsConfigContext);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div className="relative">
      <select
        className={cn(
          'pl-2.5 pr-6 py-1 rounded-lg text-xs font-medium border cursor-pointer focus:outline-0',
          codeBlockTheme === 'system'
            ? 'text-gray-600 dark:text-gray-400 border-standard hover:bg-gray-50 dark:hover:bg-white/[0.03]'
            : 'text-gray-200 border-gray-800/50 hover:bg-gray-800/50 bg-gray-800/50'
        )}
        value={selectedSection}
        onChange={(event) => setSelectedSection(event.target.value as ResponseSection)}
      >
        {responseSections.map((option) => (
          <option value={option} key={option}>
            {(option[0]?.toUpperCase() ?? '') + option.slice(1)}
          </option>
        ))}
      </select>
      <div className="pointer-events-none absolute inset-0 flex items-center justify-end pr-2.5">
        <Icon
          icon="angle-down"
          iconType="solid"
          className={cn(
            'w-2.5 h-2.5',
            codeBlockTheme === 'system' ? 'bg-gray-400 dark:bg-gray-500' : 'bg-gray-200'
          )}
        />
      </div>
    </div>
  );
};
