'use client';

import { Endpoint } from '@mintlify/validation';
import { ChevronsUpDownIcon } from 'lucide-react';
import { useContext, useEffect, useMemo, useState } from 'react';

import { getMethodColor } from '@/components/Api/colors';
import { MethodPill } from '@/components/ApiExamples/MethodPill';
import { useApiPlaygroundInputs } from '@/hooks/useMDXContentController/useApiPlaygroundInputs';
import {
  addPathParams,
  addServerVariables,
  joinWithSingleSlash,
} from '@/hooks/useSendPlaygroundRequest';
import { Classes } from '@/lib/local/selectors';
import { MethodPillType } from '@/types/api';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';
import { copyToClipboard } from '@/utils/copyToClipboard';

import { ApiPlaygroundContext } from '../ApiPlaygroundContext';

type EndpointHeaderProps = {
  endpoint: Endpoint;
  display: 'simple' | 'none' | 'interactive';
  onClickInteractive?: () => void;
  hasGrayBackground?: boolean;
};

export const EndpointHeader = ({
  endpoint,
  display,
  onClickInteractive,
  hasGrayBackground,
}: EndpointHeaderProps) => {
  return (
    <div className="flex items-center space-x-1.5">
      <PathHeader endpoint={endpoint} display={display} hasGrayBackground={hasGrayBackground} />
      {display === 'simple' ? null : (
        <SendPill method={endpoint.method} label="Try it" onClick={onClickInteractive} />
      )}
    </div>
  );
};

const BasePathSelector = () => {
  const { baseUrlOptions, selectedBaseUrlIndex, setSelectedBaseUrlIndex } =
    useContext(ApiPlaygroundContext);
  const [selectWidth, setSelectWidth] = useState(0);

  const selectedOption = baseUrlOptions?.[selectedBaseUrlIndex];

  useEffect(() => {
    // Create a temporary span to measure the text width to dynamically set the width of the select
    const span = document.createElement('span');
    span.className = 'text-sm absolute font-mono invisible';
    span.textContent = selectedOption ?? '';
    document.body.appendChild(span);

    const paddingWidth = 16;

    setSelectWidth(span.offsetWidth + paddingWidth);
    document.body.removeChild(span);
  }, [selectedOption]);

  if (!baseUrlOptions || baseUrlOptions.length <= 1 || !setSelectedBaseUrlIndex) return null;

  return (
    <div className="flex items-center gap-1 relative -mr-1">
      <select
        className="bg-transparent text-sm text-gray-600 dark:text-gray-400 outline-none font-mono cursor-pointer hover:text-gray-900 dark:hover:text-gray-300"
        value={selectedBaseUrlIndex}
        onClick={(e) => e.stopPropagation()}
        onChange={(e) => {
          setSelectedBaseUrlIndex(Number(e.target.value));
        }}
        style={{ width: `${selectWidth}px` }}
      >
        {baseUrlOptions.map((url, i) => (
          <option key={url} value={i}>
            {url}
          </option>
        ))}
      </select>
      <ChevronsUpDownIcon
        className="absolute right-0 w-3 h-3 text-gray-400 dark:text-white/30 pointer-events-none"
        strokeWidth={2.5}
      />
    </div>
  );
};

export const PathHeader = ({ endpoint, display, hasGrayBackground }: EndpointHeaderProps) => {
  const [isCopiedActive, setIsCopiedActive] = useState(false);
  const { baseUrlOptions, selectedBaseUrlIndex } = useContext(ApiPlaygroundContext);
  const inputs = useApiPlaygroundInputs();

  const baseUrl = baseUrlOptions?.[selectedBaseUrlIndex] ?? baseUrlOptions?.[0] ?? '';

  const onCopy = async () => {
    const pathWithInputs = addPathParams(endpoint.path, inputs.path);
    const toCopy = joinWithSingleSlash(addServerVariables(baseUrl, inputs.server), pathWithInputs);

    const result = await copyToClipboard(toCopy);
    if (result === 'success') {
      setIsCopiedActive(true);
      setTimeout(() => {
        setIsCopiedActive(false);
      }, 2000);
    }
  };

  return (
    <div
      className={cn(
        'relative flex-1 flex gap-2 min-w-0 rounded-xl items-center cursor-pointer p-1.5',
        display != 'simple' && 'border-standard',
        hasGrayBackground && 'bg-gray-50 dark:bg-white/5'
      )}
      onClick={onCopy}
    >
      <MethodPill
        method={endpoint.type === 'webhook' ? 'webhook' : endpoint.method}
        active={false}
      />
      <div className="flex items-center space-x-2 overflow-x-auto flex-1 no-scrollbar">
        {endpoint.type !== 'webhook' && <BasePathSelector />}
        <PrettyPath
          endpoint={endpoint}
          display={display}
          isCopiedActive={isCopiedActive}
          hasGrayBackground={hasGrayBackground}
        />
      </div>
    </div>
  );
};

const PrettyPath = ({
  endpoint,
  isCopiedActive,
  hasGrayBackground,
}: EndpointHeaderProps & { isCopiedActive: boolean }) => {
  const pathParts = useMemo(() => {
    return endpoint.path.split('/').flatMap((component, i) => {
      const componentParts = [];
      if (i > 0 && component) {
        componentParts.push(<SlashPart key={i} />);
      }

      component.split('{').forEach((paramAndText, j) => {
        if (paramAndText.includes('}')) {
          const [param, text] = paramAndText.split('}');
          componentParts.push(
            <ParamPart key={`${i}-${j}-param`} method={endpoint.method}>
              {param!}
            </ParamPart>
          );
          if (text) {
            componentParts.push(<LiteralPart key={`${i}-${j}`}>{text}</LiteralPart>);
          }
        } else if (paramAndText) {
          componentParts.push(<LiteralPart key={`${i}-${j}`}>{paramAndText}</LiteralPart>);
        }
      });

      return componentParts;
    });
  }, [endpoint.path, endpoint.method]);

  return (
    <div className="group flex items-center flex-1 gap-0.5 font-mono">
      <div
        className={cn(
          'absolute right-0 p-2 bg-background-light dark:bg-background-dark rounded-lg',
          hasGrayBackground && 'bg-transparent dark:bg-transparent',
          isCopiedActive ? 'block' : 'hidden group-hover:block'
        )}
      >
        {isCopiedActive ? (
          <Icon
            icon="circle-check"
            iconType="solid"
            className="w-4 h-4 bg-primary dark:bg-primary-light"
          />
        ) : (
          <Icon icon="clone" className="w-4 h-4 bg-gray-400 dark:bg-white/30" />
        )}
      </div>
      {pathParts}
    </div>
  );
};

const LiteralPart = ({ children }: { children: string }) => (
  <div className="text-sm font-medium text-gray-800 dark:text-white min-w-max">{children}</div>
);

const ParamPart = ({ children, method }: { children: string; method: MethodPillType }) => {
  const { paramStyle } = getMethodColor(method);
  return (
    <div
      className={cn('text-sm font-mono font-medium rounded-md px-1 border-2 min-w-max', paramStyle)}
    >{`\{${children}\}`}</div>
  );
};

const SlashPart = () => <div className="text-sm text-gray-400">/</div>;

export const SendPill = ({
  label,
  method,
  onClick,
  isSending,
  className,
}: {
  label: string;
  method: MethodPillType;
  onClick?: () => void;
  isSending?: boolean;
  className?: string;
}) => {
  const { activeNavPillBg } = getMethodColor(method);
  return (
    <button
      className={cn(
        Classes.TryitButton,
        'flex items-center justify-center px-3 h-9 text-white font-medium rounded-xl mouse-pointer disabled:opacity-70 hover:opacity-80 gap-1.5',
        activeNavPillBg,
        className
      )}
      onClick={onClick}
      disabled={isSending}
    >
      {isSending ? (
        <Icon icon="spinner-third" iconType="duotone" className="w-4 h-4 bg-white animate-spin" />
      ) : (
        <>
          <span>{label}</span>
          <Icon icon="play" iconType="solid" className="w-3 h-3 bg-white" />
        </>
      )}
    </button>
  );
};
