import { ContextualOption as ContextualOptionType } from '@mintlify/validation';
import { ArrowUpRightIcon } from 'lucide-react';
import { useContext, useEffect, useMemo, useState } from 'react';

import { DropdownMenu, DropdownMenuContent, DropdownMenuTrigger } from '@/components/Dropdown';
import { DeploymentMetadataContext, DocsConfigContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { useSelectedLocale } from '@/contexts/NavigationContext';
import { copyMarkdownToClipboard } from '@/hooks/useMarkdownCopy';
import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

import { DuplicateIcon, MarkdownIcon, ChatGPTIcon, ClaudeIcon, DropdownArrowIcon } from './icons';

type ContextualOption = {
  id: ContextualOptionType;
  title: string;
  description: string;
  icon: React.ElementType;
  action: () => void;
  externalLink?: boolean;
};

export const PageContextMenu = () => {
  const { docsConfig } = useContext(DocsConfigContext);
  const {
    copyPage,
    viewAsMarkdown: viewAsMarkdownText,
    openInChatGPT,
    openInClaude,
    copyPageAsMarkdown: copyPageAsMarkdowntext,
    viewPageAsMarkdown,
    askQuestionsAboutPage,
  } = useSelectedLocale();

  const router = useMintlifyRouter();
  const { actualSubdomain } = useContext(DeploymentMetadataContext);
  const [isOpen, setIsOpen] = useState(false);
  const [isCopied, setIsCopied] = useState(false);

  useEffect(() => {
    if (isCopied) {
      setTimeout(() => {
        setIsCopied(false);
      }, 2000);
    }
  }, [isCopied]);

  const copyPageAsMarkdown = async () => {
    setIsCopied(true);
    try {
      await copyMarkdownToClipboard(router, actualSubdomain);
    } catch (error) {
      console.error('Failed to copy markdown:', error);
    }
  };

  const viewAsMarkdown = () => {
    const markdownPath = `${router.basePath}${router.asPath}.md`;
    window.open(markdownPath, '_blank');
  };

  const openInAIProviders = async (provider: 'chatgpt' | 'claude') => {
    const currentUrl = window.location.href;
    const prompt = `Read from ${currentUrl}.md so I can ask questions about it.`;
    if (provider === 'chatgpt') {
      window.open(
        `https://chat.openai.com/?hints=search&q=${encodeURIComponent(prompt)}`,
        '_blank'
      );
    } else {
      window.open(`https://claude.ai/new?q=${encodeURIComponent(prompt)}`, '_blank');
    }
  };

  const wrapAction = (actionFn: () => void) => {
    return () => {
      setIsOpen(false);
      actionFn();
    };
  };

  const allOptions: ContextualOption[] = [
    {
      id: 'copy',
      title: copyPage,
      description: copyPageAsMarkdowntext,
      icon: DuplicateIcon,
      action: wrapAction(copyPageAsMarkdown),
    },
    {
      id: 'view',
      title: viewAsMarkdownText,
      description: viewPageAsMarkdown,
      icon: MarkdownIcon,
      action: wrapAction(viewAsMarkdown),
      externalLink: true,
    },
    {
      id: 'chatgpt',
      title: openInChatGPT,
      description: askQuestionsAboutPage,
      icon: ChatGPTIcon,
      action: wrapAction(() => openInAIProviders('chatgpt')),
      externalLink: true,
    },
    {
      id: 'claude',
      title: openInClaude,
      description: askQuestionsAboutPage,
      icon: ClaudeIcon,
      action: wrapAction(() => openInAIProviders('claude')),
      externalLink: true,
    },
  ];

  const options = useMemo(() => {
    const options = docsConfig?.contextual?.options
      .map((option) => allOptions.find((o) => o.id === option))
      .filter(Boolean) as ContextualOption[];
    return Array.from(new Set(options));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [docsConfig?.contextual?.options]);

  if (!options.length) return null;

  const firstOption = options[0]!;

  return (
    <div
      id={Ids.PageContextMenu}
      className={cn(
        'flex items-center justify-end shrink-0 ml-auto',
        firstOption.id === 'copy' && 'min-w-[156px]'
      )}
    >
      <button
        id={Ids.PageContextMenuButton}
        className={cn(
          'rounded-l-xl px-3 text-gray-700 dark:text-gray-300 py-1.5 border border-gray-200 dark:border-white/[0.07] bg-background-light dark:bg-background-dark hover:bg-gray-600/5 dark:hover:bg-gray-200/5',
          options.length === 1 ? 'rounded-xl' : 'border-r-0',
          isCopied && 'text-gray-600 dark:text-gray-400'
        )}
        onClick={() => {
          void firstOption.action();
        }}
      >
        <div className="flex items-center gap-2 text-sm text-center font-medium">
          {firstOption.id === 'copy' ? (
            <>
              <DuplicateIcon className="w-4 h-4" />
              <span>{isCopied ? 'Copied' : copyPage}</span>
            </>
          ) : (
            <>
              {typeof firstOption.icon === 'function' ? (
                <firstOption.icon className="w-4 h-4 shrink-0" />
              ) : (
                firstOption.icon
              )}
              <span>{firstOption.title}</span>
            </>
          )}
        </div>
      </button>
      {options.length > 1 && (
        <DropdownMenu open={isOpen} onOpenChange={setIsOpen}>
          <DropdownMenuTrigger asChild className="rounded-none">
            <button className="rounded-r-xl border px-3 border-gray-200 aspect-square dark:border-white/[0.07] bg-background-light dark:bg-background-dark hover:bg-gray-600/5 dark:hover:bg-gray-200/5">
              <DropdownArrowIcon className={cn('rotate-90 ', isOpen && 'rotate-[270deg]')} />
            </button>
          </DropdownMenuTrigger>
          <DropdownMenuContent
            side="bottom"
            align="end"
            className="inline-flex max-h-[420px] p-1 border border-gray-200 dark:border-white/[0.07] flex-col"
          >
            {options.map((option, index) => (
              <div
                key={option.id + index}
                onClick={option.action}
                className="flex items-center p-1.5 gap-1 hover:bg-gray-600/5 dark:hover:bg-gray-200/5 cursor-pointer rounded-xl"
              >
                <div className="border border-gray-200 dark:border-white/[0.07] rounded-lg p-1.5">
                  {typeof option.icon === 'function' ? (
                    <option.icon className="w-4 h-4 shrink-0" />
                  ) : (
                    option.icon
                  )}
                </div>
                <div className="flex flex-col px-1">
                  <div className="text-sm font-medium text-gray-800 dark:text-gray-300 flex items-center gap-1">
                    {option.title}
                    {option.externalLink && (
                      <ArrowUpRightIcon className="w-3 h-3 text-gray-600 dark:text-gray-400 shrink-0" />
                    )}
                  </div>
                  <div className="text-xs text-gray-600 dark:text-gray-400">
                    {option.description}
                  </div>
                </div>
              </div>
            ))}
          </DropdownMenuContent>
        </DropdownMenu>
      )}
    </div>
  );
};
