import { isPage } from '@mintlify/common';
import { getFirstPageFromNavigation } from '@mintlify/common';
import { isAbsoluteUrl } from '@mintlify/common';
import { LocaleType } from '@mintlify/models';
import {
  DecoratedAnchorConfig,
  DecoratedDivisionNavigationType,
  DecoratedDropdownConfig,
  DecoratedGroupConfig,
  DecoratedLanguageConfig,
  DecoratedNavigationConfig,
  DecoratedPageConfig,
  DecoratedPageOrGroupConfig,
  DecoratedTabConfig,
  DecoratedVersionConfig,
  Division,
  divisions,
  GlobalConfig,
} from '@mintlify/validation';

import { isEqualIgnoringLeadingSlash } from '../paths/isEqualIgnoringLeadingSlash';
import { checkNavAccess } from './checkNavAccess';
import { DivisionMatch, updateFirstHrefInDivision } from './updateFirstHrefInDivision';

export type FilterDivisionsParams = {
  currentPath: string;
  currentVersion: string | undefined;
  currentLanguage: LocaleType | undefined;
  decoratedNav: DecoratedNavigationConfig | undefined;
  userGroups: Set<string>;
  shouldUseDivisionMatch: boolean;
};

export type DivisionState = {
  name: string;
  href: string; // first page path in the division or VersionMatchLevel
  isActive: boolean;
  isGlobal: boolean;
};

export type FilterDivisionsResult = {
  tabs: (DecoratedTabConfig & DivisionState)[];
  anchors: (DecoratedAnchorConfig & DivisionState)[];
  versions: (DecoratedVersionConfig & DivisionState)[];
  languages: (DecoratedLanguageConfig & DivisionState)[];
  dropdowns: (DecoratedDropdownConfig & DivisionState)[];
  groupsOrPages: DecoratedPageOrGroupConfig[];
  firstHrefInVersion: Map<string | undefined, DivisionMatch>;
  firstHrefInLanguage: Map<LocaleType | undefined, DivisionMatch>;
};

type DivisionArrayTypes = {
  tabs: Array<DecoratedTabConfig & DivisionState>;
  anchors: Array<DecoratedAnchorConfig & DivisionState>;
  versions: Array<DecoratedVersionConfig & DivisionState>;
  languages: Array<DecoratedLanguageConfig & DivisionState>;
  dropdowns: Array<DecoratedDropdownConfig & DivisionState>;
  groups: Array<DecoratedPageOrGroupConfig>;
};

export type NavigationDivisions = Record<
  (typeof divisions)[number],
  DivisionArrayTypes[keyof DivisionArrayTypes]
>;

type DecoratedDivisionConfig =
  | DecoratedAnchorConfig
  | DecoratedTabConfig
  | DecoratedVersionConfig
  | DecoratedLanguageConfig
  | DecoratedDropdownConfig;

export const filterDivisions = ({
  currentPath,
  currentVersion,
  currentLanguage,
  decoratedNav,
  userGroups,
  shouldUseDivisionMatch = true,
}: FilterDivisionsParams): FilterDivisionsResult => {
  let navigationDivisions: NavigationDivisions = {
    tabs: [],
    anchors: [],
    versions: [],
    languages: [],
    dropdowns: [],
  };
  const firstHrefInVersion = new Map<string | undefined, DivisionMatch>();
  const firstHrefInLanguage = new Map<LocaleType | undefined, DivisionMatch>();

  function findPageInNavigation(
    currentPath: string,
    entry: DecoratedNavigationConfig | DecoratedPageOrGroupConfig,
    parentGroups: DecoratedGroupConfig[]
  ): {
    page: DecoratedPageConfig | undefined;
    groupsOrPages: DecoratedGroupConfig[] | DecoratedPageOrGroupConfig[] | undefined;
  } {
    if (typeof entry !== 'object') {
      return { page: undefined, groupsOrPages: undefined };
    }

    if (isPage(entry)) {
      return checkNavAccess(entry, userGroups) &&
        isEqualIgnoringLeadingSlash(entry.href, currentPath)
        ? {
            page: entry,
            groupsOrPages: parentGroups,
          }
        : { page: undefined, groupsOrPages: undefined };
    }

    if ('pages' in entry) {
      for (const page of entry.pages) {
        if (typeof page === 'object') {
          const { page: foundPage, groupsOrPages } = findPageInNavigation(
            currentPath,
            page,
            parentGroups.length
              ? parentGroups
              : entry.pages
                  .map((page: DecoratedPageOrGroupConfig) =>
                    filterInaccessiblePagesRecursive(page, userGroups)
                  )
                  .filter(
                    (page: DecoratedPageOrGroupConfig): page is DecoratedPageOrGroupConfig =>
                      page !== undefined
                  )
          );
          if (foundPage) {
            return { page: foundPage, groupsOrPages };
          }
        }
      }
    }

    if ('groups' in entry) {
      const newParentGroups = filterInaccessiblePages(entry.groups, userGroups);
      for (const group of newParentGroups) {
        const { page, groupsOrPages } = findPageInNavigation(
          currentPath,
          group as DecoratedPageOrGroupConfig,
          newParentGroups
        );
        if (page) {
          return { page, groupsOrPages };
        }
      }
    }

    for (const key of divisions) {
      if (key in entry) {
        const subDivisions = entry[key as keyof typeof entry] as DecoratedDivisionConfig[];
        if (Array.isArray(subDivisions)) {
          for (const subDivision of subDivisions) {
            if (
              shouldUseDivisionMatch &&
              shouldSkipDivisionMatch({
                subDivision,
                key,
                currentVersion,
                currentLanguage,
                subDivisions,
              })
            )
              continue;

            const { page, groupsOrPages } = findPageInNavigation(
              currentPath,
              subDivision,
              parentGroups
            );
            if (page) {
              const filteredSubDivisions = subDivisions
                .filter((division) => isDivisionAccessible(division, userGroups))
                .map((item) => {
                  const isActive = compareDivisions(item, subDivision);
                  if (item.hidden && !isActive) return undefined;
                  return buildDivisionItem({
                    item,
                    isActive,
                  });
                })
                .filter(Boolean);

              if ('global' in subDivision) {
                upsertGlobalDivisions(navigationDivisions, subDivision.global);
              }

              if (filteredSubDivisions.length) {
                navigationDivisions[key].push(...filteredSubDivisions);
                return { page, groupsOrPages };
              }
            }
          }
        }
      }
    }

    return { page: undefined, groupsOrPages: undefined };
  }

  let groups: FilterDivisionsResult['groupsOrPages'] = [];

  const { page: page, groupsOrPages: filteredGroupsOrPages } = findPageInNavigation(
    currentPath === '/' || currentPath === ''
      ? getFirstPageFromNavigation(decoratedNav)?.href || ''
      : currentPath,
    decoratedNav,
    []
  );

  if (!page && decoratedNav) {
    const { divisions, groupsOrPages } = findMostRelevantDivisions(
      currentPath,
      decoratedNav,
      currentVersion,
      currentLanguage,
      userGroups,
      shouldUseDivisionMatch
    );
    navigationDivisions = divisions;
    groups = groupsOrPages;
  }

  if (filteredGroupsOrPages && filteredGroupsOrPages.length) groups = filteredGroupsOrPages;

  if (navigationDivisions.versions.length) {
    updateFirstHrefInDivision({
      type: 'version',
      currentPath,
      entry: decoratedNav,
      parentGroups: [],
      nearestDivisionValue: undefined,
      isActiveGroup: false,
      inActiveDivision: false,
      currentDivisionValue: navigationDivisions.versions.find((version) => version.isActive)?.name,
      allDivisionValues: navigationDivisions.versions.map((version) => version.name),
      firstHrefInDivision: firstHrefInVersion,
      navigationDivisions,
    });
  }

  if (navigationDivisions.languages.length) {
    updateFirstHrefInDivision({
      type: 'language',
      currentPath,
      entry: decoratedNav,
      parentGroups: [],
      nearestDivisionValue: undefined,
      isActiveGroup: false,
      inActiveDivision: false,
      currentDivisionValue: navigationDivisions.languages.find((language) => language.isActive)
        ?.language,
      allDivisionValues: navigationDivisions.languages.map((language) => language.language),
      firstHrefInDivision: firstHrefInLanguage,
      navigationDivisions,
    });
  }

  if (decoratedNav?.global) {
    upsertGlobalDivisions(navigationDivisions, decoratedNav.global);
  }

  return {
    tabs: navigationDivisions.tabs as DivisionArrayTypes['tabs'],
    anchors: navigationDivisions.anchors as DivisionArrayTypes['anchors'],
    versions: navigationDivisions.versions as DivisionArrayTypes['versions'],
    languages: navigationDivisions.languages as DivisionArrayTypes['languages'],
    dropdowns: navigationDivisions.dropdowns as DivisionArrayTypes['dropdowns'],
    groupsOrPages: groups,
    firstHrefInVersion,
    firstHrefInLanguage,
  };
};

function upsertGlobalDivisions(
  navigationDivisions: NavigationDivisions,
  globalConfig: GlobalConfig | undefined
) {
  if (!globalConfig) return;

  Object.entries(navigationDivisions).forEach(([key, items]) => {
    if (key in globalConfig) {
      const globalItems = globalConfig[key as keyof GlobalConfig];
      if (globalItems) {
        const filteredGlobalItems = globalItems.filter((item) => !item.hidden);
        items.push(
          ...filteredGlobalItems.map((item) => ({
            name: findDivisionName(item),
            ...item,
            href: item.href,
            isActive: false,
            isGlobal: true,
          }))
        );
      }
    }
  });
}

function buildDivisionItem(param: {
  item: DecoratedNavigationConfig | DecoratedDivisionConfig;
  isActive: boolean;
}) {
  const { item, isActive } = param;
  const page = getFirstPageFromNavigation(item as DecoratedNavigationConfig);
  const keysToDrop = [...divisions, 'groups', 'pages'];

  const divisionItem = Object.fromEntries(
    Object.entries(item).filter(([key]) => !keysToDrop.includes(key))
  );

  // if href is absolute url, use it, otherwise use the href from the first page
  const href =
    'href' in item
      ? isAbsoluteUrl(item.href as string)
        ? item.href
        : page?.href || '/'
      : page?.href || '/';

  return {
    name: findDivisionName(item as DecoratedDivisionConfig),
    ...divisionItem,
    href: href,
    isActive,
    isGlobal: false,
  };
}

function findDivisionName(node: DecoratedDivisionConfig): string {
  const divisionNameMap: Record<Division, string> = {
    tabs: 'tab',
    anchors: 'anchor',
    versions: 'version',
    languages: 'language',
    dropdowns: 'dropdown',
  };

  if ('name' in node && typeof node.name === 'string') return node.name;
  const divisionName = Object.values(divisionNameMap).find((value) => value in node);
  if (divisionName) {
    const name = node[divisionName as keyof typeof node];
    if (typeof name === 'string') return name;
  }
  return '';
}

function filterInaccessiblePages(groups: DecoratedGroupConfig[], userGroups: Set<string>) {
  if (!Array.isArray(groups) || !groups.length) return [];
  return groups
    .map((group) => filterInaccessiblePagesRecursive(group, userGroups))
    .filter((group): group is DecoratedGroupConfig => group !== undefined && !group.hidden);
}

function filterInaccessiblePagesRecursive(
  nav: DecoratedGroupConfig,
  userGroups: Set<string>
): DecoratedGroupConfig | DecoratedPageOrGroupConfig | undefined {
  if (typeof nav !== 'object') return undefined;

  if (isPage(nav)) {
    return checkNavAccess(nav, userGroups) ? nav : undefined;
  }

  const newNav = { ...nav };

  if ('pages' in nav) {
    const filteredPages = nav.pages
      .map((page) => filterInaccessiblePagesRecursive(page, userGroups))
      .filter((page): page is DecoratedPageOrGroupConfig => page !== undefined);

    if (filteredPages.length === 0) return undefined;
    newNav.pages = filteredPages;
  }

  return newNav;
}

/**
 * Return true if checkNavAccess passes on some page in provided entry and also groups or pages are not empty
 */
function isDivisionAccessible(
  entry: DecoratedNavigationConfig | DecoratedDivisionConfig,
  userGroups: Set<string>
): boolean {
  if (typeof entry !== 'object') return false;

  if (isPage(entry)) {
    return checkNavAccess(entry, userGroups);
  }

  if ('pages' in entry) {
    if (entry.pages.length === 0) return false;
    let hasAccessiblePage = false;

    for (const page of entry.pages) {
      if (typeof page === 'object') {
        const isAccessible = isDivisionAccessible(page, userGroups);
        if (isAccessible) {
          hasAccessiblePage = true;
          break;
        }
      }
    }

    if (!hasAccessiblePage) return false;
  }

  if ('groups' in entry) {
    if (entry.groups.length === 0) return false;
    if (entry.groups.every((group) => group.hidden)) return false;
    let hasAccessibleGroup = false;

    for (const group of entry.groups) {
      const isAccessible = isDivisionAccessible(group, userGroups);
      if (isAccessible) {
        hasAccessibleGroup = true;
        break;
      }
    }

    if (!hasAccessibleGroup) return false;
  }

  for (const key of divisions) {
    if (key in entry) {
      const subDivisions = entry[key as keyof typeof entry] as DecoratedDivisionNavigationType[];
      if (subDivisions.every((item) => item.hidden)) return false;
      if (!subDivisions.some((subDivision) => isDivisionAccessible(subDivision, userGroups)))
        return false;
    }
  }

  return true;
}

/**
 * This function will find all most relevant divisions and groups or pages
 * for the current path if the current path directs to a page that is not listed in the navigation.
 *
 * Logic:
 * 1. find the most relevant groups or pages
 * 2. find the most relevant divisions based on the most relevant groups or pages
 *
 * Example:
 * if the current path is 1-1-3, it should return all groups under "anchor" and
 * return all divisions on the top level
 * - divisions
 * - [division_name]: "anchor"
 * - groups:
 * - group: "1"
 * - pages: ["1-1", "1-2"]
 *
 * - group: "1-1"
 * - pages: ["1-1-1", "1-1-2"]
 *
 * - group: "2"
 * - pages: ["2-1", "2-2"]
 */
function findMostRelevantDivisions(
  currentPath: string,
  decoratedNav: DecoratedNavigationConfig,
  currentVersion: string | undefined,
  currentLanguage: LocaleType | undefined,
  userGroups: Set<string>,
  shouldUseDivisionMatch: boolean
): {
  divisions: NavigationDivisions;
  groupsOrPages: DecoratedGroupConfig[];
} {
  const navigationDivisions: NavigationDivisions = {
    tabs: [],
    anchors: [],
    versions: [],
    languages: [],
    dropdowns: [],
  };

  let mostSpecificGroups: DecoratedGroupConfig[] | undefined;
  let maxMatchedPrefix: string | undefined;
  let isPageAccessible: boolean = true;

  function findMostRelevantGroupsHelper(
    entry: DecoratedNavigationConfig,
    parentGroups: DecoratedGroupConfig[]
  ) {
    if (typeof entry !== 'object') return;

    if (isPage(entry)) {
      if (
        !isEqualIgnoringLeadingSlash(currentPath, entry.href) &&
        checkNavAccess(entry, userGroups)
      ) {
        const matchedPrefix = findMaxMatchingPrefix(currentPath, entry.href);
        maxMatchedPrefix =
          matchedPrefix.length > (maxMatchedPrefix?.length || 0) ? matchedPrefix : maxMatchedPrefix;
        if (maxMatchedPrefix === matchedPrefix) {
          mostSpecificGroups = parentGroups;
        }
      } else if (isEqualIgnoringLeadingSlash(currentPath, entry.href)) {
        isPageAccessible = checkNavAccess(entry, userGroups);
      }
    }

    if ('pages' in entry) {
      for (const page of entry.pages) {
        if (typeof page === 'object') {
          findMostRelevantGroupsHelper(
            page,
            parentGroups.length
              ? parentGroups
              : entry.pages
                  .map((page: DecoratedPageOrGroupConfig) =>
                    filterInaccessiblePagesRecursive(page, userGroups)
                  )
                  .filter(
                    (page: DecoratedPageOrGroupConfig): page is DecoratedPageOrGroupConfig =>
                      page !== undefined
                  )
          );
        }
      }
    }

    if ('groups' in entry) {
      const newParentGroups = filterInaccessiblePages(entry.groups, userGroups);
      for (const group of entry.groups) {
        findMostRelevantGroupsHelper(group as DecoratedPageOrGroupConfig, newParentGroups);
      }
    }

    for (const key of divisions) {
      if (key in entry) {
        const subDivisions = entry[key as keyof typeof entry];
        if (Array.isArray(subDivisions)) {
          for (const subDivision of subDivisions) {
            if (
              shouldUseDivisionMatch &&
              shouldSkipDivisionMatch({
                subDivision,
                key,
                currentVersion,
                currentLanguage,
                subDivisions,
              })
            )
              continue;

            findMostRelevantGroupsHelper(subDivision, parentGroups);
          }
        }
      }
    }
  }

  findMostRelevantGroupsHelper(decoratedNav, []);

  // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition
  mostSpecificGroups = isPageAccessible ? mostSpecificGroups : [];

  if (mostSpecificGroups) {
    const isStrictlyEqual = (entry: DecoratedGroupConfig[]) => {
      return (
        JSON.stringify(structuredClone(mostSpecificGroups)) ===
        JSON.stringify(structuredClone(entry))
      );
    };

    // look for divisions based on the most specific groups
    function findMostRelevantDivisionsHelper(
      entry: DecoratedNavigationConfig,
      parentGroups: DecoratedGroupConfig[]
    ): boolean {
      if (typeof entry !== 'object') return false;

      if ('pages' in entry) {
        return isStrictlyEqual(
          parentGroups.length
            ? parentGroups
            : entry.pages
                .map((page: DecoratedPageOrGroupConfig) =>
                  filterInaccessiblePagesRecursive(page, userGroups)
                )
                .filter(
                  (page: DecoratedPageOrGroupConfig): page is DecoratedPageOrGroupConfig =>
                    page !== undefined
                )
        );
      }

      if ('groups' in entry) {
        const newParentGroups = filterInaccessiblePages(entry.groups, userGroups);
        return isStrictlyEqual(newParentGroups);
      }

      for (const key of divisions) {
        if (key in entry) {
          const subDivisions = entry[key as keyof typeof entry];

          if (Array.isArray(subDivisions)) {
            for (const subDivision of subDivisions) {
              if (
                shouldUseDivisionMatch &&
                shouldSkipDivisionMatch({
                  subDivision,
                  key,
                  currentVersion,
                  currentLanguage,
                  subDivisions,
                })
              )
                continue;

              const founded = findMostRelevantDivisionsHelper(subDivision, parentGroups);
              /**
               * display divisions if
               * - the page is not accessible and we need to display all accessible divisions
               * OR
               * - the specific group has been found in the division
               */
              if (founded || !isPageAccessible) {
                const filteredSubDivisions = subDivisions
                  .filter((division) => isDivisionAccessible(division, userGroups))
                  .map((item) => {
                    const isActive =
                      compareDivisions(item, subDivision) && isPageAccessible === true;
                    if (item.hidden && !isActive) return undefined;
                    return buildDivisionItem({
                      item,
                      isActive,
                    });
                  })
                  .filter(Boolean);

                navigationDivisions[key].push(...filteredSubDivisions);
                return true;
              }
            }
          }
        }
      }
      return false;
    }

    findMostRelevantDivisionsHelper(decoratedNav, []);
  }

  return {
    divisions: navigationDivisions,
    groupsOrPages: mostSpecificGroups || [],
  };
}

function findMaxMatchingPrefix(path1: string, path2: string): string {
  const minLength: number = Math.min(path1.length, path2.length);
  let matchingLength: number = 0;
  for (let i = 0; i < minLength; i++) {
    if (path1[i] !== path2[i]) {
      break;
    }
    matchingLength++;
  }
  return path1.substring(0, matchingLength);
}

const compareDivisions = (item: DecoratedDivisionConfig, subDivision: DecoratedDivisionConfig) => {
  return JSON.stringify(structuredClone(item)) === JSON.stringify(structuredClone(subDivision));
};

function shouldSkipDivisionMatch({
  subDivision,
  key,
  currentVersion,
  currentLanguage,
  subDivisions,
}: {
  subDivision: DecoratedDivisionConfig;
  key: string;
  currentVersion?: string;
  currentLanguage?: LocaleType;
  subDivisions: DecoratedDivisionConfig[];
}): boolean {
  const isVersionsConfig = key === 'versions' && !!currentVersion;
  const isLanguagesConfig = key === 'languages' && !!currentLanguage;

  if (isVersionsConfig) {
    const versionConfig = subDivision as DecoratedVersionConfig;
    const availableVersions = subDivisions.map((d) => (d as DecoratedVersionConfig).version);
    return versionConfig.version !== currentVersion && availableVersions.includes(currentVersion);
  }

  if (isLanguagesConfig) {
    const languageConfig = subDivision as DecoratedLanguageConfig;
    const availableLanguages = subDivisions.map((d) => (d as DecoratedLanguageConfig).language);

    return (
      languageConfig.language !== currentLanguage && availableLanguages.includes(currentLanguage)
    );
  }

  return false;
}
