import { ChannelData } from '@mintlify/common';
import { useState, useEffect } from 'react';

import { AsyncApiPlaygroundContextController } from '@/contexts/AsyncApiPlaygroundContext';
import { useApiPlaygroundDisplay } from '@/hooks/useApiPlaygroundDisplay';
import { joinWithSingleSlash } from '@/hooks/useSendPlaygroundRequest';
import { copyToClipboard } from '@/utils/copyToClipboard';

import { ApiPlaygroundContext } from '../ApiPlaygroundContext';
import { SendPill } from '../components/SendPill';
import { Bindings } from './Bindings';
import { ChannelPathHeader } from './ChannelPathHeader';
import { MessageExamples } from './MessageExamples';
import { OperationMessages } from './OperationMessages';
import { Parameters } from './Parameters';
import { Playground } from './Playground';
import { SecuritySchemes } from './SecuritySchemes';

type AsyncApiPlaygroundProps = {
  channel: ChannelData;
};

export const AsyncApiPlayground = ({ channel }: AsyncApiPlaygroundProps) => {
  const display = useApiPlaygroundDisplay();
  const [selectedBaseUrlIndex, setSelectedBaseUrlIndex] = useState(0);
  const [isPlaygroundExpanded, setIsPlaygroundExpanded] = useState(false);
  const [isCopiedActive, setIsCopiedActive] = useState(false);
  const supportedSecuritySchemeTypes = ['httpApiKey'];
  const hasSupportedSecuritySchemes = channel.securitySchemes.every((scheme) =>
    supportedSecuritySchemeTypes.includes(scheme.type)
  );
  const playgroundDisabled = display === 'simple' || !hasSupportedSecuritySchemes;

  useEffect(() => {
    const searchParams = new URLSearchParams(window.location.search);
    if (searchParams.has('playground')) {
      requestAnimationFrame(() => {
        setIsPlaygroundExpanded(true);
      });
    }
  }, []);

  const setIsPlaygroundExpandedWithQueryParam = (expanded: boolean) => {
    setIsPlaygroundExpanded(expanded);
    if (expanded) {
      const url = new URL(window.location.href);
      url.searchParams.set('playground', 'open');
      window.history.replaceState({}, '', url);
    } else {
      const url = new URL(window.location.href);
      url.searchParams.delete('playground');
      window.history.replaceState({}, '', url);
    }
  };

  const baseUrlOptions = channel.servers.map((server) => {
    const protocol = server.protocol;
    const host = server.host;
    return `${protocol}://${host}`;
  });

  const onCopy = async () => {
    const basePath = baseUrlOptions[selectedBaseUrlIndex] ?? baseUrlOptions[0] ?? '';
    const channelAddress = channel.address ?? '';
    const joinedPath = joinWithSingleSlash(basePath, channelAddress);
    const result = await copyToClipboard(joinedPath);
    if (result === 'success') {
      setIsCopiedActive(true);
      setTimeout(() => {
        setIsCopiedActive(false);
      }, 2000);
    }
  };

  return (
    <ApiPlaygroundContext.Provider
      value={{
        isSending: false,
        setSelectedBaseUrlIndex,
        selectedBaseUrlIndex,
        baseUrlOptions,
        isPlaygroundExpanded,
        setIsPlaygroundExpanded: setIsPlaygroundExpandedWithQueryParam,
      }}
    >
      <div className="flex flex-col gap-8">
        <div className="mt-6 flex w-full flex-col space-y-4">
          <div className="flex w-full flex-col bg-background-light dark:bg-background-dark border-standard rounded-2xl p-1.5">
            <div className="flex items-center space-x-1.5">
              <ChannelPathHeader
                onCopy={onCopy}
                baseUrlOptions={baseUrlOptions}
                selectedBaseUrlIndex={selectedBaseUrlIndex}
                setSelectedBaseUrlIndex={setSelectedBaseUrlIndex}
                isCopiedActive={isCopiedActive}
                hasGrayBackground={false}
                display={display}
                path={channel.address ?? ''}
                method="websocket"
              />
              {playgroundDisabled ? null : (
                <SendPill
                  label="Connect"
                  method="websocket"
                  onClick={() => setIsPlaygroundExpanded(true)}
                  className="bg-[#000000] dark:bg-[#52525c]"
                  isSending={false}
                />
              )}
            </div>
          </div>
          <div className="mt-8 lg:mt-0 flex flex-col gap-6 xl:hidden">
            <MessageExamples name="Messages" channel={channel} />
          </div>
          <div className="flex flex-col gap-6">
            {channel.parameters.length > 0 && <Parameters parameters={channel.parameters} />}
            {channel.securitySchemes.length > 0 && (
              <SecuritySchemes securitySchemes={channel.securitySchemes} />
            )}
            {channel.bindings.length > 0 && <Bindings bindings={channel.bindings} />}
            {channel.sendOperations.length > 0 && (
              <OperationMessages operations={channel.sendOperations} type="send" />
            )}
            {channel.receiveOperations.length > 0 && (
              <OperationMessages operations={channel.receiveOperations} type="receive" />
            )}
          </div>
        </div>
      </div>
      {!playgroundDisabled && (
        <AsyncApiPlaygroundContextController>
          <Playground
            channel={channel}
            isPlaygroundExpanded={isPlaygroundExpanded}
            setIsPlaygroundExpanded={setIsPlaygroundExpanded}
            baseUrlOptions={baseUrlOptions}
            initialSelectedBaseUrlIndex={selectedBaseUrlIndex}
          />
        </AsyncApiPlaygroundContextController>
      )}
    </ApiPlaygroundContext.Provider>
  );
};
