import Link from 'next/link';
import { NextRouter } from 'next/router';

import { MethodPill } from '@/components/ApiExamples/MethodPill';
import { ApiPageData } from '@/types/api';
import { cn } from '@/utils/cn';
import { isEqualIgnoringLeadingSlash } from '@/utils/paths/isEqualIgnoringLeadingSlash';

export const EndpointsMenuLink = ({ page, router }: { page: ApiPageData; router: NextRouter }) => {
  const pathWithoutParams = router.asPath.split('?')[0];
  const isActive = isEqualIgnoringLeadingSlash(page.href, pathWithoutParams);
  const shortMethod = page.type === 'asyncapi';
  const noPlayground =
    page.playground === 'none' || page.playground === 'simple' || page.method === 'webhook';

  const handleClick = (e: React.MouseEvent) => {
    e.preventDefault();
    if (noPlayground) {
      void router.push(page.href);
    } else {
      void router.push(`${page.href}?playground=open`);
    }
  };

  return (
    <Link href={page.href} title={page.title} onClick={handleClick}>
      <div
        className={cn(
          'flex items-center gap-x-2 hover:bg-gray-50 dark:hover:bg-white/5 rounded-xl p-1.5 group cursor-pointer',
          isActive
            ? 'bg-gray-50 dark:bg-white/5 text-primary dark:text-primary-light font-medium'
            : 'text-gray-700 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300'
        )}
      >
        <MethodPill
          method={page.method}
          shortMethod={shortMethod}
          className="font-medium px-1 py-px text-xs rounded-md"
          active={isActive}
        />
        <div className="text-sm truncate">{page.title}</div>
      </div>
    </Link>
  );
};
