'use client';

import { LocalizationSelect } from './LocalizationSelect';
import { VersionSelect } from './VersionSelect';

export function VersioningSelect({
  triggerClassName,
  triggerLabelClassName,
  showLocalization = true,
}: {
  triggerClassName?: string;
  triggerLabelClassName?: string;
  showLocalization?: boolean;
}) {
  return (
    <div className="flex items-center gap-x-2">
      <VersionSelect triggerClassName={triggerClassName} />
      {showLocalization && (
        <LocalizationSelect
          triggerClassName={triggerClassName}
          triggerLabelClassName={triggerLabelClassName}
        />
      )}
    </div>
  );
}
