'use client';

import { SecurityOption } from '@mintlify/validation';
import { useState, useMemo } from 'react';

import { getDescriptionForSecurityParameter } from '@/api-playground/ApiPlayground/Request/SecuritySection';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import { RecursiveParamField } from './ParamFields/RecursiveParamField';
import { SectionHeading } from './components/SectionHeading';

export type EndpointSecurityParams = {
  securityOptions: SecurityOption[];
};

export const EndpointSecurity = ({ securityOptions }: EndpointSecurityParams) => {
  const [selectedIndex, setSelectedIndex] = useState(0);
  const dropdownOptions = useMemo(() => {
    return securityOptions.map((security) => security.title);
  }, [securityOptions]);

  const selectedSecurity = securityOptions[selectedIndex];
  if (selectedSecurity === undefined) return null;

  const parameters = (['header', 'query', 'cookie'] as const).flatMap((location) =>
    Object.entries(selectedSecurity.parameters[location]).map(([name, parameter]) => (
      <RecursiveParamField
        key={`${location}:${name}`}
        fieldType="authorization"
        schemaArray={[
          {
            type: 'string',
            description: getDescriptionForSecurityParameter(parameter),
            required: true,
            default: parameter['x-default' as never],
          },
        ]}
        name={name}
        location={location}
      />
    ))
  );

  return (
    <div className={cn(Classes.APISection)}>
      <SectionHeading
        title="Authorizations"
        options={dropdownOptions}
        selectedIndex={selectedIndex}
        onSelectOption={setSelectedIndex}
      />
      {parameters}
    </div>
  );
};
