import { cn } from '@/utils/cn';

import { ComponentIcon } from '../Icon';
import { FileWithContentIcon, HashtagIcon, TerminalIcon } from '../icons';

export type SearchResultIconType = 'page' | 'section' | 'api-playground';

type SearchResultIconProps = {
  type: SearchResultIconType;
  icon?: string;
};

export const SearchResultIcon = ({ type, icon }: SearchResultIconProps) => {
  const baseIconClassName = 'text-gray-500 dark:text-gray-400 h-4 w-4 shrink-0';

  if (type === 'api-playground') {
    return (
      <TerminalIcon className={cn(baseIconClassName, 'text-primary dark:text-primary-light')} />
    );
  }

  if (type === 'section') {
    return <HashtagIcon className={baseIconClassName} />;
  }

  if (icon) {
    return (
      <ComponentIcon
        icon={icon}
        iconType="regular"
        className={cn(baseIconClassName, 'bg-gray-500 dark:bg-gray-400')}
        overrideColor
      />
    );
  }

  return <FileWithContentIcon className={baseIconClassName} />;
};
