'use client';

import { NullSchema } from '@mintlify/validation';

import { InputContainer } from '../../components/InputContainer';
import { GenericInput } from './RecursiveInput';

export const NullInput: GenericInput<NullSchema> = ({
  name,
  schema,
  typeDropdown,
  clearValue,
  parentNames,
  arrayIndex,
}) => {
  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={parentNames}
      arrayIndex={arrayIndex}
    >
      <input
        className="flex-1 min-w-0 outline-none bg-transparent text-playground-input"
        type="text"
        placeholder="--"
        contentEditable="false"
        disabled
      />
    </InputContainer>
  );
};
