'use client';

import * as React from 'react';

import { cn } from '@/utils/cn';

interface ChatMessageProps extends React.HTMLAttributes<HTMLDivElement> {
  message: string;
}

export const ChatMessage = React.forwardRef<HTMLDivElement, ChatMessageProps>(
  ({ className, message, ...props }, ref) => {
    return (
      <div className="flex justify-end w-full">
        <div
          ref={ref}
          className={cn(
            'flex px-3.5 py-2.5 items-start gap-4 w-fit rounded-2xl bg-gray-100 dark:bg-white/5',
            className
          )}
          {...props}
        >
          <div className="flex items-start gap-4 w-full">
            <div className="flex flex-col space-y-1 w-full">
              <div className="break-words text-sm text-gray-800 dark:text-gray-200">{message}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
);
ChatMessage.displayName = 'ChatMessage';
