'use client';

import { ParameterGroup } from '@mintlify/validation';
import { useMemo } from 'react';

import { InputSectionContainer } from '../components/InputSectionContainer';
import { RecursiveInput } from './inputs/RecursiveInput';

type ParameterSectionParams = {
  name: string;
  parameters: ParameterGroup;
  value: Record<string, unknown>;
  setValue: (value: Record<string, unknown>) => void;
};

export const ParameterSection = ({ name, parameters, value, setValue }: ParameterSectionParams) => {
  const inputs = useMemo(() => {
    return Object.entries(parameters).map(([paramName, parameter]) => {
      const paramValue = value[paramName];
      const setParamValue = (newParamValue: unknown) => {
        const newObj = { ...value };
        newObj[paramName] = newParamValue;
        setValue(newObj);
      };

      const clearParamValue =
        paramValue === undefined
          ? undefined
          : () => {
              const newObj = { ...value };
              delete newObj[paramName];
              setValue(newObj);
            };

      return (
        <RecursiveInput
          key={paramName}
          name={paramName}
          schemaArray={parameter.schema}
          style={parameter.style}
          explode={parameter.explode}
          value={paramValue}
          setValue={setParamValue}
          clearValue={clearParamValue}
        />
      );
    });
  }, [parameters, setValue, value]);

  if (inputs.length === 0) {
    return null;
  }

  return <InputSectionContainer name={name}>{inputs}</InputSectionContainer>;
};
