import { ApiPlaygroundInputs } from '@mintlify/validation';
import type { Request } from 'har-format';

import { initialRequest } from '@/constants/initialRequest';
import {
  addPathParams,
  addServerVariables,
  joinWithSingleSlash,
} from '@/hooks/useSendPlaygroundRequest';
import { getPostData } from '@/openapi/apiExampleGeneration/createBodyData';
import { createHeaders } from '@/openapi/apiExampleGeneration/createHeaders';

import { GeneratedRequestExampleParams } from './GeneratedRequestExample';
import { getRequiredProperties } from './getRequiredProperties';

type QueryParam = { name: string; value: string };

type GenerateRequestParams = GeneratedRequestExampleParams & {
  exampleData: unknown;
  selectedBodyContentType?: string;
  requiredOnly?: boolean;
};

/**
 * Constructs the request from the URL, schema, and inputs
 */
export const generateRequest = ({
  exampleData,
  endpoint,
  baseUrl,
  inputs,
  selectedBodyContentType,
  requiredOnly,
}: GenerateRequestParams): Request => {
  const queryParamsWithDeepObjects = Object.entries(endpoint.request.parameters.query)
    .filter(([_, p]) => p.style === 'deepObject')
    .map(([name]) => name);

  const queryString = createQueryString(inputs, queryParamsWithDeepObjects);
  const path = addPathParams(endpoint.path, inputs.path);
  const headers = createHeaders(endpoint, inputs.header, selectedBodyContentType);

  const bodyData =
    inputs.body !== undefined
      ? typeof exampleData === 'object'
        ? deepMergeTruthyValues(exampleData, inputs.body)
        : inputs.body
      : exampleData;

  const requiredProperties =
    requiredOnly && !inputs.body
      ? getRequiredProperties(endpoint, selectedBodyContentType)
      : undefined;

  const postData = getPostData(bodyData, selectedBodyContentType, requiredProperties);

  return {
    ...initialRequest,
    method: endpoint.method.toUpperCase(),
    url: joinWithSingleSlash(addServerVariables(baseUrl, inputs.server), path),
    queryString,
    headers,
    postData,
  };
};

const createQueryString = (
  inputs: ApiPlaygroundInputs,
  queryParamsWithDeepObjects?: string[]
): QueryParam[] => {
  return Object.entries(inputs.query).flatMap(([name, value]) => {
    if (!value) return [];

    if (Array.isArray(value)) {
      return value.filter(Boolean).map((element) => ({
        name,
        value: element.toString(),
      }));
    }

    if (typeof value === 'object') {
      if (queryParamsWithDeepObjects?.includes(name)) {
        return Object.entries(value)
          .filter(([_, value]) => value)
          .map(([key, value]) => ({
            name: `${name}[${key}]`,
            value: value.toString(),
          }));
      }

      return [{ name, value: JSON.stringify(value) }];
    }

    return [
      {
        name,
        value: value.toString(),
      },
    ];
  });
};

const deepMergeTruthyValues = (target: unknown, source: unknown): unknown => {
  if (!source || typeof source !== 'object' || Array.isArray(source)) {
    return source && source !== '' ? source : target;
  }

  if (!target || typeof target !== 'object' || Array.isArray(target)) {
    return target;
  }

  const result = { ...(target as Record<string, unknown>) };

  Object.keys(target as Record<string, unknown>).forEach((key) => {
    if (key in source) {
      result[key] = deepMergeTruthyValues(
        (target as Record<string, unknown>)[key],
        (source as Record<string, unknown>)[key]
      );
    }
  });

  Object.keys(source as Record<string, unknown>).forEach((key) => {
    if (!(key in target)) {
      result[key] = (source as Record<string, unknown>)[key];
    }
  });

  return result;
};
