'use client';

import Link from 'next/link';

import { useSelectedLocale } from '@/contexts/NavigationContext';

import AlertIcon from './Alert';
import EditIcon from './Edit';

export enum FeedbackButtonIconType {
  Edit,
  Alert,
}

const IconButton = ({ href, type }: { href: string; type: FeedbackButtonIconType }) => {
  const { suggestEdits, raiseIssue } = useSelectedLocale();
  return (
    <Link
      href={href}
      className="px-3.5 py-2 flex flex-row gap-3 items-center border-standard rounded-xl text-gray-600 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-300 bg-white/50 dark:bg-codeblock/50 hover:border-gray-500 hover:dark:border-gray-500"
      target="_blank"
      rel="noopener noreferrer"
    >
      {type === FeedbackButtonIconType.Alert && <AlertIcon className="fill-current" />}
      {type === FeedbackButtonIconType.Edit && <EditIcon className="fill-current" />}
      {type === FeedbackButtonIconType.Edit && (
        <small className="text-sm leading-4">{suggestEdits}</small>
      )}
      {type === FeedbackButtonIconType.Alert && (
        <small className="text-sm leading-4">{raiseIssue}</small>
      )}
    </Link>
  );
};

export default IconButton;
