'use client';

import { MessageExamples } from '@/api-playground/ApiPlayground/AsyncApi/MessageExamples';
import ApiExamples from '@/components/ApiExamples/ApiExamples';
import { MdxPanel } from '@/components/MdxPanel';
import { useChannel } from '@/hooks/useChannel';
import { useHasEndpoint } from '@/hooks/useEndpoint';
import { useHasPanel } from '@/hooks/useHasPanel';
import { useMDXContent } from '@/hooks/useMDXContent';
import { useHasMdxCodeExample } from '@/hooks/useMdxCodeExampleData';
import { useHasSchema } from '@/hooks/useSchema';

import { ChangelogFilters } from './ChangelogFilters';
import { ContentSideLayout } from './ContentSideLayout';
import { TableOfContents } from './TableOfContents';

export type SidePanelProps = {
  showExample: boolean;
};

export const SidePanel = () => {
  const hasEndpoint = useHasEndpoint();
  const hasMdxCodeExample = useHasMdxCodeExample();
  const hasSchema = useHasSchema();
  const [{ changelogFilters }] = useMDXContent();
  const showExample = hasEndpoint || hasMdxCodeExample || hasSchema;
  const channel = useChannel();

  const hasPanel = useHasPanel();

  if (hasPanel) {
    return (
      <ContentSideLayout>
        <MdxPanel />
        {channel && !showExample && <MessageExamples name="Messages" channel={channel} />}
        {showExample && <ApiExamples />}
      </ContentSideLayout>
    );
  }

  if (channel) {
    return (
      <ContentSideLayout>
        <MessageExamples name="Messages" channel={channel} />
      </ContentSideLayout>
    );
  }

  if (showExample) {
    return (
      <ContentSideLayout>
        <ApiExamples />
      </ContentSideLayout>
    );
  }

  if (!!changelogFilters.length) {
    return (
      <ContentSideLayout>
        <ChangelogFilters />
      </ContentSideLayout>
    );
  }

  return (
    <ContentSideLayout>
      <TableOfContents />
    </ContentSideLayout>
  );
};
