'use client';

import { BodySchema, DataSchemaArray, IncrementalDataSchemaArray } from '@mintlify/validation';
import { useMemo, useState } from 'react';

import { Tab, Tabs } from '@/components/Tabs';
import { NoAnyDataSchema } from '@/hooks/usePlaygroundSchemaOptions';

import { InputSectionContainer } from '../components/InputSectionContainer';
import { TypeDropdown } from '../components/TypeDropdown';
import { RecursiveInput } from './inputs/RecursiveInput';

type BodySectionParams = {
  body: BodySchema;
  value: unknown;
  setValue: (value: unknown) => void;
};

export const BodySection = ({ body, value, setValue }: BodySectionParams) => {
  const [selectedSchemaArrayIndex, setSelectedSchemaArrayIndex] = useState(0);
  const [selectedContentTypeIndex, setSelectedContentTypeIndex] = useState(0);

  const bodyArray = useMemo(() => {
    return Object.entries(body);
  }, [body]);

  const contentTypeAndContent = bodyArray[selectedContentTypeIndex] ?? bodyArray[0];
  if (contentTypeAndContent === undefined) return null;

  const [_contentType, { schemaArray }] = contentTypeAndContent;

  return (
    <InputSectionContainer
      name="Body"
      rightElement={
        bodyArray.length > 1 && (
          <div onClick={(e) => e.stopPropagation()} className="text-xs font-mono">
            <TypeDropdown
              options={bodyArray.map(([label, schema]) => ({
                label,
                schema: schema as unknown as NoAnyDataSchema,
              }))}
              selectedIndex={selectedContentTypeIndex}
              onSelectOption={(index) => {
                setSelectedContentTypeIndex(index);
              }}
            />
          </div>
        )
      }
    >
      {schemaArray.length > 1 ? (
        <Tabs
          defaultTabIndex={selectedSchemaArrayIndex}
          onClickTab={(index) => setSelectedSchemaArrayIndex(index)}
          className="!mb-0"
        >
          {schemaArray.map((schema, index) => {
            const title = schema.title ?? `Option ${index + 1}`;
            return (
              <Tab key={title} title={title}>
                <RecursiveInput
                  schemaArray={[schema] as DataSchemaArray | IncrementalDataSchemaArray}
                  value={value}
                  setValue={setValue}
                  clearValue={value === undefined ? undefined : () => setValue(undefined)}
                  isBody
                />
              </Tab>
            );
          })}
        </Tabs>
      ) : (
        <RecursiveInput
          schemaArray={schemaArray}
          value={value}
          setValue={setValue}
          clearValue={value === undefined ? undefined : () => setValue(undefined)}
          isBody
        />
      )}
    </InputSectionContainer>
  );
};
