import { GitSource } from '@mintlify/validation';

import { optionallyRemoveLeadingSlash } from './paths/optionallyRemoveLeadingSlash';

export function createGithubEditUrl(path: string, githubSource: GitSource) {
  const { owner, repo, deployBranch, contentDirectory } = githubSource;
  return `https://github.com/${owner}/${repo}/edit/${deployBranch}/${
    contentDirectory ? `${contentDirectory}/` : ''
  }${optionallyRemoveLeadingSlash(path)}.mdx`;
}

export function createGithubIssueUrl(path: string, githubSource: GitSource) {
  const { owner, repo } = githubSource;
  return `https://github.com/${owner}/${repo}/issues/new?title=Issue on docs&body=Path: ${path}`;
}
