import { isAbsoluteUrl } from '@mintlify/common';
import Link from 'next/link';
import { useCallback, useContext, useMemo } from 'react';

import { AuthContext, DocsConfigContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import initiatePkceAndRedirect from '@/hooks/useUserInfo/initiatePkceAndRedirect';
import { Classes } from '@/lib/local/selectors';
import { ComponentIcon } from '@/ui/Icon';
import { cn } from '@/utils/cn';

import { TopbarPrimary } from '../mint/shared/TopbarPrimary';

const actionClassname =
  'flex items-center gap-1.5 whitespace-nowrap font-medium text-gray-600 hover:text-gray-900 dark:text-gray-400 dark:hover:text-gray-300';

export const WillowNavbarLinks = () => {
  const { docsConfig } = useContext(DocsConfigContext);
  const { userAuth, userInfo, auth } = useContext(AuthContext);
  const { basePath } = useMintlifyRouter();
  const trackNavigationClick = useAnalyticsContext('header_nav_item_click');

  const userAuthLoginButton = useMemo(() => {
    if (userInfo || !userAuth) return null;

    switch (userAuth.type) {
      case 'jwt': {
        return (
          <Link href={userAuth.loginUrl} className={actionClassname}>
            Log In
          </Link>
        );
      }
      case 'shared-session': {
        if (!userAuth.loginUrl) return null;
        return (
          <Link href={userAuth.loginUrl} className={actionClassname}>
            Log In
          </Link>
        );
      }
      case 'oauth': {
        return (
          <button
            onClick={() => initiatePkceAndRedirect(userAuth, basePath)}
            className={actionClassname}
          >
            Log In
          </button>
        );
      }
    }
  }, [userAuth, userInfo, basePath]);

  const authLoginButton = useMemo(() => {
    if (userInfo || !auth || !auth.partial) return null;

    const link = `/${basePath}/login`.replace(/\/{2,}/g, '/');
    return (
      <Link href={link} className={actionClassname}>
        Log In
      </Link>
    );
  }, [auth, userInfo, basePath]);

  const onClickNavigation = useCallback(
    (name: string | undefined, url: string) => trackNavigationClick({ name, url }),
    [trackNavigationClick]
  );

  return (
    <>
      {docsConfig?.navbar?.links?.map((link) => {
        const isAbsolute = isAbsoluteUrl(link.href);
        const { label } = link as { label?: string };
        if (isAbsolute) {
          return (
            <li key={label} className={cn(Classes.NavBarLink)}>
              <a
                href={link.href}
                className={actionClassname}
                onClick={() => onClickNavigation(label, link.href)}
                target="_blank"
              >
                {link.icon && (
                  <ComponentIcon
                    icon={typeof link.icon === 'string' ? link.icon : link.icon.name}
                    iconType={
                      typeof link.icon === 'string' ? 'regular' : link.icon.style || 'regular'
                    }
                    className={cn('h-4 w-4')}
                  />
                )}
                {label}
              </a>
            </li>
          );
        } else {
          return (
            <li key={label} className={cn(Classes.NavBarLink)}>
              <Link
                href={link.href || '/'}
                className={actionClassname}
                onClick={() => onClickNavigation(label, link.href)}
              >
                {link.icon && (
                  <ComponentIcon
                    icon={typeof link.icon === 'string' ? link.icon : link.icon.name}
                    iconType={
                      typeof link.icon === 'string' ? 'regular' : link.icon.style || 'regular'
                    }
                    className={cn('h-4 w-4')}
                  />
                )}
                {label}
              </Link>
            </li>
          );
        }
      })}
      {userAuthLoginButton && <li key="login">{userAuthLoginButton}</li>}
      {authLoginButton && <li key="partial-auth-login">{authLoginButton}</li>}
      <TopbarPrimary />
    </>
  );
};
