import { ReactElement, useContext, useState } from 'react';

import { AdvancedFooter, Footer } from '@/components/Footer';
import { MdxPanel } from '@/components/MdxPanel';
import { PageContext } from '@/contexts/ConfigContext';
import { MDXContentContext } from '@/contexts/MDXContentContext';
import { useMDXContentReducer } from '@/contexts/MDXContentContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { Api } from '@/ui/Content/Api';
import { Content } from '@/ui/Content/Content';
import { PageHeader } from '@/ui/Content/PageHeader';
import { SidePanel } from '@/ui/Content/SidePanel';
import { UserFeedback } from '@/ui/Feedback/UserFeedback';
import { ChatAssistantSheet } from '@/ui/search/ChatAssistantSheet';
import { cn } from '@/utils/cn';

import { PalmNav } from './Nav';
import { PalmPagination } from './Pagination';
import { PalmTopBar } from './Topbar/PalmTopbar';

export function PalmLayout({ children }: { children: ReactElement }) {
  const router = useMintlifyRouter();
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const { pageMetadata } = useContext(PageContext);
  const isCustom = pageMetadata.mode === 'custom';

  return (
    <>
      {!isCustom && <PalmTopBar />}
      <div className={cn(!isCustom && 'lg:flex')}>
        {!isCustom && <PalmNav isSidebarOpen={isSidebarOpen} setIsSidebarOpen={setIsSidebarOpen} />}
        <MDXContentController key={router.asPath}>{children}</MDXContentController>
      </div>
      {!isCustom && <AdvancedFooter className="w-full bg-gray-950/[0.03] dark:bg-white/[0.03]" />}
    </>
  );
}

function MDXContentController({ children }: { children: ReactElement }) {
  const { pageMetadata, mdxExtracts } = useContext(PageContext);

  const initialSelectedBodyContentType = Object.keys(mdxExtracts?.endpoint?.request.body ?? {})[0];

  // Set initial values.
  const ctx = useMDXContentReducer({
    pageMetadata,
    selectedBodyContentType: initialSelectedBodyContentType,
    ...mdxExtracts,
  });

  return (
    <MDXContentContext.Provider value={ctx}>
      <PalmContentContainer>
        <>
          <PageHeader />
          <MdxPanel mobile />
          <Api />
          <Content>{children}</Content>
        </>
      </PalmContentContainer>

      <ChatAssistantSheet />
    </MDXContentContext.Provider>
  );
}

function PalmContentContainer({ children }: { children: ReactElement }) {
  const { isCustom, isWide, isCenter } = usePageMode();

  if (isCustom) {
    return (
      <>
        <PalmTopBar />
        {children}
      </>
    );
  }

  return (
    <>
      <div
        className={cn(
          'flex-1 px-5 pt-36 lg:pt-10 lg:pl-24 transition-all duration-100',
          isWide && 'px-14',
          isCenter && 'lg:pl-0 max-w-3xl mx-auto'
        )}
        id={Ids.ContentContainer}
      >
        <div className="mx-auto max-w-5xl flex gap-x-12">
          <div className="w-full overflow-x-auto" id={Ids.ContentArea}>
            {children}
            <div className="leading-6 mt-14">
              <UserFeedback />
              <PalmPagination />
            </div>
            <Footer className="border-t-0" />
          </div>
          {!isWide && <SidePanel />}
        </div>
      </div>
    </>
  );
}
