'use client';

import {
  AnySchema,
  BooleanSchema,
  DataSchema,
  FileSchema,
  IncrementalDataSchema,
  NullSchema,
  NumberEnumSchema,
  NumberSchema,
  StringEnumSchema,
  StringSchema,
} from '@mintlify/validation';

import { ParamHead } from '@/components/Param';
import { FieldType } from '@/types/api';
import { cn } from '@/utils/cn';

import { Description } from '../../components/Description';
import { ExampleDescription } from '../ExampleDescription';
import { NumberMetadata } from './NumberMetadata';
import { StringMetadata } from './StringMetadata';

export type PrimitiveParamFieldParams = {
  fieldType: FieldType;
  schema:
    | BooleanSchema
    | StringSchema
    | NumberSchema
    | StringEnumSchema
    | NumberEnumSchema
    | FileSchema
    | NullSchema
    | AnySchema;
  typeOptions: string[];
  name?: string | null;
  location?: string;
  selectedIndex?: number;
  onSelectTypeOption?: (index: number) => void;
  parentName?: string;
  paramId?: string;
  style?: string;
  explode?: boolean;
};

export const PrimitiveParamField = ({
  fieldType,
  schema,
  name,
  location,
  typeOptions,
  selectedIndex,
  onSelectTypeOption,
  parentName,
  paramId,
  style,
  explode,
}: PrimitiveParamFieldParams) => {
  const typeLabel = typeOptions[selectedIndex ?? 0];

  const description =
    schema.description ??
    (name === undefined ? `The ${fieldType} is of type \`${typeLabel}\`.` : undefined);

  return (
    <div className="py-6">
      <ParamHead
        id={paramId}
        name={name}
        typeLabel={typeLabel}
        location={location}
        fieldType={fieldType}
        required={schema.required}
        deprecated={schema.deprecated}
        defaultValue={schema.default}
        typeOptions={typeOptions}
        selectedTypeOptionIndex={selectedIndex}
        onSelectTypeOption={onSelectTypeOption}
        parentName={parentName}
        style={style}
        explode={explode}
      />
      <div className={cn({ 'mt-4': description || isEnum(schema) })}>
        {description && <Description markdown={description} />}
        {isEnum(schema) && <EnumOptions schema={schema} />}
        {isStringSchema(schema) && <StringMetadata schema={schema} description={description} />}
        {isNumberSchema(schema) && <NumberMetadata schema={schema} />}
        <ExampleDescription schema={schema} />
      </div>
    </div>
  );
};

export const isEnum = (
  schema: DataSchema | IncrementalDataSchema
): schema is NumberEnumSchema | StringEnumSchema => {
  return (
    schema.type === 'enum<integer>' ||
    schema.type === 'enum<number>' ||
    schema.type === 'enum<string>'
  );
};

export const EnumOptions = ({ schema }: { schema: NumberEnumSchema | StringEnumSchema }) => {
  if (schema.enum.length === 0) {
    return null;
  }

  return (
    <div className="whitespace-pre-wrap prose-sm mt-6">
      Available options:{' '}
      {schema.enum.map((enumValue: string | number, i: number) => (
        <div key={`${enumValue}-${i}`} className="inline-block">
          <code>{enumValue}</code>
          {i !== schema.enum.length - 1 && ','}{' '}
        </div>
      ))}
    </div>
  );
};

const isStringSchema = (schema: DataSchema | IncrementalDataSchema): schema is StringSchema => {
  return schema.type === 'string';
};

const isNumberSchema = (schema: DataSchema | IncrementalDataSchema): schema is NumberSchema => {
  return schema.type === 'number' || schema.type === 'integer';
};
