'use client';

import type { MdxCodeExampleData } from '@mintlify/common';
import parse from 'html-react-parser';

import { CodeBlock } from '@/components/CodeBlock';
import { CodeGroup } from '@/components/CodeGroup';
import { StaticSpan } from '@/components/Static';
import { getLanguageFromClassName } from '@/utils/shiki';

export const MdxCodeExample = ({ data }: { data: MdxCodeExampleData }) => {
  return (
    <CodeGroup isSmallText noMargins>
      {data.children?.map((child, i) => {
        const jsx = parse(child.html ?? '');
        const lang =
          typeof jsx === 'object' && !Array.isArray(jsx) && jsx.type === 'pre'
            ? getLanguageFromClassName(jsx.props.className, child.filename)
            : undefined;
        return (
          <CodeBlock language={lang} filename={child.filename} key={(child.filename ?? '') + i}>
            <StaticSpan>{jsx}</StaticSpan>
          </CodeBlock>
        );
      })}
    </CodeGroup>
  );
};
