'use client';

import { ArrowDown } from 'lucide-react';
import {
  ComponentPropsWithoutRef,
  ForwardedRef,
  forwardRef,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { useExpandable } from '@/hooks/useExpandable';
import { Classes } from '@/lib/local/selectors';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';
import { getCodeBlockScrollbarClassname } from '@/utils/getScrollbarClassname';
import { useGetShikiHighlightedHtml } from '@/utils/shiki';

import { copyToClipboard, CopyToClipboardResult } from '../../utils/copyToClipboard';
import { getHighlightedLines } from './getHighlightedLines';
import { getNodeText } from './getNodeText';

export interface CodeBlockPropsBase {
  language?: string;
  filename?: string;
  /**
   * The callback function when a user clicks on the copied to clipboard button
   */
  onCopied?: (result: CopyToClipboardResult, textToCopy?: string) => void;
  expandable?: boolean;
}

export type CodeBlockProps = CodeBlockPropsBase &
  Omit<ComponentPropsWithoutRef<'div'>, keyof CodeBlockPropsBase>;

export const CodeBlock = forwardRef(function CodeBlock(
  { filename, onCopied, children, className, expandable = false, ...props }: CodeBlockProps,
  ref: ForwardedRef<HTMLDivElement>
) {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeString = getNodeText(children);
  const highlightedLines = getHighlightedLines(
    typeof children === 'object' && children ? (children as JSX.Element) : ({} as JSX.Element)
  );
  const { isExpanded, calculatedHeight, contentRef, toggleExpanded } = useExpandable(expandable);
  const CopyButton = (props: Partial<ComponentPropsWithoutRef<typeof CopyToClipboardButton>>) => (
    <CopyToClipboardButton textToCopy={codeString} onCopied={onCopied} {...props} />
  );
  const codeBlockTheme = docsConfig?.styling?.codeblocks;
  const opts = highlightedLines.length ? { opts: { highlightedLines } } : undefined;

  const html = useGetShikiHighlightedHtml(
    props.language
      ? { codeString, codeBlockTheme, language: props.language, ...opts }
      : { codeString, codeBlockTheme, className, ...opts }
  );

  const CodeElement = () => {
    return html ? (
      <div
        suppressHydrationWarning
        ref={contentRef}
        tabIndex={-1}
        dangerouslySetInnerHTML={{ __html: html }}
      />
    ) : (
      <div suppressHydrationWarning ref={contentRef} tabIndex={-1}>
        {children}
      </div>
    );
  };

  return (
    <div
      className={cn(
        Classes.CodeBlock,
        'mt-5 mb-8 not-prose rounded-2xl relative group',
        codeBlockTheme === 'system' &&
          'text-gray-950 bg-white dark:bg-codeblock dark:text-gray-50 border border-gray-200/70 dark:border-gray-800/50 codeblock-light',
        (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
          'text-gray-50 bg-[#0F1117] dark:bg-codeblock ring-1 ring-transparent dark:ring-gray-800/50 codeblock-dark',
        className
      )}
      ref={ref}
      {...props}
    >
      {filename ? (
        <CodeTabBar filename={filename}>
          <CopyButton />
        </CodeTabBar>
      ) : (
        <CopyButton className="absolute mt-0.5 top-4 right-4" />
      )}
      <div
        className={cn(
          'min-w-full max-w-full relative text-sm leading-6 children:!my-0 children:!shadow-none children:!bg-transparent',
          'transition-[height] duration-300 ease-in-out overflow-x-auto',
          'p-5 h-full [&_*]:ring-0 [&_*]:outline-none [&_*]:focus:ring-0 [&_*]:focus:outline-none',
          `[&_pre>code]:pr-[3rem] [&>pre>code>span.line-highlight]:min-w-[calc(100%+3rem)]`,
          isExpanded ? 'overflow-y-auto' : 'overflow-y-hidden',
          getCodeBlockScrollbarClassname(codeBlockTheme)
        )}
        data-component-part="code-block-root"
        style={{
          fontVariantLigatures: 'none',
          height: expandable ? (!isExpanded ? '100px' : `${calculatedHeight}px`) : 'auto',
        }}
        tabIndex={-1}
      >
        <CodeElement />
        {expandable && (
          <div
            className={cn(
              'absolute left-0 z-[10] right-0 flex justify-center items-end',
              isExpanded ? 'bottom-0 rounded-b-2xl' : 'rounded-2xl h-full top-0 bg-transparent',

              codeBlockTheme === 'system'
                ? 'bg-gradient-to-b from-transparent to-background-light dark:from-transparent dark:to-background-dark'
                : 'bg-gradient-to-b from-transparent to-black/80'
            )}
          >
            <button
              onClick={toggleExpanded}
              className={cn(
                'flex items-center gap-2 rounded-lg px-2 py-1.5 text-sm backdrop-blur-sm',
                codeBlockTheme === 'system'
                  ? 'bg-gray-500/20 text-gray-900 dark:bg-white/20 dark:text-white'
                  : 'bg-white/20 text-white',
                isExpanded ? 'mb-6' : 'mb-8'
              )}
            >
              <ArrowDown
                className={cn(
                  'size-3 transition-transform duration-400',
                  isExpanded && 'rotate-180'
                )}
              />
              {isExpanded ? 'Collapse code' : 'Expand code'}
            </button>
          </div>
        )}
      </div>
    </div>
  );
});

/**
 * Different from CodeGroup because we cannot use Headless UI's Tab component outside a Tab.Group
 * Styling should look the same though.
 */
function CodeTabBar({ filename, children }: { filename: string; children?: ReactNode }) {
  const { docsConfig } = useContext(DocsConfigContext);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div
      className={cn(
        'flex rounded-t-2xl text-gray-400 text-xs leading-6 border-b font-medium',
        codeBlockTheme === 'system' &&
          'bg-gray-100/40 dark:bg-black/40 border-gray-200/50 dark:border-gray-800',
        (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
          'bg-black/40 border-gray-900/80'
      )}
      data-component-part="code-block-tab-bar"
    >
      <div
        className={cn(
          'flex-none border-b px-4 pt-2.5 pb-2 flex items-center',
          codeBlockTheme === 'system' &&
            'text-primary dark:text-primary-light border-primary dark:border-primary-light',
          (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
            'text-primary-light border-primary-light'
        )}
        data-component-part="code-block-tab-bar-filename"
      >
        {filename}
      </div>
      <div className="flex-1 mr-4 flex items-center justify-end">{children}</div>
    </div>
  );
}

export function CopyToClipboardButton({
  textToCopy,
  onCopied,
  className,
}: {
  textToCopy: string;
  onCopied?: (result: CopyToClipboardResult, textToCopy?: string) => void;
  className?: string;
} & ComponentPropsWithoutRef<'button'>) {
  const trimmedTextToCopy = textToCopy.trim();
  const [isCopiedActive, setIsCopiedActive] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true);

  useEffect(() => {
    // Hide copy button if the browser does not support it
    // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition
    if (typeof window !== 'undefined' && !navigator.clipboard) {
      console.warn(
        "The browser's Clipboard API is unavailable. The Clipboard API is only available on HTTPS."
      );
      setIsDisabled(true);
    } else {
      setIsDisabled(false);
    }
  }, []);

  // Hide copy button if you would copy an empty string
  if (!trimmedTextToCopy || isDisabled) {
    return null;
  }

  const onCopy = async () => {
    const result = await copyToClipboard(trimmedTextToCopy);
    if (onCopied) {
      onCopied(result, trimmedTextToCopy);
    }
    if (result === 'success') {
      setIsCopiedActive(true);
      setTimeout(() => {
        setIsCopiedActive(false);
      }, 2000);
    }
  };

  return <CopyIconButton onClick={onCopy} isCopiedActive={isCopiedActive} className={className} />;
}

export function CopyIconButton({
  onClick,
  isCopiedActive,
  className,
}: {
  onClick: () => void;
  isCopiedActive: boolean;
  className?: string;
}) {
  const { docsConfig } = useContext(DocsConfigContext);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div className={cn('z-10 relative', className)}>
      <button
        className={
          'h-7 w-7 flex items-center justify-center rounded-md backdrop-blur peer group/copy-button'
        }
        onClick={onClick}
        data-testid="copy-code-button"
        aria-label="Copy the contents from the code block"
      >
        {isCopiedActive ? (
          <svg
            className={cn(
              codeBlockTheme === 'system' && 'fill-primary dark:fill-primary-light',
              (codeBlockTheme === 'dark' || codeBlockTheme == undefined) && 'fill-primary-light'
            )}
            width="16"
            height="11"
            viewBox="0 0 16 11"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path d="M14.7813 1.21873C15.0751 1.51248 15.0751 1.98748 14.7813 2.2781L6.53135 10.5312C6.2376 10.825 5.7626 10.825 5.47197 10.5312L1.21885 6.28123C0.925098 5.98748 0.925098 5.51248 1.21885 5.22185C1.5126 4.93123 1.9876 4.9281 2.27822 5.22185L5.99697 8.9406L13.7188 1.21873C14.0126 0.924976 14.4876 0.924976 14.7782 1.21873H14.7813Z" />
          </svg>
        ) : (
          <Icon
            icon="clone"
            className={cn(
              'w-4 h-4',
              codeBlockTheme === 'system' &&
                'bg-gray-400 group-hover/copy-button:bg-gray-500 dark:bg-white/40 dark:group-hover/copy-button:bg-white/60',
              (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
                'bg-white/40 group-hover/copy-button:bg-white/60'
            )}
          />
        )}
      </button>
      <CodeBlockTooltip text={isCopiedActive ? 'Copied!' : 'Copy'} />
    </div>
  );
}

export function CodeBlockTooltip({ text }: { text: string }) {
  return (
    <div
      aria-hidden
      className="absolute top-11 left-1/2 transform -translate-x-1/2 -translate-y-1/2 peer-hover:opacity-100 opacity-0 text-white rounded-lg px-1.5 py-0.5 text-xs bg-primary-dark"
    >
      {text}
    </div>
  );
}
