'use client';

import { useContext, useMemo } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';

import { useEndpoint } from './useEndpoint';

export const useBaseUrlOptions = () => {
  const endpoint = useEndpoint();
  const { docsConfig } = useContext(DocsConfigContext);

  return useMemo(() => {
    const endpointUrls = endpoint?.servers?.map(({ url }) => url);
    if (endpointUrls && endpointUrls.length > 0) {
      return endpointUrls;
    }

    const configUrlOrUrls = docsConfig?.api?.mdx?.server;
    return typeof configUrlOrUrls === 'string' ? [configUrlOrUrls] : configUrlOrUrls;
  }, [endpoint?.servers, docsConfig?.api?.mdx?.server]);
};
