import { ChannelData } from '@mintlify/common';
import { useContext, useEffect, useState } from 'react';

import { AsyncApiPlaygroundContext } from '@/contexts/AsyncApiPlaygroundContext';
import { useKeyboardShortcut } from '@/hooks/useKeyboardShortcut';
import { useWebsocketConnection } from '@/hooks/useWebsocketConnection';
import { joinAddress } from '@/utils/asyncapi';
import { cn } from '@/utils/cn';
import { copyToClipboard } from '@/utils/copyToClipboard';

import { ConnectButton } from '../components/ConnectButton';
import { EndpointsMenu } from '../components/EndpointsMenu';
import { ModalHeader } from '../components/ModalHeader';
import { PlaygroundModalWrapper } from '../components/PlaygroundModalWrapper';
import { ChannelPathHeader } from './ChannelPathHeader';
import { InputParameters } from './InputParameters';
import { InputSecurity } from './InputSecurity';
import { MessageResults } from './MessageResults';
import { SendMessage } from './SendMessage';

type PlaygroundProps = {
  channel: ChannelData;
  isPlaygroundExpanded: boolean;
  setIsPlaygroundExpanded: (isPlaygroundExpanded: boolean) => void;
  baseUrlOptions: string[];
  initialSelectedBaseUrlIndex: number;
};

export const Playground = ({
  channel,
  isPlaygroundExpanded,
  setIsPlaygroundExpanded,
  baseUrlOptions,
  initialSelectedBaseUrlIndex,
}: PlaygroundProps) => {
  const [selectedBaseUrlIndex, setSelectedBaseUrlIndex] = useState(initialSelectedBaseUrlIndex);
  const [isCopiedActive, setIsCopiedActive] = useState(false);
  const {
    address,
    setAddress,
    onMessage,
    parameterValues,
    handleParameterChange,
    result,
    clearResult,
    securitySchemeValues,
    handleSecuritySchemeChange,
  } = useContext(AsyncApiPlaygroundContext);
  const { connect, disconnect, connecting, isConnected, sendMessage } = useWebsocketConnection({
    address,
    onMessage,
  });

  useEffect(() => {
    const joinedAddress = joinAddress({
      address: channel.address ?? '',
      baseUrl: baseUrlOptions[selectedBaseUrlIndex] ?? baseUrlOptions[0] ?? '',
      parameterValues,
      securitySchemeValues,
    });

    setAddress(joinedAddress);
  }, [
    selectedBaseUrlIndex,
    baseUrlOptions,
    channel,
    setAddress,
    parameterValues,
    securitySchemeValues,
  ]);

  const onCopy = async () => {
    const joinedAddress = joinAddress({
      address: channel.address ?? '',
      baseUrl: baseUrlOptions[selectedBaseUrlIndex] ?? baseUrlOptions[0] ?? '',
      parameterValues,
      securitySchemeValues,
    });

    const result = await copyToClipboard(joinedAddress);
    if (result === 'success') {
      setIsCopiedActive(true);
      setTimeout(() => {
        setIsCopiedActive(false);
      }, 2000);
    }
  };

  const handleClosePlayground = () => {
    if (isConnected) {
      disconnect();
    }
    setIsPlaygroundExpanded(false);
  };

  useKeyboardShortcut(
    'Escape',
    isPlaygroundExpanded ? () => handleClosePlayground() : undefined,
    true
  );

  return (
    <PlaygroundModalWrapper
      isPlaygroundExpanded={isPlaygroundExpanded}
      setIsPlaygroundExpanded={setIsPlaygroundExpanded}
    >
      <div className="flex items-center justify-between gap-x-2">
        <EndpointsMenu channel={channel} />
        <ChannelPathHeader
          onCopy={onCopy}
          baseUrlOptions={baseUrlOptions}
          selectedBaseUrlIndex={selectedBaseUrlIndex}
          setSelectedBaseUrlIndex={setSelectedBaseUrlIndex}
          onBaseUrlChange={isConnected ? disconnect : () => {}}
          isCopiedActive={isCopiedActive}
          hasGrayBackground={true}
          display="interactive"
          path={channel.address ?? ''}
          method="websocket"
        />
        <ConnectButton
          connecting={connecting}
          isConnected={isConnected}
          onClick={isConnected ? disconnect : connect}
        />
      </div>
      <div className="grid grid-cols-1 lg:grid-cols-5 gap-x-8 gap-y-6 mt-5 relative">
        <div className="col-span-1 lg:col-span-3">
          <ModalHeader title={channel.title} description={channel.description} />
          <div className="space-y-2 mt-6">
            {channel.securitySchemes.length > 0 && (
              <InputSecurity
                name="Security Schemes"
                securitySchemes={channel.securitySchemes}
                securitySchemeValues={securitySchemeValues}
                handleSecuritySchemeChange={handleSecuritySchemeChange}
              />
            )}
            {channel.parameters.length > 0 && (
              <InputParameters
                name="Path Parameters"
                parameters={channel.parameters}
                parameterValues={parameterValues}
                onChange={handleParameterChange}
              />
            )}
            {channel.sendOperations.length > 0 && (
              <SendMessage
                sendOperations={channel.sendOperations}
                sendMessage={sendMessage}
                isConnected={isConnected}
              />
            )}
          </div>
        </div>
        <div
          className={cn(
            'p-px col-span-1 lg:col-span-2 lg:space-y-6 lg:flex lg:sticky lg:top-0 lg:self-start lg:h-[calc(100vh-5.5rem)]',
            result.length > 0 && 'lg:flex-col overflow-y-auto lg:-top-12 lg:h-screen'
          )}
        >
          <MessageResults
            name="Messages"
            messages={result}
            isConnected={isConnected}
            clearResult={clearResult}
          />
        </div>
      </div>
    </PlaygroundModalWrapper>
  );
};
