import { NextRouter } from 'next/router';
import { useEffect } from 'react';

import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { getCleanPathname } from '@/utils/path';
import { optionallyRemoveLeadingSlash } from '@/utils/paths/optionallyRemoveLeadingSlash';

export async function copyMarkdownToClipboard(router: NextRouter, actualSubdomain?: string) {
  const path = optionallyRemoveLeadingSlash(router.asPath);
  try {
    const res = await fetch(
      `${router.basePath}/_mintlify/_markdown/_sites/${actualSubdomain}${getCleanPathname(path)}`
    );
    const markdown = await res.text();
    await navigator.clipboard.writeText(markdown);
    return true;
  } catch (error) {
    console.error('Failed to copy markdown:', error);
    return false;
  }
}

export function useMarkdownCopy(actualSubdomain?: string) {
  const router = useMintlifyRouter();

  useEffect(() => {
    const handleKeyPress = async (e: KeyboardEvent) => {
      if (e.metaKey && e.key === 'c') {
        const selection = window.getSelection();
        if (selection && selection.toString().length > 0) {
          return;
        }
        await copyMarkdownToClipboard(router, actualSubdomain);
      }
    };

    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [router.basePath, actualSubdomain, router.asPath]);
}
