import { SerializeSuccess } from '@mintlify/mdx';
import { DocsConfig } from '@mintlify/validation';
import { createContext, useEffect, useState } from 'react';

interface BannerContextType {
  banner?: SerializeSuccess | string | null;
  dismissBanner: () => void;
}

const initialContext: BannerContextType = {
  banner: null,
  dismissBanner: () => {},
};

export const BannerContext = createContext<BannerContextType>(initialContext);

export const BANNER_DISMISSED_KEY = 'bannerDismissed';

export const BannerProvider = ({
  children,
  initialBanner,
  config,
  subdomain,
}: {
  children: React.ReactNode;
  initialBanner: SerializeSuccess | string | null;
  config: DocsConfig['banner'];
  subdomain: string | undefined;
}) => {
  const [banner, setBanner] = useState<SerializeSuccess | string | null>(null);

  const bannerKey = subdomain ? `${subdomain}-${BANNER_DISMISSED_KEY}` : BANNER_DISMISSED_KEY;

  useEffect(() => {
    if (initialBanner) {
      const isBannerDismissed = localStorage.getItem(bannerKey);
      if (!isBannerDismissed || isBannerDismissed !== config?.content) {
        setBanner(initialBanner);
      }
    }
  }, [initialBanner, config?.content, bannerKey]);

  const dismissBanner = () => {
    if (config?.dismissible) {
      setBanner(null);
      localStorage.setItem(bannerKey, config.content);
    }
  };

  return (
    <BannerContext.Provider
      value={{
        banner: banner,
        dismissBanner,
      }}
    >
      {children}
    </BannerContext.Provider>
  );
};
