export function getShikiLanguage(lang: string | undefined): ShikiLanguage {
  const text = 'text' as ShikiLanguage;
  const possibleStatusCode = Number(lang);
  const isStatusCode =
    !isNaN(possibleStatusCode) &&
    isFinite(possibleStatusCode) &&
    possibleStatusCode > 99 &&
    possibleStatusCode < 600;
  return isStatusCode
    ? 'json'
    : lang === undefined
      ? text
      : shikiLangMap[lang.toLowerCase()] ?? text;
}

export function getShikiDisplayLanguage(lang: ShikiLanguage): string {
  return (shikiDisplayLangMap[lang] as string | undefined) ?? '';
}

export function getLanguageFromClassName(className: string | undefined, fallback?: string): string {
  const match = /language-(\w+)/.exec(className ?? '');
  return match ? match[1] ?? 'text' : fallback ?? 'text';
}

export type ShikiHighlightedHtmlArgs = {
  codeString: string;
  codeBlockTheme: 'system' | 'dark' | undefined;
  opts?: Partial<CodeToHastOptions> & { highlightedLines?: Array<number>; noAsync?: boolean };
} & (
  | {
      className: string | undefined;
      fileName?: string;
    }
  | {
      language: string;
    }
);

export function getShikiHighlightedHtml(
  props: ShikiHighlightedHtmlArgs & {
    opts: ShikiHighlightedHtmlArgs['opts'] & { noAsync: true };
  }
): string | undefined;
export function getShikiHighlightedHtml(
  props: ShikiHighlightedHtmlArgs
): string | undefined | Promise<string | undefined>;
export function getShikiHighlightedHtml(
  props: ShikiHighlightedHtmlArgs
): string | undefined | Promise<string | undefined> {
  let lang: string;
  if ('language' in props) {
    lang = props.language;
  } else {
    lang = getLanguageFromClassName(props.className, props.fileName);
  }

  if (props.codeString.length > MAX_PREVIEW_BYTES) {
    if (props.opts?.noAsync === true) {
      return undefined;
    }

    try {
      const worker = getShikiWorker();
      return worker == undefined ? undefined : worker.highlight(props);
    } catch {
      return undefined;
    }
  }

  let html: string | Root | undefined = undefined;
  if (lang) {
    try {
      const serializer = props.opts?.highlightedLines ? 'codeToHast' : 'codeToHtml';
      const code = props.codeString.trim();
      html = highlighter[serializer](code, {
        lang: getShikiLanguage(lang),
        themes: {
          dark: shikiThemeMap.dark,
          light: props.codeBlockTheme !== 'system' ? shikiThemeMap.dark : shikiThemeMap.light,
        },
        colorReplacements: { ...shikiColorReplacements },
        ...props.opts,
      });
    } catch {}
  }

  if (typeof html !== 'object') {
    return html;
  }

  const firstChild = html.children[0];
  if (!firstChild) return undefined;
  if (firstChild.type === 'element' && firstChild.tagName === 'pre') {
    (firstChild.children[0] as Element).children.forEach((child, index) => {
      if (
        props.opts?.highlightedLines?.includes(index) &&
        child.type === 'element' &&
        child.tagName === 'span' &&
        typeof child.properties.class === 'string'
      ) {
        child.properties.class = child.properties.class + ' line-highlight';
      }
    });
  }

  return hastToHtml(html);
}

export function useGetShikiHighlightedHtml(props: ShikiHighlightedHtmlArgs): string | undefined {
  const htmlStrOrPromise = getShikiHighlightedHtml(
    'language' in props
      ? {
          codeString: props.codeString,
          codeBlockTheme: props.codeBlockTheme,
          language: props.language,
          opts: props.opts,
        }
      : {
          codeString: props.codeString,
          codeBlockTheme: props.codeBlockTheme,
          className: props.className,
          opts: props.opts,
        }
  );

  const [html, setHtml] = useState<string | undefined>(
    htmlStrOrPromise instanceof Promise ? undefined : htmlStrOrPromise
  );

  useIsomorphicLayoutEffect(() => {
    if (!(htmlStrOrPromise instanceof Promise)) {
      setHtml(htmlStrOrPromise);
      return;
    }

    async function getHtml() {
      try {
        const htmlStr = await htmlStrOrPromise;
        setHtml(htmlStr);
      } catch (err) {
        console.error(err);
        setHtml(undefined);
      }
    }
    void getHtml();
  }, [props, htmlStrOrPromise]);

  return useMemo(() => html, [html]);
}
