'use client';

import { useMemo } from 'react';

import { useMDXContent } from './useMDXContent';

export const usePageMode = () => {
  const [state] = useMDXContent();

  const isWide = useMemo(() => {
    return state.pageMetadata.mode === 'wide';
  }, [state.pageMetadata.mode]);

  const isCenter = useMemo(() => {
    return state.pageMetadata.mode === 'center';
  }, [state.pageMetadata.mode]);

  const isCustom = useMemo(() => {
    return state.pageMetadata.mode === 'custom';
  }, [state.pageMetadata.mode]);

  return { isWide, isCenter, isCustom };
};
