import { FontConfigType, FontDetailsType } from '@mintlify/models';

export function getDetailedFontType(
  font: FontConfigType | undefined,
  type: 'heading' | 'body' | 'headings'
): FontDetailsType | undefined {
  if (!font) return;
  if ('family' in font) {
    return font;
  } else if (type in font) {
    const detailedFont: FontDetailsType = font[type as keyof FontConfigType];
    return typeof detailedFont === 'string' ? { family: detailedFont } : detailedFont;
  }
}
