import { AsyncApiParameter } from '@mintlify/common';
import { ObjectSchema } from '@mintlify/validation';

import { RecursiveInput } from '../Request/inputs/RecursiveInput';
import { ExpandableSectionContainer } from '../components/ExpandableSectionContainer';

type InputParametersProps = {
  name: string;
  parameters?: AsyncApiParameter[];
  parameterValues: Record<string, unknown>;
  onChange: (parameterId: string, value: unknown) => void;
};

export const InputParameters = ({
  name,
  parameters,
  parameterValues,
  onChange,
}: InputParametersProps) => {
  if (!parameters || parameters.length === 0) return null;

  return (
    <ExpandableSectionContainer name={name}>
      {parameters.map((parameter) => {
        const jsonSchemaWithLabels = {
          ...parameter.jsonSchema,
          deprecated: parameter.deprecated,
          required: parameter.required,
        };
        return (
          <RecursiveInput
            key={parameter.id}
            name={parameter.id}
            schemaArray={[jsonSchemaWithLabels as ObjectSchema]}
            value={parameterValues[parameter.id]}
            setValue={(value) => onChange(parameter.id, value)}
            clearValue={() => onChange(parameter.id, undefined)}
          />
        );
      })}
    </ExpandableSectionContainer>
  );
};
