'use client';

const ARGS = {};

export function getHighlightedLines(node: JSX.Element): number[] {
  const lines: number[] = [];
  const nodeIsObject = typeof node === 'object' && !!node;
  if (!nodeIsObject) return lines;

  if (typeof node.type === 'function' && node.type(ARGS)?.props?.as === 'code') {
    if (Array.isArray(node.props?.children)) {
      node.props.children.forEach((child: JSX.Element | null | undefined, index: number) => {
        if (
          typeof child === 'object' &&
          child &&
          child.props?.className &&
          typeof child.props.className === 'string' &&
          child.props.className.includes('line-highlight')
        ) {
          lines.push(index);
        }
      });
    }
    return lines;
  }

  if (Array.isArray(node.props?.children)) {
    node.props.children.forEach((child: JSX.Element | undefined) => {
      if (!child || typeof child !== 'object') return;
      const result = getHighlightedLines(child);
      if (result.length > 0) {
        lines.push(...result);
      }
    });
  } else if (typeof node.props?.children === 'object' && !!node.props?.children) {
    return getHighlightedLines(node.props.children);
  }

  return lines;
}
