import { ArrowUpIcon } from 'lucide-react';
import { useContext, useEffect, useRef } from 'react';
import TextareaAutosize from 'react-textarea-autosize';

import { useSelectedLocale } from '@/contexts/NavigationContext';
import { Ids } from '@/lib/local/identifiers';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

import { ChatAssistantContext } from '..';

interface ChatTextAreaProps {
  onSendMessage: (query: string) => void;
}

export const ChatTextArea = ({ onSendMessage }: ChatTextAreaProps) => {
  const { askAQuestion } = useSelectedLocale();
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const { isChatSheetOpen, query, setQuery, shouldFocusChatSheet, setShouldFocusChatSheet } =
    useContext(ChatAssistantContext);

  useEffect(() => {
    if (isChatSheetOpen) {
      const element = textareaRef.current;
      if (element && shouldFocusChatSheet) {
        element.focus();
        element.setSelectionRange(element.value.length, element.value.length);
        setShouldFocusChatSheet(false);
      }
    } else {
      textareaRef.current?.blur();
    }
  }, [isChatSheetOpen, shouldFocusChatSheet, setShouldFocusChatSheet]);

  const onInputValueChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setQuery(e.target.value);
  };

  const sendMessage = () => {
    onSendMessage(query);
    setQuery('');
  };

  const onKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  return (
    <div className="flex items-end gap-2 relative">
      <TextareaAutosize
        id={Ids.ChatAssistantTextArea}
        ref={textareaRef}
        autoComplete="off"
        placeholder={askAQuestion}
        value={query}
        onChange={onInputValueChange}
        cacheMeasurements={false}
        minRows={1}
        className={cn(
          'grow text-sm w-full px-3.5 pr-10 py-2.5 bg-background-light dark:bg-background-dark border border-gray-200 dark:border-gray-600/30 rounded-2xl shadow-sm focus:outline-none focus:border-primary dark:focus:border-primary-light text-gray-900 dark:text-gray-100',
          Classes.ChatAssistantInput
        )}
        onKeyDown={onKeyDown}
        style={{
          resize: 'none',
        }}
      />
      <button
        className={cn(
          'absolute right-2.5 bottom-[9px] flex justify-center items-center p-1 w-6 h-6 rounded-lg',
          query.trim() === ''
            ? 'bg-primary/30 dark:bg-primary-dark/30'
            : 'bg-primary dark:bg-primary-dark',
          Classes.ChatAssistantSendButton
        )}
        aria-label="Send message"
        onClick={sendMessage}
      >
        <ArrowUpIcon className={cn('w-3.5 h-3.5 text-white dark:text-white')} />
      </button>
    </div>
  );
};
