'use client';

import clsx from 'clsx';
import React, { ReactNode } from 'react';

import { Classes } from '@/lib/local/selectors';

type CardGroupProps = {
  children: ReactNode;
  cols?: 1 | 2 | 3 | 4 | '1' | '2' | '3' | '4';
  className?: string;
};

export function CardGroup({ children, cols = 2, className }: CardGroupProps) {
  let colsClassName = '';
  cols = Number(cols) as 1 | 2 | 3 | 4;
  switch (cols) {
    case 1:
      colsClassName = 'sm:grid-cols-1';
      break;
    case 2:
      colsClassName = 'sm:grid-cols-2';
      break;
    case 3:
      colsClassName = 'sm:grid-cols-3';
      break;
    case 4:
      colsClassName = 'sm:grid-cols-4';
      break;
    default:
      colsClassName = '';
      break;
  }
  return (
    <div className={clsx(Classes.CardGroup, `not-prose grid gap-x-4`, colsClassName, className)}>
      {children}
    </div>
  );
}

export function Columns({ children, ...props }: CardGroupProps) {
  return <CardGroup {...props}>{children}</CardGroup>;
}
