'use client';

import { ApiPlaygroundInputs, Endpoint } from '@mintlify/validation';
import { useContext, useState } from 'react';

import { DescriptionWithoutHeaders } from '@/api-playground/EndpointFields/components/Description';
import ApiExamples from '@/components/ApiExamples/ApiExamples';
import { useMDXContent } from '@/hooks/useMDXContent';
import { getFilteredSecurityOptions } from '@/utils/api-reference/getFilteredSecurityOptions';
import { cn } from '@/utils/cn';

import { ApiPlaygroundContext } from '../ApiPlaygroundContext';
import { Response } from '../Response';
import { EndpointsMenu } from '../components/EndpointsMenu';
import { PathHeader, SendPill } from '../components/PathHeader';
import { PlaygroundModalWrapper } from '../components/PlaygroundModalWrapper';
import { BodySection } from './BodySection';
import { ParameterSection } from './ParameterSection';
import { SecuritySection } from './SecuritySection';
import { ServerSection } from './ServerSection';

export function PlaygroundModal({
  endpoint,
  inputs,
  setInputs,
}: {
  endpoint: Endpoint;
  inputs: ApiPlaygroundInputs;
  setInputs: (inputs: ApiPlaygroundInputs) => void;
}) {
  const [{ pageMetadata }] = useMDXContent();
  const [selectedSecurityOptionIndex, setSelectedSecurityOptionIndex] = useState(0);

  const {
    isSending,
    selectedBaseUrlIndex,
    isPlaygroundExpanded,
    setIsPlaygroundExpanded,
    sendRequest,
    result,
  } = useContext(ApiPlaygroundContext);
  const server = endpoint.servers?.[selectedBaseUrlIndex];

  const filteredSecurityOptions = getFilteredSecurityOptions(endpoint);

  const selectedSecurityOption = filteredSecurityOptions[selectedSecurityOptionIndex];

  const variables = server?.variables;

  return (
    <PlaygroundModalWrapper
      isPlaygroundExpanded={isPlaygroundExpanded}
      setIsPlaygroundExpanded={setIsPlaygroundExpanded}
    >
      <div className="flex items-center justify-between gap-x-2">
        <EndpointsMenu endpoint={endpoint} />
        <PathHeader endpoint={endpoint} hasGrayBackground display="interactive" />
        <SendPill
          label="Send"
          method={endpoint.method}
          isSending={isSending}
          onClick={sendRequest}
          className="w-20"
        />
      </div>
      <div className="grid grid-cols-1 lg:grid-cols-5 gap-x-8 gap-y-6 mt-5 relative">
        <div className="col-span-1 lg:col-span-3">
          <div className="space-y-1 min-w-full w-full max-w-full overflow-hidden">
            <div className="text-base font-medium text-gray-900 dark:text-white max-w-full">
              {endpoint.title}
            </div>
            <div className="flex items-start gap-x-12 justify-between !max-w-full overflow-x-auto">
              {pageMetadata.description && (
                <DescriptionWithoutHeaders
                  markdown={pageMetadata.description}
                  className="text-sm text-gray-500 dark:text-gray-400"
                />
              )}
            </div>
          </div>
          {result && <Response result={result} className="mt-6 block lg:hidden" />}
          <div className="space-y-2 mt-6">
            {variables && Object.keys(variables).length > 0 && (
              <ServerSection
                variables={variables}
                value={inputs.server}
                setValue={(server) => setInputs({ ...inputs, server })}
              />
            )}
            {selectedSecurityOption && (
              <SecuritySection
                inputs={inputs}
                setInputs={setInputs}
                securityOptions={filteredSecurityOptions}
                selectedSecurityOptionIndex={selectedSecurityOptionIndex}
                setSelectedSecurityOptionIndex={setSelectedSecurityOptionIndex}
              />
            )}
            <ParameterSection
              name="Header"
              value={inputs.header}
              setValue={(header) => setInputs({ ...inputs, header })}
              parameters={endpoint.request.parameters.header}
            />
            <ParameterSection
              name="Cookie"
              value={inputs.cookie}
              setValue={(cookie) => setInputs({ ...inputs, cookie })}
              parameters={endpoint.request.parameters.cookie}
            />
            <ParameterSection
              name="Path"
              value={inputs.path}
              setValue={(path) => setInputs({ ...inputs, path })}
              parameters={endpoint.request.parameters.path}
            />
            <ParameterSection
              name="Query"
              value={inputs.query}
              setValue={(query) => setInputs({ ...inputs, query })}
              parameters={endpoint.request.parameters.query}
            />

            <BodySection
              value={inputs.body}
              setValue={(body) => setInputs({ ...inputs, body })}
              body={endpoint.request.body}
            />
          </div>
        </div>
        <div
          className={cn(
            'p-px col-span-1 lg:col-span-2 lg:space-y-6 lg:flex lg:sticky lg:top-0 lg:self-start lg:h-[calc(100vh-5.5rem)]',
            result && 'lg:flex-col overflow-y-auto lg:-top-12 lg:h-screen'
          )}
        >
          {result && <Response result={result} className="hidden lg:block min-h-fit" />}
          <ApiExamples className="xl:w-full" />
        </div>
      </div>
    </PlaygroundModalWrapper>
  );
}
