import { optionallyRemoveLeadingSlash } from './paths/optionallyRemoveLeadingSlash';

export function getCleanPathname(path: string) {
  return path ? `/${path}` : '';
}

export function pathsAreEquivalent(path1: string, path2: string): boolean {
  return optionallyRemoveLeadingSlash(path1) === optionallyRemoveLeadingSlash(path2);
}

export function isIndexRoute(path: string): boolean {
  return path.endsWith('/index');
}
