'use client';

import { createContext, Reducer, useReducer } from 'react';

import { ChangelogFilterType, HeadingType, MDXContentState } from '@/types/mdxContentController';

export const initialState: MDXContentState = {
  headings: [],
  apiBaseIndex: 0,
  codeExamples: {},
  pageMetadata: {},
  selectedBodyContentType: undefined,
  selectedResponseContentType: undefined,
  tableOfContents: [],
  changelogFilters: [],
};

/**
 * Actions that can be dispatched in the MDXContentReducer.
 */
type MDXContentAction =
  | { type: 'set_state'; payload: Partial<MDXContentState> }
  | { type: 'set_api_base_index'; payload: number }
  | { type: 'set_selected_body_content_type'; payload: string }
  | { type: 'set_selected_response_content_type'; payload: string }
  | { type: 'register_heading'; payload: HeadingType }
  | { type: 'unregister_heading'; payload: string }
  | { type: 'toggle_changelog_filter'; payload: ChangelogFilterType };

export type MDXContentActionType = MDXContentAction['type'];

/**
 * Consolidates state and update logic of MDXContentController.
 * @param state - current state.
 * @param action - state action to dispatch.
 * @returns MDXContentState - new state.
 */
export const MDXContentReducer: Reducer<MDXContentState, MDXContentAction> = (
  state: MDXContentState,
  action
) => {
  const type = action.type;
  switch (type) {
    case 'set_state':
      return {
        ...state,
        ...action.payload,
      };
    case 'register_heading':
      const { id, top, isAtRootLevel } = action.payload;
      return {
        ...state,
        headings: [...state.headings.filter((h) => id !== h.id), { id, top, isAtRootLevel }],
      };
    case 'unregister_heading':
      return {
        ...state,
        headings: state.headings.filter((h) => action.payload !== h.id),
      };
    case 'set_selected_body_content_type':
      return {
        ...state,
        selectedBodyContentType: action.payload,
      };
    case 'set_selected_response_content_type':
      return {
        ...state,
        selectedResponseContentType: action.payload,
      };
    case 'set_api_base_index':
      return {
        ...state,
        apiBaseIndex: action.payload,
      };
    case 'toggle_changelog_filter':
      const existingFilterIndex = state.changelogFilters.findIndex(
        (filter) => filter.tag === action.payload.tag
      );
      if (existingFilterIndex !== -1) {
        const newFilters = [...state.changelogFilters];
        newFilters[existingFilterIndex] = action.payload;
        return {
          ...state,
          changelogFilters: newFilters,
        };
      }
      return {
        ...state,
        changelogFilters: [...state.changelogFilters, action.payload],
      };
  }
};

export const useMDXContentReducer = (state?: Partial<MDXContentState>) => {
  const initializedState = {
    ...initialState,
    ...state,
  };
  return useReducer(MDXContentReducer, initializedState);
};

export type MDXContentContextType = ReturnType<typeof useMDXContentReducer>;

export const MDXContentContext = createContext<MDXContentContextType>([initialState, () => null]);

MDXContentContext.displayName = 'MDXContentContext';
