'use client';

import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { cn } from '@/utils/cn';

import { AudioResponseData } from '..';

type AudioResponseContentParams = {
  data: AudioResponseData;
};

export const AudioResponseContent = ({ data }: AudioResponseContentParams) => {
  const { docsConfig } = useContext(DocsConfigContext);

  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <div
      className={cn(
        codeBlockTheme === 'system' ? 'bg-response-body-auto' : 'bg-response-body-dark',
        'flex p-12 items-center rounded-b-xl h-full'
      )}
    >
      <audio controls className="w-full" src={data.url} />
    </div>
  );
};
