import { parseAsyncApiString, potentiallyParseOpenApiString, slugToTitle } from '@mintlify/common';
import { ChannelData } from '@mintlify/common/dist/types/asyncapi';
import { DecoratedNavigationPage } from '@mintlify/models';
import { DecoratedPageOrGroupConfig, Endpoint } from '@mintlify/validation';
import { ChevronDownIcon } from 'lucide-react';
import { useContext, useState, useMemo } from 'react';

import { MethodPill } from '@/components/ApiExamples/MethodPill';
import { DropdownMenu, DropdownMenuContent, DropdownMenuTrigger } from '@/components/Dropdown';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { useMDXContent } from '@/hooks/useMDXContent';
import { ApiPageData } from '@/types/api';

import { EndpointsMenuLink } from './EndpointsMenuLink';
import { EndpointsMenuSearch } from './EndpointsMenuSearch';

type EndpointsMenuProps = {
  endpoint?: Endpoint;
  channel?: ChannelData;
};

const getAllApiPages = (navigation: DecoratedPageOrGroupConfig[]): DecoratedNavigationPage[] => {
  return navigation.reduce<DecoratedNavigationPage[]>((acc, entry) => {
    if ('pages' in entry) {
      return [...acc, ...getAllApiPages(entry.pages as DecoratedPageOrGroupConfig[])];
    } else if ('openapi' in entry || 'api' in entry || 'asyncapi' in entry) {
      return [...acc, entry];
    }
    return acc;
  }, []);
};

const mapApiPageData = (pages: DecoratedNavigationPage[]): ApiPageData[] => {
  return pages
    .map((page: DecoratedNavigationPage): ApiPageData | undefined => {
      if (page.asyncapi) {
        const parsedAsyncApi = parseAsyncApiString(page.asyncapi);
        if (parsedAsyncApi == undefined) {
          return undefined;
        }
        return {
          method: 'websocket',
          title: page.title || slugToTitle(page.href),
          href: page.href,
          type: 'asyncapi',
          playground: page.playground,
        };
      } else {
        const endpointStr = page.openapi || page.api;
        if (endpointStr == undefined) {
          return undefined;
        }
        const potentiallyParsedOpenApiString = potentiallyParseOpenApiString(endpointStr);
        if (potentiallyParsedOpenApiString == undefined) {
          return undefined;
        }
        return {
          method: potentiallyParsedOpenApiString.method,
          title: page.title || slugToTitle(page.href),
          href: page.href,
          type: 'openapi',
          playground: page.playground,
        };
      }
    })
    .filter((pageData) => pageData != undefined);
};

export const EndpointsMenu = ({ endpoint, channel }: EndpointsMenuProps) => {
  const { divisions } = useContext(NavigationContext);
  const [{ pageMetadata }] = useMDXContent();
  const currentPath = useCurrentPath();
  const router = useMintlifyRouter();
  const pageTitle = pageMetadata.title || slugToTitle(currentPath);
  const [search, setSearch] = useState('');

  const allApiPages = getAllApiPages(divisions.groupsOrPages);
  const apiPages = useMemo(() => mapApiPageData(allApiPages), [allApiPages]);

  const title = endpoint ? endpoint.title || pageTitle : channel?.title || pageTitle;
  const method = endpoint ? endpoint.method : 'websocket';
  const shortMethod = method === 'websocket';

  const searchResults = useMemo(() => {
    if (!search) return apiPages;
    return apiPages.filter((page) => {
      return page.title.toLowerCase().includes(search.toLowerCase());
    });
  }, [search, apiPages]);

  return (
    <DropdownMenu>
      <DropdownMenuTrigger className="hidden lg:block">
        <div className="flex items-center gap-x-2 border-standard rounded-xl p-1.5 pr-3 min-w-80 hover:bg-gray-50 dark:hover:bg-white/5">
          <MethodPill method={method} shortMethod={shortMethod} active={true} />
          <div className="flex-1 text-left text-sm font-medium text-gray-900 dark:text-white truncate">
            {title}
          </div>
          <div>
            <ChevronDownIcon className="w-4 h-4 text-gray-500 dark:text-gray-400" />
          </div>
        </div>
      </DropdownMenuTrigger>
      <DropdownMenuContent className="hidden lg:block w-80 border border-gray-100 dark:border-white/10 rounded-xl px-2 pb-2 pt-0">
        <div className="flex flex-col">
          <EndpointsMenuSearch search={search} setSearch={setSearch} />
          {searchResults.map((page) => (
            <div key={`search-${page.title}-${page.href}`}>
              <EndpointsMenuLink page={page} router={router} />
            </div>
          ))}
        </div>
      </DropdownMenuContent>
    </DropdownMenu>
  );
};
