'use client';

import { useContext } from 'react';
import ReactMarkdown, { Components } from 'react-markdown';
import remarkGfm from 'remark-gfm';

import { MarkdownCode } from '@/components/Code/MarkdownCode';
import { DocsConfigContext } from '@/contexts/ConfigContext';
import '@/css/code.css';
import { cn } from '@/utils/cn';

const commonRemarkPlugins = [remarkGfm];

export function Markdown({
  children,
  className,
  components,
}: {
  children: string;
  className?: string;
  components?: Partial<Components> | null | undefined;
}) {
  const { docsConfig } = useContext(DocsConfigContext);
  const codeBlockTheme = docsConfig?.styling?.codeblocks;

  return (
    <ReactMarkdown
      remarkPlugins={commonRemarkPlugins}
      components={{
        p: ({ children }) => <p className="whitespace-pre-line">{children}</p>,
        pre: ({ children, ...props }) => {
          return (
            <pre
              {...props}
              className={cn(
                'p-3 text-sm rounded-xl',
                codeBlockTheme === 'system' &&
                  'text-gray-950 bg-white codeblock-light dark:codeblock-dark dark:bg-codeblock dark:text-gray-50 border border-gray-200/70 dark:border-gray-800/50',
                (codeBlockTheme === 'dark' || codeBlockTheme == undefined) &&
                  'text-gray-50 bg-[#0F1117] dark:codeblock-dark dark:bg-codeblock border border-transparent dark:border-gray-800/50 codeblock-dark',
                props.className
              )}
            >
              {children}
            </pre>
          );
        },
        code: ({ children, ...props }) => {
          return (
            <MarkdownCode suppressHydrationWarning codeBlockTheme={codeBlockTheme} {...props}>
              {children}
            </MarkdownCode>
          );
        },
        ...components,
      }}
      className={className}
    >
      {children}
    </ReactMarkdown>
  );
}

export const MarkdownWithoutHeaders = ({
  children,
  className,
}: {
  children: string;
  className?: string;
}) => {
  const headingClass = 'text-base font-medium';
  return (
    <Markdown
      components={{
        h1: ({ children }) => <h1 className={headingClass}>{children}</h1>,
        h2: ({ children }) => <h2 className={headingClass}>{children}</h2>,
        h3: ({ children }) => <h3 className={headingClass}>{children}</h3>,
        h4: ({ children }) => <h4 className={headingClass}>{children}</h4>,
        h5: ({ children }) => <h5 className={headingClass}>{children}</h5>,
        h6: ({ children }) => <h6 className={headingClass}>{children}</h6>,
      }}
      className={className}
    >
      {children}
    </Markdown>
  );
};
