'use client';

import { useContext, useMemo } from 'react';

import { ApiReferenceContext } from '@/contexts/ConfigContext';

export const useSchemaExampleString = () => {
  const { apiReferenceData } = useContext(ApiReferenceContext);

  return useMemo(() => {
    const example = apiReferenceData.schemaData?.example;

    if (example == undefined) return undefined;

    return JSON.stringify(example, null, 2);
  }, [apiReferenceData.schemaData?.example]);
};
