'use client';

import { useContext } from 'react';

import { Option, PillSelect } from '@/components/PillSelect';
import { NavigationContext } from '@/contexts/NavigationContext';

export function VersionSelect({ triggerClassName }: { triggerClassName?: string }) {
  const { divisions } = useContext(NavigationContext);
  const versions = divisions.versions;
  const { selectedVersion, setSelectedVersion } = useContext(NavigationContext);

  // It's possible to show a selected version that doesn't exist in versionOptions, for example by navigating to
  // a secret v3 page when the menu only shows v1 and v2. Thus, we only hide the dropdown when nothing is selected.
  if (!selectedVersion) {
    return null;
  }

  const onVersionChange = (version: Option) => {
    setSelectedVersion(typeof version === 'object' ? version.name : version);
  };

  return (
    <PillSelect
      options={versions}
      onChange={onVersionChange}
      defaultOption={selectedVersion}
      selectedOptionClass="text-primary dark:text-primary-light"
      triggerClassName={triggerClassName}
    />
  );
}
