'use client';

import { ReactNode } from 'react';

import EndpointFields from '@/api-playground/EndpointFields';
import { SchemaFields } from '@/api-playground/EndpointFields/SchemaFields';
import { useMDXContent } from '@/hooks/useMDXContent';
import { usePageMode } from '@/hooks/usePageMode';
import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

export const Content = ({ children }: { children: ReactNode }) => {
  const [{ pageMetadata }] = useMDXContent();
  const { isCustom } = usePageMode();

  return (
    <div
      className={cn(
        Classes.Content,
        'relative',
        !isCustom && 'mt-8 prose prose-gray dark:prose-invert'
      )}
    >
      {children}
      {/* we only want to render EndpointFields if the source of the endpoint is OpenAPI */}
      {pageMetadata.openapi && <EndpointFields />}
      {pageMetadata['openapi-schema'] && <SchemaFields />}
    </div>
  );
};
