'use client';

import Link from 'next/link';
import React, { CSSProperties, ReactNode, MouseEventHandler } from 'react';

import { Classes } from '@/lib/local/selectors';
import { cn } from '@/utils/cn';

export function DynamicLink({
  href,
  ref,
  onMouseEnter,
  onMouseLeave,
  style,
  className,
  children,
  onClick,
  ...props
}: React.AnchorHTMLAttributes<HTMLAnchorElement> & {
  href?: string;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  ref?: any;
  onClick?: MouseEventHandler<HTMLAnchorElement>;
  onMouseEnter?: MouseEventHandler<HTMLAnchorElement>;
  onMouseLeave?: MouseEventHandler<HTMLAnchorElement>;
  style?: CSSProperties;
  className?: string;
  children?: ReactNode;
}) {
  if (href?.startsWith('/') || href?.startsWith('#')) {
    // next/link is used for internal links to avoid extra network calls
    return (
      <Link
        href={href}
        ref={ref}
        passHref={true}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        style={style}
        className={cn(Classes.Link, className)}
        onClick={onClick}
        {...props}
      >
        {children}
      </Link>
    );
  }

  if (href?.startsWith('./') || href?.startsWith('../')) {
    // Cannot use a Link, it doesn't work because it would be relative to the [[...slug]] file
    return (
      <a
        href={href}
        ref={ref}
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        style={style}
        className={cn(Classes.Link, className)}
        onClick={onClick}
        {...props}
      >
        {children}
      </a>
    );
  }

  return (
    <a
      href={href}
      target="_blank"
      rel="noreferrer"
      ref={ref}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      style={style}
      className={cn(Classes.Link, className)}
      onClick={onClick}
      {...props}
    >
      {children}
    </a>
  );
}
