import { ChannelData } from '@mintlify/common';
import { AsyncApiMessage } from '@mintlify/common';

import { ExpandableMessages } from '../components/ExpandableMessages';

type MessageExamplesProps = {
  name: string;
  channel: ChannelData;
};

export const MessageExamples = ({ name, channel }: MessageExamplesProps) => {
  const sendMessages = channel.sendMessages.map((message: AsyncApiMessage) => ({
    type: 'send',
    id: message.id,
    title: message.title ?? message.example ?? '',
    content: message.example,
  }));

  const receiveMessages = channel.receiveMessages.map((message: AsyncApiMessage) => ({
    type: 'receive',
    id: message.id,
    title: message.title ?? message.example ?? '',
    content: message.example,
  }));

  return <ExpandableMessages name={name} messages={[...sendMessages, ...receiveMessages]} />;
};
