'use client';

import { ThumbsDownIcon, ThumbsUpIcon, ChevronRightIcon, RefreshCwIcon } from 'lucide-react';
import Link from 'next/link';
import * as React from 'react';
import { useEffect, useState } from 'react';

import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { Citation } from '@/types/chat';
import { Markdown } from '@/ui/Markdown';
import { AiStatusIcons } from '@/ui/icons';
import { cn } from '@/utils/cn';

interface ChatResponseProps extends React.HTMLAttributes<HTMLDivElement> {
  message: string;
  isGenerating: boolean;
  isGeneratingError: boolean;
  onGeneratingRetry: () => void;
  isRateLimited: boolean;
  formattedContent: string;
  citations: Citation[];
  onCitationClick: () => void;
  isLoading: boolean;
}

const ThumbsBeforeFeedbackClassname =
  'rounded-md px-1.5 py-1 hover:bg-gray-100 dark:hover:bg-white/10 cursor-pointer text-gray-500';
const ThumbsAfterFeedbackClassname =
  'rounded-md px-1.5 py-1 bg-primary dark:bg-bg-primary-light text-white dark:text-gray-950';
const RetryClassname =
  'rounded-md px-1.5 py-1 hover:bg-gray-100 dark:hover:bg-white/10 cursor-pointer text-gray-500';

export const ChatResponse = React.forwardRef<HTMLDivElement, ChatResponseProps>(
  (
    {
      className,
      message,
      isGenerating,
      isGeneratingError,
      isRateLimited,
      formattedContent,
      citations,
      onCitationClick,
      onGeneratingRetry,
      isLoading,
      ...props
    },
    ref
  ) => {
    const [isThumbedUp, setIsThumbedUp] = useState(false);
    const [isThumbedDown, setIsThumbedDown] = useState(false);
    const [isSourcesOpen, setIsSourcesOpen] = useState(false);
    const [dotCount, setDotCount] = useState(1);

    const trackCitationClick = useAnalyticsContext('ai_chat_citation_click');
    const trackThumbedUp = useAnalyticsContext('ai_chat_feedback_positive_click');
    const trackThumbedDown = useAnalyticsContext('ai_chat_feedback_negative_click');

    useEffect(() => {
      if (isGenerating) {
        const interval = setInterval(() => {
          setDotCount((prev) => (prev === 3 ? 1 : prev + 1));
        }, 200);
        return () => clearInterval(interval);
      }
    }, [isGenerating]);

    if (isGeneratingError) {
      const errorMessage = isRateLimited
        ? 'You have reached your chat limit. Please try again later.'
        : 'Sorry, we could not generate a response to your question.';

      return (
        <div ref={ref} className={cn('py-4 text-sm', className)} {...props}>
          <span>{errorMessage}</span>
          <button
            className="mt-6 group text-primary dark:text-primary-light hover:text-primary-light dark:hover:text-primary-light text-sm cursor-pointer flex items-center gap-0.5 whitespace-nowrap self-start"
            onClick={onGeneratingRetry}
          >
            <AiStatusIcons.Retry />
            <span className="px-1">Retry</span>
          </button>
        </div>
      );
    } else if (isGenerating) {
      return (
        <div ref={ref} className={cn('py-4 text-sm', className)} {...props}>
          {`Generating${'.'.repeat(dotCount)}`}
        </div>
      );
    }

    return (
      <div
        ref={ref}
        className={cn('flex flex-col py-[14px] gap-4 self-stretch', className)}
        {...props}
      >
        <div
          className={cn(
            'flex flex-col gap-2 text-primary dark:text-primary-light',
            citations.length === 0 && 'hidden'
          )}
        >
          <button
            className="text-sm flex items-center gap-1.5 whitespace-nowrap self-start font-medium hover:brightness-[0.75] dark:hover:brightness-[1.35]"
            onClick={() => setIsSourcesOpen(!isSourcesOpen)}
          >
            <ChevronRightIcon className={cn(isSourcesOpen ? 'rotate-90' : '', 'w-3.5 h-3.5')} />
            {citations.length} sources
          </button>

          {isSourcesOpen && (
            <div className="flex gap-1 flex-col">
              {citations.map((citation: Citation, i) => {
                return (
                  <Link
                    key={citation.url}
                    href={citation.url}
                    onClick={() => {
                      trackCitationClick({ index: i }).catch(console.error);
                      onCitationClick();
                    }}
                  >
                    <div className="flex items-center gap-1 py-1 text-xs hover:brightness-[0.75] dark:hover:brightness-[1.35] rounded-md">
                      <span className="truncate font-medium">{citation.title}</span>
                    </div>
                  </Link>
                );
              })}
            </div>
          )}
        </div>
        <Markdown className="prose prose-sm dark:prose-invert overflow-x-auto">
          {formattedContent}
        </Markdown>
        {isLoading === false && formattedContent.length > 0 && (
          <div className="flex items-start gap-2 w-full">
            <div className="flex items-center gap-1">
              <div
                className={RetryClassname}
                onClick={() => {
                  onGeneratingRetry();
                }}
              >
                <RefreshCwIcon size={12} absoluteStrokeWidth strokeWidth={1.5} />
              </div>
              <div
                className={cn(
                  isThumbedUp ? ThumbsAfterFeedbackClassname : ThumbsBeforeFeedbackClassname
                )}
                onClick={() => {
                  if (!isThumbedUp) {
                    trackThumbedUp({ message }).catch(console.error);
                    setIsThumbedUp(true);
                    setIsThumbedDown(false);
                  }
                }}
              >
                <ThumbsUpIcon size={12} absoluteStrokeWidth strokeWidth={1.5} />
              </div>
              <div
                className={cn(
                  isThumbedDown ? ThumbsAfterFeedbackClassname : ThumbsBeforeFeedbackClassname
                )}
                onClick={() => {
                  if (!isThumbedDown) {
                    trackThumbedDown({ message }).catch(console.error);
                    setIsThumbedUp(false);
                    setIsThumbedDown(true);
                  }
                }}
              >
                <ThumbsDownIcon size={12} absoluteStrokeWidth strokeWidth={1.5} />
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
);
ChatResponse.displayName = 'ChatResponse';
