import type { ContentSchema, ResponseSchema } from '@mintlify/validation';

import type { ExampleCodeSnippet } from '@/types/exampleCodeSnippet';

type ExampleMap = { [title: string]: ExampleCodeSnippet };
type ResponseExampleMap = { [status: string]: ExampleMap };

export const generateResponseExampleMap = (
  response: ResponseSchema,
  selectedContentType?: string
): ResponseExampleMap => {
  return Object.entries(response)
    .sort()
    .reduce<ResponseExampleMap>((map, [status, body]) => {
      const content: ContentSchema | undefined =
        selectedContentType !== undefined && body[selectedContentType]
          ? body[selectedContentType]
          : Object.values(body)[0];
      if (content === undefined) {
        map[status] = {
          '': {
            filename: status,
            code: 'This response has no body data.',
            language: '',
          },
        };
        return map;
      }
      if (Object.values(content.examples)[0]?.value === undefined) {
        map[status] = {
          '': {
            filename: status,
            code: 'This response does not have an example.',
            language: '',
          },
        };
        return map;
      }
      const exampleMap = Object.entries(content.examples).reduce<ExampleMap>(
        (exampleMap, [title, example]) => {
          const stringifiedExample = JSON.stringify(example.value, null, 2);
          exampleMap[title] = {
            filename: status,
            language: 'json',
            code: stringifiedExample,
          };
          return exampleMap;
        },
        {}
      );
      map[status] = exampleMap;
      return map;
    }, {});
};
