import { SnippetPreset } from '@/types/snippetPreset';

export type ShikiSnippetLangs =
  | 'bash'
  | 'c'
  | 'c#'
  | 'c++'
  | 'go'
  | 'java'
  | 'javascript'
  | 'php'
  | 'python'
  | 'typescript'
  | 'ruby';

// map from some common language aliases to the form used by Shiki
export const userLangToShikiLang: Record<string, ShikiSnippetLangs> = {
  curl: 'bash',
  golang: 'go',
  js: 'javascript',
  sh: 'bash',
  ts: 'typescript',
};

// map from standardized Shiki language names to a more human-readable label
export const shikiLangToDisplayLang: Record<string, string> = {
  bash: 'cURL',
  c: 'C',
  'c#': 'C#',
  'c++': 'C++',
  go: 'Go',
  java: 'Java',
  javascript: 'JavaScript',
  php: 'PHP',
  python: 'Python',
  typescript: 'TypeScript',
  ruby: 'Ruby',
} satisfies Record<ShikiSnippetLangs, string>;

const curlSnippetPreset: SnippetPreset = {
  filename: 'cURL',
  snippet: {
    target: 'shell',
  },
  language: 'bash',
};

const pythonSnippetPreset: SnippetPreset = {
  filename: 'Python',
  snippet: {
    target: 'python',
    client: 'requests',
  },
  language: 'python',
};

const javascriptSnippetPreset: SnippetPreset = {
  filename: 'JavaScript',
  snippet: {
    target: 'javascript',
    client: 'fetch',
  },
  language: 'javascript',
};

const phpSnippetPreset: SnippetPreset = {
  filename: 'PHP',
  snippet: {
    target: 'php',
    client: 'curl',
  },
  language: 'php',
};

const goSnippetPreset: SnippetPreset = {
  filename: 'Go',
  snippet: {
    target: 'go',
  },
  language: 'go',
};

const javaSnippetPreset: SnippetPreset = {
  filename: 'Java',
  snippet: {
    target: 'java',
  },
  language: 'java',
};

export const snippetPresets: SnippetPreset[] = [
  curlSnippetPreset,
  pythonSnippetPreset,
  javascriptSnippetPreset,
  phpSnippetPreset,
  goSnippetPreset,
  javaSnippetPreset,
];

export const langToPresetMap: Record<string, SnippetPreset> = {
  bash: curlSnippetPreset,
  python: pythonSnippetPreset,
  javascript: javascriptSnippetPreset,
  php: phpSnippetPreset,
  go: goSnippetPreset,
  java: javaSnippetPreset,
} satisfies { [k in ShikiSnippetLangs]?: SnippetPreset };

export const toShikiLang = (lang: string) => {
  const lowerLang = lang.toLowerCase();
  return userLangToShikiLang[lowerLang] ?? lowerLang;
};
