import { DataSchemaArray, IncrementalDataSchemaArray } from '@mintlify/validation';

import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';

import { generateSimpleLabels } from './generateSimpleLabels';
import { generateTypeStringFromSchema } from './generateTypeStringFromSchema';

export const generateOptionLabels = (
  schemas: DataSchemaArray | IncrementalDataSchemaArray,
  componentSchemas: ComponentSchemas | undefined,
  allObjects: boolean,
  nullable: boolean
): string[] => {
  if (schemas.length <= 1) {
    return [generateTypeStringFromSchema(schemas[0], componentSchemas, nullable)];
  }

  if (allObjects) {
    return generateSimpleLabels(schemas);
  }

  const typeStringArray = schemas.map((schema) =>
    generateTypeStringFromSchema(schema, componentSchemas, nullable)
  );
  const uniqueTypes = new Set(typeStringArray).size === typeStringArray.length;

  if (uniqueTypes) {
    return typeStringArray;
  }
  return typeStringArray.map(
    (typeString, i) => (schemas[i]?.title ?? `Option ${i + 1}`) + '  ' + typeString
  );
};
