'use client';

import { ArraySchema, DataSchemaArray, IncrementalArraySchema } from '@mintlify/validation';
import { ReactNode, useCallback, useMemo, useState } from 'react';

import { Expandable } from '@/components/Expandable';
import { ParamHead } from '@/components/Param';
import { useEndpointLocation } from '@/contexts/EndpointLocationContext';
import { useApiParamFieldOption } from '@/hooks/useApiParamFieldOption';
import { useComponentSchemas } from '@/hooks/useComponentSchemas';
import { useGenerateNextItems } from '@/hooks/useGenerateNextItems';
import { useIntersectionObserver } from '@/hooks/useIntersectionObserver';
import { useOptionInformation } from '@/hooks/useOptionInformation';
import { usePropertiesIfObject } from '@/hooks/usePropertiesIfObject';
import { generateNextItems } from '@/openapi/generateNextItems';
import { FieldType, RenderedRefs } from '@/types/api';
import { ComponentSchemas } from '@/utils/api-reference/getApiReferenceData';
import { cn } from '@/utils/cn';

import { Description } from '../components/Description';
import { OneOfSelection } from '../components/OneOfDropdown';
import { ArrayMetadata } from './ArrayParamField/ArrayMetadata';
import { ExampleDescription } from './ExampleDescription';
import { EnumOptions, isEnum } from './PrimitiveParamField';
import {
  renderNextPropertiesRecursively,
  renderTruncatedNextProperties,
  RecursiveParamField,
  getRecursiveParentName,
} from './RecursiveParamField';

export type ArrayParamFieldParams = {
  fieldType: FieldType;
  schema: ArraySchema | IncrementalArraySchema;
  renderedRefs: RenderedRefs;
  typeOptions: string[];
  name?: string | null;
  location?: string;
  selectedIndex?: number;
  onSelectTypeOption?: (index: number) => void;
  parentName?: string;
  /** Tracks the nesting level of array fields. Used to control lazy loading of deeply nested fields. */
  depth?: number;
  circularRefs?: string[];
  paramId?: string;
  shouldRenderChildren?: boolean;
};

function recurseOverParamFields(
  propField: JSX.Element,
  componentSchemas: ComponentSchemas | undefined,
  location: string | undefined
): JSX.Element | undefined {
  const schemaArray = propField.props.schemaArray as DataSchemaArray;
  const schema = schemaArray[0];
  const depth = propField.props.depth ?? 0;
  const renderedRefs = propField.props.renderedRefs ?? new Set();

  if (schemaArray.length > 1 || schema.type === 'object') {
    return propField;
  }

  if (schema.type !== 'array') {
    return undefined;
  }

  if (
    schema.minItems !== undefined ||
    schema.maxItems !== undefined ||
    schema.uniqueItems !== undefined
  ) {
    const isAnySchema = !Object.keys(schema.items).length;

    const newPropField = (
      <RecursiveParamField
        fieldType="parameter"
        key="nestedArray"
        schemaArray={[schema]}
        parentName=""
        depth={depth + 1}
        renderedRefs={new Set(renderedRefs)}
        name=""
      />
    );

    return isAnySchema ? (
      <Expandable defaultOpen={false} lazy={depth >= 5}>
        {newPropField}
      </Expandable>
    ) : (
      newPropField
    );
  }

  const nextSchema = generateNextItems(
    schema,
    componentSchemas,
    location as 'request' | 'response' | undefined
  );

  return recurseOverParamFields(
    <RecursiveParamField
      fieldType="parameter"
      key="nestedArray"
      schemaArray={nextSchema}
      parentName=""
      depth={depth + 1}
      renderedRefs={new Set(renderedRefs)}
      name={null}
    />,
    componentSchemas,
    location
  );
}

export const ArrayParamField = ({
  fieldType,
  schema,
  typeOptions,
  name,
  location,
  selectedIndex,
  onSelectTypeOption,
  parentName,
  depth = 0,
  renderedRefs,
  circularRefs,
  paramId,
  shouldRenderChildren,
}: ArrayParamFieldParams) => {
  const { ref, isVisible } = useIntersectionObserver();
  const { anchor } = useEndpointLocation();
  const containsAnchorToExpand = Boolean(paramId && anchor?.includes(paramId));
  const renderChildren = shouldRenderChildren || isVisible || containsAnchorToExpand;
  const componentSchemas = useComponentSchemas();

  const typeLabel = typeOptions[selectedIndex ?? 0];

  const items = useGenerateNextItems(schema);

  const apiParamFieldOption = useApiParamFieldOption();
  const defaultOpen = apiParamFieldOption === 'all' || containsAnchorToExpand;

  // get information about item options
  const {
    originalIndices: originalItemIndices,
    typeOptionLabels: itemTypeOptionLabels,
    allObjects: itemAllObjects,
  } = useOptionInformation(items);

  const [selectedItemIndex, setSelectedItemIndex] = useState(0);
  const originalItemIndex = originalItemIndices[selectedItemIndex] ?? 0;

  const onSelectItemOption = useCallback((index: number) => {
    setSelectedItemIndex(index);
  }, []);

  const hasObjectOptions = itemAllObjects && itemTypeOptionLabels.length > 1;

  const PropFieldsContainer = useMemo(() => {
    const Container = ({ children }: { children?: ReactNode }) => {
      if (!hasObjectOptions) return <>{children}</>;
      return (
        <div className="mt-4 rounded-xl border border-gray-100 px-4 pb-4 pt-2 dark:border-white/10">
          <OneOfSelection options={itemTypeOptionLabels} onSelectOption={onSelectItemOption} />
          {children}
        </div>
      );
    };
    Container.displayName = 'PropFieldsContainer';
    return Container;
  }, [hasObjectOptions, itemTypeOptionLabels, onSelectItemOption]);

  const selectedItemSchema = items[originalItemIndex];

  const [loading, properties, additionalProperties] = usePropertiesIfObject(selectedItemSchema);

  if (selectedItemSchema === undefined) return null;
  const schemaRef = selectedItemSchema.refIdentifier ?? selectedItemSchema.title;
  const isCircularRef = schemaRef && circularRefs?.includes(schemaRef);

  const nextProperties = Object.entries(properties ?? {});

  const nextChildren = isCircularRef
    ? renderTruncatedNextProperties({
        properties: nextProperties,
        fieldType,
        parentName,
        name,
        depth,
      })
    : renderNextPropertiesRecursively({
        currentSchemaRef: schemaRef,
        properties: nextProperties,
        fieldType,
        parentName,
        name: '',
        depth,
        refs: new Set(renderedRefs),
      });

  const isVariadicSchema =
    items.length > 1 && !items.every((subschema) => subschema.type === selectedItemSchema.type);

  const propFields = nextChildren;
  let propField: JSX.Element | undefined = undefined;

  switch (true) {
    case isVariadicSchema: {
      propFields.push(
        <RecursiveParamField
          fieldType="parameter"
          key="nestedArray"
          schemaArray={items}
          parentName=""
          depth={depth + 1}
          renderedRefs={new Set(renderedRefs)}
          name=""
        />
      );
      break;
    }

    case isEnum(selectedItemSchema): {
      propFields.push(
        <RecursiveParamField
          fieldType="parameter"
          key="nestedArray"
          schemaArray={[selectedItemSchema]}
          parentName=""
          depth={depth + 1}
          renderedRefs={new Set(renderedRefs)}
          name=""
        />
      );
      break;
    }

    case selectedItemSchema.type === 'array': {
      propField = recurseOverParamFields(
        <RecursiveParamField
          fieldType="parameter"
          key="nestedArray"
          schemaArray={[selectedItemSchema] as DataSchemaArray}
          parentName=""
          depth={depth + 1}
          renderedRefs={new Set(renderedRefs)}
          name=""
        />,
        componentSchemas,
        location
      );
      if (propField?.props?.schemaArray?.[0]?.type === 'array') {
        propFields.push(propField);
        propField = undefined;
      }
      break;
    }

    case typeof additionalProperties === 'object': {
      propFields.push(
        <RecursiveParamField
          fieldType={fieldType}
          key={'{key}'}
          schemaArray={additionalProperties}
          name={'{key}'}
          parentName={getRecursiveParentName(name, parentName)}
          depth={depth + 1}
          renderedRefs={new Set(renderedRefs)}
        />
      );
      break;
    }
  }

  // we are in a top-level array
  if (name === undefined) {
    // if either of these conditions is met, PropFieldsContainer is guaranteed to render *something*
    if (hasObjectOptions || propFields.length) {
      return <PropFieldsContainer>{propFields}</PropFieldsContainer>;
    }

    return (
      <div className="pb-5 mb-5 border-gray-100 dark:border-gray-800 border-b last:border-b-0">
        <Description
          markdown={schema.description ?? `The ${fieldType} is of type \`${typeLabel}\`.`}
        />
        {isEnum(selectedItemSchema) && <EnumOptions schema={selectedItemSchema} />}
      </div>
    );
  }

  const hasPropFields = propFields.length > 0;
  const isLoadingOrCutOff = loading || !renderChildren;

  const ExpandableInfo = isLoadingOrCutOff ? (
    <Expandable />
  ) : (
    <Expandable key={paramId} defaultOpen={defaultOpen} lazy={depth >= 5} uniqueParamId={paramId}>
      {propFields}
    </Expandable>
  );

  return (
    <div ref={ref} className="py-6">
      <ParamHead
        id={paramId}
        name={name}
        location={location}
        typeLabel={typeLabel}
        required={schema.required}
        fieldType={fieldType}
        deprecated={schema.deprecated}
        typeOptions={typeOptions}
        selectedTypeOptionIndex={selectedIndex}
        onSelectTypeOption={onSelectTypeOption}
        parentName={parentName}
      />
      <div
        className={cn({
          'mt-4':
            schema.description ||
            hasObjectOptions ||
            isEnum(selectedItemSchema) ||
            !!propFields.length,
        })}
      >
        {schema.description && <Description markdown={schema.description} />}
        <ArrayMetadata schema={schema} description={schema.description} />
        <PropFieldsContainer>
          {selectedItemSchema.description && !hasPropFields && (
            <Description markdown={selectedItemSchema.description} />
          )}
          {hasPropFields ? (
            <div
              className={cn(
                !hasObjectOptions
                  ? ''
                  : selectedItemSchema.type === 'object' &&
                      !selectedItemSchema.description &&
                      '-mt-4'
              )}
            >
              {ExpandableInfo}
            </div>
          ) : (
            propField && (
              <div
                className={cn(
                  (selectedItemSchema.type !== 'array' ||
                    propField.props?.schemaArray?.[0]?.type === 'object') &&
                    '-mt-4'
                )}
              >
                {propField}
              </div>
            )
          )}
        </PropFieldsContainer>
        <ExampleDescription schema={schema} />
      </div>
    </div>
  );
};
