import { ReactNode } from 'react';
import { useContext } from 'react';

import { useActionKey } from '@/hooks/useActionKey';

import { SearchContext } from '.';

export function SearchButton({
  className,
  children,
  id,
}: {
  className: string;
  children: ReactNode | ((props: { actionKey: string[] | undefined }) => ReactNode);
  id?: string;
}) {
  const actionKey = useActionKey();
  const { onSearchModalOpen } = useContext(SearchContext);

  return (
    <button type="button" onClick={onSearchModalOpen} className={className} id={id}>
      {typeof children === 'function' ? children({ actionKey }) : children}
    </button>
  );
}
