'use client';

import { Tab, Tabs } from '@/components/Tabs';

export const OneOfSelection = ({
  options,
  selectedIndex,
  onSelectOption,
}: {
  options: string[];
  selectedIndex?: number;
  onSelectOption?: (index: number) => void;
}) => (
  <div className="-mt-2">
    <Tabs
      defaultTabIndex={selectedIndex}
      onClickTab={onSelectOption}
      className="!border-gray-100 !mb-4 dark:!border-white/10"
    >
      {options.map((option) => (
        <Tab title={option} key={option} />
      ))}
    </Tabs>
  </div>
);
