'use client';

import { ApiPlaygroundResponseType } from '@mintlify/models';

import { ResponsePlaceholder } from './ResponsePlaceholder';
import { ResponseTable } from './ResponseTable';

export const ResponseHeaders = ({ response }: { response: ApiPlaygroundResponseType }) => {
  const headers = Object.entries(response.headers);
  if (headers.length === 0) {
    return <ResponsePlaceholder message="No headers received from the server" icon="table" />;
  }

  return <ResponseTable data={headers} />;
};
