'use client';

import clsx from 'clsx';
import { ReactNode, useEffect, useRef, useState } from 'react';

import { useAnalyticsContext } from '@/hooks/useAnalyticsContext';
import { useExpandableMemory } from '@/hooks/useExpandableMemory';
import { Classes } from '@/lib/local/selectors';
import Icon from '@/ui/Icon';
import { cn } from '@/utils/cn';

const EXPANDABLE_CONTENT_CLASS = 'expandable-content';

export function Expandable({
  title,
  defaultOpen = false,
  onChange: onChangeProp,
  lazy,
  children,
  uniqueParamId,
}: {
  title?: string;
  defaultOpen?: boolean;
  onChange?: (open: boolean) => void;
  lazy?: boolean;
  children?: ReactNode;
  uniqueParamId?: string;
}) {
  // if uniqueParamId is provided, we use session storage to
  // track if a user manually toggled the param field expandable
  const shouldUseSessionStorage = !!uniqueParamId;
  const { isExpanded, onManualToggle } = useExpandableMemory(uniqueParamId || '', defaultOpen);

  // in case we ever render an expandable that is not a param field,
  // we can just use regular state to track it
  const [localOpen, setLocalOpen] = useState(defaultOpen);

  const open = shouldUseSessionStorage ? isExpanded : localOpen;

  // setShouldRenderChildren should only ever be called with true - we never want to unmount stateful components
  const [shouldRenderChildren, setShouldRenderChildren] = useState(open || !lazy);

  const openAnalyticsListener = useAnalyticsContext('expandable_open');
  const closeAnalyticsListener = useAnalyticsContext('expandable_close');

  const expandableRef = useRef<HTMLDivElement>(null);

  const openClosestExpandables = (element: HTMLElement) => {
    let parent = element.closest(`.${EXPANDABLE_CONTENT_CLASS}`);
    while (parent) {
      const button = parent.previousElementSibling as HTMLButtonElement;
      if (button.getAttribute('aria-expanded') === 'false') {
        button.click();
      }
      parent = parent.parentElement?.closest(`.${EXPANDABLE_CONTENT_CLASS}`) ?? null;
    }
  };

  useEffect(() => {
    const checkHashAndExpand = () => {
      if (typeof window === 'undefined') return;

      const hash = window.location.hash;
      if (!hash) return;

      const targetElement = document.getElementById(hash.substring(1));
      if (targetElement) {
        if (expandableRef.current && expandableRef.current.contains(targetElement)) {
          if (shouldUseSessionStorage) {
            onManualToggle(true);
          } else {
            setLocalOpen(true);
          }
          openClosestExpandables(targetElement);
          requestAnimationFrame(() => {
            targetElement.scrollIntoView();
          });
        }
      }
    };

    checkHashAndExpand();
    window.addEventListener('hashchange', checkHashAndExpand);
    return () => {
      window.removeEventListener('hashchange', checkHashAndExpand);
    };
  }, [shouldUseSessionStorage, onManualToggle]);

  const onChange = (open: boolean) => {
    setShouldRenderChildren(true);

    if (onChangeProp) {
      onChangeProp(open);
    }

    if (open) {
      void openAnalyticsListener({ title });
    } else {
      void closeAnalyticsListener({ title });
    }
  };

  const handleManualToggle = () => {
    const newOpenState = !open;

    if (shouldUseSessionStorage) {
      onManualToggle(newOpenState);
    } else {
      setLocalOpen(newOpenState);
    }

    onChange(newOpenState);
  };

  return (
    <div
      key={title}
      role="listitem"
      className={cn(Classes.Expandable, 'mt-4 border-standard rounded-xl ')}
    >
      <button
        onClick={handleManualToggle}
        className={clsx(
          `not-prose text-sm flex flex-row items-center content-center w-full text-gray-600 hover:text-gray-900 dark:text-gray-300 dark:hover:text-gray-200 py-3 px-3.5 hover:bg-gray-50/50 dark:hover:bg-white/5 rounded-t-xl`,
          !open && 'rounded-b-xl'
        )}
        aria-controls="Children attributes"
        aria-expanded={open}
        data-component-part="expandable-button"
      >
        <Icon
          icon="angle-right"
          className={cn('h-2.5 w-2.5 bg-zinc-400 transition-transform', open && 'rotate-90')}
          data-component-part="expandable-icon"
        />
        <div className="ml-3 leading-tight text-left">
          <p className="m-0" contentEditable={false}>
            {open ? 'Hide' : 'Show'} {title || 'child attributes'}
          </p>
        </div>
      </button>
      <div
        id={title + 'Children'}
        ref={expandableRef}
        className={clsx(
          EXPANDABLE_CONTENT_CLASS,
          'mx-3 px-2 border-t border-gray-100 dark:border-white/10',
          !open && 'hidden'
        )}
        data-component-part={EXPANDABLE_CONTENT_CLASS}
      >
        {shouldRenderChildren && children}
      </div>
    </div>
  );
}
