import { isGroup, isPage } from '@mintlify/common';
import { DecoratedGroupConfig, DecoratedPageOrGroupConfig } from '@mintlify/validation';
import { Fragment, useContext } from 'react';

import { NavigationContext } from '@/contexts/NavigationContext';
import { Classes } from '@/lib/local/selectors';
import { NavItem, NavItemProps } from '@/ui/NavItem';
import { cn } from '@/utils/cn';

import { ComponentIcon } from './Icon';
import { NavTagPill } from './NavTagPill';

type SideNavGroupsProps = {
  nav: DecoratedPageOrGroupConfig[];
  mobile?: boolean;
  navItemProps?: Partial<NavItemProps>;
  showDivider?: boolean;
  shouldLimitMarginWithAnchors?: boolean;
  classNames?: {
    groupLabel?: string;
    icon?: string;
  };
};

export function SideNavGroups({
  nav,
  mobile,
  navItemProps,
  showDivider,
  shouldLimitMarginWithAnchors,
  classNames,
}: SideNavGroupsProps) {
  const { divisions } = useContext(NavigationContext);
  const { sidebarItemStyle, arrowPosition } = navItemProps ?? {};
  const { groupLabel, icon } = classNames ?? {};
  const hasDropdowns = divisions.dropdowns.length > 0;
  const hasAnchors = divisions.anchors.length > 0;

  const marginTop = (i: number) => {
    const marginTopClass = 'mt-12 lg:mt-8';
    if (shouldLimitMarginWithAnchors) {
      if (i !== 0 && showDivider) return 'my-2';
      return cn((!Boolean(i === 0 && !hasAnchors) || hasDropdowns) && marginTopClass);
    }
    return marginTopClass;
  };

  const Divider = ({ as }: { as: 'li' | 'div' }) => {
    const Tag = as;
    return (
      <Tag className="px-1 py-3">
        <div className="h-px w-full bg-gray-100 dark:bg-white/10" />
      </Tag>
    );
  };

  return (
    <>
      {nav
        .map((entry, i) => {
          if (isPage(entry)) {
            const isPreviousEntryGroup = () => {
              if (i === 0) return false;
              const previousEntry = nav[i - 1];
              return isGroup(previousEntry);
            };
            return (
              <Fragment key={entry.href}>
                {showDivider && i > 0 && <Divider as="li" />}
                <ul
                  key={i}
                  className={cn(
                    Classes.SidebarGroup,
                    (isPreviousEntryGroup() || i === 0) && marginTop(i)
                  )}
                >
                  <NavItem
                    key={i}
                    entry={entry}
                    mobile={mobile}
                    sidebarItemStyle={sidebarItemStyle}
                    arrowPosition={arrowPosition}
                  />
                </ul>
              </Fragment>
            );
          }

          const { group, pages } = entry as DecoratedGroupConfig;

          return (
            <Fragment key={`${i} ${group}`}>
              {showDivider && i > 0 && <Divider as="div" />}
              <div key={i} className={marginTop(i)}>
                <div
                  className={cn(
                    Classes.SidebarGroupHeader,
                    'flex items-center gap-2.5 pl-4 mb-3.5 lg:mb-2.5 font-semibold text-gray-900 dark:text-gray-200',
                    groupLabel
                  )}
                >
                  {entry.icon && (
                    <ComponentIcon
                      icon={typeof entry.icon === 'string' ? entry.icon : entry.icon?.name}
                      iconType={entry.iconType || 'regular'}
                      className={cn(Classes.SidebarGroupIcon, 'h-3.5 w-3.5 bg-current', icon)}
                      overrideColor={true}
                    />
                  )}
                  <h5 id={Classes.SidebarTitle}>{group}</h5>
                  {entry.tag && <NavTagPill tag={entry.tag} />}
                </div>

                <ul id={Classes.SidebarGroup}>
                  {pages.map((entry, i) => {
                    return (
                      <NavItem
                        key={i}
                        entry={entry}
                        mobile={mobile}
                        sidebarItemStyle={sidebarItemStyle}
                        arrowPosition={arrowPosition}
                      />
                    );
                  })}
                </ul>
              </div>
            </Fragment>
          );
        })
        .filter(Boolean)}
    </>
  );
}
