'use client';

import { Markdown, MarkdownWithoutHeaders } from '@/ui/Markdown';
import { cn } from '@/utils/cn';

export const Description = ({ markdown, className }: { markdown: string; className?: string }) => {
  return (
    <div className={cn('prose prose-sm prose-gray dark:prose-invert', className)}>
      <Markdown>{markdown}</Markdown>
    </div>
  );
};

export const DescriptionWithoutHeaders = ({
  markdown,
  className,
}: {
  markdown: string;
  className?: string;
}) => {
  return (
    <div className={cn('prose prose-sm prose-gray dark:prose-invert', className)}>
      <MarkdownWithoutHeaders>{markdown}</MarkdownWithoutHeaders>
    </div>
  );
};
