'use client';

import { ApiPlaygroundInputs, UserInfoInputs, SecurityOption } from '@mintlify/validation';

import { loadEncrypted, storeEncrypted } from '@/utils/storeEncrypted';

const CREDENTIALS_LOCALSTORAGE_KEY = 'mintlify-auth';

export type SecurityInputs = Pick<ApiPlaygroundInputs, 'query' | 'header' | 'cookie'>;

export const storeCredentials = (
  securityOption: SecurityOption,
  inputs: SecurityInputs,
  userInfoInputs: UserInfoInputs | undefined
) => {
  const initialCredentialStore = loadCredentialStore();
  const credentialStore = generateCredentialStoreFromInputs(
    securityOption,
    initialCredentialStore,
    inputs,
    userInfoInputs
  );
  storeEncrypted(CREDENTIALS_LOCALSTORAGE_KEY, credentialStore);
};

const generateCredentialStoreFromInputs = (
  securityOption: SecurityOption,
  initialCredentialStore: SecurityInputs,
  inputs: SecurityInputs,
  userInfoInputs: UserInfoInputs | undefined
): SecurityInputs => {
  const credentialStore: SecurityInputs = {
    query: { ...initialCredentialStore.query },
    header: { ...initialCredentialStore.header },
    cookie: { ...initialCredentialStore.cookie },
  };

  for (const section of ['query', 'header', 'cookie'] as const) {
    Object.keys(securityOption.parameters[section]).forEach((name) => {
      // don't store a value if it is identical to the user info value
      const inputValue = inputs[section][name];
      const valueToStore =
        inputValue === userInfoInputs?.[section]?.[name] ? undefined : inputValue;
      credentialStore[section][name] = valueToStore;
    });
  }
  return credentialStore;
};

export function loadCredentialStore(): SecurityInputs {
  return (
    loadEncrypted(CREDENTIALS_LOCALSTORAGE_KEY, isValidInputType) ?? {
      query: {},
      header: {},
      cookie: {},
    }
  );
}

const isValidInputType = (info: unknown): info is SecurityInputs => {
  if (typeof info !== 'object' || info == null) {
    return false;
  }

  const infoObj = info as Record<string, unknown>;
  return ['header', 'cookie', 'query'].every(
    (key) => typeof infoObj[key] === 'object' && infoObj[key] != null
  );
};
