'use client';

import { useMemo } from 'react';

import { useApiPlaygroundInputs as useApiPlaygroundInputsCtx } from '@/hooks/useApiPlaygroundInputs';

export const useApiPlaygroundInputs = () => {
  const [context] = useApiPlaygroundInputsCtx();
  return useMemo(() => {
    return context;
  }, [context]);
};
