'use client';

import { useContext, useMemo } from 'react';

import { DeploymentMetadataContext } from '@/contexts/ConfigContext';
import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';

/**
 * @returns Path with a leading slash, no base path
 */
export function useCurrentPath() {
  const router = useMintlifyRouter();
  const { subdomain } = useContext(DeploymentMetadataContext);

  return useMemo(() => {
    /**
     * NB: `asPath` is _supposed_ to match the path as seen in the browser,
     * but the value differs between the server and client. On the server,
     * `asPath` includes the `/_sites/[subdomain]` routing prefix. Even on
     * the client, `asPath` is not guaranteed to be accurate on page load.
     *
     * There is an `isReady` prop on the router that indicates whether
     * `asPath` is accurate. Rather than banking on that, we just check
     * for the routing prefix and strip it if present.
     */
    const path = router.asPath.replace(`/_sites/${subdomain}`, '');

    // path needs to be decoded to match the path in the sidebar
    const decodedPath = decodeURIComponent(path);

    // strip query and/or hash, if present
    return decodedPath.split(/[?#]/)[0] ?? '/';
  }, [router.asPath, subdomain]);
}
