import { ErrorBoundary } from '@sentry/nextjs';
import { useContext } from 'react';

import { AuthContext } from '@/contexts/ConfigContext';
import { PageContext } from '@/contexts/ConfigContext';
import { Classes } from '@/lib/local/selectors';
import { MdxContent } from '@/ui/MdxContent';
import { cn } from '@/utils/cn';

export function MdxPanel({ mobile }: { mobile?: boolean }) {
  const { pageMetadata, panelMdxSource, panelMdxSourceWithNoJs } = useContext(PageContext);
  const { userInfo } = useContext(AuthContext);

  if (!panelMdxSource || !panelMdxSourceWithNoJs) return null;
  return (
    <div
      className={cn(
        Classes.Panel,
        'w-full xl:w-[28rem] mb-6 relative max-h-[calc(100%-32px)]',
        mobile ? 'mt-8 lg:mt-0 flex flex-col gap-6 xl:hidden' : 'overflow-y-auto'
      )}
    >
      <ErrorBoundary>
        <MdxContent
          mdxSource={panelMdxSource}
          mdxSourceWithNoJs={panelMdxSourceWithNoJs}
          pageMetadata={pageMetadata}
          userInfo={userInfo}
        />
      </ErrorBoundary>
    </div>
  );
}
