'use client';

import { StringSchema } from '@mintlify/validation';
import { useCallback } from 'react';

import { Ids } from '@/lib/local/identifiers';
import { cn } from '@/utils/cn';

import { InputContainer } from '../../components/InputContainer';
import { GenericInput } from './RecursiveInput';

export const StringInput: GenericInput<StringSchema> = ({
  name,
  schema,
  typeDropdown,
  value,
  setValue,
  clearValue,
  parentNames,
  arrayIndex,
  style,
  explode,
}) => {
  const { prefix, placeholder } = getInputAttributesForFormat(schema.format);

  // remove modifications
  const unmodifiedValue =
    prefix && value?.startsWith(prefix) ? value.substring(prefix.length) : value;

  const setModifiedValue = useCallback(
    (newValue: string) => {
      // apply modifications
      const modifiedValue = `${prefix ?? ''}${newValue}`;

      setValue(modifiedValue);
    },
    [setValue, prefix]
  );

  return (
    <InputContainer
      name={name}
      typeDropdown={typeDropdown}
      schema={schema}
      onClear={clearValue}
      parentNames={parentNames}
      arrayIndex={arrayIndex}
      style={style}
      explode={explode}
    >
      <div className="relative flex flex-1 items-center">
        <div className="absolute left-2.5 top-0 bottom-0 flex items-center justify-center text-sm text-gray-800 dark:text-gray-100">
          {prefix}
        </div>
        <input
          id={Ids.APIPlaygroundInput}
          className={cn('flex-1 min-w-0 bg-transparent outline-none text-playground-input')}
          style={{ paddingLeft: prefix ? prefix.length * 0.52 + 'rem' : undefined }}
          placeholder={schema.placeholder ?? placeholder ?? `enter ${name ?? 'value'}`}
          type="text"
          value={unmodifiedValue ?? ''}
          onChange={(event) => setModifiedValue(event.target.value)}
          spellCheck={false}
        />
      </div>
    </InputContainer>
  );
};

type InputAttributes = {
  prefix?: string;
  placeholder?: string;
};

const getInputAttributesForFormat = (format: string | undefined): InputAttributes => {
  switch (format?.toLowerCase()) {
    case 'bearer':
      return {
        prefix: 'Bearer ',
        placeholder: 'enter bearer token',
      };
  }
  return {};
};
