import { ApiPlaygroundInputs, CodeSample, type Endpoint } from '@mintlify/validation';

import { langToPresetMap } from '@/constants/snippetPresets';
import { generateRequest } from '@/openapi/apiExampleGeneration/generators/generateRequest';
import { generateSnippet } from '@/openapi/apiExampleGeneration/generators/generateSnippet';

export const generateInteractiveCodeSample = ({
  endpoint,
  baseUrl,
  inputs,
  apiPlaygroundMode,
  lang,
  requiredOnly,
}: {
  lang: string;
  baseUrl?: string;
  endpoint: Endpoint;
  inputs: ApiPlaygroundInputs;
  apiPlaygroundMode: string;
  requiredOnly?: boolean;
}): CodeSample | undefined => {
  if (baseUrl && apiPlaygroundMode !== 'hide') {
    try {
      const [contentTypeAndContent] = Object.entries(endpoint.request.body);
      let example = undefined;
      let type = undefined;
      if (contentTypeAndContent) {
        const [contentType, content] = contentTypeAndContent;
        const firstExample = Object.values(content.examples)[0];
        example = firstExample?.value;
        type = contentType;
      }
      const request = generateRequest({
        baseUrl,
        endpoint,
        inputs,
        exampleData: example,
        selectedBodyContentType: type,
        requiredOnly,
      });
      const preset = langToPresetMap[lang];
      if (preset == undefined) {
        return;
      }
      const [snippet] = generateSnippet({
        request,
        pathInputs: inputs.path,
        snippetPresets: [preset],
      });
      if (snippet)
        return {
          label: 'Interactive Example',
          lang: snippet.language,
          source: snippet.code,
        };
    } catch (err) {}
  }
};
