import Icon from '@/ui/Icon';
import { ConnectIcon, DisconnectIcon } from '@/ui/icons';

type ConnectButtonProps = {
  connecting?: boolean;
  isConnected?: boolean;
  onClick: () => void;
};

export const ConnectButton = ({ connecting, isConnected, onClick }: ConnectButtonProps) => {
  const label = isConnected ? 'Disconnect' : 'Connect';
  const icon = connecting ? (
    <Icon icon="spinner-third" iconType="duotone" className="w-4 h-4 bg-white animate-spin" />
  ) : isConnected ? (
    <DisconnectIcon />
  ) : (
    <ConnectIcon />
  );
  return (
    <button
      className="flex items-center justify-center px-3 h-9 text-white font-medium rounded-xl mouse-pointer disabled:opacity-70 hover:opacity-80 gap-1.5 bg-[#000000] dark:bg-[#52525c]"
      onClick={onClick}
      disabled={connecting}
    >
      <span>{label}</span>
      {icon}
    </button>
  );
};
