'use client';

import { isAbsoluteUrl } from '@mintlify/common';
import { IconLibrary, IconType, iconTypes } from '@mintlify/models';
import clsx from 'clsx';
import { CSSProperties, useContext } from 'react';

import { DocsConfigContext, PageContext } from '@/contexts/ConfigContext';
import { cn } from '@/utils/cn';

type IconProps = {
  icon: string;
  iconType?: IconType;
  className?: string;
  color?: string;
  overrideColor?: boolean;
  style?: CSSProperties;
  iconLibrary?: IconLibrary;
};

export function getIconUrl(icon: string, iconType?: IconType, iconLibrary?: IconLibrary) {
  if (isBrandsIcon(icon)) {
    return `https://mintlify.b-cdn.net/v6.6.0/brands/${icon}.svg`;
  }
  if (iconLibrary === 'lucide') {
    return `https://mintlify.b-cdn.net/v6.6.0/lucide/${icon}.svg`;
  }
  const type = iconType ?? 'regular';
  return `https://mintlify.b-cdn.net/v6.6.0/${type}/${icon}.svg`;
}

export default function Icon({ style, icon, iconType, iconLibrary, className, color }: IconProps) {
  const { pageType } = useContext(PageContext);
  const isPdf = pageType === 'pdf';
  const url = getIconUrl(icon, iconType, iconLibrary);

  if (isPdf) {
    return (
      <img
        src={url}
        className={className}
        style={{
          backgroundColor: 'transparent',
          ...style,
        }}
        alt={icon}
      />
    );
  }

  return (
    <svg
      className={className}
      style={{
        WebkitMaskImage: `url(${url})`,
        WebkitMaskRepeat: 'no-repeat',
        WebkitMaskPosition: 'center',
        maskImage: `url(${url})`,
        maskRepeat: 'no-repeat',
        maskPosition: 'center',
        maskSize: iconLibrary == 'lucide' ? '100%' : undefined,
        backgroundColor: color,
        ...style,
      }}
    ></svg>
  );
}

export function ComponentIcon({
  icon,
  iconType,
  className,
  style,
  color,
  overrideColor,
}: IconProps) {
  const { docsConfig } = useContext(DocsConfigContext);
  const iconLibrary = docsConfig?.icons?.library;

  // Validate the types
  if (iconType && !iconTypes.includes(iconType)) {
    console.log(
      `Invalid iconType ${iconType} expected a string equal to one of: brands, duotone, light, regular, sharp-solid, solid, thin`
    );
    return null;
  }

  if (typeof icon === 'string' && (isAbsoluteUrl(icon) || icon.startsWith('/'))) {
    return <img src={icon} alt={icon} className={cn(className, 'bg-transparent')} style={style} />;
  }

  return (
    <Icon
      icon={icon.toLowerCase()}
      iconType={iconType}
      iconLibrary={iconLibrary}
      className={clsx(className, !color && !overrideColor && 'bg-gray-800 dark:bg-gray-100')}
      style={style}
      color={color}
    />
  );
}

