'use client';

import { ReactNode, useContext } from 'react';

import { BannerContext } from '@/contexts/BannerContext';
import { usePageMode } from '@/hooks/usePageMode';
import { Ids } from '@/lib/local/identifiers';
import { SidePanel } from '@/ui/Content/SidePanel';
import { cn } from '@/utils/cn';

export const Container = ({ children }: { children: ReactNode }) => {
  const { banner } = useContext(BannerContext);
  const { isWide, isCustom, isCenter } = usePageMode();
  const showSidePanel = !(isWide || isCustom || isCenter);

  return (
    <div
      className={cn(
        'flex flex-row gap-12 box-border w-full',
        isCustom ? 'pt-[120px] lg:pt-0' : `${!!banner ? 'pt-[200px]' : 'pt-40'} lg:pt-10`
      )}
    >
      <div
        className={cn(
          'relative grow box-border flex-col w-full',
          !isCustom && !isCenter && 'mx-auto px-1 lg:pl-[23.7rem] lg:-ml-12 ',
          showSidePanel ? 'xl:w-[calc(100%-28rem)]' : 'xl:w-[calc(100%-19rem)] xl:min-w-full'
        )}
        id={Ids.ContentArea}
      >
        {children}
      </div>
      <SidePanel />
    </div>
  );
};
