import { isPage } from '@mintlify/common';
import { LocaleType } from '@mintlify/models';
import {
  DecoratedNavigationConfig,
  divisions,
  DecoratedLanguageConfig,
  DecoratedVersionConfig,
} from '@mintlify/validation';

import { isEqualIgnoringLeadingSlash } from '../paths/isEqualIgnoringLeadingSlash';
import { isCommonPage } from './isCommonPage';

/**
 * Determine which division contains a given page.
 *
 * @param type The type of division to get
 * @param decoratedNav The decorated navigation. Each page entry must have an `href` field that is prefixed with a `/`
 * @param path The current path of the page, prefixed with a `/`
 * @param defaultValue The default division value to use if the current page occurs multiple times in different divisions
 * @param initialDivisionValue The initial division value to use if the current page is unversioned or unlocalized
 */
export function getVersionOrLanguageFromPath<T extends 'version' | 'language'>(
  type: T,
  entry: DecoratedNavigationConfig,
  path: string,
  defaultValue: T extends 'version' ? string | undefined : LocaleType | undefined,
  initialDivisionValue: T extends 'version' ? string | undefined : LocaleType | undefined
): T extends 'version' ? string : LocaleType | undefined {
  const result = getDivisionFromPathHelper<T>(type, entry, entry, path, defaultValue, undefined);
  const backupResult = defaultValue || initialDivisionValue;
  return (result || backupResult) as T extends 'version' ? string : LocaleType | undefined;
}

type Result<T extends 'version' | 'language'> = T extends 'version'
  ? string
  : LocaleType | undefined;
type NearestDivisionValue<T extends 'version' | 'language'> = T extends 'version'
  ? string | undefined
  : LocaleType | undefined;

function getDivisionFromPathHelper<T extends 'version' | 'language'>(
  type: T,
  entry: DecoratedNavigationConfig,
  parentEntry: DecoratedNavigationConfig,
  path: string,
  defaultValue: T extends 'version' ? string | undefined : LocaleType | undefined,
  nearestDivisionValue: NearestDivisionValue<T>
): Result<T> {
  const undefinedResult = (type === 'version' ? '' : undefined) as Result<T>;

  if (typeof entry !== 'object') return undefinedResult;

  if (isPage(entry)) {
    if (isEqualIgnoringLeadingSlash(entry.href, path)) {
      const isCommon = isCommonPage(parentEntry, path);
      // logic: if this pages was shared in multiple divisions, we should use the default one,
      // if the page was not shared and only used in one division, we should use the nearest division
      if (isCommon && defaultValue && defaultValue !== nearestDivisionValue) return undefinedResult;
      return (
        type === 'version' ? entry.version ?? nearestDivisionValue : nearestDivisionValue
      ) as Result<T>;
    } else {
      return undefinedResult;
    }
  }

  for (const key of [...divisions, 'groups', 'pages']) {
    if (key in entry) {
      const subDivisions = entry[key as keyof typeof entry] as DecoratedNavigationConfig[];
      if (Array.isArray(subDivisions)) {
        for (const subDivision of subDivisions) {
          let divisionValue: NearestDivisionValue<T>;
          if (type === 'version') {
            divisionValue = (
              'version' in subDivision ? (subDivision as DecoratedVersionConfig).version : undefined
            ) as NearestDivisionValue<T>;
          } else {
            divisionValue = (
              'language' in subDivision
                ? (subDivision as DecoratedLanguageConfig).language
                : undefined
            ) as NearestDivisionValue<T>;
          }

          const divisionFromSubDivision = getDivisionFromPathHelper(
            type,
            subDivision,
            parentEntry,
            path,
            defaultValue,
            divisionValue ?? nearestDivisionValue
          );

          if (divisionFromSubDivision && divisionFromSubDivision !== undefinedResult)
            return divisionFromSubDivision;
        }
      }
    }
  }

  return undefinedResult;
}
