import { isPage, replaceSlashIndex } from '@mintlify/common';
import type { DecoratedNavigationPage } from '@mintlify/models';
import { DecoratedPageOrGroupConfig } from '@mintlify/validation';

export const flattenDecoratedNavigationConfig = (
  groupsOrPages: DecoratedPageOrGroupConfig[]
): DecoratedNavigationPage[] => {
  const pages: DecoratedNavigationPage[] = [];

  const traverseDecoratedNavigationConfig = (node: DecoratedPageOrGroupConfig) => {
    if (isPage(node)) {
      pages.push(node);
    }

    if ('pages' in node) {
      for (const page of node.pages) {
        if (isPage(page)) {
          const newPage = { ...page, href: replaceSlashIndex(page.href) };
          pages.push(newPage);
        } else {
          traverseDecoratedNavigationConfig(page);
        }
      }
    }
  };

  for (const node of groupsOrPages) {
    traverseDecoratedNavigationConfig(node);
  }

  return pages;
};
