'use client';

import { isPage } from '@mintlify/common';
import { useContext, useRef } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { NavigationContext } from '@/contexts/NavigationContext';
import { useCurrentPath } from '@/hooks/useCurrentPath';
import { useIsomorphicLayoutEffect } from '@/hooks/useIsomorphicLayoutEffect';
import { Ids } from '@/lib/local/identifiers';
import { SideNavGroups } from '@/ui/SideNavGroups';
import { cn } from '@/utils/cn';
import { nearestScrollableContainer } from '@/utils/nav/nearestScrollableContainer';

import { Dropdowns } from '../mint/MainContentLayout/Dropdowns';
import { TopLevelNavTabsMobile } from '../mint/MainContentLayout/TopLevelNavTabsMobile';
import { Anchors } from '../palm/Nav';

export function SideNav({ mobile = false }: { mobile?: boolean }) {
  const currentPath = useCurrentPath();
  const { divisions } = useContext(NavigationContext);
  const { docsConfig } = useContext(DocsConfigContext);

  const activeItemRef = useRef<HTMLDivElement>();
  const previousActiveItemRef = useRef<HTMLDivElement>();
  const scrollRef = useRef<HTMLDivElement>(null);

  const numPages = divisions.groupsOrPages.reduce((acc, groupOrPage) => {
    if (isPage(groupOrPage)) {
      return acc + 1;
    }
    return acc + groupOrPage.pages.length;
  }, 0);
  useIsomorphicLayoutEffect(() => {
    function updatePreviousRef() {
      previousActiveItemRef.current = activeItemRef.current;
    }

    if (activeItemRef.current) {
      if (activeItemRef.current === previousActiveItemRef.current) {
        updatePreviousRef();
        return;
      }

      updatePreviousRef();

      const scrollable = scrollRef.current
        ? nearestScrollableContainer(scrollRef.current)
        : document.body;

      const scrollRect = scrollable.getBoundingClientRect();
      const activeItemRect = activeItemRef.current.getBoundingClientRect();

      const top = activeItemRef.current.offsetTop;
      const bottom = top - scrollRect.height + activeItemRect.height;

      if (scrollable.scrollTop > top || scrollable.scrollTop < bottom) {
        scrollable.scrollTop = top - scrollRect.height / 2 + activeItemRect.height / 2;
      }
    }
  }, [currentPath]);

  return (
    <div ref={scrollRef} className="relative lg:text-sm lg:leading-6">
      {!mobile && (
        <div
          className={cn(
            'sticky top-0 h-8',
            docsConfig?.background?.image == null &&
              docsConfig?.background?.decoration == null &&
              'z-10 bg-gradient-to-b from-background-light dark:from-background-dark'
          )}
        />
      )}
      <div id={Ids.NavigationItems}>
        <Dropdowns triggerClassName="rounded-lg" />
        {divisions.anchors.length > 0 && <Anchors />}
        {divisions.tabs.length > 0 && mobile && <TopLevelNavTabsMobile />}
        {numPages > 0 && (
          <SideNavGroups
            nav={divisions.groupsOrPages}
            shouldLimitMarginWithAnchors={true}
            navItemProps={{
              sidebarItemStyle: 'arrow',
            }}
          />
        )}
      </div>
    </div>
  );
}
